import React, { useState, useEffect } from 'react';
import styled from 'styled-components';
import { useVisualNovel } from '../../context/VisualNovelContext';

const PanelContainer = styled.div`
  padding: 20px;
  background-color: var(--color-surface);
  border-radius: 8px;
  margin-bottom: 20px;
  transition: background-color 0.3s ease;
`;

const SectionTitle = styled.h3`
  color: var(--color-primary);
  margin-bottom: 15px;
  font-size: 16px;
  border-bottom: 1px solid var(--color-border);
  padding-bottom: 8px;
  transition: color 0.3s ease, border-color 0.3s ease;
`;

const FormGroup = styled.div`
  margin-bottom: 15px;
`;

const Label = styled.label`
  display: block;
  color: var(--color-textPrimary);
  margin-bottom: 5px;
  font-size: 14px;
  transition: color 0.3s ease;
`;

const Input = styled.input`
  width: 100%;
  padding: 8px;
  background-color: var(--color-surfaceSecondary);
  border: 1px solid var(--color-border);
  color: var(--color-textPrimary);
  border-radius: 4px;
  font-size: 14px;
  transition: all 0.3s ease;

  &:focus {
    outline: none;
    border-color: var(--color-primary);
  }
`;

const Select = styled.select`
  width: 100%;
  padding: 8px;
  background-color: var(--color-surfaceSecondary);
  border: 1px solid var(--color-border);
  color: var(--color-textPrimary);
  border-radius: 4px;
  font-size: 14px;
  transition: all 0.3s ease;

  &:focus {
    outline: none;
    border-color: var(--color-primary);
  }
`;

const TextArea = styled.textarea`
  width: 100%;
  padding: 8px;
  background-color: var(--color-surfaceSecondary);
  border: 1px solid var(--color-border);
  color: var(--color-textPrimary);
  border-radius: 4px;
  font-size: 14px;
  min-height: 80px;
  resize: vertical;
  transition: all 0.3s ease;

  &:focus {
    outline: none;
    border-color: var(--color-primary);
  }
`;

const ColorInput = styled.input`
  width: 60px;
  height: 30px;
  padding: 0;
  background-color: var(--color-surfaceSecondary);
  border: 1px solid var(--color-border);
  border-radius: 4px;
  cursor: pointer;
  transition: all 0.3s ease;

  &:focus {
    outline: none;
    border-color: var(--color-primary);
  }
`;

const Row = styled.div`
  display: flex;
  gap: 15px;
  align-items: center;
`;

const Button = styled.button`
  background-color: var(--color-primary);
  color: var(--color-textInverse);
  border: 1px solid var(--color-primary);
  padding: 10px 18px;
  border-radius: 6px;
  cursor: pointer;
  font-size: 14px;
  font-weight: 500;
  margin-right: 10px;
  transition: all 0.3s ease;
  box-shadow: 0 2px 4px var(--color-shadow);

  &:hover {
    background-color: var(--color-primaryHover);
    border-color: var(--color-primaryHover);
    transform: translateY(-1px);
    box-shadow: 0 4px 8px var(--color-shadowHover);
  }

  &:active {
    transform: translateY(0);
    box-shadow: 0 2px 4px var(--color-shadow);
  }

  &.secondary {
    background-color: var(--color-border);
    border-color: var(--color-border);
    
    &:hover {
      background-color: var(--color-borderSecondary);
      border-color: var(--color-borderSecondary);
    }
  }
`;

const CheckboxContainer = styled.div`
  display: flex;
  align-items: center;
  gap: 8px;
`;

const Checkbox = styled.input`
  width: 16px;
  height: 16px;
`;

const InfoText = styled.p`
  color: var(--color-textTertiary);
  font-size: 12px;
  margin-top: 5px;
  font-style: italic;
  transition: color 0.3s ease;
`;

function AdvancedConfigPanel() {
  const { state, dispatch } = useVisualNovel();
  const [config, setConfig] = useState({
    // Configuración básica
    name: state.name || '',
    version: state.version || '1.0',
    author: state.author || '',
    description: state.description || '',
    
    // Configuración de pantalla
    screenWidth: 1280,
    screenHeight: 720,
    
    // Colores de interfaz
    accentColor: '#0099cc',
    idleColor: '#888888',
    hoverColor: '#66c1e0',
    selectedColor: '#ffffff',
    textColor: '#ffffff',
    
    // Fuentes
    textFont: 'DejaVuSans.ttf',
    nameFont: 'DejaVuSans.ttf',
    interfaceFont: 'DejaVuSans.ttf',
    
    // Tamaños de texto
    textSize: 22,
    nameTextSize: 30,
    interfaceTextSize: 22,
    
    // Configuración de diálogo
    textboxHeight: 185,
    textboxYAlign: 1.0,
    nameXPos: 240,
    nameYPos: 0,
    dialogueXPos: 268,
    dialogueYPos: 50,
    dialogueWidth: 744,
    
    // Configuración de botones
    buttonWidth: null,
    buttonHeight: null,
    choiceButtonWidth: 790,
    
    // Configuración de audio
    hasSound: true,
    hasMusic: true,
    hasVoice: true,
    
    // Transiciones
    enterTransition: 'dissolve',
    exitTransition: 'dissolve',
    intraTransition: 'dissolve',
    
    // Configuración de ventana
    windowMode: 'auto',
    
    // Configuración de historial
    historyLength: 250,
    historyHeight: 140,
    
    // Configuración de guardado
    saveDirectory: '',
    thumbnailWidth: 256,
    thumbnailHeight: 144,
    fileSlotCols: 3,
    fileSlotRows: 2,
    
    // Configuración de idioma
    language: 'unicode',
    
    // Configuración de build
    buildName: '',
    googlePlayKey: '',
    itchProject: ''
  });

  // Sincronizar el estado local con el estado del proyecto
  useEffect(() => {
    const projectConfig = state.project.advancedConfig || {};
    const projectInfo = {
      name: state.project.name || '',
      version: state.project.version || '',
      author: state.project.author || '',
      description: state.project.description || ''
    };
    
    setConfig(prev => ({
      ...prev,
      ...projectConfig,
      ...projectInfo
    }));
  }, [state.project]);

  const handleInputChange = (field, value) => {
    setConfig(prev => ({
      ...prev,
      [field]: value
    }));
  };

  const handleSave = () => {
    // Actualizar la configuración avanzada
    dispatch({
      type: 'UPDATE_PROJECT_CONFIG',
      payload: config
    });
    
    // Actualizar la información básica del proyecto
    dispatch({
      type: 'SET_PROJECT_NAME',
      payload: config.name
    });
    
    dispatch({
      type: 'SET_PROJECT_VERSION',
      payload: config.version
    });
    
    dispatch({
      type: 'SET_PROJECT_AUTHOR',
      payload: config.author
    });
    
    dispatch({
      type: 'SET_PROJECT_DESCRIPTION',
      payload: config.description
    });
    
    alert('Configuración guardada correctamente');
  };

  const handleReset = () => {
    if (window.confirm('¿Estás seguro de que quieres resetear la configuración a los valores por defecto?')) {
      setConfig({
        ...config,
        screenWidth: 1280,
        screenHeight: 720,
        accentColor: '#0099cc',
        idleColor: '#888888',
        hoverColor: '#66c1e0',
        selectedColor: '#ffffff',
        textColor: '#ffffff',
        textFont: 'DejaVuSans.ttf',
        nameFont: 'DejaVuSans.ttf',
        interfaceFont: 'DejaVuSans.ttf',
        textSize: 22,
        nameTextSize: 30,
        interfaceTextSize: 22,
        textboxHeight: 185,
        textboxYAlign: 1.0,
        nameXPos: 240,
        nameYPos: 0,
        dialogueXPos: 268,
        dialogueYPos: 50,
        dialogueWidth: 744,
        buttonWidth: null,
        buttonHeight: null,
        choiceButtonWidth: 790,
        hasSound: true,
        hasMusic: true,
        hasVoice: true,
        enterTransition: 'dissolve',
        exitTransition: 'dissolve',
        intraTransition: 'dissolve',
        windowMode: 'auto',
        historyLength: 250,
        historyHeight: 140,
        saveDirectory: '',
        thumbnailWidth: 256,
        thumbnailHeight: 144,
        fileSlotCols: 3,
        fileSlotRows: 2,
        language: 'unicode',
        buildName: '',
        googlePlayKey: '',
        itchProject: ''
      });
    }
  };

  return (
    <PanelContainer>
      <SectionTitle>Configuración Avanzada de Ren'Py</SectionTitle>
      
      {/* Configuración Básica */}
      <SectionTitle>Información del Proyecto</SectionTitle>
      <FormGroup>
        <Label>Nombre del Juego:</Label>
        <Input
          type="text"
          value={config.name}
          onChange={(e) => handleInputChange('name', e.target.value)}
          placeholder="Nombre de tu novela visual"
        />
      </FormGroup>
      
      <Row>
        <FormGroup style={{ flex: 1 }}>
          <Label>Versión:</Label>
          <Input
            type="text"
            value={config.version}
            onChange={(e) => handleInputChange('version', e.target.value)}
            placeholder="1.0"
          />
        </FormGroup>
        <FormGroup style={{ flex: 1 }}>
          <Label>Autor:</Label>
          <Input
            type="text"
            value={config.author}
            onChange={(e) => handleInputChange('author', e.target.value)}
            placeholder="Tu nombre"
          />
        </FormGroup>
      </Row>
      
      <FormGroup>
        <Label>Descripción:</Label>
        <TextArea
          value={config.description}
          onChange={(e) => handleInputChange('description', e.target.value)}
          placeholder="Descripción de tu novela visual"
        />
      </FormGroup>
      
      {/* Configuración de Pantalla */}
      <SectionTitle>Configuración de Pantalla</SectionTitle>
      <Row>
        <FormGroup style={{ flex: 1 }}>
          <Label>Ancho de Pantalla:</Label>
          <Input
            type="number"
            value={config.screenWidth}
            onChange={(e) => handleInputChange('screenWidth', parseInt(e.target.value))}
          />
        </FormGroup>
        <FormGroup style={{ flex: 1 }}>
          <Label>Alto de Pantalla:</Label>
          <Input
            type="number"
            value={config.screenHeight}
            onChange={(e) => handleInputChange('screenHeight', parseInt(e.target.value))}
          />
        </FormGroup>
      </Row>
      
      {/* Colores */}
      <SectionTitle>Colores de Interfaz</SectionTitle>
      <Row>
        <FormGroup>
          <Label>Color Principal:</Label>
          <ColorInput
            type="color"
            value={config.accentColor}
            onChange={(e) => handleInputChange('accentColor', e.target.value)}
          />
        </FormGroup>
        <FormGroup>
          <Label>Color Inactivo:</Label>
          <ColorInput
            type="color"
            value={config.idleColor}
            onChange={(e) => handleInputChange('idleColor', e.target.value)}
          />
        </FormGroup>
        <FormGroup>
          <Label>Color Hover:</Label>
          <ColorInput
            type="color"
            value={config.hoverColor}
            onChange={(e) => handleInputChange('hoverColor', e.target.value)}
          />
        </FormGroup>
        <FormGroup>
          <Label>Color Seleccionado:</Label>
          <ColorInput
            type="color"
            value={config.selectedColor}
            onChange={(e) => handleInputChange('selectedColor', e.target.value)}
          />
        </FormGroup>
      </Row>
      
      {/* Fuentes */}
      <SectionTitle>Fuentes</SectionTitle>
      <Row>
        <FormGroup style={{ flex: 1 }}>
          <Label>Fuente de Texto:</Label>
          <Input
            type="text"
            value={config.textFont}
            onChange={(e) => handleInputChange('textFont', e.target.value)}
            placeholder="DejaVuSans.ttf"
          />
        </FormGroup>
        <FormGroup style={{ flex: 1 }}>
          <Label>Fuente de Nombres:</Label>
          <Input
            type="text"
            value={config.nameFont}
            onChange={(e) => handleInputChange('nameFont', e.target.value)}
            placeholder="DejaVuSans.ttf"
          />
        </FormGroup>
      </Row>
      
      {/* Tamaños de Texto */}
      <SectionTitle>Tamaños de Texto</SectionTitle>
      <Row>
        <FormGroup style={{ flex: 1 }}>
          <Label>Tamaño de Texto:</Label>
          <Input
            type="number"
            value={config.textSize}
            onChange={(e) => handleInputChange('textSize', parseInt(e.target.value))}
          />
        </FormGroup>
        <FormGroup style={{ flex: 1 }}>
          <Label>Tamaño de Nombres:</Label>
          <Input
            type="number"
            value={config.nameTextSize}
            onChange={(e) => handleInputChange('nameTextSize', parseInt(e.target.value))}
          />
        </FormGroup>
        <FormGroup style={{ flex: 1 }}>
          <Label>Tamaño de Interfaz:</Label>
          <Input
            type="number"
            value={config.interfaceTextSize}
            onChange={(e) => handleInputChange('interfaceTextSize', parseInt(e.target.value))}
          />
        </FormGroup>
      </Row>
      
      {/* Configuración de Diálogo */}
      <SectionTitle>Configuración de Diálogo</SectionTitle>
      <Row>
        <FormGroup style={{ flex: 1 }}>
          <Label>Altura de Caja de Texto:</Label>
          <Input
            type="number"
            value={config.textboxHeight}
            onChange={(e) => handleInputChange('textboxHeight', parseInt(e.target.value))}
          />
        </FormGroup>
        <FormGroup style={{ flex: 1 }}>
          <Label>Posición Vertical (0.0-1.0):</Label>
          <Input
            type="number"
            step="0.1"
            min="0"
            max="1"
            value={config.textboxYAlign}
            onChange={(e) => handleInputChange('textboxYAlign', parseFloat(e.target.value))}
          />
        </FormGroup>
      </Row>
      
      <Row>
        <FormGroup style={{ flex: 1 }}>
          <Label>Posición X del Nombre:</Label>
          <Input
            type="number"
            value={config.nameXPos}
            onChange={(e) => handleInputChange('nameXPos', parseInt(e.target.value))}
          />
        </FormGroup>
        <FormGroup style={{ flex: 1 }}>
          <Label>Posición Y del Nombre:</Label>
          <Input
            type="number"
            value={config.nameYPos}
            onChange={(e) => handleInputChange('nameYPos', parseInt(e.target.value))}
          />
        </FormGroup>
        <FormGroup style={{ flex: 1 }}>
          <Label>Ancho del Diálogo:</Label>
          <Input
            type="number"
            value={config.dialogueWidth}
            onChange={(e) => handleInputChange('dialogueWidth', parseInt(e.target.value))}
          />
        </FormGroup>
      </Row>
      
      {/* Configuración de Audio */}
      <SectionTitle>Configuración de Audio</SectionTitle>
      <Row>
        <CheckboxContainer>
          <Checkbox
            type="checkbox"
            checked={config.hasSound}
            onChange={(e) => handleInputChange('hasSound', e.target.checked)}
          />
          <Label>Habilitar Sonidos</Label>
        </CheckboxContainer>
        <CheckboxContainer>
          <Checkbox
            type="checkbox"
            checked={config.hasMusic}
            onChange={(e) => handleInputChange('hasMusic', e.target.checked)}
          />
          <Label>Habilitar Música</Label>
        </CheckboxContainer>
        <CheckboxContainer>
          <Checkbox
            type="checkbox"
            checked={config.hasVoice}
            onChange={(e) => handleInputChange('hasVoice', e.target.checked)}
          />
          <Label>Habilitar Voces</Label>
        </CheckboxContainer>
      </Row>
      
      {/* Transiciones */}
      <SectionTitle>Transiciones</SectionTitle>
      <Row>
        <FormGroup style={{ flex: 1 }}>
          <Label>Transición de Entrada:</Label>
          <Select
            value={config.enterTransition}
            onChange={(e) => handleInputChange('enterTransition', e.target.value)}
          >
            <option value="dissolve">Dissolve</option>
            <option value="fade">Fade</option>
            <option value="pixellate">Pixellate</option>
            <option value="vpunch">Vpunch</option>
            <option value="hpunch">Hpunch</option>
            <option value="None">None</option>
          </Select>
        </FormGroup>
        <FormGroup style={{ flex: 1 }}>
          <Label>Transición de Salida:</Label>
          <Select
            value={config.exitTransition}
            onChange={(e) => handleInputChange('exitTransition', e.target.value)}
          >
            <option value="dissolve">Dissolve</option>
            <option value="fade">Fade</option>
            <option value="pixellate">Pixellate</option>
            <option value="vpunch">Vpunch</option>
            <option value="hpunch">Hpunch</option>
            <option value="None">None</option>
          </Select>
        </FormGroup>
      </Row>
      
      {/* Configuración de Guardado */}
      <SectionTitle>Configuración de Guardado</SectionTitle>
      <Row>
        <FormGroup style={{ flex: 1 }}>
          <Label>Ancho de Miniatura:</Label>
          <Input
            type="number"
            value={config.thumbnailWidth}
            onChange={(e) => handleInputChange('thumbnailWidth', parseInt(e.target.value))}
          />
        </FormGroup>
        <FormGroup style={{ flex: 1 }}>
          <Label>Alto de Miniatura:</Label>
          <Input
            type="number"
            value={config.thumbnailHeight}
            onChange={(e) => handleInputChange('thumbnailHeight', parseInt(e.target.value))}
          />
        </FormGroup>
      </Row>
      
      <Row>
        <FormGroup style={{ flex: 1 }}>
          <Label>Columnas de Guardado:</Label>
          <Input
            type="number"
            value={config.fileSlotCols}
            onChange={(e) => handleInputChange('fileSlotCols', parseInt(e.target.value))}
          />
        </FormGroup>
        <FormGroup style={{ flex: 1 }}>
          <Label>Filas de Guardado:</Label>
          <Input
            type="number"
            value={config.fileSlotRows}
            onChange={(e) => handleInputChange('fileSlotRows', parseInt(e.target.value))}
          />
        </FormGroup>
      </Row>
      
      {/* Configuración de Build */}
      <SectionTitle>Configuración de Build</SectionTitle>
      <FormGroup>
        <Label>Nombre de Build:</Label>
        <Input
          type="text"
          value={config.buildName}
          onChange={(e) => handleInputChange('buildName', e.target.value)}
          placeholder="Nombre para el ejecutable"
        />
        <InfoText>Nombre breve para ejecutables y directorios (solo ASCII, sin espacios)</InfoText>
      </FormGroup>
      
      <FormGroup>
        <Label>Clave de Google Play (opcional):</Label>
        <Input
          type="text"
          value={config.googlePlayKey}
          onChange={(e) => handleInputChange('googlePlayKey', e.target.value)}
          placeholder="Clave para compras en Google Play"
        />
      </FormGroup>
      
      <FormGroup>
        <Label>Proyecto de Itch.io (opcional):</Label>
        <Input
          type="text"
          value={config.itchProject}
          onChange={(e) => handleInputChange('itchProject', e.target.value)}
          placeholder="usuario/proyecto"
        />
        <InfoText>Formato: usuario/proyecto</InfoText>
      </FormGroup>
      
      {/* Botones de Acción */}
      <div style={{ marginTop: '20px', display: 'flex', gap: '10px' }}>
        <Button onClick={handleSave}>
          Guardar Configuración
        </Button>
        <Button className="secondary" onClick={handleReset}>
          Resetear a Valores por Defecto
        </Button>
      </div>
    </PanelContainer>
  );
}

export default AdvancedConfigPanel;
