import React, { useState } from 'react';
import styled from 'styled-components';

const GuideContainer = styled.div`
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: var(--color-background);
  z-index: 3000;
  overflow-y: auto;
  transition: background-color 0.3s ease;
`;

const GuideHeader = styled.div`
  background-color: var(--color-surface);
  padding: 20px;
  border-bottom: 2px solid var(--color-border);
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: sticky;
  top: 0;
  z-index: 3001;
  transition: background-color 0.3s ease, border-color 0.3s ease;
`;

const GuideTitle = styled.h1`
  margin: 0;
  color: var(--color-primary);
  font-size: 28px;
  display: flex;
  align-items: center;
  gap: 15px;
  transition: color 0.3s ease;
`;

const CloseButton = styled.button`
  background-color: var(--color-error);
  color: var(--color-textInverse);
  border: none;
  border-radius: 8px;
  padding: 12px 20px;
  cursor: pointer;
  font-size: 16px;
  font-weight: bold;
  transition: all 0.3s ease;

  &:hover {
    background-color: var(--color-errorHover);
    transform: scale(1.05);
  }
`;

const GuideContent = styled.div`
  padding: 30px;
  max-width: 1200px;
  margin: 0 auto;
`;

const SearchBox = styled.div`
  background-color: var(--color-surfaceSecondary);
  border: 2px solid var(--color-border);
  border-radius: 12px;
  padding: 25px;
  margin-bottom: 30px;
  transition: background-color 0.3s ease, border-color 0.3s ease;
  box-shadow: 0 4px 12px var(--color-shadow);

  &:hover {
    border-color: var(--color-primary);
    box-shadow: 0 6px 16px var(--color-shadowHover);
  }
`;

const SearchTitle = styled.h2`
  color: var(--color-primary);
  margin: 0 0 15px 0;
  font-size: 20px;
  display: flex;
  align-items: center;
  gap: 10px;
  transition: color 0.3s ease;
`;

const SearchInput = styled.input`
  width: 100%;
  padding: 12px 16px;
  background-color: var(--color-backgroundSecondary);
  border: 2px solid var(--color-border);
  border-radius: 8px;
  color: var(--color-textPrimary);
  font-size: 16px;
  font-weight: 500;
  transition: all 0.3s ease;
  box-shadow: 0 2px 6px var(--color-shadow);

  &:focus {
    outline: none;
    border-color: var(--color-primary);
    box-shadow: 0 4px 12px var(--color-shadowHover);
    transform: translateY(-1px);
  }

  &:hover {
    border-color: var(--color-primary);
    box-shadow: 0 3px 8px var(--color-shadowHover);
  }

  &::placeholder {
    color: var(--color-textTertiary);
    font-weight: 400;
  }
`;

const CategoryTabs = styled.div`
  display: flex;
  gap: 10px;
  margin-bottom: 30px;
  flex-wrap: wrap;
`;

const CategoryTab = styled.button`
  background-color: ${props => props.active ? 'var(--color-primary)' : 'var(--color-surfaceSecondary)'};
  color: ${props => props.active ? 'var(--color-textInverse)' : 'var(--color-textPrimary)'};
  border: 2px solid ${props => props.active ? 'var(--color-primary)' : 'var(--color-border)'};
  padding: 14px 22px;
  border-radius: 8px;
  cursor: pointer;
  font-size: 14px;
  font-weight: 700;
  transition: all 0.3s ease;
  box-shadow: 0 3px 8px var(--color-shadow);

  &:hover {
    background-color: ${props => props.active ? 'var(--color-primaryHover)' : 'var(--color-border)'};
    border-color: ${props => props.active ? 'var(--color-primaryHover)' : 'var(--color-primary)'};
    color: var(--color-textInverse);
    transform: translateY(-3px);
    box-shadow: 0 6px 16px var(--color-shadowHover);
  }

  &:active {
    transform: translateY(-1px);
    box-shadow: 0 4px 12px var(--color-shadowHover);
  }
`;

const GuideGrid = styled.div`
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(400px, 1fr));
  gap: 25px;
  margin-bottom: 40px;
`;

const GuideCard = styled.div`
  background-color: var(--color-surfaceSecondary);
  border: 2px solid var(--color-border);
  border-radius: 12px;
  padding: 24px;
  transition: all 0.3s ease;
  box-shadow: 0 4px 12px var(--color-shadow);

  &:hover {
    border-color: var(--color-primary);
    background-color: var(--color-surfaceElevated);
    transform: translateY(-8px);
    box-shadow: 0 12px 32px var(--color-shadowHover);
  }
`;

const GuideCardHeader = styled.div`
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  margin-bottom: 15px;
`;

const GuideCardTitle = styled.h3`
  margin: 0;
  color: var(--color-primary);
  font-size: 18px;
  font-weight: 700;
  display: flex;
  align-items: center;
  gap: 10px;
  transition: color 0.3s ease;
  text-shadow: 0 1px 2px var(--color-shadow);
`;

const GuideCategory = styled.span`
  background-color: ${props => {
    switch(props.category) {
      case 'getting-started': return 'var(--color-success)';
      case 'language': return 'var(--color-info)';
      case 'customization': return 'var(--color-warning)';
      case 'advanced': return 'var(--color-primary)';
      case 'tools': return 'var(--color-error)';
      case 'community': return 'var(--color-border)';
      default: return 'var(--color-borderSecondary)';
    }
  }};
  color: var(--color-textInverse);
  padding: 6px 12px;
  border-radius: 6px;
  font-size: 12px;
  font-weight: 700;
  transition: all 0.3s ease;
  box-shadow: 0 2px 6px var(--color-shadow);
  border: 1px solid rgba(255, 255, 255, 0.1);

  &:hover {
    transform: translateY(-1px);
    box-shadow: 0 4px 8px var(--color-shadowHover);
  }
`;

const GuideDescription = styled.p`
  color: var(--color-textSecondary);
  margin: 0 0 15px 0;
  line-height: 1.6;
  font-size: 15px;
  font-weight: 500;
  transition: color 0.3s ease;
`;

const CodeExample = styled.div`
  background-color: var(--color-backgroundSecondary);
  border: 2px solid var(--color-border);
  border-radius: 8px;
  padding: 16px;
  margin: 15px 0;
  font-family: 'Courier New', monospace;
  font-size: 13px;
  font-weight: 500;
  color: var(--color-primary);
  overflow-x: auto;
  transition: all 0.3s ease;
  box-shadow: 0 2px 6px var(--color-shadow);

  &:hover {
    border-color: var(--color-primary);
    box-shadow: 0 4px 12px var(--color-shadowHover);
  }
`;

const InfoBox = styled.div`
  background-color: var(--color-surfaceSecondary);
  border: 2px solid var(--color-primary);
  border-radius: 12px;
  padding: 20px;
  margin: 20px 0;
  transition: all 0.3s ease;
  box-shadow: 0 4px 16px var(--color-shadow);

  &:hover {
    border-color: var(--color-primaryHover);
    box-shadow: 0 6px 20px var(--color-shadowHover);
    transform: translateY(-2px);
  }
`;

const InfoTitle = styled.div`
  color: var(--color-primary);
  font-weight: 700;
  font-size: 16px;
  margin-bottom: 12px;
  display: flex;
  align-items: center;
  gap: 8px;
  transition: color 0.3s ease;
  text-shadow: 0 1px 2px var(--color-shadow);
`;

const InfoText = styled.div`
  color: var(--color-textPrimary);
  font-size: 15px;
  line-height: 1.6;
  font-weight: 500;
  transition: color 0.3s ease;
`;

const TipBox = styled.div`
  background-color: var(--color-surfaceSecondary);
  border: 2px solid var(--color-warning);
  border-radius: 12px;
  padding: 20px;
  margin: 20px 0;
  transition: all 0.3s ease;
  box-shadow: 0 4px 16px var(--color-shadow);

  &:hover {
    border-color: var(--color-warningHover);
    box-shadow: 0 6px 20px var(--color-shadowHover);
    transform: translateY(-2px);
  }
`;

const TipTitle = styled.div`
  color: var(--color-warning);
  font-weight: 700;
  font-size: 16px;
  margin-bottom: 12px;
  display: flex;
  align-items: center;
  gap: 8px;
  transition: color 0.3s ease;
  text-shadow: 0 1px 2px var(--color-shadow);
`;

const TipText = styled.div`
  color: var(--color-textPrimary);
  font-size: 15px;
  line-height: 1.6;
  font-weight: 500;
  transition: color 0.3s ease;
`;

const LinkButton = styled.a`
  display: inline-block;
  background-color: var(--color-primary);
  color: var(--color-textInverse);
  text-decoration: none;
  padding: 12px 20px;
  border-radius: 8px;
  font-size: 14px;
  font-weight: 700;
  transition: all 0.3s ease;
  margin: 5px 5px 5px 0;
  box-shadow: 0 3px 8px var(--color-shadow);
  border: 2px solid var(--color-primary);

  &:hover {
    background-color: var(--color-primaryHover);
    border-color: var(--color-primaryHover);
    transform: translateY(-2px) scale(1.02);
    text-decoration: none;
    color: var(--color-textInverse);
    box-shadow: 0 6px 16px var(--color-shadowHover);
  }

  &:active {
    transform: translateY(0) scale(0.98);
    box-shadow: 0 3px 8px var(--color-shadow);
  }
`;

function GuidePage({ onClose }) {
  const [activeCategory, setActiveCategory] = useState('getting-started');
  const [searchTerm, setSearchTerm] = useState('');

  const guideData = {
    'getting-started': [
      {
        id: 'quickstart',
        title: 'Guía de Inicio Rápido',
        icon: '🚀',
        category: 'getting-started',
        description: 'Aprende los conceptos básicos de Ren\'Py en minutos. Perfecto para comenzar tu primera novela visual.',
        example: `# Tu primer juego Ren'Py
label start:
    "¡Hola! Este es mi primer juego."
    "¿Cómo te llamas?"
    $ name = renpy.input("¿Cuál es tu nombre?")
    "[name], ¡bienvenido a Ren'Py!"`,
        tip: 'Comienza con el tutorial oficial de Ren\'Py para entender los conceptos básicos.',
        links: [
          { text: 'Documentación Oficial', url: 'https://www.renpy.org/doc/html/' },
          { text: 'Quickstart Guide', url: 'https://www.renpy.org/doc/html/quickstart.html' }
        ]
      },
      {
        id: 'installation',
        title: 'Instalación y Configuración',
        icon: '⚙️',
        category: 'getting-started',
        description: 'Cómo descargar, instalar y configurar Ren\'Py en tu sistema.',
        example: `# Pasos de instalación:
# 1. Descargar Ren'Py desde renpy.org
# 2. Extraer en una carpeta
# 3. Ejecutar renpy.exe (Windows) o renpy.sh (Linux/Mac)
# 4. Crear un nuevo proyecto`,
        tip: 'Ren\'Py es multiplataforma y funciona en Windows, Mac y Linux.',
        links: [
          { text: 'Descargar Ren\'Py', url: 'https://www.renpy.org/latest.html' }
        ]
      },
      {
        id: 'first-project',
        title: 'Crear tu Primer Proyecto',
        icon: '🎮',
        category: 'getting-started',
        description: 'Guía paso a paso para crear tu primera novela visual con Ren\'Py.',
        example: `# Estructura básica de un proyecto:
game/
├── script.rpy      # Tu historia
├── images/         # Imágenes del juego
├── audio/          # Música y sonidos
└── gui/           # Elementos de interfaz`,
        tip: 'Usa el Launcher de Ren\'Py para crear proyectos fácilmente.',
        links: [
          { text: 'Tutorial Completo', url: 'https://www.renpy.org/doc/html/quickstart.html' }
        ]
      }
    ],
    'language': [
      {
        id: 'basic-syntax',
        title: 'Sintaxis Básica de Ren\'Py',
        icon: '📝',
        category: 'language',
        description: 'Aprende los elementos fundamentales del lenguaje de Ren\'Py.',
        example: `# Diálogos
character "Hola, ¿cómo estás?"

# Narración
"Esta es una línea de narración."

# Etiquetas
label start:
    "El juego comienza aquí"

# Saltos
jump another_scene`,
        tip: 'Ren\'Py usa indentación para estructurar el código, similar a Python.',
        links: [
          { text: 'Language Basics', url: 'https://www.renpy.org/doc/html/language_basics.html' }
        ]
      },
      {
        id: 'characters',
        title: 'Sistema de Personajes',
        icon: '👤',
        category: 'language',
        description: 'Cómo crear y usar personajes en tus diálogos.',
        example: `# Definir personajes
define eileen = Character("Eileen", color="#c8ffc8")
define narrator = Character(None)

# Usar personajes
eileen "¡Hola! Soy Eileen."
narrator "El narrador habla sin nombre visible."`,
        tip: 'Los personajes pueden tener colores, voces y comportamientos personalizados.',
        links: [
          { text: 'Dialogue and Narration', url: 'https://www.renpy.org/doc/html/dialogue.html' }
        ]
      },
      {
        id: 'menus',
        title: 'Menús y Decisiones',
        icon: '🎯',
        category: 'language',
        description: 'Crear menús interactivos y decisiones que afecten la historia.',
        example: `menu:
    "¿Qué quieres hacer?"
    
    "Ir a la izquierda":
        jump left_path
    
    "Ir a la derecha":
        jump right_path
    
    "Quedarse aquí":
        "Decidiste quedarte."`,
        tip: 'Los menús deben estar correctamente indentados para funcionar.',
        links: [
          { text: 'In-Game Menus', url: 'https://www.renpy.org/doc/html/menus.html' }
        ]
      }
    ],
    'customization': [
      {
        id: 'gui-customization',
        title: 'Personalización de GUI',
        icon: '🎨',
        category: 'customization',
        description: 'Cómo personalizar la apariencia de tu juego.',
        example: `# En gui.rpy
define gui.accent_color = '#ff6b6b'
define gui.text_font = "DejaVuSans.ttf"
define gui.text_size = 33

# En screens.rpy
screen main_menu():
    add gui.main_menu_background`,
        tip: 'La personalización de GUI se hace principalmente en gui.rpy y screens.rpy.',
        links: [
          { text: 'GUI Customization Guide', url: 'https://www.renpy.org/doc/html/gui.html' }
        ]
      },
      {
        id: 'images',
        title: 'Manejo de Imágenes',
        icon: '🖼️',
        category: 'customization',
        description: 'Cómo mostrar y manipular imágenes en tu juego.',
        example: `# Mostrar imágenes
show eileen happy at center
scene bg room
hide eileen

# Con transiciones
show eileen sad at center with fade
scene bg forest with dissolve`,
        tip: 'Las imágenes deben estar en la carpeta images/ para ser reconocidas.',
        links: [
          { text: 'Displaying Images', url: 'https://www.renpy.org/doc/html/displaying_images.html' }
        ]
      },
      {
        id: 'audio',
        title: 'Audio y Música',
        icon: '🎵',
        category: 'customization',
        description: 'Cómo agregar música y efectos de sonido a tu juego.',
        example: `# Reproducir música
play music "audio/bgm/main_theme.ogg"

# Reproducir efectos
play sound "audio/sfx/click.ogg"

# Reproducir voz
play voice "audio/voice/eileen_001.ogg"`,
        tip: 'Ren\'Py soporta formatos OGG, MP3 y WAV para audio.',
        links: [
          { text: 'Audio Documentation', url: 'https://www.renpy.org/doc/html/audio.html' }
        ]
      }
    ],
    'advanced': [
      {
        id: 'python',
        title: 'Python en Ren\'Py',
        icon: '🐍',
        category: 'advanced',
        description: 'Usar código Python para lógica compleja en tu juego.',
        example: `# Bloque de Python
python:
    player_score = 100
    enemy_score = 80
    
    if player_score > enemy_score:
        winner = "Jugador"
    else:
        winner = "Enemigo"

"El ganador es [winner]!"`,
        tip: 'Python se integra perfectamente con Ren\'Py para lógica compleja.',
        links: [
          { text: 'Python Statements', url: 'https://www.renpy.org/doc/html/python.html' }
        ]
      },
      {
        id: 'screens',
        title: 'Screens y Screen Language',
        icon: '🖥️',
        category: 'advanced',
        description: 'Crear interfaces personalizadas usando Screen Language.',
        example: `screen inventory():
    tag menu
    
    vbox:
        text "Inventario" size 40
        
        for item in inventory:
            textbutton item action Return(item)`,
        tip: 'Screen Language permite crear interfaces complejas sin programar.',
        links: [
          { text: 'Screens and Screen Language', url: 'https://www.renpy.org/doc/html/screens.html' }
        ]
      },
      {
        id: 'transforms',
        title: 'Transforms y Transiciones',
        icon: '🔄',
        category: 'advanced',
        description: 'Crear efectos visuales y animaciones avanzadas.',
        example: `# Definir transform
transform fadein:
    alpha 0.0
    linear 1.0 alpha 1.0

# Usar transform
show eileen happy at fadein

# Transiciones personalizadas
scene bg room with pixellate`,
        tip: 'Los transforms permiten crear animaciones complejas y efectos visuales.',
        links: [
          { text: 'Transforms', url: 'https://www.renpy.org/doc/html/transforms.html' },
          { text: 'Transitions', url: 'https://www.renpy.org/doc/html/transitions.html' }
        ]
      }
    ],
    'tools': [
      {
        id: 'launcher',
        title: 'Ren\'Py Launcher',
        icon: '🚀',
        category: 'tools',
        description: 'Herramientas del launcher para gestionar proyectos.',
        example: `# Funciones del launcher:
# - Crear nuevos proyectos
# - Editar proyectos existentes
# - Compilar y distribuir
# - Actualizar Ren'Py
# - Verificar scripts`,
        tip: 'El launcher es tu centro de control para todos los proyectos Ren\'Py.',
        links: [
          { text: 'Launcher Documentation', url: 'https://www.renpy.org/doc/html/launcher.html' }
        ]
      },
      {
        id: 'developer-tools',
        title: 'Developer Tools',
        icon: '🔧',
        category: 'tools',
        description: 'Herramientas avanzadas para desarrolladores.',
        example: `# Herramientas disponibles:
# - Interactive Director
# - Performance Monitor
# - Memory Profiler
# - Script Lint`,
        tip: 'Las developer tools ayudan a depurar y optimizar tu juego.',
        links: [
          { text: 'Developer Tools', url: 'https://www.renpy.org/doc/html/developer_tools.html' }
        ]
      },
      {
        id: 'building',
        title: 'Building Distributions',
        icon: '📦',
        category: 'tools',
        description: 'Cómo compilar y distribuir tu juego para diferentes plataformas.',
        example: `# Plataformas soportadas:
# - Windows (exe)
# - Mac (app)
# - Linux (tar.bz2)
# - Android (apk)
# - iOS (ipa)
# - Web (HTML5)`,
        tip: 'Ren\'Py puede compilar para múltiples plataformas automáticamente.',
        links: [
          { text: 'Building Distributions', url: 'https://www.renpy.org/doc/html/build.html' }
        ]
      }
    ],
    'community': [
      {
        id: 'forums',
        title: 'Lemma Soft Forums',
        icon: '👥',
        category: 'community',
        description: 'La comunidad oficial de Ren\'Py donde puedes obtener ayuda y compartir tu trabajo.',
        example: `# Secciones importantes:
# - Ren'Py Cookbook (código reutilizable)
# - Works in Progress (proyectos en desarrollo)
# - Completed Games (juegos terminados)
# - Technical Questions (ayuda técnica)`,
        tip: 'Los foros son el mejor lugar para obtener ayuda de la comunidad.',
        links: [
          { text: 'Ren\'Py Forums', url: 'https://lemmasoft.renai.us/forums/viewforum.php?f=51' }
        ]
      },
      {
        id: 'cookbook',
        title: 'Ren\'Py Cookbook',
        icon: '📚',
        category: 'community',
        description: 'Biblioteca de código y ejemplos reutilizables para Ren\'Py.',
        example: `# Tipos de código en el cookbook:
# - Sistemas de inventario
# - Minijuegos
# - Efectos visuales
# - Sistemas de guardado
# - Personalización de GUI`,
        tip: 'El cookbook contiene código probado y documentado por la comunidad.',
        links: [
          { text: 'Ren\'Py Cookbook', url: 'https://lemmasoft.renai.us/forums/viewforum.php?f=51' }
        ]
      },
      {
        id: 'resources',
        title: 'Recursos de la Comunidad',
        icon: '🎨',
        category: 'community',
        description: 'Recursos gratuitos y herramientas creadas por la comunidad.',
        example: `# Recursos disponibles:
# - Sprites y fondos gratuitos
# - Música libre de derechos
# - Herramientas de desarrollo
# - Tutoriales y guías
# - Templates de proyectos`,
        tip: 'La comunidad de Ren\'Py es muy generosa compartiendo recursos gratuitos.',
        links: [
          { text: 'Community Resources', url: 'https://lemmasoft.renai.us/forums/' }
        ]
      }
    ]
  };

  const getCurrentGuides = () => {
    let currentGuides = guideData[activeCategory] || [];
    
    if (searchTerm) {
      currentGuides = currentGuides.filter(guide =>
        guide.title.toLowerCase().includes(searchTerm.toLowerCase()) ||
        guide.description.toLowerCase().includes(searchTerm.toLowerCase()) ||
        guide.example.toLowerCase().includes(searchTerm.toLowerCase())
      );
    }
    
    return currentGuides;
  };

  const getCategoryText = (category) => {
    switch(category) {
      case 'getting-started': return 'Inicio';
      case 'language': return 'Lenguaje';
      case 'customization': return 'Personalización';
      case 'advanced': return 'Avanzado';
      case 'tools': return 'Herramientas';
      case 'community': return 'Comunidad';
      default: return 'Otro';
    }
  };

  return (
    <GuideContainer>
      <GuideHeader>
        <GuideTitle>
          📖 Guía Completa de Ren'Py
        </GuideTitle>
        <CloseButton onClick={onClose}>
          ✕ Cerrar
        </CloseButton>
      </GuideHeader>

      <GuideContent>
        <SearchBox>
          <SearchTitle>
            🔍 Buscar en la Guía
          </SearchTitle>
          <SearchInput
            type="text"
            placeholder="Busca temas específicos, conceptos o funciones..."
            value={searchTerm}
            onChange={(e) => setSearchTerm(e.target.value)}
          />
        </SearchBox>

        <CategoryTabs>
          <CategoryTab 
            active={activeCategory === 'getting-started'} 
            onClick={() => setActiveCategory('getting-started')}
          >
            🚀 Inicio
          </CategoryTab>
          <CategoryTab 
            active={activeCategory === 'language'} 
            onClick={() => setActiveCategory('language')}
          >
            📝 Lenguaje
          </CategoryTab>
          <CategoryTab 
            active={activeCategory === 'customization'} 
            onClick={() => setActiveCategory('customization')}
          >
            🎨 Personalización
          </CategoryTab>
          <CategoryTab 
            active={activeCategory === 'advanced'} 
            onClick={() => setActiveCategory('advanced')}
          >
            🔧 Avanzado
          </CategoryTab>
          <CategoryTab 
            active={activeCategory === 'tools'} 
            onClick={() => setActiveCategory('tools')}
          >
            🛠️ Herramientas
          </CategoryTab>
          <CategoryTab 
            active={activeCategory === 'community'} 
            onClick={() => setActiveCategory('community')}
          >
            👥 Comunidad
          </CategoryTab>
        </CategoryTabs>

        <GuideGrid>
          {getCurrentGuides().map((guide) => (
            <GuideCard key={guide.id}>
              <GuideCardHeader>
                <GuideCardTitle>
                  {guide.icon} {guide.title}
                </GuideCardTitle>
                <GuideCategory category={guide.category}>
                  {getCategoryText(guide.category)}
                </GuideCategory>
              </GuideCardHeader>
              
              <GuideDescription>
                {guide.description}
              </GuideDescription>

              <CodeExample>
                {guide.example}
              </CodeExample>

              {guide.tip && (
                <TipBox>
                  <TipTitle>
                    💡 Consejo
                  </TipTitle>
                  <TipText>
                    {guide.tip}
                  </TipText>
                </TipBox>
              )}

              {guide.links && guide.links.length > 0 && (
                <InfoBox>
                  <InfoTitle>
                    🔗 Enlaces Útiles
                  </InfoTitle>
                  <InfoText>
                    {guide.links.map((link, index) => (
                      <LinkButton 
                        key={index}
                        href={link.url} 
                        target="_blank" 
                        rel="noopener noreferrer"
                      >
                        {link.text}
                      </LinkButton>
                    ))}
                  </InfoText>
                </InfoBox>
              )}
            </GuideCard>
          ))}
        </GuideGrid>

        {getCurrentGuides().length === 0 && searchTerm && (
          <div style={{ 
            textAlign: 'center', 
            color: 'var(--color-textTertiary)', 
            fontSize: '18px',
            marginTop: '50px'
          }}>
            No se encontraron guías que coincidan con "{searchTerm}"
          </div>
        )}

        <InfoBox>
          <InfoTitle>
            📚 Información Importante
          </InfoTitle>
          <InfoText>
            Esta guía está basada en la documentación oficial de Ren'Py y recursos de la comunidad. 
            Cada sección incluye ejemplos prácticos y enlaces directos a la documentación oficial.
          </InfoText>
        </InfoBox>

        <TipBox>
          <TipTitle>
            💡 Cómo Usar Esta Guía
          </TipTitle>
          <TipText>
            Navega por las categorías para encontrar el tema que necesitas, o usa la búsqueda para encontrar conceptos específicos. 
            Todos los enlaces abren la documentación oficial de Ren'Py en una nueva pestaña.
          </TipText>
        </TipBox>

        <InfoBox>
          <InfoTitle>
            🔗 Recursos Oficiales
          </InfoTitle>
          <InfoText>
            Para información más detallada, consulta siempre:
            <br />• <a href="https://www.renpy.org/doc/html/" target="_blank" rel="noopener noreferrer" style={{color: 'var(--color-primary)'}}>Documentación Oficial de Ren'Py</a>
            <br />• <a href="https://www.renpy.org/doc/html/quickstart.html" target="_blank" rel="noopener noreferrer" style={{color: 'var(--color-primary)'}}>Guía de Inicio Rápido</a>
            <br />• <a href="https://lemmasoft.renai.us/forums/viewforum.php?f=51" target="_blank" rel="noopener noreferrer" style={{color: 'var(--color-primary)'}}>Ren'Py Cookbook</a>
          </InfoText>
        </InfoBox>
      </GuideContent>
    </GuideContainer>
  );
}

export default GuidePage;
