// Sistema de backup con archivos .ryn
import JSZip from 'jszip';
import { saveAs } from 'file-saver';
import { AssetStorage } from './assetStorage';
import { FileManager } from './fileManager';

export class RynBackupManager {
  constructor() {
    this.assetStorage = new AssetStorage();
    this.fileManager = new FileManager();
    this.rynVersion = '1.0.0';
  }

  // ===== EXPORTAR PROYECTO COMO .ryn =====

  /**
   * Exportar proyecto completo como archivo .ryn
   * @param {Object} project - Proyecto completo del editor
   * @param {Object} fullState - Estado completo de la aplicación
   * @param {string} filename - Nombre del archivo (opcional)
   */
  async exportProjectAsRyn(project, fullState, filename = null) {
    try {
      console.log('🔄 Iniciando exportación .ryn...');
      
      const zip = new JSZip();
      
      // 1. METADATOS DEL PROYECTO
      const projectMetadata = {
        rynVersion: this.rynVersion,
        editorVersion: '1.0.0',
        exportedAt: new Date().toISOString(),
        projectName: project.name,
        projectVersion: project.version,
        author: project.author,
        description: project.description,
        totalSize: 0,
        fileCount: 0
      };

      // 2. ESTADO COMPLETO DEL EDITOR
      const editorState = {
        project: project,
        currentScene: fullState.currentScene,
        selectedElement: fullState.selectedElement,
        previewMode: fullState.previewMode,
        isPlaying: fullState.isPlaying,
        lastActiveTab: this.getLastActiveTab(),
        lastScrollPosition: this.getLastScrollPosition()
      };

      // 3. ARCHIVOS MULTIMEDIA
      const mediaFiles = await this.exportMediaFiles();
      
      // 4. CONFIGURACIÓN AVANZADA
      const advancedConfig = project.advancedConfig || {};

      // 5. HISTORIAL DE CAMBIOS (si existe)
      const changeHistory = this.getChangeHistory();

      // 6. PREFERENCIAS DEL USUARIO
      const userPreferences = this.getUserPreferences();

      // Crear estructura del archivo .ryn
      zip.file('metadata.json', JSON.stringify(projectMetadata, null, 2));
      zip.file('editor_state.json', JSON.stringify(editorState, null, 2));
      zip.file('advanced_config.json', JSON.stringify(advancedConfig, null, 2));
      zip.file('change_history.json', JSON.stringify(changeHistory, null, 2));
      zip.file('user_preferences.json', JSON.stringify(userPreferences, null, 2));

      // Agregar archivos multimedia organizados
      if (mediaFiles.backgrounds.length > 0) {
        const bgFolder = zip.folder('media/backgrounds');
        mediaFiles.backgrounds.forEach(file => {
          bgFolder.file(file.name, this.base64ToBlob(file.data, file.type));
        });
      }

      if (mediaFiles.characters.length > 0) {
        const charFolder = zip.folder('media/characters');
        mediaFiles.characters.forEach(file => {
          charFolder.file(file.name, this.base64ToBlob(file.data, file.type));
        });
      }

      if (mediaFiles.videos.length > 0) {
        const videoFolder = zip.folder('media/videos');
        mediaFiles.videos.forEach(file => {
          videoFolder.file(file.name, this.base64ToBlob(file.data, file.type));
        });
      }

      if (mediaFiles.audio.length > 0) {
        const audioFolder = zip.folder('media/audio');
        mediaFiles.audio.forEach(file => {
          audioFolder.file(file.name, this.base64ToBlob(file.data, file.type));
        });
      }

      // 7. SCRIPTS REN'PY GENERADOS (para referencia)
      const renpyScripts = await this.generateRenPyScripts(project);
      if (renpyScripts.main) {
        zip.file('renpy/script.rpy', renpyScripts.main);
      }
      if (renpyScripts.scenes) {
        Object.entries(renpyScripts.scenes).forEach(([fileName, content]) => {
          zip.file(`renpy/${fileName}`, content);
        });
      }

      // 8. README DEL PROYECTO
      const readme = this.generateProjectReadme(project, projectMetadata);
      zip.file('README.md', readme);

      // 9. ARCHIVO DE VERIFICACIÓN
      const verification = {
        checksum: await this.generateChecksum(project),
        fileCount: this.countFiles(zip),
        totalSize: this.calculateTotalSize(zip),
        verifiedAt: new Date().toISOString()
      };
      zip.file('verification.json', JSON.stringify(verification, null, 2));

      // Generar archivo .ryn
      const content = await zip.generateAsync({ 
        type: 'blob',
        compression: 'DEFLATE',
        compressionOptions: { level: 6 }
      });

      // Descargar archivo
      const finalFilename = filename || `${project.name.replace(/[^a-zA-Z0-9]/g, '_')}_${new Date().toISOString().split('T')[0]}.ryn`;
      saveAs(content, finalFilename);

      console.log('✅ Archivo .ryn exportado exitosamente:', finalFilename);
      return { success: true, filename: finalFilename, size: content.size };

    } catch (error) {
      console.error('❌ Error al exportar .ryn:', error);
      return { success: false, error: error.message };
    }
  }

  // ===== IMPORTAR PROYECTO DESDE .ryn =====

  /**
   * Importar proyecto desde archivo .ryn
   * @param {File} rynFile - Archivo .ryn seleccionado
   */
  async importProjectFromRyn(rynFile) {
    try {
      console.log('🔄 Iniciando importación .ryn...');
      
      const zip = await JSZip.loadAsync(rynFile);
      
      // 1. Verificar archivo .ryn válido
      if (!zip.file('metadata.json')) {
        throw new Error('Archivo .ryn inválido: falta metadata.json');
      }

      // 2. Cargar metadatos
      const metadata = JSON.parse(await zip.file('metadata.json').async('text'));
      console.log('📋 Metadatos cargados:', metadata);

      // 3. Cargar estado del editor
      const editorState = JSON.parse(await zip.file('editor_state.json').async('text'));
      console.log('🎮 Estado del editor cargado');

      // 4. Cargar archivos multimedia
      const mediaFiles = await this.importMediaFiles(zip);
      console.log('📁 Archivos multimedia cargados:', mediaFiles);

      // 5. Restaurar configuración avanzada
      const advancedConfig = JSON.parse(await zip.file('advanced_config.json').async('text'));
      console.log('⚙️ Configuración avanzada cargada');

      // 6. Verificar integridad
      const verification = JSON.parse(await zip.file('verification.json').async('text'));
      const currentChecksum = await this.generateChecksum(editorState.project);
      
      if (verification.checksum !== currentChecksum) {
        console.warn('⚠️ Advertencia: El archivo puede estar corrupto');
      }

      // 7. Restaurar preferencias del usuario
      const userPreferences = JSON.parse(await zip.file('user_preferences.json').async('text'));
      this.restoreUserPreferences(userPreferences);

      console.log('✅ Proyecto .ryn importado exitosamente');
      return {
        success: true,
        project: editorState.project,
        fullState: editorState,
        metadata: metadata,
        mediaFiles: mediaFiles
      };

    } catch (error) {
      console.error('❌ Error al importar .ryn:', error);
      return { success: false, error: error.message };
    }
  }

  // ===== MÉTODOS AUXILIARES =====

  /**
   * Exportar archivos multimedia organizados por categoría
   */
  async exportMediaFiles() {
    try {
      await this.assetStorage.init();
      const allFiles = await this.assetStorage.getAllFiles();
      
      const organizedFiles = {
        backgrounds: [],
        characters: [],
        videos: [],
        audio: [],
        gui: [],
        effects: []
      };

      allFiles.forEach(file => {
        const category = file.category || 'general';
        if (organizedFiles[category]) {
          organizedFiles[category].push({
            name: file.name,
            data: file.data,
            type: file.type,
            size: file.size,
            uploadedAt: file.uploadedAt
          });
        }
      });

      return organizedFiles;
    } catch (error) {
      console.error('Error al exportar archivos multimedia:', error);
      return { backgrounds: [], characters: [], videos: [], audio: [], gui: [], effects: [] };
    }
  }

  /**
   * Importar archivos multimedia desde ZIP
   */
  async importMediaFiles(zip) {
    const mediaFiles = { backgrounds: [], characters: [], videos: [], audio: [] };
    
    try {
      // Importar fondos
      const bgFolder = zip.folder('media/backgrounds');
      if (bgFolder) {
        bgFolder.forEach(async (relativePath, file) => {
          const content = await file.async('base64');
          mediaFiles.backgrounds.push({
            name: relativePath,
            data: `data:image/jpeg;base64,${content}`,
            category: 'backgrounds'
          });
        });
      }

      // Importar personajes
      const charFolder = zip.folder('media/characters');
      if (charFolder) {
        charFolder.forEach(async (relativePath, file) => {
          const content = await file.async('base64');
          mediaFiles.characters.push({
            name: relativePath,
            data: `data:image/png;base64,${content}`,
            category: 'characters'
          });
        });
      }

      // Importar videos
      const videoFolder = zip.folder('media/videos');
      if (videoFolder) {
        videoFolder.forEach(async (relativePath, file) => {
          const content = await file.async('base64');
          mediaFiles.videos.push({
            name: relativePath,
            data: `data:video/webm;base64,${content}`,
            category: 'videos'
          });
        });
      }

      // Importar audio
      const audioFolder = zip.folder('media/audio');
      if (audioFolder) {
        audioFolder.forEach(async (relativePath, file) => {
          const content = await file.async('base64');
          mediaFiles.audio.push({
            name: relativePath,
            data: `data:audio/ogg;base64,${content}`,
            category: 'audio'
          });
        });
      }

      return mediaFiles;
    } catch (error) {
      console.error('Error al importar archivos multimedia:', error);
      return mediaFiles;
    }
  }

  /**
   * Generar scripts Ren'Py para referencia
   */
  async generateRenPyScripts(project) {
    try {
      // Importar generador dinámicamente para evitar dependencias circulares
      const { RenPyGenerator } = await import('./renpyGenerator');
      const generator = new RenPyGenerator();
      
      const mainScript = await generator.generateScript(project);
      const sceneFiles = await generator.generateSceneFiles(project);
      
      return {
        main: mainScript,
        scenes: sceneFiles
      };
    } catch (error) {
      console.error('Error al generar scripts Ren\'Py:', error);
      return { main: '', scenes: {} };
    }
  }

  /**
   * Generar README del proyecto
   */
  generateProjectReadme(project, metadata) {
    return `# ${project.name}

## Información del Proyecto
- **Versión**: ${project.version}
- **Autor**: ${project.author || 'Sin especificar'}
- **Descripción**: ${project.description || 'Sin descripción'}
- **Creado**: ${metadata.exportedAt}
- **Editor**: RynEditor v${metadata.editorVersion}

## Estructura del Proyecto
- **Personajes**: ${project.characters?.length || 0}
- **Escenas**: ${project.sceneFiles?.length || 0}
- **Elementos de script**: ${project.sceneFiles?.[0]?.elements?.length || 0}

## Archivos Incluidos
- \`metadata.json\` - Metadatos del proyecto
- \`editor_state.json\` - Estado completo del editor
- \`advanced_config.json\` - Configuración avanzada
- \`media/\` - Archivos multimedia organizados
- \`renpy/\` - Scripts Ren'Py generados
- \`verification.json\` - Verificación de integridad

## Cómo Usar
1. Abre RynEditor
2. Ve a "Gestión de Proyectos"
3. Selecciona "Restaurar desde .ryn"
4. Elige este archivo
5. ¡Tu proyecto estará completamente restaurado!

---
*Archivo generado por RynEditor - Editor de Novelas Visuales*
`;
  }

  /**
   * Generar checksum para verificación de integridad
   */
  async generateChecksum(project) {
    const projectString = JSON.stringify(project);
    const encoder = new TextEncoder();
    const data = encoder.encode(projectString);
    const hashBuffer = await crypto.subtle.digest('SHA-256', data);
    const hashArray = Array.from(new Uint8Array(hashBuffer));
    return hashArray.map(b => b.toString(16).padStart(2, '0')).join('');
  }

  /**
   * Convertir base64 a Blob
   */
  base64ToBlob(base64, mimeType) {
    const byteCharacters = atob(base64.split(',')[1]);
    const byteNumbers = new Array(byteCharacters.length);
    
    for (let i = 0; i < byteCharacters.length; i++) {
      byteNumbers[i] = byteCharacters.charCodeAt(i);
    }
    
    const byteArray = new Uint8Array(byteNumbers);
    return new Blob([byteArray], { type: mimeType });
  }

  /**
   * Contar archivos en ZIP
   */
  countFiles(zip) {
    let count = 0;
    zip.forEach(() => count++);
    return count;
  }

  /**
   * Calcular tamaño total
   */
  calculateTotalSize(zip) {
    let totalSize = 0;
    zip.forEach((relativePath, file) => {
      totalSize += file._data ? file._data.uncompressedSize : 0;
    });
    return totalSize;
  }

  /**
   * Obtener historial de cambios
   */
  getChangeHistory() {
    try {
      return JSON.parse(localStorage.getItem('visual_novel_change_history') || '[]');
    } catch {
      return [];
    }
  }

  /**
   * Obtener preferencias del usuario
   */
  getUserPreferences() {
    try {
      return {
        lastActiveTab: localStorage.getItem('visual_novel_last_tab_sidebar') || 'project',
        lastScrollPosition: JSON.parse(localStorage.getItem('visual_novel_scroll_position') || '{"x":0,"y":0}'),
        theme: localStorage.getItem('visual_novel_theme') || 'dark',
        language: localStorage.getItem('visual_novel_language') || 'es'
      };
    } catch {
      return {};
    }
  }

  /**
   * Restaurar preferencias del usuario
   */
  restoreUserPreferences(preferences) {
    try {
      if (preferences.lastActiveTab) {
        localStorage.setItem('visual_novel_last_tab_sidebar', preferences.lastActiveTab);
      }
      if (preferences.lastScrollPosition) {
        localStorage.setItem('visual_novel_scroll_position', JSON.stringify(preferences.lastScrollPosition));
      }
      if (preferences.theme) {
        localStorage.setItem('visual_novel_theme', preferences.theme);
      }
      if (preferences.language) {
        localStorage.setItem('visual_novel_language', preferences.language);
      }
    } catch (error) {
      console.error('Error al restaurar preferencias:', error);
    }
  }

  /**
   * Obtener última pestaña activa
   */
  getLastActiveTab() {
    return localStorage.getItem('visual_novel_last_tab_sidebar') || 'project';
  }

  /**
   * Obtener última posición de scroll
   */
  getLastScrollPosition() {
    try {
      return JSON.parse(localStorage.getItem('visual_novel_scroll_position') || '{"x":0,"y":0}');
    } catch {
      return { x: 0, y: 0 };
    }
  }
}
