import React from 'react';
import styled, { keyframes, css } from 'styled-components';
import { useVisualNovel } from '../context/VisualNovelContext';

// Animaciones
const pulse = keyframes`
  0% { opacity: 1; }
  50% { opacity: 0.5; }
  100% { opacity: 1; }
`;

const slideIn = keyframes`
  from { transform: translateX(100%); opacity: 0; }
  to { transform: translateX(0); opacity: 1; }
`;

const slideOut = keyframes`
  from { transform: translateX(0); opacity: 1; }
  to { transform: translateX(100%); opacity: 0; }
`;

// Componentes estilizados
const IndicatorContainer = styled.div`
  position: fixed;
  top: 20px;
  right: 20px;
  z-index: 1000;
  display: flex;
  align-items: center;
  gap: 8px;
  padding: 8px 16px;
  border-radius: 20px;
  font-size: 12px;
  font-weight: 500;
  backdrop-filter: blur(10px);
  animation: ${slideIn} 0.3s ease-out;
  
  ${props => props.hidden && css`
    animation: ${slideOut} 0.3s ease-in forwards;
  `}
`;

const SavedIndicator = styled(IndicatorContainer)`
  background: var(--color-success);
  color: var(--color-textInverse);
  border: 2px solid var(--color-successHover);
  box-shadow: 0 4px 12px var(--color-shadow);
  font-weight: 600;
  text-shadow: 0 1px 2px var(--color-shadow);
`;

const SavingIndicator = styled(IndicatorContainer)`
  background: var(--color-warning);
  color: var(--color-textInverse);
  border: 2px solid var(--color-warningHover);
  box-shadow: 0 4px 12px var(--color-shadow);
  font-weight: 600;
  text-shadow: 0 1px 2px var(--color-shadow);
  animation: ${pulse} 1.5s infinite;
`;

const ErrorIndicator = styled(IndicatorContainer)`
  background: var(--color-error);
  color: var(--color-textInverse);
  border: 2px solid var(--color-errorHover);
  box-shadow: 0 4px 12px var(--color-shadow);
  font-weight: 600;
  text-shadow: 0 1px 2px var(--color-shadow);
`;

const StatusIcon = styled.span`
  font-size: 14px;
  display: flex;
  align-items: center;
`;

const StatusText = styled.span`
  white-space: nowrap;
`;

const LastSavedText = styled.span`
  font-size: 10px;
  opacity: 0.8;
  margin-left: 4px;
`;

function AutoSaveIndicator() {
  const { saveStatus, storage } = useVisualNovel();
  const [showIndicator, setShowIndicator] = React.useState(false);
  const [lastSaved, setLastSaved] = React.useState(null);

  // Mostrar indicador cuando cambie el estado
  React.useEffect(() => {
    if (saveStatus !== 'saved') {
      setShowIndicator(true);
    } else {
      // Mostrar brevemente el estado "guardado" antes de ocultar
      setShowIndicator(true);
      const timer = setTimeout(() => {
        setShowIndicator(false);
      }, 2000);
      return () => clearTimeout(timer);
    }
  }, [saveStatus]);

  // Obtener información del último guardado
  React.useEffect(() => {
    const updateLastSaved = () => {
      const saveInfo = storage.getLastSaveInfo();
      if (saveInfo) {
        setLastSaved(new Date(saveInfo.lastSaved));
      }
    };

    updateLastSaved();
    const interval = setInterval(updateLastSaved, 30000); // Actualizar cada 30 segundos
    return () => clearInterval(interval);
  }, [storage]);

  // Formatear tiempo transcurrido
  const formatTimeAgo = (date) => {
    if (!date) return '';
    
    const now = new Date();
    const diff = now - date;
    const minutes = Math.floor(diff / 60000);
    const hours = Math.floor(diff / 3600000);
    
    if (minutes < 1) return 'ahora';
    if (minutes < 60) return `${minutes}m`;
    if (hours < 24) return `${hours}h`;
    return date.toLocaleDateString();
  };

  if (!showIndicator) return null;

  const renderIndicator = () => {
    switch (saveStatus) {
      case 'saving':
        return (
          <SavingIndicator>
            <StatusIcon>💾</StatusIcon>
            <StatusText>Guardando...</StatusText>
          </SavingIndicator>
        );
      
      case 'error':
        return (
          <ErrorIndicator>
            <StatusIcon>⚠️</StatusIcon>
            <StatusText>Error al guardar</StatusText>
          </ErrorIndicator>
        );
      
      case 'saved':
      default:
        return (
          <SavedIndicator>
            <StatusIcon>✅</StatusIcon>
            <StatusText>
              Guardado
              {lastSaved && (
                <LastSavedText>({formatTimeAgo(lastSaved)})</LastSavedText>
              )}
            </StatusText>
          </SavedIndicator>
        );
    }
  };

  return renderIndicator();
}

export default AutoSaveIndicator;
