// Utilidad para codificar y decodificar IPs para colaboración
class IPEncoder {
  constructor() {
    // Clave secreta para la codificación (en producción debería ser más compleja)
    this.secretKey = 'ryn-editor-2024';
    // Almacenar códigos generados para decodificación
    this.generatedCodes = new Map();
  }

  // Codificación Base64 compatible con Node.js y navegador
  base64Encode(str) {
    if (typeof btoa !== 'undefined') {
      return btoa(str);
    } else {
      return Buffer.from(str, 'utf8').toString('base64');
    }
  }

  // Decodificación Base64 compatible con Node.js y navegador
  base64Decode(str) {
    if (typeof atob !== 'undefined') {
      return atob(str);
    } else {
      return Buffer.from(str, 'base64').toString('utf8');
    }
  }

  // Codificar IP y puerto en un código corto
  encodeConnectionInfo(ip, port = 3002) {
    try {
      // Crear string simple con la información
      const connectionString = `${ip}:${port}:${Date.now()}`;
      
      // Codificar en Base64
      const encoded = this.base64Encode(connectionString);
      
      // Crear código corto usando solo los primeros caracteres
      const shortCode = encoded.substring(0, 12).toUpperCase();
      
      // Agregar checksum simple
      const checksum = this.generateChecksum(shortCode);
      
      // Formato final: ABC123-XYZ789 (12 caracteres + guión + 6 caracteres checksum)
      const finalCode = `${shortCode}-${checksum}`;
      
      // Guardar el código completo para decodificación
      this.generatedCodes.set(finalCode, {
        ip: ip,
        port: port,
        timestamp: Date.now(),
        fullEncoded: encoded
      });
      
      return finalCode;
    } catch (error) {
      console.error('Error codificando IP:', error);
      return null;
    }
  }

  // Decodificar código corto a IP y puerto
  decodeConnectionInfo(shortCode) {
    try {
      // Verificar formato
      if (!shortCode || typeof shortCode !== 'string') {
        throw new Error('Código inválido');
      }

      // Separar código y checksum
      const parts = shortCode.split('-');
      if (parts.length !== 2) {
        throw new Error('Formato de código inválido');
      }

      const [code, checksum] = parts;

      // Verificar checksum
      if (!this.verifyChecksum(code, checksum)) {
        throw new Error('Código corrupto o inválido');
      }

      // Buscar en códigos generados
      if (this.generatedCodes.has(shortCode)) {
        const codeData = this.generatedCodes.get(shortCode);
        
        // Verificar que el código no sea muy antiguo (24 horas)
        const maxAge = 24 * 60 * 60 * 1000; // 24 horas en ms
        if (Date.now() - codeData.timestamp > maxAge) {
          throw new Error('Código expirado');
        }

        return {
          ip: codeData.ip,
          port: codeData.port,
          timestamp: codeData.timestamp,
          valid: true
        };
      }

      // Si no se encuentra en códigos generados, intentar decodificar directamente
      const decodedData = this.attemptDecode(code);
      
      if (decodedData) {
        const [ip, port, timestamp] = decodedData.split(':');
        
        if (ip && port) {
          // Verificar que el código no sea muy antiguo (24 horas)
          const maxAge = 24 * 60 * 60 * 1000; // 24 horas en ms
          if (Date.now() - parseInt(timestamp) > maxAge) {
            throw new Error('Código expirado');
          }

          return {
            ip: ip,
            port: parseInt(port),
            timestamp: parseInt(timestamp),
            valid: true
          };
        }
      }

      throw new Error('No se pudo decodificar el código');
    } catch (error) {
      console.error('Error decodificando código:', error);
      return {
        valid: false,
        error: error.message
      };
    }
  }

  // Intentar decodificar el código (método robusto)
  attemptDecode(code) {
    try {
      // Intentar decodificar directamente
      const decoded = this.base64Decode(code);
      return decoded;
    } catch (error) {
      // Si falla, intentar con padding
      try {
        const paddedCode = code + '=='; // Agregar padding Base64
        const decoded = this.base64Decode(paddedCode);
        return decoded;
      } catch (error2) {
        // Si sigue fallando, intentar con más padding
        try {
          const paddedCode2 = code + '====';
          const decoded = this.base64Decode(paddedCode2);
          return decoded;
        } catch (error3) {
          console.error('Error en attemptDecode:', error3);
          return null;
        }
      }
    }
  }

  // Generar checksum simple
  generateChecksum(code) {
    let hash = 0;
    for (let i = 0; i < code.length; i++) {
      const char = code.charCodeAt(i);
      hash = ((hash << 5) - hash) + char;
      hash = hash & hash; // Convertir a 32-bit integer
    }
    
    // Convertir a string de 6 caracteres
    const checksum = Math.abs(hash).toString(36).toUpperCase().substring(0, 6);
    return checksum.padEnd(6, '0'); // Rellenar con ceros si es necesario
  }

  // Verificar checksum
  verifyChecksum(code, checksum) {
    const expectedChecksum = this.generateChecksum(code);
    return expectedChecksum === checksum;
  }

  // Validar formato de código
  validateCodeFormat(code) {
    if (!code || typeof code !== 'string') return false;
    
    // Formato: ABC123-XYZ789 (12 caracteres + guión + 6 caracteres)
    const pattern = /^[A-Z0-9]{12}-[A-Z0-9]{6}$/;
    return pattern.test(code);
  }

  // Generar código de ejemplo (para testing)
  generateExampleCode() {
    return this.encodeConnectionInfo('192.168.1.100', 3002);
  }

  // Obtener información del código sin decodificar completamente
  getCodeInfo(code) {
    try {
      if (!this.validateCodeFormat(code)) {
        return { valid: false, error: 'Formato inválido' };
      }

      const parts = code.split('-');
      const [mainCode, checksum] = parts;

      if (!this.verifyChecksum(mainCode, checksum)) {
        return { valid: false, error: 'Checksum inválido' };
      }

      return {
        valid: true,
        format: 'correcto',
        length: code.length,
        checksum: 'válido'
      };
    } catch (error) {
      return { valid: false, error: error.message };
    }
  }

  // Limpiar códigos expirados
  cleanupExpiredCodes() {
    const now = Date.now();
    const maxAge = 24 * 60 * 60 * 1000; // 24 horas en ms
    
    for (const [code, data] of this.generatedCodes.entries()) {
      if (now - data.timestamp > maxAge) {
        this.generatedCodes.delete(code);
      }
    }
  }
}

// Instancia singleton
export const ipEncoder = new IPEncoder();

// Funciones de conveniencia
export const encodeConnectionInfo = (ip, port) => ipEncoder.encodeConnectionInfo(ip, port);
export const decodeConnectionInfo = (code) => ipEncoder.decodeConnectionInfo(code);
export const validateCodeFormat = (code) => ipEncoder.validateCodeFormat(code);
export const getCodeInfo = (code) => ipEncoder.getCodeInfo(code);

export default ipEncoder;