import React, { useState } from 'react';
import styled from 'styled-components';

const HelpContainer = styled.div`
  background: linear-gradient(135deg, var(--color-primary) 0%, var(--color-primaryHover) 100%);
  border-radius: 12px;
  padding: 16px;
  margin-bottom: 16px;
  color: var(--color-textInverse);
  box-shadow: 0 4px 12px rgba(74, 158, 255, 0.3);
`;

const HelpTitle = styled.h3`
  margin: 0 0 12px 0;
  font-size: 14px;
  font-weight: 700;
  display: flex;
  align-items: center;
  gap: 8px;
  color: #000000 !important;
  text-shadow: 0 1px 2px rgba(255, 255, 255, 0.8);
  
  /* Asegurar que el color negro se mantenga */
  * {
    color: #000000 !important;
  }
`;

const HelpContent = styled.div`
  font-size: 12px;
  line-height: 1.4;
`;

const HelpStep = styled.div`
  margin-bottom: 8px;
  padding-left: 20px;
  position: relative;
  
  &::before {
    content: '${props => props.number}';
    position: absolute;
    left: 0;
    top: 0;
    background-color: rgba(255, 255, 255, 0.2);
    color: var(--color-textInverse);
    width: 16px;
    height: 16px;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 10px;
    font-weight: 600;
  }
`;

const HelpButton = styled.button`
  background-color: rgba(255, 255, 255, 0.2);
  color: var(--color-textInverse);
  border: 1px solid rgba(255, 255, 255, 0.3);
  padding: 6px 12px;
  border-radius: 6px;
  cursor: pointer;
  font-size: 11px;
  font-weight: 600;
  margin-top: 8px;
  transition: all 0.3s ease;
  
  &:hover {
    background-color: rgba(255, 255, 255, 0.3);
    border-color: rgba(255, 255, 255, 0.5);
  }
`;

const ToggleButton = styled.button`
  background-color: rgba(255, 255, 255, 0.9);
  color: #000000;
  border: 1px solid #000000;
  cursor: pointer;
  font-size: 12px;
  font-weight: 600;
  padding: 4px 8px;
  border-radius: 6px;
  transition: all 0.3s ease;
  
  &:hover {
    background-color: #000000;
    color: #ffffff;
    transform: translateY(-1px);
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.3);
  }
  
  &:active {
    transform: translateY(0);
  }
`;

function CollaborationHelp({ onStartRealServer }) {
  const [showHelp, setShowHelp] = useState(false);

  const handleCopyCommand = async () => {
    try {
      await navigator.clipboard.writeText('npm run server:collaboration');
      alert('📋 Comando copiado al portapapeles');
    } catch (error) {
      console.log('No se pudo copiar al portapapeles');
    }
  };

  return (
    <HelpContainer>
      <HelpTitle>
        🤝 Guía de Colaboración
        <ToggleButton onClick={() => setShowHelp(!showHelp)}>
          {showHelp ? '👆 Ocultar' : '👇 Mostrar'}
        </ToggleButton>
      </HelpTitle>
      
      {showHelp && (
        <HelpContent>
          <HelpStep number="1">
            <strong>Activa la colaboración</strong> usando el botón de arriba
          </HelpStep>
          
          <HelpStep number="2">
            <strong>Inicia el servidor real:</strong> Haz clic en "Iniciar Servidor Real" para ver las instrucciones
          </HelpStep>
          
          <HelpStep number="3">
            <strong>Navega a la carpeta del editor</strong> en la terminal (muy importante)
          </HelpStep>
          
          <HelpStep number="4">
            <strong>Ejecuta el comando</strong> que aparece en las instrucciones
          </HelpStep>
          
          <HelpStep number="5">
            <strong>Crea una sala</strong> y comparte el código con otros
          </HelpStep>
          
          <HelpStep number="6">
            <strong>¡Colabora!</strong> Los cambios se sincronizan en tiempo real
          </HelpStep>
          
          <HelpStep number="7">
            <strong>🔐 Códigos seguros:</strong> El editor genera códigos que ocultan tu IP real (ej: ABC123-XYZ789)
          </HelpStep>
          
          <HelpStep number="8">
            <strong>Seguridad:</strong> Comparte el código seguro en lugar de tu IP pública
          </HelpStep>
          
          <HelpStep number="9">
            <strong>🔄 Sincronización:</strong> Todos los cambios se sincronizan automáticamente en tiempo real
          </HelpStep>
          
          <HelpStep number="10">
            <strong>✨ Colaboración:</strong> Edita personajes, script y escenas simultáneamente con otros usuarios
          </HelpStep>
          
          <div style={{ marginTop: '12px', display: 'flex', gap: '8px' }}>
            <HelpButton onClick={onStartRealServer}>
              🏠 Iniciar Servidor Real
            </HelpButton>
            <HelpButton onClick={handleCopyCommand}>
              📋 Copiar Comando
            </HelpButton>
          </div>
        </HelpContent>
      )}
    </HelpContainer>
  );
}

export default CollaborationHelp;
