// Configuración para optimización del ScriptEditor
export const SCRIPT_EDITOR_CONFIG = {
  // Configuración de scroll
  scroll: {
    sensitivity: 4.0,
    timeout: 200,
    arrowScrollAmount: 300,
    pageScrollAmount: 600,
    spaceScrollAmount: 400
  },
  
  // Configuración de hints
  hints: {
    showDelay: 1000,
    hideDelay: 4000
  },
  
  // Configuración de animaciones
  animations: {
    fadeInDuration: 300,
    slideInDuration: 500,
    pulseDuration: 2000
  },
  
  // Configuración de formularios
  forms: {
    textareaMinHeight: 80,
    inputPadding: '10px 12px',
    borderRadius: 8
  },
  
  // Configuración de rendimiento
  performance: {
    maxElementsToRender: 100, // Límite para virtualización si es necesario
    debounceDelay: 100,
    throttleDelay: 16
  }
};

// Función para obtener configuración específica
export const getScriptEditorConfig = (key) => {
  return SCRIPT_EDITOR_CONFIG[key] || null;
};

// Función para validar si hay demasiados elementos
export const shouldVirtualizeElements = (elementCount) => {
  return elementCount > SCRIPT_EDITOR_CONFIG.performance.maxElementsToRender;
};
