// Utilidad para guardar y cargar proyectos localmente
export class ProjectStorage {
  constructor() {
    this.storageKey = 'visual_novel_projects';
    this.currentProjectKey = 'current_visual_novel_project';
    this.fullStateKey = 'visual_novel_full_state';
    this.autoSaveKey = 'visual_novel_autosave';
  }

  // Guardar proyecto actual
  saveCurrentProject(project) {
    try {
      localStorage.setItem(this.currentProjectKey, JSON.stringify(project));
      return true;
    } catch (error) {
      console.error('Error al guardar proyecto:', error);
      return false;
    }
  }

  // Cargar proyecto actual
  loadCurrentProject() {
    try {
      const projectData = localStorage.getItem(this.currentProjectKey);
      return projectData ? JSON.parse(projectData) : null;
    } catch (error) {
      console.error('Error al cargar proyecto:', error);
      return null;
    }
  }

  // Guardar proyecto con nombre específico
  saveProject(projectName, project) {
    try {
      // Validar proyecto antes de guardar
      if (!this.validateProject(project)) {
        console.error('❌ Proyecto no válido, no se puede guardar');
        return false;
      }

      const projects = this.getAllProjects();
      projects[projectName] = {
        ...project,
        savedAt: new Date().toISOString(),
        name: projectName,
        version: project.version || '1.0.0'
      };
      
      localStorage.setItem(this.storageKey, JSON.stringify(projects));
      
      // También guardar como proyecto actual para consistencia
      this.saveCurrentProject(project);
      
      console.log('✅ Proyecto guardado exitosamente:', {
        name: projectName,
        characters: project.characters?.length || 0,
        sceneFiles: project.sceneFiles?.length || 0
      });
      
      return true;
    } catch (error) {
      console.error('❌ Error al guardar proyecto:', error);
      return false;
    }
  }

  // Cargar proyecto por nombre
  loadProject(projectName) {
    try {
      const projects = this.getAllProjects();
      const project = projects[projectName];
      
      if (project && this.validateProject(project)) {
        console.log('✅ Proyecto cargado exitosamente:', {
          name: projectName,
          characters: project.characters?.length || 0,
          sceneFiles: project.sceneFiles?.length || 0
        });
        return project;
      } else if (project) {
        console.warn('⚠️ Proyecto encontrado pero no es válido:', projectName);
        return null;
      }
      
      return null;
    } catch (error) {
      console.error('❌ Error al cargar proyecto:', error);
      return null;
    }
  }

  // Obtener todos los proyectos guardados
  getAllProjects() {
    try {
      const projects = localStorage.getItem(this.storageKey);
      if (projects) {
        const parsedProjects = JSON.parse(projects);
        
        // Filtrar proyectos válidos
        const validProjects = {};
        Object.keys(parsedProjects).forEach(name => {
          if (this.validateProject(parsedProjects[name])) {
            validProjects[name] = parsedProjects[name];
          } else {
            console.warn('⚠️ Proyecto inválido encontrado y filtrado:', name);
          }
        });
        
        return validProjects;
      }
      return {};
    } catch (error) {
      console.error('❌ Error al obtener proyectos:', error);
      return {};
    }
  }

  // Eliminar proyecto
  async deleteProject(projectName) {
    try {
      console.log(`🗑️ Eliminando proyecto: ${projectName}`);
      
      // Obtener el proyecto antes de eliminarlo para limpiar sus assets
      const projects = this.getAllProjects();
      const projectToDelete = projects[projectName];
      
      if (projectToDelete) {
        // Limpiar assets del proyecto si tiene personajes
        if (projectToDelete.characters && projectToDelete.characters.length > 0) {
          await this.clearProjectAssets(projectToDelete);
        }
      }
      
      // Eliminar el proyecto de la lista
      delete projects[projectName];
      localStorage.setItem(this.storageKey, JSON.stringify(projects));
      
      // Si el proyecto eliminado es el proyecto actual, limpiar también el proyecto actual
      const currentProject = this.loadCurrentProject();
      if (currentProject && currentProject.name === projectName) {
        console.log('🧹 Limpiando proyecto actual ya que fue eliminado');
        this.clearCurrentProjectState();
      }
      
      console.log(`✅ Proyecto "${projectName}" eliminado exitosamente`);
      return true;
    } catch (error) {
      console.error('❌ Error al eliminar proyecto:', error);
      return false;
    }
  }

  // Limpiar assets de un proyecto específico
  async clearProjectAssets(project) {
    try {
      console.log('🧹 Limpiando assets del proyecto...');
      
      // Importar AssetStorage dinámicamente
      const { AssetStorage } = await import('./assetStorage.js');
      const assetStorage = new AssetStorage();
      await assetStorage.init();
      
      // Obtener todos los archivos
      const allFiles = await assetStorage.getAllFiles();
      
      // Filtrar archivos que pertenecen a los personajes del proyecto
      const projectCharacterIds = project.characters.map(char => char.id);
      const filesToDelete = allFiles.filter(file => 
        file.characterId && projectCharacterIds.includes(file.characterId)
      );
      
      console.log(`🗑️ Eliminando ${filesToDelete.length} archivos del proyecto`);
      
      // Eliminar archivos uno por uno
      for (const file of filesToDelete) {
        try {
          await assetStorage.deleteFile(file.id);
          console.log(`✅ Archivo eliminado: ${file.name}`);
        } catch (error) {
          console.error(`❌ Error eliminando archivo ${file.name}:`, error);
        }
      }
      
      console.log('✅ Assets del proyecto limpiados');
      return true;
    } catch (error) {
      console.error('❌ Error limpiando assets del proyecto:', error);
      return false;
    }
  }

  // Obtener lista de nombres de proyectos
  getProjectNames() {
    const projects = this.getAllProjects();
    return Object.keys(projects).map(name => ({
      name,
      savedAt: projects[name].savedAt,
      description: projects[name].description || 'Sin descripción'
    }));
  }

  // Verificar si hay espacio suficiente
  checkStorageSpace() {
    try {
      const testData = 'x'.repeat(1024 * 1024); // 1MB
      localStorage.setItem('test', testData);
      localStorage.removeItem('test');
      return true;
    } catch (error) {
      return false;
    }
  }

  // Limpiar todos los proyectos
  clearAllProjects() {
    try {
      localStorage.removeItem(this.storageKey);
      localStorage.removeItem(this.currentProjectKey);
      localStorage.removeItem(this.fullStateKey);
      localStorage.removeItem(this.autoSaveKey);
      return true;
    } catch (error) {
      console.error('Error al limpiar proyectos:', error);
      return false;
    }
  }

  // Limpiar completamente todos los datos (proyectos + assets)
  async clearAllData() {
    try {
      console.log('🧹 Iniciando limpieza completa de datos...');
      
      // Limpiar localStorage
      this.clearAllProjects();
      
      // Limpiar sessionStorage
      sessionStorage.removeItem('visual_novel_session');
      sessionStorage.clear();
      
      // Limpiar IndexedDB
      await this.clearIndexedDB();
      
      // Limpiar datos huérfanos
      await this.cleanupOrphanedData();
      
      // Limpiar cookies relacionadas (si las hay)
      document.cookie.split(";").forEach(function(c) { 
        document.cookie = c.replace(/^ +/, "").replace(/=.*/, "=;expires=" + new Date().toUTCString() + ";path=/"); 
      });
      
      console.log('✅ Limpieza completa realizada');
      return true;
    } catch (error) {
      console.error('❌ Error durante limpieza completa:', error);
      return false;
    }
  }

  // Limpiar IndexedDB
  async clearIndexedDB() {
    try {
      console.log('🗑️ Limpiando IndexedDB...');
      
      // Importar AssetStorage dinámicamente para evitar dependencias circulares
      const { AssetStorage } = await import('./assetStorage.js');
      const assetStorage = new AssetStorage();
      
      // Inicializar y limpiar
      await assetStorage.init();
      await assetStorage.clearAllFiles();
      
      console.log('✅ IndexedDB limpiado');
      return true;
    } catch (error) {
      console.error('❌ Error limpiando IndexedDB:', error);
      return false;
    }
  }

  // Limpiar estado actual del proyecto
  clearCurrentProjectState() {
    try {
      console.log('🧹 Limpiando estado actual del proyecto...');
      
      // Limpiar todas las claves relacionadas con el estado actual
      localStorage.removeItem(this.currentProjectKey);
      localStorage.removeItem(this.fullStateKey);
      localStorage.removeItem(this.autoSaveKey);
      
      // Limpiar cualquier otra clave que pueda contener datos del proyecto
      const keysToRemove = [];
      for (let i = 0; i < localStorage.length; i++) {
        const key = localStorage.key(i);
        if (key && (
          key.startsWith('visual_novel_') ||
          key.startsWith('ryn-editor_') ||
          key.includes('project') ||
          key.includes('character') ||
          key.includes('scene')
        )) {
          keysToRemove.push(key);
        }
      }
      
      keysToRemove.forEach(key => {
        localStorage.removeItem(key);
        console.log(`🗑️ Clave eliminada: ${key}`);
      });
      
      console.log('✅ Estado actual del proyecto limpiado');
      return true;
    } catch (error) {
      console.error('❌ Error limpiando estado actual:', error);
      return false;
    }
  }

  // Verificar y limpiar datos huérfanos
  async cleanupOrphanedData() {
    try {
      console.log('🔍 Verificando datos huérfanos...');
      
      // Obtener todos los proyectos válidos
      const validProjects = this.getAllProjects();
      const validProjectNames = Object.keys(validProjects);
      
      // Obtener todos los archivos de IndexedDB
      const { AssetStorage } = await import('./assetStorage.js');
      const assetStorage = new AssetStorage();
      await assetStorage.init();
      const allFiles = await assetStorage.getAllFiles();
      
      // Encontrar archivos huérfanos (archivos que no pertenecen a ningún proyecto válido)
      const orphanedFiles = [];
      for (const file of allFiles) {
        if (file.characterId) {
          // Verificar si el personaje existe en algún proyecto válido
          let characterExists = false;
          for (const projectName of validProjectNames) {
            const project = validProjects[projectName];
            if (project.characters && project.characters.some(char => char.id === file.characterId)) {
              characterExists = true;
              break;
            }
          }
          
          if (!characterExists) {
            orphanedFiles.push(file);
          }
        }
      }
      
      console.log(`🗑️ Encontrados ${orphanedFiles.length} archivos huérfanos`);
      
      // Eliminar archivos huérfanos
      for (const file of orphanedFiles) {
        try {
          await assetStorage.deleteFile(file.id);
          console.log(`✅ Archivo huérfano eliminado: ${file.name}`);
        } catch (error) {
          console.error(`❌ Error eliminando archivo huérfano ${file.name}:`, error);
        }
      }
      
      console.log('✅ Limpieza de datos huérfanos completada');
      return orphanedFiles.length;
    } catch (error) {
      console.error('❌ Error limpiando datos huérfanos:', error);
      return 0;
    }
  }

  // Exportar todos los proyectos como JSON
  exportAllProjects() {
    try {
      const projects = this.getAllProjects();
      const currentProject = this.loadCurrentProject();
      
      const exportData = {
        projects,
        currentProject,
        exportedAt: new Date().toISOString(),
        version: '1.0.0'
      };

      const blob = new Blob([JSON.stringify(exportData, null, 2)], { 
        type: 'application/json' 
      });
      
      const url = URL.createObjectURL(blob);
      const a = document.createElement('a');
      a.href = url;
      a.download = `visual_novel_projects_backup_${new Date().toISOString().split('T')[0]}.json`;
      document.body.appendChild(a);
      a.click();
      document.body.removeChild(a);
      URL.revokeObjectURL(url);
      
      return true;
    } catch (error) {
      console.error('Error al exportar proyectos:', error);
      return false;
    }
  }

  // Importar proyectos desde JSON
  importProjects(jsonData) {
    try {
      const data = typeof jsonData === 'string' ? JSON.parse(jsonData) : jsonData;
      
      if (data.projects) {
        localStorage.setItem(this.storageKey, JSON.stringify(data.projects));
      }
      
      if (data.currentProject) {
        localStorage.setItem(this.currentProjectKey, JSON.stringify(data.currentProject));
      }
      
      return true;
    } catch (error) {
      console.error('Error al importar proyectos:', error);
      return false;
    }
  }

  // ===== NUEVOS MÉTODOS PARA GUARDADO AUTOMÁTICO =====

  // Guardar estado completo de la aplicación
  saveFullState(state) {
    try {
      const stateToSave = {
        ...state,
        lastSaved: new Date().toISOString(),
        version: '1.0.0'
      };

      // Guardar estado completo
      localStorage.setItem(this.fullStateKey, JSON.stringify(stateToSave));
      
      // También guardar como backup en autosave
      localStorage.setItem(this.autoSaveKey, JSON.stringify(stateToSave));
      
      console.log('💾 Estado completo guardado:', {
        timestamp: stateToSave.lastSaved,
        projectName: state.project.name,
        characters: state.project.characters.length,
        sceneFiles: state.project.sceneFiles.length,
        currentScene: state.currentScene,
        selectedElement: state.selectedElement?.type || 'none'
      });

      return true;
    } catch (error) {
      console.error('❌ Error al guardar estado completo:', error);
      return false;
    }
  }

  // Cargar estado completo de la aplicación
  loadFullState() {
    try {
      // Intentar cargar estado principal
      let stateData = localStorage.getItem(this.fullStateKey);
      
      // Si no existe, intentar cargar desde backup
      if (!stateData) {
        stateData = localStorage.getItem(this.autoSaveKey);
      }

      if (stateData) {
        const state = JSON.parse(stateData);
        
        // Validar que el estado cargado sea válido
        if (this.validateState(state)) {
          console.log('📂 Estado completo cargado:', {
            timestamp: state.lastSaved,
            projectName: state.project?.name || 'Sin nombre',
            characters: state.project?.characters?.length || 0,
            sceneFiles: state.project?.sceneFiles?.length || 0
          });
          return state;
        } else {
          console.warn('⚠️ Estado cargado no es válido, intentando recuperar desde proyecto guardado');
          // Intentar cargar desde proyecto guardado como fallback
          return this.loadProjectAsState();
        }
      }

      return null;
    } catch (error) {
      console.error('❌ Error al cargar estado completo:', error);
      // Intentar cargar desde proyecto guardado como fallback
      return this.loadProjectAsState();
    }
  }

  // Validar que el estado sea válido
  validateState(state) {
    try {
      // Verificar estructura básica
      if (!state || typeof state !== 'object') return false;
      if (!state.project || typeof state.project !== 'object') return false;
      
      // Verificar propiedades esenciales del proyecto
      if (!state.project.name || typeof state.project.name !== 'string') return false;
      if (!Array.isArray(state.project.characters)) return false;
      if (!Array.isArray(state.project.sceneFiles)) return false;
      
      // Verificar que tenga al menos un archivo de escena
      if (state.project.sceneFiles.length === 0) return false;
      
      // Verificar que el archivo de escena actual exista
      if (state.project.currentSceneFile) {
        const currentFileExists = state.project.sceneFiles.some(
          file => file.id === state.project.currentSceneFile
        );
        if (!currentFileExists) return false;
      }
      
      return true;
    } catch (error) {
      console.error('Error validando estado:', error);
      return false;
    }
  }

  // Cargar proyecto guardado como estado completo
  loadProjectAsState() {
    try {
      const savedProject = this.loadCurrentProject();
      if (savedProject && this.validateProject(savedProject)) {
        console.log('📂 Cargando proyecto guardado como estado completo');
        
        // Convertir proyecto a estado completo
        const fullState = {
          project: savedProject,
          currentScene: null,
          selectedElement: null,
          previewMode: false,
          isPlaying: false,
          theme: 'dark-default',
          lastSaved: new Date().toISOString(),
          version: '1.0.0'
        };
        
        return fullState;
      }
      return null;
    } catch (error) {
      console.error('Error cargando proyecto como estado:', error);
      return null;
    }
  }

  // Validar que un proyecto sea válido
  validateProject(project) {
    try {
      if (!project || typeof project !== 'object') return false;
      if (!project.name || typeof project.name !== 'string') return false;
      if (!Array.isArray(project.characters)) return false;
      if (!Array.isArray(project.sceneFiles)) return false;
      if (project.sceneFiles.length === 0) return false;
      
      return true;
    } catch (error) {
      console.error('Error validando proyecto:', error);
      return false;
    }
  }

  // Guardar estado de sesión (para recuperación rápida)
  saveSessionState(state) {
    try {
      const sessionData = {
        currentScene: state.currentScene,
        selectedElement: state.selectedElement,
        previewMode: state.previewMode,
        isPlaying: state.isPlaying,
        lastActiveTab: this.getLastActiveTab(),
        lastScrollPosition: this.getLastScrollPosition(),
        timestamp: new Date().toISOString()
      };

      sessionStorage.setItem('visual_novel_session', JSON.stringify(sessionData));
      return true;
    } catch (error) {
      console.error('Error al guardar estado de sesión:', error);
      return false;
    }
  }

  // Cargar estado de sesión
  loadSessionState() {
    try {
      const sessionData = sessionStorage.getItem('visual_novel_session');
      if (sessionData) {
        const session = JSON.parse(sessionData);
        console.log('🔄 Estado de sesión cargado:', session);
        return session;
      }
      return null;
    } catch (error) {
      console.error('Error al cargar estado de sesión:', error);
      return null;
    }
  }

  // Obtener información del último guardado
  getLastSaveInfo() {
    try {
      const stateData = localStorage.getItem(this.fullStateKey);
      if (stateData) {
        const state = JSON.parse(stateData);
        return {
          lastSaved: state.lastSaved,
          projectName: state.project?.name || 'Sin nombre',
          hasUnsavedChanges: this.hasUnsavedChanges(state)
        };
      }
      return null;
    } catch (error) {
      console.error('Error al obtener info del último guardado:', error);
      return null;
    }
  }

  // Verificar si hay cambios sin guardar
  hasUnsavedChanges(currentState) {
    try {
      const savedState = this.loadFullState();
      if (!savedState) return true;

      // Comparar elementos clave
      const currentHash = this.generateStateHash(currentState);
      const savedHash = this.generateStateHash(savedState);

      return currentHash !== savedHash;
    } catch (error) {
      console.error('Error al verificar cambios sin guardar:', error);
      return true;
    }
  }

  // Generar hash del estado para comparación
  generateStateHash(state) {
    try {
      const keyData = {
        project: state.project,
        currentScene: state.currentScene,
        selectedElement: state.selectedElement
      };
      return btoa(JSON.stringify(keyData)).substring(0, 50);
    } catch (error) {
      return Date.now().toString();
    }
  }

  // Limpiar estado guardado
  clearSavedState() {
    try {
      localStorage.removeItem(this.fullStateKey);
      localStorage.removeItem(this.autoSaveKey);
      sessionStorage.removeItem('visual_novel_session');
      console.log('🗑️ Estado guardado limpiado');
      return true;
    } catch (error) {
      console.error('Error al limpiar estado guardado:', error);
      return false;
    }
  }

  // Métodos auxiliares para UI
  getLastActiveTab(context = 'sidebar') {
    try {
      const key = `visual_novel_last_tab_${context}`;
      const defaultValue = context === 'mainEditor' ? 'script' : 'project';
      return localStorage.getItem(key) || defaultValue;
    } catch {
      return context === 'mainEditor' ? 'script' : 'project';
    }
  }

  setLastActiveTab(tab, context = 'sidebar') {
    try {
      const key = `visual_novel_last_tab_${context}`;
      localStorage.setItem(key, tab);
    } catch (error) {
      console.error('Error al guardar última pestaña activa:', error);
    }
  }

  getLastScrollPosition() {
    try {
      const position = localStorage.getItem('visual_novel_scroll_position');
      return position ? JSON.parse(position) : { x: 0, y: 0 };
    } catch {
      return { x: 0, y: 0 };
    }
  }

  setLastScrollPosition(x, y) {
    try {
      localStorage.setItem('visual_novel_scroll_position', JSON.stringify({ x, y }));
    } catch (error) {
      console.error('Error al guardar posición de scroll:', error);
    }
  }

  // Obtener estadísticas de almacenamiento
  getStorageStats() {
    try {
      const fullStateSize = localStorage.getItem(this.fullStateKey)?.length || 0;
      const autoSaveSize = localStorage.getItem(this.autoSaveKey)?.length || 0;
      const projectSize = localStorage.getItem(this.currentProjectKey)?.length || 0;
      const totalSize = fullStateSize + autoSaveSize + projectSize;

      return {
        fullStateSize: this.formatBytes(fullStateSize),
        autoSaveSize: this.formatBytes(autoSaveSize),
        projectSize: this.formatBytes(projectSize),
        totalSize: this.formatBytes(totalSize),
        totalSizeBytes: totalSize
      };
    } catch (error) {
      console.error('Error al obtener estadísticas:', error);
      return null;
    }
  }

  // Formatear bytes a formato legible
  formatBytes(bytes) {
    if (bytes === 0) return '0 Bytes';
    const k = 1024;
    const sizes = ['Bytes', 'KB', 'MB', 'GB'];
    const i = Math.floor(Math.log(bytes) / Math.log(k));
    return parseFloat((bytes / Math.pow(k, i)).toFixed(2)) + ' ' + sizes[i];
  }

  // Limpiar datos corruptos y recuperar proyectos válidos
  cleanupCorruptedData() {
    try {
      console.log('🧹 Iniciando limpieza de datos corruptos...');
      
      let cleanedCount = 0;
      
      // Limpiar estado completo corrupto
      const fullStateData = localStorage.getItem(this.fullStateKey);
      if (fullStateData) {
        try {
          const state = JSON.parse(fullStateData);
          if (!this.validateState(state)) {
            localStorage.removeItem(this.fullStateKey);
            console.log('🗑️ Estado completo corrupto eliminado');
            cleanedCount++;
          }
        } catch (error) {
          localStorage.removeItem(this.fullStateKey);
          console.log('🗑️ Estado completo corrupto eliminado (JSON inválido)');
          cleanedCount++;
        }
      }
      
      // Limpiar autosave corrupto
      const autoSaveData = localStorage.getItem(this.autoSaveKey);
      if (autoSaveData) {
        try {
          const state = JSON.parse(autoSaveData);
          if (!this.validateState(state)) {
            localStorage.removeItem(this.autoSaveKey);
            console.log('🗑️ Autosave corrupto eliminado');
            cleanedCount++;
          }
        } catch (error) {
          localStorage.removeItem(this.autoSaveKey);
          console.log('🗑️ Autosave corrupto eliminado (JSON inválido)');
          cleanedCount++;
        }
      }
      
      // Limpiar proyecto actual corrupto
      const currentProjectData = localStorage.getItem(this.currentProjectKey);
      if (currentProjectData) {
        try {
          const project = JSON.parse(currentProjectData);
          if (!this.validateProject(project)) {
            localStorage.removeItem(this.currentProjectKey);
            console.log('🗑️ Proyecto actual corrupto eliminado');
            cleanedCount++;
          }
        } catch (error) {
          localStorage.removeItem(this.currentProjectKey);
          console.log('🗑️ Proyecto actual corrupto eliminado (JSON inválido)');
          cleanedCount++;
        }
      }
      
      // Limpiar proyectos guardados corruptos
      const projectsData = localStorage.getItem(this.storageKey);
      if (projectsData) {
        try {
          const projects = JSON.parse(projectsData);
          const validProjects = {};
          let removedCount = 0;
          
          Object.keys(projects).forEach(name => {
            if (this.validateProject(projects[name])) {
              validProjects[name] = projects[name];
            } else {
              removedCount++;
            }
          });
          
          if (removedCount > 0) {
            localStorage.setItem(this.storageKey, JSON.stringify(validProjects));
            console.log(`🗑️ ${removedCount} proyectos corruptos eliminados`);
            cleanedCount += removedCount;
          }
        } catch (error) {
          localStorage.removeItem(this.storageKey);
          console.log('🗑️ Lista de proyectos corrupta eliminada');
          cleanedCount++;
        }
      }
      
      console.log(`✅ Limpieza completada. ${cleanedCount} elementos corruptos eliminados`);
      return cleanedCount;
      
    } catch (error) {
      console.error('❌ Error durante la limpieza:', error);
      return 0;
    }
  }

  // Verificar integridad de todos los datos
  checkDataIntegrity() {
    try {
      console.log('🔍 Verificando integridad de datos...');
      
      const issues = [];
      
      // Verificar estado completo
      const fullStateData = localStorage.getItem(this.fullStateKey);
      if (fullStateData) {
        try {
          const state = JSON.parse(fullStateData);
          if (!this.validateState(state)) {
            issues.push('Estado completo corrupto');
          }
        } catch (error) {
          issues.push('Estado completo con JSON inválido');
        }
      }
      
      // Verificar autosave
      const autoSaveData = localStorage.getItem(this.autoSaveKey);
      if (autoSaveData) {
        try {
          const state = JSON.parse(autoSaveData);
          if (!this.validateState(state)) {
            issues.push('Autosave corrupto');
          }
        } catch (error) {
          issues.push('Autosave con JSON inválido');
        }
      }
      
      // Verificar proyecto actual
      const currentProjectData = localStorage.getItem(this.currentProjectKey);
      if (currentProjectData) {
        try {
          const project = JSON.parse(currentProjectData);
          if (!this.validateProject(project)) {
            issues.push('Proyecto actual corrupto');
          }
        } catch (error) {
          issues.push('Proyecto actual con JSON inválido');
        }
      }
      
      // Verificar proyectos guardados
      const projectsData = localStorage.getItem(this.storageKey);
      if (projectsData) {
        try {
          const projects = JSON.parse(projectsData);
          let corruptProjects = 0;
          
          Object.keys(projects).forEach(name => {
            if (!this.validateProject(projects[name])) {
              corruptProjects++;
            }
          });
          
          if (corruptProjects > 0) {
            issues.push(`${corruptProjects} proyectos guardados corruptos`);
          }
        } catch (error) {
          issues.push('Lista de proyectos con JSON inválido');
        }
      }
      
      if (issues.length === 0) {
        console.log('✅ Integridad de datos verificada. No se encontraron problemas.');
        return { isValid: true, issues: [] };
      } else {
        console.warn('⚠️ Problemas de integridad encontrados:', issues);
        return { isValid: false, issues };
      }
      
    } catch (error) {
      console.error('❌ Error verificando integridad:', error);
      return { isValid: false, issues: ['Error durante la verificación'] };
    }
  }
}
