import React, { useState } from 'react';
import styled from 'styled-components';

const ProjectStructureContainer = styled.div`
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: var(--color-background);
  z-index: 3000;
  overflow-y: auto;
  transition: background-color 0.3s ease;
`;

const ProjectStructureHeader = styled.div`
  background-color: var(--color-surface);
  padding: 20px;
  border-bottom: 2px solid var(--color-border);
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: sticky;
  top: 0;
  z-index: 3001;
  transition: background-color 0.3s ease, border-color 0.3s ease;
`;

const ProjectStructureTitle = styled.h1`
  margin: 0;
  color: var(--color-success);
  font-size: 32px;
  font-weight: 800;
  display: flex;
  align-items: center;
  gap: 15px;
  transition: color 0.3s ease;
  text-shadow: 0 2px 4px var(--color-shadow);
  letter-spacing: 0.5px;
`;

const CloseButton = styled.button`
  background-color: var(--color-error);
  color: var(--color-textInverse);
  border: none;
  border-radius: 8px;
  padding: 12px 20px;
  cursor: pointer;
  font-size: 16px;
  font-weight: bold;
  transition: all 0.3s ease;
  box-shadow: 0 4px 12px var(--color-shadow);

  &:hover {
    background-color: var(--color-errorHover);
    transform: scale(1.05);
    box-shadow: 0 6px 16px var(--color-shadowHover);
  }
`;

const ProjectStructureContent = styled.div`
  padding: 30px;
  max-width: 1200px;
  margin: 0 auto;
`;

const SearchBox = styled.div`
  background-color: var(--color-surfaceSecondary);
  border: 2px solid var(--color-border);
  border-radius: 12px;
  padding: 25px;
  margin-bottom: 30px;
  transition: background-color 0.3s ease, border-color 0.3s ease;
  box-shadow: 0 4px 12px var(--color-shadow);

  &:hover {
    border-color: var(--color-primary);
    box-shadow: 0 6px 16px var(--color-shadowHover);
  }
`;

const SearchTitle = styled.h2`
  color: var(--color-success);
  margin: 0 0 20px 0;
  font-size: 22px;
  font-weight: 800;
  display: flex;
  align-items: center;
  gap: 12px;
  transition: color 0.3s ease;
  text-shadow: 0 1px 3px var(--color-shadow);
  letter-spacing: 0.3px;
`;

const SearchInput = styled.input`
  width: 100%;
  padding: 12px 16px;
  background-color: var(--color-backgroundSecondary);
  border: 2px solid var(--color-border);
  border-radius: 8px;
  color: var(--color-textPrimary);
  font-size: 16px;
  font-weight: 500;
  transition: all 0.3s ease;
  box-shadow: 0 2px 6px var(--color-shadow);

  &:focus {
    outline: none;
    border-color: var(--color-success);
    box-shadow: 0 4px 12px var(--color-shadowHover);
    transform: translateY(-1px);
  }

  &:hover {
    border-color: var(--color-primary);
    box-shadow: 0 3px 8px var(--color-shadowHover);
  }

  &::placeholder {
    color: var(--color-textTertiary);
    font-weight: 400;
  }
`;

const CategoryTabs = styled.div`
  display: flex;
  gap: 10px;
  margin-bottom: 30px;
  flex-wrap: wrap;
`;

const CategoryTab = styled.button`
  background-color: ${props => props.active ? 'var(--color-success)' : 'var(--color-surfaceSecondary)'};
  color: ${props => props.active ? 'var(--color-textInverse)' : 'var(--color-textPrimary)'};
  border: 2px solid ${props => props.active ? 'var(--color-success)' : 'var(--color-border)'};
  padding: 14px 22px;
  border-radius: 8px;
  cursor: pointer;
  font-size: 14px;
  font-weight: 700;
  transition: all 0.3s ease;
  box-shadow: 0 3px 8px var(--color-shadow);

  &:hover {
    background-color: ${props => props.active ? 'var(--color-successHover)' : 'var(--color-border)'};
    border-color: ${props => props.active ? 'var(--color-successHover)' : 'var(--color-primary)'};
    color: var(--color-textInverse);
    transform: translateY(-3px);
    box-shadow: 0 6px 16px var(--color-shadowHover);
  }

  &:active {
    transform: translateY(-1px);
    box-shadow: 0 4px 12px var(--color-shadowHover);
  }
`;

const StructureGrid = styled.div`
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(400px, 1fr));
  gap: 25px;
  margin-bottom: 40px;
`;

const StructureCard = styled.div`
  background-color: var(--color-surfaceSecondary);
  border: 2px solid var(--color-border);
  border-radius: 12px;
  padding: 24px;
  transition: all 0.3s ease;
  box-shadow: 0 4px 12px var(--color-shadow);

  &:hover {
    border-color: var(--color-success);
    background-color: var(--color-surfaceElevated);
    transform: translateY(-8px);
    box-shadow: 0 12px 32px var(--color-shadowHover);
  }
`;

const StructureHeader = styled.div`
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  margin-bottom: 15px;
`;

const StructureTitle = styled.h3`
  margin: 0;
  color: var(--color-success);
  font-size: 20px;
  font-weight: 800;
  display: flex;
  align-items: center;
  gap: 12px;
  transition: color 0.3s ease;
  text-shadow: 0 2px 4px var(--color-shadow);
  letter-spacing: 0.3px;
`;

const StructureType = styled.span`
  background-color: ${props => {
    switch(props.type) {
      case 'folder': return 'var(--color-info)';
      case 'file': return 'var(--color-success)';
      case 'config': return 'var(--color-warning)';
      case 'script': return 'var(--color-primary)';
      case 'asset': return 'var(--color-border)';
      default: return 'var(--color-borderSecondary)';
    }
  }};
  color: var(--color-textInverse);
  padding: 6px 12px;
  border-radius: 6px;
  font-size: 12px;
  font-weight: 700;
  transition: all 0.3s ease;
  box-shadow: 0 2px 6px var(--color-shadow);
  border: 1px solid rgba(255, 255, 255, 0.1);

  &:hover {
    transform: translateY(-1px);
    box-shadow: 0 4px 8px var(--color-shadowHover);
  }
`;

const StructureDescription = styled.p`
  color: var(--color-textPrimary);
  margin: 0 0 18px 0;
  line-height: 1.7;
  font-size: 16px;
  font-weight: 600;
  transition: color 0.3s ease;
  text-shadow: 0 1px 2px var(--color-shadow);
`;

const StructureTags = styled.div`
  display: flex;
  flex-wrap: wrap;
  gap: 8px;
  margin: 15px 0;
`;

const Tag = styled.span`
  background-color: var(--color-border);
  color: var(--color-textInverse);
  padding: 6px 10px;
  border-radius: 6px;
  font-size: 12px;
  font-weight: 600;
  transition: all 0.3s ease;
  box-shadow: 0 2px 4px var(--color-shadow);
  border: 1px solid rgba(255, 255, 255, 0.1);

  &:hover {
    background-color: var(--color-primary);
    transform: translateY(-1px);
    box-shadow: 0 3px 6px var(--color-shadowHover);
  }
`;

const CodeExample = styled.div`
  background-color: var(--color-backgroundSecondary);
  border: 3px solid var(--color-primary);
  border-radius: 10px;
  padding: 20px;
  margin: 18px 0;
  font-family: 'Courier New', monospace;
  font-size: 14px;
  font-weight: 600;
  color: var(--color-primary);
  overflow-x: auto;
  transition: all 0.3s ease;
  box-shadow: 0 4px 12px var(--color-shadow);
  position: relative;

  &:hover {
    border-color: var(--color-primaryHover);
    box-shadow: 0 6px 16px var(--color-shadowHover);
    transform: translateY(-2px);
  }

  &::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    height: 3px;
    background: linear-gradient(90deg, var(--color-primary), var(--color-primaryHover));
    border-radius: 10px 10px 0 0;
  }
`;

const InfoBox = styled.div`
  background-color: var(--color-surfaceSecondary);
  border: 3px solid var(--color-success);
  border-radius: 14px;
  padding: 24px;
  margin: 24px 0;
  transition: all 0.3s ease;
  box-shadow: 0 6px 20px var(--color-shadow);
  position: relative;

  &:hover {
    border-color: var(--color-successHover);
    box-shadow: 0 8px 24px var(--color-shadowHover);
    transform: translateY(-3px);
  }

  &::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    height: 4px;
    background: linear-gradient(90deg, var(--color-success), var(--color-successHover));
    border-radius: 14px 14px 0 0;
  }
`;

const InfoTitle = styled.div`
  color: var(--color-success);
  font-weight: 800;
  font-size: 18px;
  margin-bottom: 16px;
  display: flex;
  align-items: center;
  gap: 10px;
  transition: color 0.3s ease;
  text-shadow: 0 2px 4px var(--color-shadow);
  letter-spacing: 0.3px;
`;

const InfoText = styled.div`
  color: var(--color-textPrimary);
  font-size: 16px;
  line-height: 1.7;
  font-weight: 600;
  transition: color 0.3s ease;
  text-shadow: 0 1px 2px var(--color-shadow);
`;

const TipBox = styled.div`
  background-color: var(--color-surfaceSecondary);
  border: 3px solid var(--color-warning);
  border-radius: 14px;
  padding: 24px;
  margin: 24px 0;
  transition: all 0.3s ease;
  box-shadow: 0 6px 20px var(--color-shadow);
  position: relative;

  &:hover {
    border-color: var(--color-warningHover);
    box-shadow: 0 8px 24px var(--color-shadowHover);
    transform: translateY(-3px);
  }

  &::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    height: 4px;
    background: linear-gradient(90deg, var(--color-warning), var(--color-warningHover));
    border-radius: 14px 14px 0 0;
  }
`;

const TipTitle = styled.div`
  color: var(--color-warning);
  font-weight: 800;
  font-size: 18px;
  margin-bottom: 16px;
  display: flex;
  align-items: center;
  gap: 10px;
  transition: color 0.3s ease;
  text-shadow: 0 2px 4px var(--color-shadow);
  letter-spacing: 0.3px;
`;

const TipText = styled.div`
  color: var(--color-textPrimary);
  font-size: 16px;
  line-height: 1.7;
  font-weight: 600;
  transition: color 0.3s ease;
  text-shadow: 0 1px 2px var(--color-shadow);
`;

const WarningBox = styled.div`
  background-color: var(--color-surfaceSecondary);
  border: 3px solid var(--color-error);
  border-radius: 14px;
  padding: 24px;
  margin: 24px 0;
  transition: all 0.3s ease;
  box-shadow: 0 6px 20px var(--color-shadow);
  position: relative;

  &:hover {
    border-color: var(--color-errorHover);
    box-shadow: 0 8px 24px var(--color-shadowHover);
    transform: translateY(-3px);
  }

  &::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    height: 4px;
    background: linear-gradient(90deg, var(--color-error), var(--color-errorHover));
    border-radius: 14px 14px 0 0;
  }
`;

const WarningTitle = styled.div`
  color: var(--color-error);
  font-weight: 800;
  font-size: 18px;
  margin-bottom: 16px;
  display: flex;
  align-items: center;
  gap: 10px;
  transition: color 0.3s ease;
  text-shadow: 0 2px 4px var(--color-shadow);
  letter-spacing: 0.3px;
`;

const WarningText = styled.div`
  color: var(--color-textPrimary);
  font-size: 16px;
  line-height: 1.7;
  font-weight: 600;
  transition: color 0.3s ease;
  text-shadow: 0 1px 2px var(--color-shadow);
`;

function ProjectStructurePage({ onClose }) {
  const [activeCategory, setActiveCategory] = useState('folders');
  const [searchTerm, setSearchTerm] = useState('');

  const structureData = {
    folders: [
      {
        id: 'game-folder',
        title: 'game/',
        icon: '📁',
        type: 'folder',
        description: 'Carpeta principal que contiene todos los archivos del juego. Es el corazón de tu proyecto Ren\'Py.',
        tags: ['principal', 'obligatorio', 'archivos del juego']
      },
      {
        id: 'images-folder',
        title: 'images/',
        icon: '🖼️',
        type: 'folder',
        description: 'Carpeta para almacenar todas las imágenes del juego: sprites, fondos, GUI, etc.',
        tags: ['imágenes', 'sprites', 'fondos', 'GUI']
      },
      {
        id: 'audio-folder',
        title: 'audio/',
        icon: '🎵',
        type: 'folder',
        description: 'Carpeta para archivos de audio: música de fondo, efectos de sonido y voces.',
        tags: ['audio', 'música', 'efectos', 'voces']
      },
      {
        id: 'gui-folder',
        title: 'gui/',
        icon: '🎨',
        type: 'folder',
        description: 'Carpeta para elementos de la interfaz gráfica personalizada.',
        tags: ['interfaz', 'GUI', 'botones', 'personalización']
      }
    ],
    files: [
      {
        id: 'script-rpy',
        title: 'script.rpy',
        icon: '📝',
        type: 'file',
        description: 'Archivo principal del script que contiene la historia y diálogos del juego.',
        tags: ['script', 'historia', 'diálogos', 'principal']
      },
      {
        id: 'options-rpy',
        title: 'options.rpy',
        icon: '⚙️',
        type: 'config',
        description: 'Archivo de configuración que define las opciones del juego como resolución, título, etc.',
        tags: ['configuración', 'opciones', 'resolución', 'título']
      },
      {
        id: 'gui-rpy',
        title: 'gui.rpy',
        icon: '🎨',
        type: 'config',
        description: 'Archivo que define los estilos y apariencia de la interfaz gráfica.',
        tags: ['GUI', 'estilos', 'interfaz', 'apariencia']
      },
      {
        id: 'screens-rpy',
        title: 'screens.rpy',
        icon: '🖥️',
        type: 'script',
        description: 'Define las pantallas del juego como menús, configuración, etc.',
        tags: ['pantallas', 'menús', 'configuración', 'screens']
      }
    ]
  };

  const getCurrentStructure = () => {
    let currentStructure = structureData[activeCategory] || [];
    
    if (searchTerm) {
      currentStructure = currentStructure.filter(item =>
        item.title.toLowerCase().includes(searchTerm.toLowerCase()) ||
        item.description.toLowerCase().includes(searchTerm.toLowerCase()) ||
        item.tags.some(tag => tag.toLowerCase().includes(searchTerm.toLowerCase()))
      );
    }
    
    return currentStructure;
  };

  const getTypeText = (type) => {
    switch(type) {
      case 'folder': return 'Carpeta';
      case 'file': return 'Archivo';
      case 'config': return 'Config';
      case 'script': return 'Script';
      case 'asset': return 'Asset';
      default: return 'Otro';
    }
  };

  return (
    <ProjectStructureContainer>
      <ProjectStructureHeader>
        <ProjectStructureTitle>
          📁 Estructura de Proyectos
        </ProjectStructureTitle>
        <CloseButton onClick={onClose}>
          ✕ Cerrar
        </CloseButton>
      </ProjectStructureHeader>

      <ProjectStructureContent>
        <SearchBox>
          <SearchTitle>
            🔍 Buscar en Estructura
          </SearchTitle>
          <SearchInput
            type="text"
            placeholder="Busca carpetas, archivos o conceptos específicos..."
            value={searchTerm}
            onChange={(e) => setSearchTerm(e.target.value)}
          />
        </SearchBox>

        <CategoryTabs>
          <CategoryTab 
            active={activeCategory === 'folders'} 
            onClick={() => setActiveCategory('folders')}
          >
            📁 Carpetas
          </CategoryTab>
          <CategoryTab 
            active={activeCategory === 'files'} 
            onClick={() => setActiveCategory('files')}
          >
            📄 Archivos
          </CategoryTab>
        </CategoryTabs>

        <StructureGrid>
          {getCurrentStructure().map((item) => (
            <StructureCard key={item.id}>
              <StructureHeader>
                <StructureTitle>
                  {item.icon} {item.title}
                </StructureTitle>
                <StructureType type={item.type}>
                  {getTypeText(item.type)}
                </StructureType>
              </StructureHeader>
              
              <StructureDescription>
                {item.description}
              </StructureDescription>

              <StructureTags>
                {item.tags.map((tag, index) => (
                  <Tag key={index}>{tag}</Tag>
                ))}
              </StructureTags>
            </StructureCard>
          ))}
        </StructureGrid>

        {getCurrentStructure().length === 0 && searchTerm && (
          <div style={{ 
            textAlign: 'center', 
            color: 'var(--color-textTertiary)', 
            fontSize: '18px',
            marginTop: '50px',
            fontWeight: '600',
            textShadow: '0 1px 2px var(--color-shadow)'
          }}>
            No se encontraron elementos que coincidan con "{searchTerm}"
          </div>
        )}

        <InfoBox>
          <InfoTitle>
            📚 Información Importante
          </InfoTitle>
          <InfoText>
            La estructura de carpetas es fundamental para el funcionamiento de Ren'Py. 
            Cada carpeta y archivo tiene un propósito específico según la documentación oficial.
          </InfoText>
        </InfoBox>

        <TipBox>
          <TipTitle>
            💡 Consejo de Organización
          </TipTitle>
          <TipText>
            Mantén una estructura organizada desde el inicio del proyecto. 
            Usa nombres descriptivos para tus archivos y organiza las imágenes por categorías.
          </TipText>
        </TipBox>

        <WarningBox>
          <WarningTitle>
            ⚠️ Importante
          </WarningTitle>
          <WarningText>
            No modifiques la estructura básica de carpetas de Ren'Py sin conocer las consecuencias. 
            Algunos archivos son obligatorios para el funcionamiento del motor.
          </WarningText>
        </WarningBox>
      </ProjectStructureContent>
    </ProjectStructureContainer>
  );
}

export default ProjectStructurePage;

