import React, { useState } from 'react';
import styled from 'styled-components';

const InstructionsContainer = styled.div`
  background-color: var(--color-background);
  border: 2px solid var(--color-primary);
  border-radius: 8px;
  padding: 16px;
  margin-bottom: 16px;
  
  .instructions-title {
    font-size: 14px;
    font-weight: 600;
    color: var(--color-primary);
    margin-bottom: 12px;
    display: flex;
    align-items: center;
    gap: 8px;
  }
  
  .instructions-content {
    font-size: 12px;
    color: var(--color-textPrimary);
    line-height: 1.5;
  }
  
  .step {
    margin-bottom: 8px;
    padding-left: 20px;
    position: relative;
    
    &::before {
      content: counter(step-counter);
      counter-increment: step-counter;
      position: absolute;
      left: 0;
      top: 0;
      background-color: var(--color-primary);
      color: var(--color-textInverse);
      width: 16px;
      height: 16px;
      border-radius: 50%;
      display: flex;
      align-items: center;
      justify-content: center;
      font-size: 10px;
      font-weight: 600;
    }
  }
  
  .steps-list {
    counter-reset: step-counter;
  }
  
  .highlight {
    background-color: var(--color-primary);
    color: var(--color-textInverse);
    padding: 2px 6px;
    border-radius: 4px;
    font-family: 'Courier New', monospace;
    font-weight: 600;
  }
  
  .toggle-button {
    background-color: var(--color-surfaceElevated);
    color: var(--color-textPrimary);
    border: 1px solid var(--color-border);
    padding: 6px 12px;
    border-radius: 4px;
    cursor: pointer;
    font-size: 11px;
    margin-bottom: 12px;
    
    &:hover {
      background-color: var(--color-borderSecondary);
    }
  }
`;

function CollaborationInstructions({ serverInfo, isHost }) {
  const [showInstructions, setShowInstructions] = useState(false);

  if (!serverInfo) return null;

  return (
    <InstructionsContainer>
      <div className="instructions-title">
        📋 Instrucciones de Conexión
        <button 
          className="toggle-button"
          onClick={() => setShowInstructions(!showInstructions)}
        >
          {showInstructions ? '👆 Ocultar' : '👇 Mostrar'}
        </button>
      </div>
      
      {showInstructions && (
        <div className="instructions-content">
          {isHost ? (
            <div className="steps-list">
              <div className="step">
                <strong>Comparte esta información:</strong> <span className="highlight">{serverInfo.display}</span>
              </div>
              <div className="step">
                Los otros usuarios deben usar esta IP para conectarse
              </div>
              <div className="step">
                Asegúrate de que el puerto <span className="highlight">3002</span> esté abierto en tu firewall
              </div>
              <div className="step">
                Si usas IP local, solo funciona en la misma red WiFi
              </div>
              <div className="step">
                Si usas IP pública, funciona desde cualquier lugar
              </div>
            </div>
          ) : (
            <div className="steps-list">
              <div className="step">
                <strong>Conecta usando:</strong> <span className="highlight">{serverInfo.display}</span>
              </div>
              <div className="step">
                Pega esta IP en el campo "URL del servidor"
              </div>
              <div className="step">
                Agrega <span className="highlight">ws://</span> al inicio y <span className="highlight">/collaboration</span> al final
              </div>
              <div className="step">
                Ejemplo: <span className="highlight">ws://{serverInfo.display}/collaboration</span>
              </div>
              <div className="step">
                Si no funciona, verifica que el host esté ejecutando el servidor
              </div>
            </div>
          )}
        </div>
      )}
    </InstructionsContainer>
  );
}

export default CollaborationInstructions;
