import React from 'react';
import styled, { keyframes, css } from 'styled-components';
import { useVisualNovel } from '../context/VisualNovelContext';
import ScriptEditor from './ScriptEditor';
import TimelineView from './TimelineView';
import DecisionMapPanel from './panels/DecisionMapPanel';
import VariablesPanel from './panels/VariablesPanel';

// Configuración centralizada de pestañas del editor
const EDITOR_TABS_CONFIG = [
  {
    id: 'script',
    label: 'Editor de Script',
    icon: '📝',
    color: '#4a9eff',
    description: 'Crear y editar elementos de la historia',
    component: ScriptEditor
  },
  {
    id: 'timeline',
    label: 'Vista de Línea de Tiempo',
    icon: '⏱️',
    color: '#9eff4a',
    description: 'Ver la secuencia temporal de elementos',
    component: TimelineView
  },
  {
    id: 'decisions',
    label: 'Mapa de Decisiones',
    icon: '🗺️',
    color: '#ff9e4a',
    description: 'Visualizar el flujo de decisiones',
    component: DecisionMapPanel
  },
  {
    id: 'variables',
    label: 'Variables y Flags',
    icon: '📊',
    color: '#ff4a9e',
    description: 'Gestionar variables del juego',
    component: VariablesPanel
  }
];

// Animaciones
const slideIn = keyframes`
  from {
    opacity: 0;
    transform: translateY(10px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
`;

const tabGlow = keyframes`
  0%, 100% {
    box-shadow: 0 0 0 rgba(37, 99, 235, 0);
  }
  50% {
    box-shadow: 0 0 20px rgba(37, 99, 235, 0.3);
  }
`;

const EditorContainer = styled.div`
  flex: 1;
  display: flex;
  flex-direction: column;
  background: linear-gradient(135deg, var(--color-backgroundSecondary) 0%, var(--color-surface) 100%);
  overflow: hidden;
  transition: all 0.3s ease;
  border-radius: 16px 0 0 0;
  border: 1px solid var(--color-border);
  box-shadow: 0 4px 16px rgba(0, 0, 0, 0.1);
`;

const EditorTabs = styled.div`
  display: flex;
  background: linear-gradient(135deg, var(--color-surface) 0%, var(--color-backgroundElevated) 100%);
  border-bottom: 2px solid var(--color-border);
  transition: all 0.3s ease;
  padding: 8px 16px 0 16px;
  border-radius: 16px 0 0 0;
  position: relative;
  
  &::before {
    content: '';
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    height: 1px;
    background: linear-gradient(90deg, transparent, var(--color-primary), transparent);
    opacity: 0.3;
  }
`;

const Tab = styled.button`
  display: flex;
  align-items: center;
  gap: 8px;
  padding: 14px 20px;
  background: ${props => props.active 
    ? `linear-gradient(135deg, ${props.color || 'var(--color-primary)'} 0%, ${props.color || 'var(--color-primary)'}dd 100%)`
    : 'linear-gradient(135deg, transparent 0%, rgba(255, 255, 255, 0.05) 100%)'
  };
  color: ${props => props.active ? 'var(--color-textInverse)' : 'var(--color-textPrimary)'};
  border: none;
  cursor: pointer;
  font-size: 14px;
  font-weight: 600;
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  border-radius: 12px 12px 0 0;
  position: relative;
  overflow: hidden;
  min-width: 160px;
  justify-content: center;
  
  /* Efecto de brillo */
  &::before {
    content: '';
    position: absolute;
    top: 0;
    left: -100%;
    width: 100%;
    height: 100%;
    background: linear-gradient(90deg, transparent, rgba(255, 255, 255, 0.2), transparent);
    transition: left 0.5s;
  }

  &:hover {
    background: ${props => props.active 
      ? `linear-gradient(135deg, ${props.color || 'var(--color-primary)'} 0%, ${props.color || 'var(--color-primary)'}dd 100%)`
      : 'linear-gradient(135deg, var(--color-border) 0%, var(--color-surfaceSecondary) 100%)'
    };
    transform: translateY(-2px);
    box-shadow: ${props => props.active 
      ? `0 8px 24px ${props.color || 'var(--color-primary)'}40`
      : '0 4px 12px rgba(0, 0, 0, 0.1)'
    };
    
    &::before {
      left: 100%;
    }
  }
  
  &:active {
    transform: translateY(-1px);
  }
  
  /* Animación de brillo para pestaña activa */
  ${props => props.active && css`
    animation: ${tabGlow} 3s ease-in-out infinite;
  `}
`;

const EditorContent = styled.div`
  flex: 1;
  overflow: hidden;
  background: var(--color-surface);
  border-radius: 0 0 16px 0;
  position: relative;
  
  /* Efecto de entrada */
  animation: ${slideIn} 0.4s ease-out;
`;

function MainEditor() {
  const { state, storage } = useVisualNovel();
  const [activeTab, setActiveTab] = React.useState(() => {
    // Cargar la última pestaña activa del editor guardada
    return storage.getLastActiveTab('mainEditor') || 'script';
  });

  // Guardar pestaña activa cuando cambie
  const handleTabChange = (tabId) => {
    setActiveTab(tabId);
    storage.setLastActiveTab(tabId, 'mainEditor');
  };

  // Obtener la configuración de la pestaña activa
  const activeTabConfig = EDITOR_TABS_CONFIG.find(tab => tab.id === activeTab);
  const ActiveComponent = activeTabConfig?.component;

  return (
    <EditorContainer>
      <EditorTabs>
        {EDITOR_TABS_CONFIG.map(tab => (
          <Tab
            key={tab.id}
            active={activeTab === tab.id}
            color={tab.color}
            onClick={() => handleTabChange(tab.id)}
            title={tab.description}
          >
            <span style={{ fontSize: '16px' }}>{tab.icon}</span>
            <div>
              <div style={{ fontSize: '13px', fontWeight: '700' }}>{tab.label}</div>
              <div style={{ fontSize: '10px', opacity: 0.8, marginTop: '2px' }}>
                {tab.description}
              </div>
            </div>
        </Tab>
        ))}
      </EditorTabs>
      
      <EditorContent>
        {ActiveComponent && <ActiveComponent />}
      </EditorContent>
    </EditorContainer>
  );
}

export default MainEditor;
