import React from 'react';
import styled, { keyframes, css } from 'styled-components';
import { useVisualNovel } from '../context/VisualNovelContext';

// Animaciones
const spin = keyframes`
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
`;

const fadeIn = keyframes`
  from { opacity: 0; }
  to { opacity: 1; }
`;

const pulse = keyframes`
  0%, 100% { opacity: 1; }
  50% { opacity: 0.5; }
`;

// Componentes estilizados
const LoadingOverlay = styled.div`
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(26, 26, 26, 0.95);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  z-index: 9999;
  animation: ${fadeIn} 0.3s ease-out;
`;

const LoadingContent = styled.div`
  text-align: center;
  color: white;
  max-width: 400px;
  padding: 20px;
`;

const LoadingSpinner = styled.div`
  width: 50px;
  height: 50px;
  border: 3px solid rgba(74, 158, 255, 0.3);
  border-top: 3px solid var(--color-primary);
  border-radius: 50%;
  animation: ${spin} 1s linear infinite;
  margin: 0 auto 20px;
`;

const LoadingTitle = styled.h2`
  font-size: 24px;
  margin: 0 0 10px 0;
  color: var(--color-primary);
  font-weight: 600;
  transition: color 0.3s ease;
`;

const LoadingSubtitle = styled.p`
  font-size: 16px;
  margin: 0 0 20px 0;
  color: #cccccc;
  line-height: 1.4;
`;

const LoadingSteps = styled.div`
  display: flex;
  flex-direction: column;
  gap: 8px;
  margin-top: 20px;
`;

const LoadingStep = styled.div`
  display: flex;
  align-items: center;
  gap: 10px;
  font-size: 14px;
  color: ${props => props.completed ? '#4caf50' : '#888888'};
  
  ${props => props.active && css`
    color: #4a9eff;
    animation: ${pulse} 1.5s infinite;
  `}
`;

const StepIcon = styled.span`
  font-size: 16px;
  width: 20px;
  text-align: center;
`;

const LoadingProgress = styled.div`
  width: 100%;
  height: 4px;
  background: rgba(74, 158, 255, 0.2);
  border-radius: 2px;
  overflow: hidden;
  margin: 20px 0;
`;

const ProgressBar = styled.div`
  height: 100%;
  background: linear-gradient(90deg, #4a9eff, #66c1e0);
  border-radius: 2px;
  width: ${props => props.progress || 0}%;
  transition: width 0.3s ease-out;
`;

const LoadingStats = styled.div`
  margin-top: 20px;
  padding: 15px;
  background: rgba(255, 255, 255, 0.05);
  border-radius: 8px;
  border: 1px solid rgba(74, 158, 255, 0.2);
`;

const StatItem = styled.div`
  display: flex;
  justify-content: space-between;
  margin: 5px 0;
  font-size: 12px;
  color: #aaaaaa;
`;

const StatLabel = styled.span``;
const StatValue = styled.span`
  color: #4a9eff;
  font-weight: 500;
`;

function LoadingScreen() {
  const { isLoading, storage } = useVisualNovel();
  const [currentStep, setCurrentStep] = React.useState(0);
  const [progress, setProgress] = React.useState(0);
  const [stats, setStats] = React.useState(null);

  // Simular progreso de carga
  React.useEffect(() => {
    if (!isLoading) return;

    const steps = [
      { text: 'Cargando estado guardado...', duration: 800 },
      { text: 'Restaurando proyecto...', duration: 600 },
      { text: 'Cargando personajes...', duration: 400 },
      { text: 'Restaurando escenas...', duration: 500 },
      { text: 'Finalizando...', duration: 300 }
    ];

    let currentStepIndex = 0;
    let totalProgress = 0;

    const updateProgress = () => {
      if (currentStepIndex < steps.length) {
        setCurrentStep(currentStepIndex);
        
        // Actualizar progreso gradualmente
        const stepProgress = (currentStepIndex + 1) * 20;
        setProgress(stepProgress);
        
        // Obtener estadísticas del proyecto si está disponible
        if (currentStepIndex === 1) {
          const saveInfo = storage.getLastSaveInfo();
          if (saveInfo) {
            setStats({
              projectName: saveInfo.projectName,
              lastSaved: new Date(saveInfo.lastSaved).toLocaleString('es-ES')
            });
          }
        }
        
        currentStepIndex++;
        
        if (currentStepIndex < steps.length) {
          setTimeout(updateProgress, steps[currentStepIndex - 1].duration);
        } else {
          // Completar al 100%
          setProgress(100);
          setTimeout(() => {
            setCurrentStep(steps.length);
          }, 300);
        }
      }
    };

    updateProgress();
  }, [isLoading, storage]);

  if (!isLoading) return null;

  const steps = [
    { text: 'Cargando estado guardado...', icon: '📂' },
    { text: 'Restaurando proyecto...', icon: '📁' },
    { text: 'Cargando personajes...', icon: '👥' },
    { text: 'Restaurando escenas...', icon: '🎭' },
    { text: 'Finalizando...', icon: '✨' }
  ];

  return (
    <LoadingOverlay>
      <LoadingContent>
        <LoadingSpinner />
        
        <LoadingTitle>Recuperando tu trabajo</LoadingTitle>
        <LoadingSubtitle>
          No te preocupes, hemos guardado automáticamente todo tu progreso
        </LoadingSubtitle>
        
        <LoadingProgress>
          <ProgressBar progress={progress} />
        </LoadingProgress>
        
        <LoadingSteps>
          {steps.map((step, index) => (
            <LoadingStep
              key={index}
              completed={index < currentStep}
              active={index === currentStep}
            >
              <StepIcon>
                {index < currentStep ? '✅' : step.icon}
              </StepIcon>
              {step.text}
            </LoadingStep>
          ))}
        </LoadingSteps>
        
        {stats && (
          <LoadingStats>
            <StatItem>
              <StatLabel>Proyecto:</StatLabel>
              <StatValue>{stats.projectName}</StatValue>
            </StatItem>
            <StatItem>
              <StatLabel>Último guardado:</StatLabel>
              <StatValue>{stats.lastSaved}</StatValue>
            </StatItem>
          </LoadingStats>
        )}
      </LoadingContent>
    </LoadingOverlay>
  );
}

export default LoadingScreen;
