import React, { useState, useEffect } from 'react';
import styled, { keyframes } from 'styled-components';

// Configuración de botones flotantes
const FLOATING_BUTTONS_CONFIG = [
  {
    id: 'menu',
    position: 0,
    color: 'success',
    icon: '📋',
    tooltip: 'Herramientas y Guías',
    hasSubMenu: true,
    subMenuItems: [
      { id: 'templates', label: 'Plantillas', icon: '📄' },
      { id: 'troubleshooting', label: 'Solución de Problemas', icon: '🔧' },
      { id: 'beginnersGuide', label: 'Guía para Principiantes', icon: '📚' },
      { id: 'resources', label: 'Recursos', icon: '🎁' },
      { id: 'projectStructure', label: 'Estructura del Proyecto', icon: '🏗️' },
      { id: 'basicCustomization', label: 'Personalización Básica', icon: '🎨' },
      { id: 'troubleshooting2', label: 'Solución Avanzada', icon: '⚙️' },
      { id: 'glossary', label: 'Glosario', icon: '📖' },
      { id: 'guide', label: 'Guía Completa', icon: '📋' }
    ]
  },
  {
    id: 'info',
    position: 1,
    color: 'warning',
    icon: 'ℹ️',
    tooltip: 'Información del Editor',
    hasModal: true
  },
  {
    id: 'options',
    position: 2,
    color: 'error',
    icon: '⚙️',
    tooltip: 'Guía de Opciones',
    hasModal: true
  },
  {
    id: 'help',
    position: 3,
    color: 'info',
    icon: '❓',
    tooltip: 'Ayuda y Funcionalidades',
    hasModal: true
  },
  {
    id: 'about',
    position: 4,
    color: 'primary',
    icon: '💡',
    tooltip: 'Acerca de RynEditor',
    hasModal: true
  }
];

// Animaciones mejoradas
const float = keyframes`
  0%, 100% {
    transform: translateY(0px);
  }
  50% {
    transform: translateY(-3px);
  }
`;


const slideIn = keyframes`
  from {
    transform: translateX(100px) scale(0.8);
    opacity: 0;
  }
  to {
    transform: translateX(0) scale(1);
    opacity: 1;
  }
`;

const modalSlideIn = keyframes`
  from {
    transform: translate(-50%, -50%) scale(0.7);
    opacity: 0;
  }
  to {
    transform: translate(-50%, -50%) scale(1);
    opacity: 1;
  }
`;

// Contenedor principal
const FloatingButtonsContainer = styled.div`
  position: fixed;
  bottom: 20px;
  right: 20px;
  display: flex;
  flex-direction: row-reverse;
  gap: 16px;
  z-index: 1000;
  pointer-events: none; /* Permite que los botones individuales manejen los eventos */
`;

// Wrapper para controlar animaciones
const ButtonWrapper = styled.div`
  position: relative;
  pointer-events: auto;
  
  /* Animación flotante sutil solo después de la entrada */
  &:not(:hover) {
    animation: ${float} 6s ease-in-out ${props => props.delay + 1.2}s infinite;
  }
  
  /* Detener animación flotante en hover para evitar conflictos */
  &:hover {
    animation: none;
  }
`;

// Botón flotante mejorado
const FloatingButton = styled.button`
  position: relative;
  width: 56px;
  height: 56px;
  border: none;
  border-radius: 50%;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 18px;
  font-weight: 600;
  pointer-events: auto;
  
  /* Colores dinámicos */
  background: ${props => {
    const colors = {
      primary: 'linear-gradient(135deg, var(--color-primary) 0%, var(--color-primaryHover) 100%)',
      success: 'linear-gradient(135deg, var(--color-success) 0%, var(--color-successHover) 100%)',
      warning: 'linear-gradient(135deg, var(--color-warning) 0%, var(--color-warningHover) 100%)',
      error: 'linear-gradient(135deg, var(--color-error) 0%, var(--color-errorHover) 100%)',
      info: 'linear-gradient(135deg, var(--color-info) 0%, var(--color-infoHover) 100%)'
    };
    return colors[props.colorType] || colors.primary;
  }};
  
  color: var(--color-textInverse);
  box-shadow: 0 6px 20px rgba(0, 0, 0, 0.15);
  transition: transform 0.3s cubic-bezier(0.4, 0, 0.2, 1),
              box-shadow 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  
  /* Solo animación de entrada - sin conflictos */
  animation: ${slideIn} 0.6s ease-out ${props => props.delay}s both;

  /* Hover sin animación que compita */
  &:hover {
    transform: translateY(-8px) scale(1.1);
    box-shadow: 0 12px 32px rgba(0, 0, 0, 0.25);
  }

  &:active {
    transform: translateY(-4px) scale(1.05);
    box-shadow: 0 8px 24px rgba(0, 0, 0, 0.2);
  }

  /* Efecto de brillo */
  &::before {
    content: '';
    position: absolute;
    top: 2px;
    left: 2px;
    right: 2px;
    height: 50%;
    background: linear-gradient(180deg, rgba(255, 255, 255, 0.3) 0%, transparent 100%);
    border-radius: 50% 50% 0 0;
    pointer-events: none;
  }
`;

// Tooltip mejorado
const Tooltip = styled.div`
  position: absolute;
  bottom: 70px;
  left: 50%;
  transform: translateX(-50%);
  background: linear-gradient(135deg, var(--color-backgroundElevated) 0%, var(--color-surface) 100%);
  color: var(--color-textPrimary);
  padding: 8px 12px;
  border-radius: 12px;
  font-size: 12px;
  font-weight: 500;
  white-space: nowrap;
  opacity: 0;
  visibility: hidden;
  transition: opacity 0.2s ease, visibility 0.2s ease, transform 0.2s ease;
  pointer-events: none;
  z-index: 1001;
  border: 1px solid var(--color-border);
  box-shadow: 0 4px 16px rgba(0, 0, 0, 0.1);
  backdrop-filter: blur(8px);
  -webkit-backdrop-filter: blur(8px);

  /* Flecha del tooltip */
  &::after {
    content: '';
    position: absolute;
    top: 100%;
    left: 50%;
    transform: translateX(-50%);
    border: 6px solid transparent;
    border-top-color: var(--color-backgroundElevated);
  }

  ${ButtonWrapper}:hover & {
    opacity: 1;
    visibility: visible;
    transform: translateX(-50%) translateY(-4px);
  }
`;

// Modal overlay mejorado
const ModalOverlay = styled.div`
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: linear-gradient(135deg, rgba(0, 0, 0, 0.6) 0%, rgba(0, 0, 0, 0.8) 100%);
  backdrop-filter: blur(8px);
  -webkit-backdrop-filter: blur(8px);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 2000;
  opacity: ${props => props.isOpen ? 1 : 0};
  visibility: ${props => props.isOpen ? 'visible' : 'hidden'};
  transition: all 0.4s cubic-bezier(0.4, 0, 0.2, 1);
`;

// Modal content mejorado
const ModalContent = styled.div`
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%) scale(${props => props.isOpen ? 1 : 0.8});
  background: linear-gradient(135deg, var(--color-surface) 0%, var(--color-backgroundElevated) 100%);
  border-radius: 20px;
  padding: 32px;
  max-width: 90vw;
  max-height: 85vh;
  width: ${props => props.width || '600px'};
  overflow-y: auto;
  border: 1px solid var(--color-border);
  box-shadow: 0 20px 60px rgba(0, 0, 0, 0.3);
  transition: all 0.4s cubic-bezier(0.4, 0, 0.2, 1);
  animation: ${props => props.isOpen ? modalSlideIn : 'none'} 0.4s ease-out;
  
  /* Scrollbar personalizado */
  &::-webkit-scrollbar {
    width: 8px;
  }
  
  &::-webkit-scrollbar-track {
    background: transparent;
  }
  
  &::-webkit-scrollbar-thumb {
    background-color: var(--color-border);
    border-radius: 4px;
    transition: background-color 0.3s ease;
  }
  
  &::-webkit-scrollbar-thumb:hover {
    background-color: var(--color-primary);
  }
`;

// Header del modal
const ModalHeader = styled.div`
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 24px;
  padding-bottom: 16px;
  border-bottom: 2px solid var(--color-border);
`;

const ModalTitle = styled.h2`
  margin: 0;
  font-size: 24px;
  font-weight: 700;
  color: var(--color-textPrimary);
  display: flex;
  align-items: center;
  gap: 12px;
`;

const CloseButton = styled.button`
  background: none;
  border: none;
  font-size: 24px;
  cursor: pointer;
  color: var(--color-textSecondary);
  width: 32px;
  height: 32px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all 0.3s ease;

  &:hover {
    background-color: var(--color-backgroundSecondary);
    color: var(--color-textPrimary);
    transform: scale(1.1);
  }

  &:active {
    transform: scale(0.95);
  }
`;

// Estilos para el submenú
const SubMenuContainer = styled.div`
  position: absolute;
  bottom: 70px;
  right: 0;
  z-index: 1002;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 12px;
  padding: 16px;
  background: linear-gradient(135deg, var(--color-surface) 0%, var(--color-backgroundElevated) 100%);
  border-radius: 16px;
  border: 1px solid var(--color-border);
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.2);
  backdrop-filter: blur(12px);
  -webkit-backdrop-filter: blur(12px);
  opacity: ${props => props.isOpen ? 1 : 0};
  visibility: ${props => props.isOpen ? 'visible' : 'hidden'};
  transform: ${props => props.isOpen ? 'translateY(0) scale(1)' : 'translateY(10px) scale(0.9)'};
  transition: all 0.4s cubic-bezier(0.4, 0, 0.2, 1);
  pointer-events: ${props => props.isOpen ? 'auto' : 'none'};
  min-width: 320px;
`;

const SubMenuButton = styled.button`
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 8px;
  padding: 12px 8px;
  background: linear-gradient(135deg, var(--color-backgroundSecondary) 0%, var(--color-surface) 100%);
  border: 1px solid var(--color-border);
  border-radius: 12px;
  cursor: pointer;
  transition: all 0.3s ease;
  color: var(--color-textPrimary);
  font-size: 11px;
  font-weight: 500;
  text-align: center;
  line-height: 1.2;
  min-height: 70px;

  &:hover {
    background: linear-gradient(135deg, var(--color-primary) 0%, var(--color-primaryHover) 100%);
    color: var(--color-textInverse);
    transform: translateY(-2px) scale(1.05);
    box-shadow: 0 4px 16px rgba(74, 158, 255, 0.3);
  }

  &:active {
    transform: translateY(0) scale(1);
  }
`;

const SubMenuIcon = styled.span`
  font-size: 18px;
  display: block;
`;

const SubMenuLabel = styled.span`
  font-size: 10px;
  font-weight: 600;
  text-align: center;
  max-width: 100%;
  word-wrap: break-word;
  line-height: 1.1;
`;

function FloatingButtonsManager({ 
  onMenuClick,
  onInfoClick, 
  onOptionsClick, 
  onHelpClick, 
  onAboutClick,
  // Handlers para el submenú
  onNavigateToTemplates,
  onNavigateToTroubleshooting,
  onNavigateToBeginnersGuide,
  onNavigateToResources,
  onNavigateToProjectStructure,
  onNavigateToBasicCustomization,
  onNavigateToTroubleshooting2,
  onNavigateToGlossary,
  onNavigateToGuide,
  children 
}) {
  const [activeModal, setActiveModal] = useState(null);
  const [showSubMenu, setShowSubMenu] = useState(false);

  // Manejar escape key
  useEffect(() => {
    const handleEscape = (e) => {
      if (e.key === 'Escape') {
        if (activeModal) {
          setActiveModal(null);
        } else if (showSubMenu) {
          setShowSubMenu(false);
        }
      }
    };

    if (activeModal) {
      document.addEventListener('keydown', handleEscape);
      document.body.style.overflow = 'hidden';
    } else {
      document.body.style.overflow = 'unset';
    }

    if (showSubMenu || activeModal) {
      document.addEventListener('keydown', handleEscape);
    }

    return () => {
      document.removeEventListener('keydown', handleEscape);
      document.body.style.overflow = 'unset';
    };
  }, [activeModal, showSubMenu]);

  const handleButtonClick = (buttonId) => {
    const handlers = {
      menu: () => {
        setShowSubMenu(!showSubMenu);
        onMenuClick?.();
      },
      info: () => {
        setActiveModal('info');
        setShowSubMenu(false);
        onInfoClick?.();
      },
      options: () => {
        setActiveModal('options');
        setShowSubMenu(false);
        onOptionsClick?.();
      },
      help: () => {
        setActiveModal('help');
        setShowSubMenu(false);
        onHelpClick?.();
      },
      about: () => {
        setActiveModal('about');
        setShowSubMenu(false);
        onAboutClick?.();
      }
    };

    handlers[buttonId]?.();
  };

  const handleSubMenuClick = (itemId) => {
    setShowSubMenu(false);
    
    const subMenuHandlers = {
      templates: onNavigateToTemplates,
      troubleshooting: onNavigateToTroubleshooting,
      beginnersGuide: onNavigateToBeginnersGuide,
      resources: onNavigateToResources,
      projectStructure: onNavigateToProjectStructure,
      basicCustomization: onNavigateToBasicCustomization,
      troubleshooting2: onNavigateToTroubleshooting2,
      glossary: onNavigateToGlossary,
      guide: onNavigateToGuide
    };

    subMenuHandlers[itemId]?.();
  };

  const closeModal = () => {
    setActiveModal(null);
  };

  return (
    <>
      <FloatingButtonsContainer>
        {FLOATING_BUTTONS_CONFIG.map((button, index) => (
          <ButtonWrapper key={button.id} delay={index * 0.1}>
            <FloatingButton
              colorType={button.color}
              delay={index * 0.1}
              onClick={() => handleButtonClick(button.id)}
              title={button.tooltip}
            >
              {button.icon}
              <Tooltip>{button.tooltip}</Tooltip>
            </FloatingButton>
            
            {/* Submenú para el botón de herramientas */}
            {button.hasSubMenu && button.subMenuItems && (
              <SubMenuContainer isOpen={showSubMenu && button.id === 'menu'}>
                {button.subMenuItems.map((item) => (
                  <SubMenuButton
                    key={item.id}
                    onClick={() => handleSubMenuClick(item.id)}
                    title={item.label}
                  >
                    <SubMenuIcon>{item.icon}</SubMenuIcon>
                    <SubMenuLabel>{item.label}</SubMenuLabel>
                  </SubMenuButton>
                ))}
              </SubMenuContainer>
            )}
          </ButtonWrapper>
        ))}
      </FloatingButtonsContainer>

      {/* Modal universal */}
      <ModalOverlay 
        isOpen={!!activeModal} 
        onClick={closeModal}
      >
        <ModalContent 
          isOpen={!!activeModal}
          onClick={(e) => e.stopPropagation()}
        >
          <ModalHeader>
            <ModalTitle>
              {activeModal === 'info' && <>ℹ️ Información del Editor</>}
              {activeModal === 'options' && <>⚙️ Guía de Opciones</>}
              {activeModal === 'help' && <>❓ Ayuda y Funcionalidades</>}
              {activeModal === 'about' && <>💡 Acerca de RynEditor</>}
            </ModalTitle>
            <CloseButton onClick={closeModal}>×</CloseButton>
          </ModalHeader>
          
          {children && children[activeModal]}
        </ModalContent>
      </ModalOverlay>
    </>
  );
}

export default FloatingButtonsManager;
