import React, { useState } from 'react';
import styled from 'styled-components';

const GlossaryContainer = styled.div`
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: var(--color-background);
  z-index: 3000;
  overflow-y: auto;
  transition: background-color 0.3s ease;
`;

const GlossaryHeader = styled.div`
  background-color: var(--color-surface);
  padding: 20px;
  border-bottom: 2px solid var(--color-border);
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: sticky;
  top: 0;
  z-index: 3001;
  transition: background-color 0.3s ease, border-color 0.3s ease;
`;

const GlossaryTitle = styled.h1`
  margin: 0;
  color: var(--color-success);
  font-size: 32px;
  font-weight: 800;
  display: flex;
  align-items: center;
  gap: 15px;
  transition: color 0.3s ease;
  text-shadow: 0 2px 4px var(--color-shadow);
  letter-spacing: 0.5px;
`;

const CloseButton = styled.button`
  background-color: var(--color-error);
  color: var(--color-textInverse);
  border: none;
  border-radius: 8px;
  padding: 12px 20px;
  cursor: pointer;
  font-size: 16px;
  font-weight: bold;
  transition: all 0.3s ease;

  &:hover {
    background-color: var(--color-errorHover);
    transform: scale(1.05);
  }
`;

const GlossaryContent = styled.div`
  padding: 30px;
  max-width: 1200px;
  margin: 0 auto;
`;

const SearchBox = styled.div`
  background-color: var(--color-surfaceSecondary);
  border: 2px solid var(--color-border);
  border-radius: 12px;
  padding: 25px;
  margin-bottom: 30px;
  transition: background-color 0.3s ease, border-color 0.3s ease;
  box-shadow: 0 4px 12px var(--color-shadow);

  &:hover {
    border-color: var(--color-primary);
    box-shadow: 0 6px 16px var(--color-shadowHover);
  }
`;

const SearchTitle = styled.h2`
  color: var(--color-success);
  margin: 0 0 20px 0;
  font-size: 22px;
  font-weight: 800;
  display: flex;
  align-items: center;
  gap: 12px;
  transition: color 0.3s ease;
  text-shadow: 0 1px 3px var(--color-shadow);
  letter-spacing: 0.3px;
`;

const SearchInput = styled.input`
  width: 100%;
  padding: 12px 16px;
  background-color: var(--color-backgroundSecondary);
  border: 2px solid var(--color-border);
  border-radius: 8px;
  color: var(--color-textPrimary);
  font-size: 16px;
  font-weight: 500;
  transition: all 0.3s ease;
  box-shadow: 0 2px 6px var(--color-shadow);

  &:focus {
    outline: none;
    border-color: var(--color-success);
    box-shadow: 0 4px 12px var(--color-shadowHover);
    transform: translateY(-1px);
  }

  &:hover {
    border-color: var(--color-primary);
    box-shadow: 0 3px 8px var(--color-shadowHover);
  }

  &::placeholder {
    color: var(--color-textTertiary);
    font-weight: 400;
  }
`;

const CategoryTabs = styled.div`
  display: flex;
  gap: 10px;
  margin-bottom: 30px;
  flex-wrap: wrap;
`;

const CategoryTab = styled.button`
  background-color: ${props => props.active ? 'var(--color-success)' : 'var(--color-surfaceSecondary)'};
  color: ${props => props.active ? 'var(--color-textInverse)' : 'var(--color-textPrimary)'};
  border: 2px solid ${props => props.active ? 'var(--color-success)' : 'var(--color-border)'};
  padding: 14px 22px;
  border-radius: 8px;
  cursor: pointer;
  font-size: 14px;
  font-weight: 700;
  transition: all 0.3s ease;
  box-shadow: 0 3px 8px var(--color-shadow);

  &:hover {
    background-color: ${props => props.active ? 'var(--color-successHover)' : 'var(--color-border)'};
    border-color: ${props => props.active ? 'var(--color-successHover)' : 'var(--color-primary)'};
    color: var(--color-textInverse);
    transform: translateY(-3px);
    box-shadow: 0 6px 16px var(--color-shadowHover);
  }

  &:active {
    transform: translateY(-1px);
    box-shadow: 0 4px 12px var(--color-shadowHover);
  }
`;

const GlossaryGrid = styled.div`
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(400px, 1fr));
  gap: 25px;
  margin-bottom: 40px;
`;

const TermCard = styled.div`
  background-color: var(--color-surfaceSecondary);
  border: 2px solid var(--color-border);
  border-radius: 12px;
  padding: 24px;
  transition: all 0.3s ease;
  box-shadow: 0 4px 12px var(--color-shadow);

  &:hover {
    border-color: var(--color-success);
    background-color: var(--color-surfaceElevated);
    transform: translateY(-8px);
    box-shadow: 0 12px 32px var(--color-shadowHover);
  }
`;

const TermHeader = styled.div`
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  margin-bottom: 15px;
`;

const TermTitle = styled.h3`
  margin: 0;
  color: var(--color-success);
  font-size: 20px;
  font-weight: 800;
  display: flex;
  align-items: center;
  gap: 12px;
  transition: color 0.3s ease;
  text-shadow: 0 2px 4px var(--color-shadow);
  letter-spacing: 0.3px;
`;

const TermCategory = styled.span`
  background-color: ${props => {
    switch(props.category) {
      case 'language': return 'var(--color-info)';
      case 'gui': return 'var(--color-success)';
      case 'config': return 'var(--color-warning)';
      case 'script': return 'var(--color-primary)';
      case 'display': return 'var(--color-error)';
      case 'audio': return 'var(--color-border)';
      case 'python': return 'var(--color-borderSecondary)';
      default: return 'var(--color-borderSecondary)';
    }
  }};
  color: var(--color-textInverse);
  padding: 6px 12px;
  border-radius: 6px;
  font-size: 12px;
  font-weight: 700;
  transition: all 0.3s ease;
  box-shadow: 0 2px 6px var(--color-shadow);
  border: 1px solid rgba(255, 255, 255, 0.1);

  &:hover {
    transform: translateY(-1px);
    box-shadow: 0 4px 8px var(--color-shadowHover);
  }
`;

const TermDefinition = styled.p`
  color: var(--color-textPrimary);
  margin: 0 0 18px 0;
  line-height: 1.7;
  font-size: 16px;
  font-weight: 600;
  transition: color 0.3s ease;
  text-shadow: 0 1px 2px var(--color-shadow);
`;

const CodeExample = styled.div`
  background-color: var(--color-backgroundSecondary);
  border: 3px solid var(--color-primary);
  border-radius: 10px;
  padding: 20px;
  margin: 18px 0;
  font-family: 'Courier New', monospace;
  font-size: 14px;
  font-weight: 600;
  color: var(--color-primary);
  overflow-x: auto;
  transition: all 0.3s ease;
  box-shadow: 0 4px 12px var(--color-shadow);
  position: relative;

  &:hover {
    border-color: var(--color-primaryHover);
    box-shadow: 0 6px 16px var(--color-shadowHover);
    transform: translateY(-2px);
  }

  &::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    height: 3px;
    background: linear-gradient(90deg, var(--color-primary), var(--color-primaryHover));
    border-radius: 10px 10px 0 0;
  }
`;

const InfoBox = styled.div`
  background-color: var(--color-surfaceSecondary);
  border: 3px solid var(--color-success);
  border-radius: 14px;
  padding: 24px;
  margin: 24px 0;
  transition: all 0.3s ease;
  box-shadow: 0 6px 20px var(--color-shadow);
  position: relative;

  &:hover {
    border-color: var(--color-successHover);
    box-shadow: 0 8px 24px var(--color-shadowHover);
    transform: translateY(-3px);
  }

  &::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    height: 4px;
    background: linear-gradient(90deg, var(--color-success), var(--color-successHover));
    border-radius: 14px 14px 0 0;
  }
`;

const InfoTitle = styled.div`
  color: var(--color-success);
  font-weight: 800;
  font-size: 18px;
  margin-bottom: 16px;
  display: flex;
  align-items: center;
  gap: 10px;
  transition: color 0.3s ease;
  text-shadow: 0 2px 4px var(--color-shadow);
  letter-spacing: 0.3px;
`;

const InfoText = styled.div`
  color: var(--color-textPrimary);
  font-size: 16px;
  line-height: 1.7;
  font-weight: 600;
  transition: color 0.3s ease;
  text-shadow: 0 1px 2px var(--color-shadow);
`;

const TipBox = styled.div`
  background-color: var(--color-surfaceSecondary);
  border: 3px solid var(--color-warning);
  border-radius: 14px;
  padding: 24px;
  margin: 24px 0;
  transition: all 0.3s ease;
  box-shadow: 0 6px 20px var(--color-shadow);
  position: relative;

  &:hover {
    border-color: var(--color-warningHover);
    box-shadow: 0 8px 24px var(--color-shadowHover);
    transform: translateY(-3px);
  }

  &::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    height: 4px;
    background: linear-gradient(90deg, var(--color-warning), var(--color-warningHover));
    border-radius: 14px 14px 0 0;
  }
`;

const TipTitle = styled.div`
  color: var(--color-warning);
  font-weight: 800;
  font-size: 18px;
  margin-bottom: 16px;
  display: flex;
  align-items: center;
  gap: 10px;
  transition: color 0.3s ease;
  text-shadow: 0 2px 4px var(--color-shadow);
  letter-spacing: 0.3px;
`;

const TipText = styled.div`
  color: var(--color-textPrimary);
  font-size: 16px;
  line-height: 1.7;
  font-weight: 600;
  transition: color 0.3s ease;
  text-shadow: 0 1px 2px var(--color-shadow);
`;

const WarningBox = styled.div`
  background-color: var(--color-surfaceSecondary);
  border: 3px solid var(--color-error);
  border-radius: 14px;
  padding: 24px;
  margin: 24px 0;
  transition: all 0.3s ease;
  box-shadow: 0 6px 20px var(--color-shadow);
  position: relative;

  &:hover {
    border-color: var(--color-errorHover);
    box-shadow: 0 8px 24px var(--color-shadowHover);
    transform: translateY(-3px);
  }

  &::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    height: 4px;
    background: linear-gradient(90deg, var(--color-error), var(--color-errorHover));
    border-radius: 14px 14px 0 0;
  }
`;

const WarningTitle = styled.div`
  color: var(--color-error);
  font-weight: 800;
  font-size: 18px;
  margin-bottom: 16px;
  display: flex;
  align-items: center;
  gap: 10px;
  transition: color 0.3s ease;
  text-shadow: 0 2px 4px var(--color-shadow);
  letter-spacing: 0.3px;
`;

const WarningText = styled.div`
  color: var(--color-textPrimary);
  font-size: 16px;
  line-height: 1.7;
  font-weight: 600;
  transition: color 0.3s ease;
  text-shadow: 0 1px 2px var(--color-shadow);
`;

function GlossaryPage({ onClose }) {
  const [activeCategory, setActiveCategory] = useState('language');
  const [searchTerm, setSearchTerm] = useState('');

  const glossaryData = {
    language: [
      {
        id: 'label',
        title: 'Label',
        icon: '🏷️',
        category: 'language',
        definition: 'Un label es una etiqueta que marca un punto específico en el script de Ren\'Py. Los labels permiten saltar a diferentes partes del código usando jump o call.',
        example: `# Definir un label
label start:
    "¡Hola mundo!"
    jump ending

# Llamar un label
call start

# Saltar a un label
jump start`,
        tip: 'Los labels son fundamentales para la navegación en Ren\'Py. Cada juego debe tener un label "start" como punto de entrada.',
        info: 'Los nombres de labels deben ser únicos y no pueden contener espacios.'
      },
      {
        id: 'menu',
        title: 'Menu',
        icon: '📋',
        category: 'language',
        definition: 'Un menu permite al jugador elegir entre diferentes opciones. Es la base de las decisiones interactivas en Ren\'Py.',
        example: `menu:
    "¿Qué quieres hacer?"
    
    "Ir a la izquierda":
        jump left_path
    
    "Ir a la derecha":
        jump right_path
    
    "Quedarse aquí":
        jump stay_here`,
        tip: 'Los menus deben estar correctamente indentados. Cada opción lleva a un label diferente.',
        info: 'Los menus pueden anidarse y contener texto adicional.'
      },
      {
        id: 'variable',
        title: 'Variable',
        icon: '🔢',
        category: 'language',
        definition: 'Las variables almacenan datos que pueden cambiar durante el juego. Se usan para guardar el progreso del jugador y tomar decisiones.',
        example: `# Definir variables
default player_name = "Jugador"
default score = 0
default has_key = False

# Usar variables
if score > 100:
    "¡Excelente puntuación!"
elif has_key:
    "Tienes la llave mágica."`,
        tip: 'Usa "default" para variables que pueden cambiar, "define" para constantes.',
        info: 'Las variables pueden ser números, texto, booleanos o listas.'
      },
      {
        id: 'character',
        title: 'Character',
        icon: '👤',
        category: 'language',
        definition: 'Un character define cómo se muestra el diálogo de un personaje. Controla el nombre, color, voz y otros aspectos del personaje.',
        example: `# Definir personajes
define eileen = Character("Eileen", color="#c8ffc8")
define narrator = Character(None, kind=nvl)

# Usar personajes
eileen "¡Hola! ¿Cómo estás?"
narrator "El narrador habla sin nombre visible."`,
        tip: 'Los personajes pueden tener diferentes colores, fuentes y comportamientos.',
        info: 'Character(None) crea un narrador sin nombre visible.'
      }
    ],
    gui: [
      {
        id: 'screen',
        title: 'Screen',
        icon: '🖥️',
        category: 'gui',
        definition: 'Una screen es una interfaz personalizable que define cómo se ve y comporta una pantalla del juego. Se usa para menús, configuraciones y elementos de UI.',
        example: `screen main_menu():
    tag menu
    
    add gui.main_menu_background
    
    textbutton "Nueva Partida" action Start()
    textbutton "Cargar" action ShowMenu("load")
    textbutton "Configuración" action ShowMenu("preferences")
    textbutton "Salir" action Quit(confirm=True)`,
        tip: 'Las screens se definen en screens.rpy y se pueden mostrar con Show() o ShowMenu().',
        info: 'Cada screen debe tener un tag único para evitar conflictos.'
      },
      {
        id: 'style',
        title: 'Style',
        icon: '🎨',
        category: 'gui',
        definition: 'Un style define la apariencia visual de los elementos de la interfaz. Controla colores, fuentes, tamaños y otros aspectos visuales.',
        example: `style button:
    background Frame("gui/button/idle.png", Borders(6, 6, 6, 6))
    hover_background Frame("gui/button/hover.png", Borders(6, 6, 6, 6))
    padding (25, 25, 25, 25)
    text_size 33`,
        tip: 'Los styles se pueden heredar y modificar para crear variaciones.',
        info: 'Ren\'Py tiene muchos styles predefinidos que puedes personalizar.'
      },
      {
        id: 'transform',
        title: 'Transform',
        icon: '🔄',
        category: 'gui',
        definition: 'Un transform define cómo se mueve, escala o cambia un elemento visual. Se usa para animaciones y efectos visuales.',
        example: `# Definir transform
transform fadein:
    alpha 0.0
    linear 1.0 alpha 1.0

# Usar transform
show eileen happy at fadein
show bg room at zoom(1.2)`,
        tip: 'Los transforms pueden combinarse para crear efectos complejos.',
        info: 'Ren\'Py incluye transforms predefinidos como fade, dissolve y move.'
      }
    ],
    config: [
      {
        id: 'options-rpy',
        title: 'options.rpy',
        icon: '⚙️',
        category: 'config',
        definition: 'El archivo options.rpy contiene la configuración principal del juego. Define el título, resolución, opciones de audio y otras configuraciones básicas.',
        example: `## Información básica del juego
define config.name = _("Mi Juego")
define config.version = "1.0"

## Resolución
define config.screen_width = 1920
define config.screen_height = 1080

## Audio
define config.has_sound = True
define config.has_music = True
define config.has_voice = True`,
        tip: 'Modifica options.rpy para personalizar la configuración básica de tu juego.',
        info: 'Este archivo se crea automáticamente al crear un nuevo proyecto.'
      },
      {
        id: 'gui-rpy',
        title: 'gui.rpy',
        icon: '🎨',
        category: 'config',
        definition: 'El archivo gui.rpy define las variables de la interfaz gráfica. Controla colores, tamaños, fuentes y otros aspectos visuales del juego.',
        example: `## Colores
define gui.accent_color = '#ff6b6b'
define gui.idle_color = '#888888'
define gui.hover_color = '#ffffff'

## Fuentes
define gui.text_font = "DejaVuSans.ttf"
define gui.name_text_size = 45
define gui.text_size = 33`,
        tip: 'Modifica gui.rpy para cambiar la apariencia visual de tu juego.',
        info: 'Los cambios en gui.rpy afectan toda la interfaz del juego.'
      },
      {
        id: 'screens-rpy',
        title: 'screens.rpy',
        icon: '🖥️',
        category: 'config',
        definition: 'El archivo screens.rpy define las pantallas del juego. Contiene los menús principales, configuración y otras interfaces personalizadas.',
        example: `screen main_menu():
    tag menu
    
    add gui.main_menu_background
    
    textbutton "Nueva Partida" action Start()
    textbutton "Cargar" action ShowMenu("load")`,
        tip: 'Modifica screens.rpy para personalizar los menús y pantallas del juego.',
        info: 'Este archivo contiene las definiciones de todas las pantallas del juego.'
      }
    ],
    script: [
      {
        id: 'script-rpy',
        title: 'script.rpy',
        icon: '📝',
        category: 'script',
        definition: 'El archivo script.rpy es donde se escribe la historia principal del juego. Contiene diálogos, narración y la lógica del juego.',
        example: `# El juego comienza aquí
label start:
    scene bg room
    show eileen happy
    
    eileen "¡Hola! Bienvenido a mi juego."
    
    menu:
        "¿Qué quieres hacer?"
        
        "Hablar con Eileen":
            jump talk_eileen
        
        "Explorar la habitación":
            jump explore_room`,
        tip: 'Puedes dividir tu historia en múltiples archivos .rpy para mejor organización.',
        info: 'Este es el archivo principal donde se desarrolla la historia del juego.'
      },
      {
        id: 'jump',
        title: 'Jump',
        icon: '↗️',
        category: 'script',
        definition: 'Jump permite saltar a un label específico sin posibilidad de regresar. Es útil para cambiar de escena o continuar la historia.',
        example: `label start:
    "Esta es la escena inicial"
    jump next_scene

label next_scene:
    "Ahora estamos en la siguiente escena"
    return`,
        tip: 'Jump no permite regresar. Usa call si necesitas volver al punto original.',
        info: 'Jump es ideal para el flujo principal de la historia.'
      },
      {
        id: 'call',
        title: 'Call',
        icon: '📞',
        category: 'script',
        definition: 'Call permite llamar a un label y regresar al punto original cuando termine. Es útil para subrutinas y escenas reutilizables.',
        example: `label start:
    "Llamando a una subrutina"
    call show_inventory
    "Regresamos aquí después de la subrutina"

label show_inventory:
    "Mostrando inventario..."
    return`,
        tip: 'Call permite regresar al punto original usando return.',
        info: 'Call es ideal para funciones que se usan múltiples veces.'
      }
    ],
    display: [
      {
        id: 'show',
        title: 'Show',
        icon: '👁️',
        category: 'display',
        definition: 'Show muestra una imagen en la pantalla. Se usa para mostrar personajes, fondos y otros elementos visuales.',
        example: `# Mostrar personaje
show eileen happy at center

# Mostrar fondo
show bg room

# Mostrar con transición
show eileen sad at center with fade`,
        tip: 'Usa "at" para posicionar elementos y "with" para transiciones.',
        info: 'Show reemplaza elementos del mismo tipo si ya están visibles.'
      },
      {
        id: 'scene',
        title: 'Scene',
        icon: '🎬',
        category: 'display',
        definition: 'Scene limpia la pantalla y muestra una nueva imagen. Se usa para cambiar completamente la escena visual.',
        example: `# Limpiar pantalla y mostrar nuevo fondo
scene bg forest

# Mostrar escena con transición
scene bg room with dissolve

# Limpiar todo
scene black`,
        tip: 'Scene es ideal para cambios dramáticos de escena.',
        info: 'Scene oculta todos los elementos visibles antes de mostrar la nueva imagen.'
      },
      {
        id: 'hide',
        title: 'Hide',
        icon: '👻',
        category: 'display',
        definition: 'Hide oculta una imagen específica de la pantalla. Se usa para quitar personajes o elementos sin limpiar toda la pantalla.',
        example: `# Ocultar personaje
hide eileen

# Ocultar con transición
hide eileen with fade

# Ocultar múltiples elementos
hide eileen
hide bg room`,
        tip: 'Hide es útil para quitar elementos específicos sin afectar otros.',
        info: 'Hide solo afecta al elemento especificado, no a toda la pantalla.'
      }
    ],
    audio: [
      {
        id: 'play-music',
        title: 'Play Music',
        icon: '🎵',
        category: 'audio',
        definition: 'Play music reproduce música de fondo. La música se reproduce en loop y se puede cambiar o detener en cualquier momento.',
        example: `# Reproducir música
play music "audio/bgm/main_theme.ogg"

# Reproducir con transición
play music "audio/bgm/battle_theme.ogg" fadeout 1.0 fadein 1.0

# Detener música
stop music`,
        tip: 'La música se reproduce en loop automáticamente.',
        info: 'Ren\'Py soporta formatos OGG, MP3 y WAV para música.'
      },
      {
        id: 'play-sound',
        title: 'Play Sound',
        icon: '🔊',
        category: 'audio',
        definition: 'Play sound reproduce efectos de sonido. Los sonidos se reproducen una vez y pueden superponerse entre sí.',
        example: `# Reproducir efecto de sonido
play sound "audio/sfx/click.ogg"

# Reproducir múltiples sonidos
play sound "audio/sfx/click.ogg"
play sound "audio/sfx/notification.ogg"`,
        tip: 'Los efectos de sonido se pueden reproducir simultáneamente.',
        info: 'Los sonidos son ideales para efectos, notificaciones y feedback del usuario.'
      },
      {
        id: 'play-voice',
        title: 'Play Voice',
        icon: '🎤',
        category: 'audio',
        definition: 'Play voice reproduce archivos de voz para los diálogos. Se sincroniza automáticamente con el texto que se muestra.',
        example: `# Reproducir voz
play voice "audio/voice/eileen_001.ogg"
eileen "Hola, ¿cómo estás?"

# Reproducir voz con texto
eileen "Esta es mi voz" voice "audio/voice/eileen_002.ogg"`,
        tip: 'La voz se reproduce automáticamente cuando se muestra el diálogo.',
        info: 'Los archivos de voz se organizan típicamente por personaje y número de línea.'
      }
    ],
    python: [
      {
        id: 'python-block',
        title: 'Python Block',
        icon: '🐍',
        category: 'python',
        definition: 'Un bloque de Python permite ejecutar código Python dentro del script de Ren\'Py. Se usa para lógica compleja y cálculos.',
        example: `# Bloque de Python
python:
    player_score = 100
    enemy_score = 80
    
    if player_score > enemy_score:
        winner = "Jugador"
    else:
        winner = "Enemigo"

"El ganador es [winner]!"`,
        tip: 'Los bloques de Python deben estar correctamente indentados.',
        info: 'Python se integra perfectamente con Ren\'Py para lógica compleja.'
      },
      {
        id: 'renpy-function',
        title: 'Ren\'Py Functions',
        icon: '⚙️',
        category: 'python',
        definition: 'Las funciones de Ren\'Py permiten acceder a funcionalidades específicas del motor desde Python. Incluyen input, notify, save, load, etc.',
        example: `# Usar funciones de Ren'Py
$ player_name = renpy.input("¿Cuál es tu nombre?", default="Jugador")
$ renpy.notify("¡Bienvenido, " + player_name + "!")

# Guardar y cargar
$ renpy.save("save_1")
$ renpy.load("save_1")`,
        tip: 'Las funciones de Ren\'Py tienen el prefijo "renpy." en Python.',
        info: 'Estas funciones conectan Python con las funcionalidades de Ren\'Py.'
      },
      {
        id: 'conditional',
        title: 'Conditional',
        icon: '🔀',
        category: 'python',
        definition: 'Las declaraciones condicionales permiten ejecutar código diferente basado en condiciones. Se usan para crear ramas en la historia.',
        example: `# Condicional simple
if score > 100:
    "¡Excelente puntuación!"
elif score > 50:
    "Buena puntuación"
else:
    "Puedes mejorar"

# Condicional con variables
if has_key and door_unlocked:
    "Puedes pasar"
else:
    "La puerta está cerrada"`,
        tip: 'Las condicionales permiten crear historias no lineales.',
        info: 'Puedes usar operadores como and, or, not para condiciones complejas.'
      }
    ]
  };

  const getCurrentTerms = () => {
    let currentTerms = glossaryData[activeCategory] || [];
    
    if (searchTerm) {
      currentTerms = currentTerms.filter(term =>
        term.title.toLowerCase().includes(searchTerm.toLowerCase()) ||
        term.definition.toLowerCase().includes(searchTerm.toLowerCase()) ||
        term.example.toLowerCase().includes(searchTerm.toLowerCase())
      );
    }
    
    return currentTerms;
  };

  const getCategoryText = (category) => {
    switch(category) {
      case 'language': return 'Lenguaje';
      case 'gui': return 'GUI';
      case 'config': return 'Config';
      case 'script': return 'Script';
      case 'display': return 'Display';
      case 'audio': return 'Audio';
      case 'python': return 'Python';
      default: return 'Otro';
    }
  };

  return (
    <GlossaryContainer>
      <GlossaryHeader>
        <GlossaryTitle>
          📚 Glosario de Términos Técnicos
        </GlossaryTitle>
        <CloseButton onClick={onClose}>
          ✕ Cerrar
        </CloseButton>
      </GlossaryHeader>

      <GlossaryContent>
        <SearchBox>
          <SearchTitle>
            🔍 Buscar Términos
          </SearchTitle>
          <SearchInput
            type="text"
            placeholder="Busca términos técnicos específicos..."
            value={searchTerm}
            onChange={(e) => setSearchTerm(e.target.value)}
          />
        </SearchBox>

        <CategoryTabs>
          <CategoryTab 
            active={activeCategory === 'language'} 
            onClick={() => setActiveCategory('language')}
          >
            🏷️ Lenguaje
          </CategoryTab>
          <CategoryTab 
            active={activeCategory === 'gui'} 
            onClick={() => setActiveCategory('gui')}
          >
            🖥️ GUI
          </CategoryTab>
          <CategoryTab 
            active={activeCategory === 'config'} 
            onClick={() => setActiveCategory('config')}
          >
            ⚙️ Config
          </CategoryTab>
          <CategoryTab 
            active={activeCategory === 'script'} 
            onClick={() => setActiveCategory('script')}
          >
            📝 Script
          </CategoryTab>
          <CategoryTab 
            active={activeCategory === 'display'} 
            onClick={() => setActiveCategory('display')}
          >
            👁️ Display
          </CategoryTab>
          <CategoryTab 
            active={activeCategory === 'audio'} 
            onClick={() => setActiveCategory('audio')}
          >
            🎵 Audio
          </CategoryTab>
          <CategoryTab 
            active={activeCategory === 'python'} 
            onClick={() => setActiveCategory('python')}
          >
            🐍 Python
          </CategoryTab>
        </CategoryTabs>

        <GlossaryGrid>
          {getCurrentTerms().map((term) => (
            <TermCard key={term.id}>
              <TermHeader>
                <TermTitle>
                  {term.icon} {term.title}
                </TermTitle>
                <TermCategory category={term.category}>
                  {getCategoryText(term.category)}
                </TermCategory>
              </TermHeader>
              
              <TermDefinition>
                {term.definition}
              </TermDefinition>

              <CodeExample>
                {term.example}
              </CodeExample>

              {term.tip && (
                <TipBox>
                  <TipTitle>
                    💡 Consejo
                  </TipTitle>
                  <TipText>
                    {term.tip}
                  </TipText>
                </TipBox>
              )}

              {term.info && (
                <InfoBox>
                  <InfoTitle>
                    ℹ️ Información
                  </InfoTitle>
                  <InfoText>
                    {term.info}
                  </InfoText>
                </InfoBox>
              )}
            </TermCard>
          ))}
        </GlossaryGrid>

        {getCurrentTerms().length === 0 && searchTerm && (
          <div style={{ 
            textAlign: 'center', 
            color: 'var(--color-textTertiary)', 
            fontSize: '18px',
            marginTop: '50px'
          }}>
            No se encontraron términos que coincidan con "{searchTerm}"
          </div>
        )}

        <InfoBox>
          <InfoTitle>
            📚 Información Importante
          </InfoTitle>
          <InfoText>
            Este glosario contiene los términos técnicos más importantes de Ren'Py basados en la documentación oficial. 
            Cada término incluye definiciones, ejemplos de código y consejos prácticos.
          </InfoText>
        </InfoBox>

        <TipBox>
          <TipTitle>
            💡 Cómo Usar Este Glosario
          </TipTitle>
          <TipText>
            Usa la búsqueda para encontrar términos específicos, o navega por categorías para explorar conceptos relacionados. 
            Los ejemplos de código son funcionales y puedes copiarlos directamente en tu proyecto.
          </TipText>
        </TipBox>

        <WarningBox>
          <WarningTitle>
            ⚠️ Importante
          </WarningTitle>
          <WarningText>
            Los términos técnicos pueden tener diferentes usos según el contexto. 
            Siempre consulta la documentación oficial para casos específicos y actualizaciones.
          </WarningText>
        </WarningBox>

        <InfoBox>
          <InfoTitle>
            🔗 Recursos Adicionales
          </InfoTitle>
          <InfoText>
            Para más información detallada sobre estos términos, consulta:
            <br />• <a href="https://www.renpy.org/doc/html/" target="_blank" rel="noopener noreferrer" style={{color: 'var(--color-primary)'}}>Documentación Oficial de Ren'Py</a>
            <br />• <a href="https://www.renpy.org/doc/html/quickstart.html" target="_blank" rel="noopener noreferrer" style={{color: 'var(--color-primary)'}}>Guía de Inicio Rápido</a>
            <br />• <a href="https://lemmasoft.renai.us/forums/viewforum.php?f=51" target="_blank" rel="noopener noreferrer" style={{color: 'var(--color-primary)'}}>Ren'Py Cookbook</a>
          </InfoText>
        </InfoBox>
      </GlossaryContent>
    </GlossaryContainer>
  );
}

export default GlossaryPage;

