import React from 'react';
import styled from 'styled-components';

const InfoContainer = styled.div`
  background: rgba(23, 162, 184, 0.1);
  border: 1px solid rgba(23, 162, 184, 0.3);
  border-radius: 8px;
  padding: 16px;
  margin: 16px 0;
`;

const InfoTitle = styled.h4`
  color: var(--color-info);
  font-size: 16px;
  margin: 0 0 12px 0;
  display: flex;
  align-items: center;
  gap: 8px;
  transition: color 0.3s ease;
`;

const InfoList = styled.ul`
  margin: 0;
  padding-left: 20px;
  color: #cccccc;
  font-size: 14px;
  line-height: 1.6;
`;

const InfoItem = styled.li`
  margin-bottom: 8px;
  
  &:last-child {
    margin-bottom: 0;
  }
`;

const Highlight = styled.span`
  color: #4a9eff;
  font-weight: 600;
`;

const CodeBlock = styled.code`
  background: rgba(0, 0, 0, 0.3);
  padding: 2px 6px;
  border-radius: 4px;
  font-family: 'Courier New', monospace;
  font-size: 12px;
  color: #ffffff;
`;

function BackupInfo() {
  return (
    <InfoContainer>
      <InfoTitle>
        ℹ️ Información sobre archivos .ryn
      </InfoTitle>
      <InfoList>
        <InfoItem>
          <Highlight>Archivo completo:</Highlight> Contiene todo el proyecto con archivos multimedia
        </InfoItem>
        <InfoItem>
          <Highlight>Formato ZIP:</Highlight> Archivo comprimido con estructura organizada
        </InfoItem>
        <InfoItem>
          <Highlight>Metadatos:</Highlight> Información del proyecto, versión y fecha de exportación
        </InfoItem>
        <InfoItem>
          <Highlight>Estado del editor:</Highlight> Configuración, pestañas activas y preferencias
        </InfoItem>
        <InfoItem>
          <Highlight>Archivos multimedia:</Highlight> Imágenes, videos y audio organizados por categoría
        </InfoItem>
        <InfoItem>
          <Highlight>Scripts Ren'Py:</Highlight> Código generado para referencia
        </InfoItem>
        <InfoItem>
          <Highlight>Verificación:</Highlight> Checksum para verificar integridad del archivo
        </InfoItem>
        <InfoItem>
          <Highlight>Portabilidad:</Highlight> Funciona en cualquier instalación de RynEditor
        </InfoItem>
      </InfoList>
      
      <div style={{ marginTop: '16px', fontSize: '12px', color: '#aaaaaa' }}>
        <strong>Estructura del archivo .ryn:</strong>
        <br />
        <CodeBlock>metadata.json</CodeBlock> - Información del proyecto
        <br />
        <CodeBlock>editor_state.json</CodeBlock> - Estado completo del editor
        <br />
        <CodeBlock>media/</CodeBlock> - Archivos multimedia organizados
        <br />
        <CodeBlock>renpy/</CodeBlock> - Scripts Ren'Py generados
        <br />
        <CodeBlock>verification.json</CodeBlock> - Verificación de integridad
      </div>
    </InfoContainer>
  );
}

export default BackupInfo;
