// Estado inicial del proyecto
const initialState = createCleanState();

// Acciones del reducer
export const ACTIONS = {
  SET_PROJECT_NAME: 'SET_PROJECT_NAME',
  SET_PROJECT_AUTHOR: 'SET_PROJECT_AUTHOR',
  SET_PROJECT_DESCRIPTION: 'SET_PROJECT_DESCRIPTION',
  UPDATE_PROJECT_CONFIG: 'UPDATE_PROJECT_CONFIG',
  ADD_CHARACTER: 'ADD_CHARACTER',
  UPDATE_CHARACTER: 'UPDATE_CHARACTER',
  DELETE_CHARACTER: 'DELETE_CHARACTER',
  CLEAN_CHARACTER_REFERENCES: 'CLEAN_CHARACTER_REFERENCES',
  ADD_SCENE: 'ADD_SCENE',
  UPDATE_SCENE: 'UPDATE_SCENE',
  DELETE_SCENE: 'DELETE_SCENE',
  ADD_SCRIPT_ELEMENT: 'ADD_SCRIPT_ELEMENT',
  UPDATE_SCRIPT_ELEMENT: 'UPDATE_SCRIPT_ELEMENT',
  DELETE_SCRIPT_ELEMENT: 'DELETE_SCRIPT_ELEMENT',
  SET_CURRENT_SCENE: 'SET_CURRENT_SCENE',
  SET_SELECTED_ELEMENT: 'SET_SELECTED_ELEMENT',
  SET_PREVIEW_MODE: 'SET_PREVIEW_MODE',
  SET_PLAYING: 'SET_PLAYING',
  LOAD_PROJECT: 'LOAD_PROJECT',
  LOAD_FULL_STATE: 'LOAD_FULL_STATE',
  NEW_PROJECT: 'NEW_PROJECT',
  RESET_TO_CLEAN_STATE: 'RESET_TO_CLEAN_STATE',
  // Nuevas acciones para archivos de escenas
  ADD_SCENE_FILE: 'ADD_SCENE_FILE',
  UPDATE_SCENE_FILE: 'UPDATE_SCENE_FILE',
  DELETE_SCENE_FILE: 'DELETE_SCENE_FILE',
  SET_CURRENT_SCENE_FILE: 'SET_CURRENT_SCENE_FILE',
  ADD_SCENE_FILE_ELEMENT: 'ADD_SCENE_FILE_ELEMENT',
  ADD_SCENE_FILE_ELEMENT_AT_INDEX: 'ADD_SCENE_FILE_ELEMENT_AT_INDEX',
  UPDATE_SCENE_FILE_ELEMENT: 'UPDATE_SCENE_FILE_ELEMENT',
  DELETE_SCENE_FILE_ELEMENT: 'DELETE_SCENE_FILE_ELEMENT',
  // Acciones para variables y flags
  ADD_VARIABLE: 'ADD_VARIABLE',
  UPDATE_VARIABLE: 'UPDATE_VARIABLE',
  DELETE_VARIABLE: 'DELETE_VARIABLE',
  ADD_FLAG: 'ADD_FLAG',
  UPDATE_FLAG: 'UPDATE_FLAG',
  DELETE_FLAG: 'DELETE_FLAG',
  SET_THEME: 'SET_THEME',
  // Acciones para configuración del protagonista
  SET_PROTAGONIST_CONFIG: 'SET_PROTAGONIST_CONFIG',
  TOGGLE_PROTAGONIST: 'TOGGLE_PROTAGONIST'
};

// Reducer para manejar el estado
function visualNovelReducer(state, action) {
  switch (action.type) {
    case ACTIONS.SET_PROJECT_NAME:
      return {
        ...state,
        project: { ...state.project, name: action.payload }
      };
    
    case ACTIONS.SET_PROJECT_AUTHOR:
      return {
        ...state,
        project: { ...state.project, author: action.payload }
      };
    
    case ACTIONS.SET_PROJECT_DESCRIPTION:
      return {
        ...state,
        project: { ...state.project, description: action.payload }
      };
    
    case ACTIONS.UPDATE_PROJECT_CONFIG:
      return {
        ...state,
        project: { 
          ...state.project, 
          ...action.payload,
          advancedConfig: { ...state.project.advancedConfig, ...action.payload.advancedConfig }
        }
      };
    
    case ACTIONS.ADD_CHARACTER:
      return {
        ...state,
        project: {
          ...state.project,
          characters: [...state.project.characters, action.payload]
        }
      };
    
    case ACTIONS.UPDATE_CHARACTER:
      return {
        ...state,
        project: {
          ...state.project,
          characters: state.project.characters.map(char =>
            char.id === action.payload.id ? action.payload : char
          )
        }
      };
    
    case ACTIONS.DELETE_CHARACTER:
      return {
        ...state,
        project: {
          ...state.project,
          characters: state.project.characters.filter(char => char.id !== action.payload)
        }
      };
    
    case ACTIONS.CLEAN_CHARACTER_REFERENCES:
      return {
        ...state,
        project: {
          ...state.project,
          sceneFiles: action.payload
        }
      };
    
    case ACTIONS.ADD_SCENE:
      return {
        ...state,
        project: {
          ...state.project,
          scenes: [...state.project.scenes, action.payload]
        }
      };
    
    case ACTIONS.UPDATE_SCENE:
      return {
        ...state,
        project: {
          ...state.project,
          scenes: state.project.scenes.map(scene =>
            scene.id === action.payload.id ? action.payload : scene
          )
        }
      };
    
    case ACTIONS.DELETE_SCENE:
      return {
        ...state,
        project: {
          ...state.project,
          scenes: state.project.scenes.filter(scene => scene.id !== action.payload)
        }
      };
    
    case ACTIONS.ADD_SCRIPT_ELEMENT:
      return {
        ...state,
        project: {
          ...state.project,
          script: [...state.project.script, action.payload]
        }
      };
    
    case ACTIONS.UPDATE_SCRIPT_ELEMENT:
      return {
        ...state,
        project: {
          ...state.project,
          script: state.project.script.map(element =>
            element.id === action.payload.id ? action.payload : element
          )
        }
      };
    
    case ACTIONS.DELETE_SCRIPT_ELEMENT:
      return {
        ...state,
        project: {
          ...state.project,
          script: state.project.script.filter(element => element.id !== action.payload)
        }
      };
    
    case ACTIONS.SET_CURRENT_SCENE:
      return {
        ...state,
        currentScene: action.payload
      };
    
    case ACTIONS.SET_SELECTED_ELEMENT:
      return {
        ...state,
        selectedElement: action.payload
      };
    
    case ACTIONS.SET_PREVIEW_MODE:
      return {
        ...state,
        previewMode: action.payload
      };
    
    case ACTIONS.SET_PLAYING:
      return {
        ...state,
        isPlaying: action.payload
      };
    
    case ACTIONS.LOAD_PROJECT:
      return {
        ...state,
        project: action.payload,
        currentScene: null,
        selectedElement: null
      };
    
    case ACTIONS.LOAD_FULL_STATE:
      return {
        ...state,
        ...action.payload
      };
    
    case ACTIONS.NEW_PROJECT:
      return initialState;
    
    case ACTIONS.RESET_TO_CLEAN_STATE:
      console.log('🔄 Reseteando a estado completamente limpio');
      return createCleanState();
    
    // Nuevos casos para archivos de escenas
    case ACTIONS.ADD_SCENE_FILE:
      return {
        ...state,
        project: {
          ...state.project,
          sceneFiles: [...state.project.sceneFiles, action.payload]
        }
      };
    
    case ACTIONS.UPDATE_SCENE_FILE:
      return {
        ...state,
        project: {
          ...state.project,
          sceneFiles: state.project.sceneFiles.map(file =>
            file.id === action.payload.id ? action.payload : file
          )
        }
      };
    
    case ACTIONS.DELETE_SCENE_FILE:
      return {
        ...state,
        project: {
          ...state.project,
          sceneFiles: state.project.sceneFiles.filter(file => file.id !== action.payload),
          currentSceneFile: state.project.currentSceneFile === action.payload ? 'script' : state.project.currentSceneFile
        }
      };
    
    case ACTIONS.SET_CURRENT_SCENE_FILE:
      return {
        ...state,
        project: {
          ...state.project,
          currentSceneFile: action.payload
        }
      };
    
    case ACTIONS.ADD_SCENE_FILE_ELEMENT:
      console.log('Reducer - ADD_SCENE_FILE_ELEMENT ejecutado');
      console.log('Reducer - payload:', action.payload);
      console.log('Reducer - sceneFiles antes:', state.project.sceneFiles);
      
      const newState = {
        ...state,
        project: {
          ...state.project,
          sceneFiles: state.project.sceneFiles.map(file => {
            if (file.id === action.payload.sceneFileId) {
              console.log('Reducer - actualizando archivo:', file.id);
              console.log('Reducer - elementos antes:', file.elements);
              const updatedFile = { ...file, elements: [...file.elements, action.payload.element] };
              console.log('Reducer - elementos después:', updatedFile.elements);
              return updatedFile;
            }
            return file;
          })
        }
      };
      
      console.log('Reducer - nuevo estado:', newState);
      return newState;
    
    case ACTIONS.ADD_SCENE_FILE_ELEMENT_AT_INDEX:
      console.log('Reducer - ADD_SCENE_FILE_ELEMENT_AT_INDEX ejecutado');
      console.log('Reducer - payload:', action.payload);
      
      return {
        ...state,
        project: {
          ...state.project,
          sceneFiles: state.project.sceneFiles.map(file => {
            if (file.id === action.payload.sceneFileId) {
              const newElements = [...file.elements];
              const insertIndex = action.payload.insertIndex !== null ? action.payload.insertIndex : newElements.length;
              
              // Insertar el elemento en la posición especificada
              newElements.splice(insertIndex, 0, action.payload.element);
              
              return { ...file, elements: newElements };
            }
            return file;
          })
        }
      };
    
    case ACTIONS.UPDATE_SCENE_FILE_ELEMENT:
      return {
        ...state,
        project: {
          ...state.project,
          sceneFiles: state.project.sceneFiles.map(file =>
            file.id === action.payload.sceneFileId
              ? {
                  ...file,
                  elements: file.elements.map(element =>
                    element.id === action.payload.element.id ? action.payload.element : element
                  )
                }
              : file
          )
        }
      };
    
    case ACTIONS.DELETE_SCENE_FILE_ELEMENT:
      return {
        ...state,
        project: {
          ...state.project,
          sceneFiles: state.project.sceneFiles.map(file =>
            file.id === action.payload.sceneFileId
              ? { ...file, elements: file.elements.filter(element => element.id !== action.payload.elementId) }
              : file
          )
        }
      };
    
    // Casos para variables
    case ACTIONS.ADD_VARIABLE:
      return {
        ...state,
        project: {
          ...state.project,
          variables: [...state.project.variables, action.payload]
        }
      };
    
    case ACTIONS.UPDATE_VARIABLE:
      return {
        ...state,
        project: {
          ...state.project,
          variables: state.project.variables.map(variable =>
            variable.id === action.payload.id ? action.payload : variable
          )
        }
      };
    
    case ACTIONS.DELETE_VARIABLE:
      return {
        ...state,
        project: {
          ...state.project,
          variables: state.project.variables.filter(variable => variable.id !== action.payload)
        }
      };
    
    // Casos para flags
    case ACTIONS.ADD_FLAG:
      return {
        ...state,
        project: {
          ...state.project,
          flags: [...state.project.flags, action.payload]
        }
      };
    
    case ACTIONS.UPDATE_FLAG:
      return {
        ...state,
        project: {
          ...state.project,
          flags: state.project.flags.map(flag =>
            flag.id === action.payload.id ? action.payload : flag
          )
        }
      };
    
    case ACTIONS.DELETE_FLAG:
      return {
        ...state,
        project: {
          ...state.project,
          flags: state.project.flags.filter(flag => flag.id !== action.payload)
        }
      };
    
    case ACTIONS.SET_THEME:
      return {
        ...state,
        theme: action.payload
      };
    
    case ACTIONS.SET_PROTAGONIST_CONFIG:
      return {
        ...state,
        project: {
          ...state.project,
          protagonistConfig: {
            ...state.project.protagonistConfig,
            ...action.payload
          }
        }
      };
    
    case ACTIONS.TOGGLE_PROTAGONIST:
      const newEnabled = !state.project.protagonistConfig.enabled;
      return {
        ...state,
        project: {
          ...state.project,
          protagonistConfig: {
            ...state.project.protagonistConfig,
            enabled: newEnabled
          },
          // Si se activa, crear automáticamente el personaje protagonista
          characters: newEnabled && !state.project.characters.find(char => char.id === 'mc') 
            ? [...state.project.characters, {
                id: 'mc',
                name: 'Protagonista',
                displayName: 'Protagonista',
                renpyName: 'mc_name',
                isProtagonist: true,
                isPlayerControlled: true,
                color: '#ff6b6b',
                who_color: '#ffffff',
                what_color: '#ffffff',
                sprites: []
              }]
            : state.project.characters
        }
      };
    
    default:
      return state;
  }
}

// Crear el contexto
const VisualNovelContext = createContext();

// Provider del contexto
export function VisualNovelProvider({ children }) {
  const [state, dispatch] = useReducer(visualNovelReducer, initialState);
  const [isLoading, setIsLoading] = useState(true);
  const [saveStatus, setSaveStatus] = useState('saved'); // 'saved', 'saving', 'error'
  const storage = new ProjectStorage();

  // Cargar estado completo guardado al inicializar
  useEffect(() => {
    const loadSavedState = async () => {
      try {
        console.log('🔄 Cargando estado guardado...');
        
        // Verificar integridad de datos primero
        const integrityCheck = storage.checkDataIntegrity();
        if (!integrityCheck.isValid) {
          console.warn('⚠️ Problemas de integridad detectados:', integrityCheck.issues);
          
          // Limpiar datos corruptos automáticamente
          const cleanedCount = storage.cleanupCorruptedData();
          if (cleanedCount > 0) {
            console.log(`🧹 ${cleanedCount} elementos corruptos eliminados automáticamente`);
          }
        }
        
        // Intentar cargar estado completo primero
        const savedState = storage.loadFullState();
        
        if (savedState) {
          console.log('✅ Estado completo cargado exitosamente');
          dispatch({
            type: 'LOAD_FULL_STATE',
            payload: savedState
          });
        } else {
          // Si no hay estado completo, intentar cargar proyecto guardado
          const savedProject = storage.loadCurrentProject();
          
          if (savedProject) {
            console.log('✅ Proyecto guardado cargado exitosamente');
            dispatch({
              type: 'LOAD_PROJECT',
              payload: savedProject
            });
          } else {
            console.log('ℹ️ No se encontró estado guardado, usando estado inicial');
            // Mantener el estado inicial
          }
        }
        
        console.log('🎉 Recuperación automática completada');
      } catch (error) {
        console.error('❌ Error al cargar estado:', error);
        // En caso de error, mantener el estado inicial
        console.log('ℹ️ Usando estado inicial debido a error de carga');
      } finally {
        setIsLoading(false);
      }
    };

    loadSavedState();
  }, []);

  // Usar hook personalizado para guardado automático
  useAutoSave(state, storage, isLoading, setSaveStatus);

  // Guardar antes de cerrar la página
  useEffect(() => {
    const handleBeforeUnload = (e) => {
      if (state.project.name) {
        console.log('🚨 Guardando estado antes de cerrar...');
        storage.saveFullState(state);
      }
    };

    const handleVisibilityChange = () => {
      if (document.hidden && state.project.name) {
        console.log('👁️ Guardando estado al cambiar pestaña...');
        storage.saveFullState(state);
      }
    };

    window.addEventListener('beforeunload', handleBeforeUnload);
    document.addEventListener('visibilitychange', handleVisibilityChange);

    return () => {
      window.removeEventListener('beforeunload', handleBeforeUnload);
      document.removeEventListener('visibilitychange', handleVisibilityChange);
    };
  }, [state]);

  return (
    <VisualNovelContext.Provider value={{ 
      state, 
      dispatch, 
      storage, 
      isLoading, 
      saveStatus,
      setSaveStatus 
    }}>
      {children}
    </VisualNovelContext.Provider>
  );
}

// Hook personalizado para usar el contexto
export function useVisualNovel() {
  const context = useContext(VisualNovelContext);
  if (!context) {
    throw new Error('useVisualNovel debe ser usado dentro de VisualNovelProvider');
  }
  return context;
}
