import React, { useState } from 'react';
import styled from 'styled-components';
import { useVisualNovel } from '../context/VisualNovelContext';
import { useTheme } from '../hooks/useTheme';

const ThemeContainer = styled.div`
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: var(--color-background);
  z-index: 3000;
  overflow-y: auto;
  transition: background-color 0.3s ease;
`;

const ThemeHeader = styled.div`
  background-color: var(--color-surface);
  padding: 20px;
  border-bottom: 2px solid var(--color-border);
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: sticky;
  top: 0;
  z-index: 3001;
  transition: background-color 0.3s ease, border-color 0.3s ease;
`;

const ThemeTitle = styled.h1`
  margin: 0;
  color: var(--color-primary);
  font-size: 28px;
  display: flex;
  align-items: center;
  gap: 15px;
  transition: color 0.3s ease;
`;

const CloseButton = styled.button`
  background-color: var(--color-error);
  color: var(--color-textInverse);
  border: none;
  border-radius: 8px;
  padding: 12px 20px;
  cursor: pointer;
  font-size: 16px;
  font-weight: bold;
  transition: all 0.3s ease;

  &:hover {
    background-color: #c82333;
    transform: scale(1.05);
  }
`;

const ThemeContent = styled.div`
  padding: 30px;
  max-width: 1200px;
  margin: 0 auto;
`;

const ThemeSection = styled.div`
  background-color: var(--color-surface);
  border: 2px solid var(--color-border);
  border-radius: 12px;
  padding: 25px;
  margin-bottom: 30px;
  transition: background-color 0.3s ease, border-color 0.3s ease;
`;

const SectionTitle = styled.h2`
  color: var(--color-primary);
  margin: 0 0 20px 0;
  font-size: 20px;
  display: flex;
  align-items: center;
  gap: 10px;
  transition: color 0.3s ease;
`;

const ThemeGrid = styled.div`
  display: flex;
  flex-wrap: wrap;
  gap: 15px;
  margin-bottom: 20px;
`;

const ThemeCard = styled.div`
  background-color: ${props => props.selected ? 'var(--color-primary)' : 'var(--color-surfaceSecondary)'};
  border: 2px solid ${props => props.selected ? 'var(--color-primary)' : 'var(--color-border)'};
  border-radius: 8px;
  padding: 15px;
  cursor: pointer;
  transition: all 0.3s ease;
  min-width: 200px;
  flex: 1;
  max-width: 300px;

  &:hover {
    background-color: ${props => props.selected ? 'var(--color-primaryHover)' : 'var(--color-backgroundSecondary)'};
    border-color: ${props => props.selected ? 'var(--color-primaryHover)' : 'var(--color-borderSecondary)'};
    transform: translateY(-2px);
    box-shadow: 0 5px 15px var(--color-shadowHover);
  }
`;

const ThemePreview = styled.div`
  width: 100%;
  height: 80px;
  border-radius: 6px;
  background: ${props => {
    switch(props.theme) {
      case 'light-default': return 'linear-gradient(135deg, #ffffff 0%, #f8f9fa 50%, #e9ecef 100%)';
      case 'dark-default': return 'linear-gradient(135deg, #1a1a1a 0%, #2a2a2a 50%, #333333 100%)';
      case 'dark-colorblind-theme': return 'linear-gradient(135deg, #0f0f0f 0%, #1f1f1f 50%, #2f2f2f 100%)';
      case 'dark-tritanopia-theme': return 'linear-gradient(135deg, #1a0f0a 0%, #2a1f1a 50%, #3a2f2a 100%)';
      case 'blue-theme': return 'linear-gradient(135deg, #1e3a8a 0%, #3b82f6 50%, #60a5fa 100%)';
      case 'green-theme': return 'linear-gradient(135deg, #064e3b 0%, #10b981 50%, #34d399 100%)';
      case 'purple-theme': return 'linear-gradient(135deg, #581c87 0%, #8b5cf6 50%, #a78bfa 100%)';
      case 'orange-theme': return 'linear-gradient(135deg, #9a3412 0%, #f97316 50%, #fb923c 100%)';
      default: return 'linear-gradient(135deg, #1a1a1a 0%, #2a2a2a 50%, #333333 100%)';
    }
  }};
  margin-bottom: 10px;
  border: 1px solid #666666;
  position: relative;
  overflow: hidden;
`;

const ThemePreviewContent = styled.div`
  position: absolute;
  top: 8px;
  left: 8px;
  right: 8px;
  bottom: 8px;
  background: rgba(0, 0, 0, 0.1);
  border-radius: 4px;
  display: flex;
  flex-direction: column;
  gap: 2px;
`;

const PreviewLine = styled.div`
  height: 3px;
  background-color: ${props => props.theme === 'light' ? '#333333' : '#ffffff'};
  border-radius: 1px;
  opacity: 0.7;
  
  &:nth-child(1) { width: 100%; }
  &:nth-child(2) { width: 80%; }
  &:nth-child(3) { width: 60%; }
  &:nth-child(4) { width: 90%; }
`;

const ThemeName = styled.div`
  color: var(--color-textInverse);
  font-weight: bold;
  font-size: 14px;
  margin-bottom: 5px;
  display: flex;
  align-items: center;
  gap: 8px;
  transition: color 0.3s ease;
`;

const ThemeDescription = styled.div`
  color: var(--color-textSecondary);
  font-size: 12px;
  line-height: 1.4;
  transition: color 0.3s ease;
`;

const BetaLabel = styled.span`
  background-color: var(--color-warning);
  color: var(--color-textInverse);
  font-size: 10px;
  padding: 2px 6px;
  border-radius: 3px;
  font-weight: bold;
`;

const AccessibilityNote = styled.div`
  background-color: rgba(37, 99, 235, 0.1);
  border: 2px solid var(--color-primary);
  border-radius: 8px;
  padding: 15px;
  margin: 20px 0;
  transition: border-color 0.3s ease;
`;

const AccessibilityTitle = styled.div`
  color: var(--color-primary);
  font-weight: bold;
  font-size: 14px;
  margin-bottom: 8px;
  display: flex;
  align-items: center;
  gap: 8px;
  transition: color 0.3s ease;
`;

const AccessibilityText = styled.div`
  color: var(--color-textPrimary);
  font-size: 14px;
  line-height: 1.5;
  transition: color 0.3s ease;
`;

const CurrentThemeInfo = styled.div`
  background-color: rgba(16, 185, 129, 0.1);
  border: 2px solid var(--color-success);
  border-radius: 8px;
  padding: 15px;
  margin: 20px 0;
  transition: border-color 0.3s ease;
`;

const CurrentThemeTitle = styled.div`
  color: var(--color-success);
  font-weight: bold;
  font-size: 14px;
  margin-bottom: 8px;
  display: flex;
  align-items: center;
  gap: 8px;
  transition: color 0.3s ease;
`;

const CurrentThemeText = styled.div`
  color: var(--color-textPrimary);
  font-size: 14px;
  line-height: 1.5;
  transition: color 0.3s ease;
`;

const ApplyButton = styled.button`
  background-color: var(--color-primary);
  color: var(--color-textInverse);
  border: none;
  border-radius: 6px;
  padding: 10px 20px;
  cursor: pointer;
  font-size: 14px;
  font-weight: bold;
  transition: all 0.3s ease;
  margin-top: 10px;

  &:hover {
    background-color: var(--color-primaryHover);
    transform: translateY(-2px);
  }

  &:disabled {
    background-color: var(--color-backgroundTertiary);
    cursor: not-allowed;
    transform: none;
  }
`;

function ThemePage({ onClose }) {
  const { state, dispatch } = useVisualNovel();
  const { currentTheme, changeTheme } = useTheme();
  const [selectedTheme, setSelectedTheme] = useState(currentTheme);

  // Cargar tema seleccionado desde localStorage al abrir (solo una vez)
  React.useEffect(() => {
    const savedSelectedTheme = localStorage.getItem('selected-theme');
    if (savedSelectedTheme) {
      setSelectedTheme(savedSelectedTheme);
    }
  }, []);

  // Guardar tema seleccionado en localStorage cuando cambie (pero no al cargar inicial)
  React.useEffect(() => {
    // Solo guardar si no es la carga inicial
    if (selectedTheme !== currentTheme) {
      localStorage.setItem('selected-theme', selectedTheme);
    }
  }, [selectedTheme, currentTheme]);

  const themes = {
    light: [
      {
        id: 'light-default',
        name: 'Light default',
        description: 'Tema claro estándar con colores neutros',
        theme: 'light',
        beta: false
      }
    ],
    dark: [
      {
        id: 'dark-default',
        name: 'Dark default',
        description: 'Tema oscuro estándar, ideal para trabajo nocturno',
        theme: 'dark',
        beta: false
      }
    ],
    accessibility: [
      {
        id: 'dark-colorblind-theme',
        name: 'Dark Colorblind Beta',
        description: 'Tema oscuro optimizado para daltonismo general',
        theme: 'dark',
        beta: true
      },
      {
        id: 'dark-tritanopia-theme',
        name: 'Dark Tritanopia Beta',
        description: 'Tema oscuro para tritanopia (deficiencia azul-amarillo)',
        theme: 'dark',
        beta: true
      }
    ],
    custom: [
      {
        id: 'blue-theme',
        name: 'Blue theme',
        description: 'Tema azul profesional',
        theme: 'blue',
        beta: false
      },
      {
        id: 'green-theme',
        name: 'Green theme',
        description: 'Tema verde relajante',
        theme: 'green',
        beta: false
      },
      {
        id: 'purple-theme',
        name: 'Purple theme',
        description: 'Tema púrpura creativo',
        theme: 'purple',
        beta: false
      },
      {
        id: 'orange-theme',
        name: 'Orange theme',
        description: 'Tema naranja energético',
        theme: 'orange',
        beta: false
      }
    ]
  };

  const handleThemeSelect = (themeId) => {
    setSelectedTheme(themeId);
  };

  const handleApplyTheme = () => {
    dispatch({
      type: 'SET_THEME',
      payload: selectedTheme
    });
    changeTheme(selectedTheme);
    console.log('Aplicando tema:', selectedTheme);
  };

  const getCurrentTheme = () => {
    const allThemes = [...themes.light, ...themes.dark, ...themes.accessibility, ...themes.custom];
    return allThemes.find(theme => theme.id === selectedTheme);
  };

  return (
    <ThemeContainer>
      <ThemeHeader>
        <ThemeTitle>
          🎨 Selector de Temas
        </ThemeTitle>
        <CloseButton onClick={onClose}>
          ✕ Cerrar
        </CloseButton>
      </ThemeHeader>

      <ThemeContent>
        <ThemeSection>
          <SectionTitle>
            ☀️ Temas Claros
          </SectionTitle>
          <ThemeGrid>
            {themes.light.map(theme => (
              <ThemeCard
                key={theme.id}
                selected={selectedTheme === theme.id}
                onClick={() => handleThemeSelect(theme.id)}
              >
                <ThemePreview theme={theme.id}>
                  <ThemePreviewContent>
                    <PreviewLine theme="light" />
                    <PreviewLine theme="light" />
                    <PreviewLine theme="light" />
                    <PreviewLine theme="light" />
                  </ThemePreviewContent>
                </ThemePreview>
                <ThemeName>
                  {theme.name}
                  {theme.beta && <BetaLabel>Beta</BetaLabel>}
                </ThemeName>
                <ThemeDescription>
                  {theme.description}
                </ThemeDescription>
              </ThemeCard>
            ))}
          </ThemeGrid>
        </ThemeSection>

        <ThemeSection>
          <SectionTitle>
            🌙 Temas Oscuros
          </SectionTitle>
          <ThemeGrid>
            {themes.dark.map(theme => (
              <ThemeCard
                key={theme.id}
                selected={selectedTheme === theme.id}
                onClick={() => handleThemeSelect(theme.id)}
              >
                <ThemePreview theme={theme.id}>
                  <ThemePreviewContent>
                    <PreviewLine theme="dark" />
                    <PreviewLine theme="dark" />
                    <PreviewLine theme="dark" />
                    <PreviewLine theme="dark" />
                  </ThemePreviewContent>
                </ThemePreview>
                <ThemeName>
                  {theme.name}
                  {theme.beta && <BetaLabel>Beta</BetaLabel>}
                </ThemeName>
                <ThemeDescription>
                  {theme.description}
                </ThemeDescription>
              </ThemeCard>
            ))}
          </ThemeGrid>
        </ThemeSection>

        <ThemeSection>
          <SectionTitle>
            ♿ Temas de Accesibilidad
          </SectionTitle>
          <ThemeGrid>
            {themes.accessibility.map(theme => (
              <ThemeCard
                key={theme.id}
                selected={selectedTheme === theme.id}
                onClick={() => handleThemeSelect(theme.id)}
              >
                <ThemePreview theme={theme.id}>
                  <ThemePreviewContent>
                    <PreviewLine theme="dark" />
                    <PreviewLine theme="dark" />
                    <PreviewLine theme="dark" />
                    <PreviewLine theme="dark" />
                  </ThemePreviewContent>
                </ThemePreview>
                <ThemeName>
                  {theme.name}
                  {theme.beta && <BetaLabel>Beta</BetaLabel>}
                </ThemeName>
                <ThemeDescription>
                  {theme.description}
                </ThemeDescription>
              </ThemeCard>
            ))}
          </ThemeGrid>
        </ThemeSection>

        <ThemeSection>
          <SectionTitle>
            🎨 Temas Personalizados
          </SectionTitle>
          <ThemeGrid>
            {themes.custom.map(theme => (
              <ThemeCard
                key={theme.id}
                selected={selectedTheme === theme.id}
                onClick={() => handleThemeSelect(theme.id)}
              >
                <ThemePreview theme={theme.id}>
                  <ThemePreviewContent>
                    <PreviewLine theme="dark" />
                    <PreviewLine theme="dark" />
                    <PreviewLine theme="dark" />
                    <PreviewLine theme="dark" />
                  </ThemePreviewContent>
                </ThemePreview>
                <ThemeName>
                  {theme.name}
                </ThemeName>
                <ThemeDescription>
                  {theme.description}
                </ThemeDescription>
              </ThemeCard>
            ))}
          </ThemeGrid>
        </ThemeSection>

        {getCurrentTheme() && (
          <CurrentThemeInfo>
            <CurrentThemeTitle>
              ✅ Tema Seleccionado
            </CurrentThemeTitle>
            <CurrentThemeText>
              <strong>{getCurrentTheme().name}</strong> - {getCurrentTheme().description}
              {getCurrentTheme().beta && (
                <span style={{ color: '#f59e0b', marginLeft: '8px' }}>
                  (Versión Beta)
                </span>
              )}
            </CurrentThemeText>
            <ApplyButton onClick={handleApplyTheme}>
              🎨 Aplicar Tema
            </ApplyButton>
          </CurrentThemeInfo>
        )}

        <AccessibilityNote>
          <AccessibilityTitle>
            ♿ Información de Accesibilidad
          </AccessibilityTitle>
          <AccessibilityText>
            Los temas marcados como "Beta" incluyen mejoras de accesibilidad para usuarios con daltonismo. 
            Estos temas utilizan colores y contrastes optimizados para diferentes tipos de deficiencia visual, 
            incluyendo protanopia, deuteranopia y tritanopia.
          </AccessibilityText>
        </AccessibilityNote>

        <AccessibilityNote>
          <AccessibilityTitle>
            💡 Consejos de Uso
          </AccessibilityTitle>
          <AccessibilityText>
            • Los temas claros son ideales para trabajo diurno y espacios bien iluminados<br/>
            • Los temas oscuros reducen la fatiga visual durante el trabajo nocturno<br/>
            • Los temas personalizados ofrecen opciones de colores específicos para diferentes preferencias<br/>
            • Los temas Beta incluyen mejoras de accesibilidad que se actualizan regularmente
          </AccessibilityText>
        </AccessibilityNote>
      </ThemeContent>
    </ThemeContainer>
  );
}

export default ThemePage;
