// Utilidades para efectos de transición en el preview
// Basado en las transiciones de Ren'Py: https://www.renpy.org/doc/html/transitions.html

import React, { useState, useEffect, useCallback } from 'react';
import { getAllTransitions } from '../config/renpyTransitions';

// Clase para manejar transiciones en el preview
export class TransitionManager {
  constructor() {
    this.currentTransition = null;
    this.transitionQueue = [];
    this.isTransitioning = false;
  }

  // Aplicar transición
  async applyTransition(transitionType, duration = null) {
    if (this.isTransitioning) {
      this.transitionQueue.push({ transitionType, duration });
      return;
    }

    this.isTransitioning = true;
    this.currentTransition = { transitionType, duration };

    try {
      const transition = getAllTransitions().find(t => t.id === transitionType);
      if (!transition) {
        console.warn(`Transición no encontrada: ${transitionType}`);
        return;
      }

      const actualDuration = duration || transition.duration;
      await this.executeTransition(transitionType, actualDuration);
    } catch (error) {
      console.error('Error aplicando transición:', error);
    } finally {
      this.isTransitioning = false;
      this.currentTransition = null;
      
      // Procesar siguiente transición en cola
      if (this.transitionQueue.length > 0) {
        const nextTransition = this.transitionQueue.shift();
        this.applyTransition(nextTransition.transitionType, nextTransition.duration);
      }
    }
  }

  // Ejecutar transición específica
  async executeTransition(transitionType, duration) {
    return new Promise((resolve) => {
      const element = document.querySelector('.preview-transition-container');
      if (!element) {
        resolve();
        return;
      }

      // Aplicar clase de transición
      element.classList.add(`transition-${transitionType}`);
      element.style.setProperty('--transition-duration', `${duration}s`);

      // Remover clase después de la duración
      setTimeout(() => {
        element.classList.remove(`transition-${transitionType}`);
        element.style.removeProperty('--transition-duration');
        resolve();
      }, duration * 1000);
    });
  }

  // Obtener estilos CSS para transiciones
  static getTransitionStyles() {
    return `
      .preview-transition-container {
        position: relative;
        overflow: hidden;
      }

      /* Transiciones básicas */
      .transition-dissolve {
        animation: dissolve var(--transition-duration, 0.5s) ease-in-out;
      }

      .transition-fade {
        animation: fade var(--transition-duration, 1.0s) ease-in-out;
      }

      .transition-pixellate {
        animation: pixellate var(--transition-duration, 1.0s) ease-in-out;
      }

      /* Transiciones de movimiento */
      .transition-moveinright {
        animation: moveInRight var(--transition-duration, 0.5s) ease-out;
      }

      .transition-moveinleft {
        animation: moveInLeft var(--transition-duration, 0.5s) ease-out;
      }

      .transition-moveintop {
        animation: moveInTop var(--transition-duration, 0.5s) ease-out;
      }

      .transition-moveinbottom {
        animation: moveInBottom var(--transition-duration, 0.5s) ease-out;
      }

      .transition-moveoutright {
        animation: moveOutRight var(--transition-duration, 0.5s) ease-in;
      }

      .transition-moveoutleft {
        animation: moveOutLeft var(--transition-duration, 0.5s) ease-in;
      }

      .transition-moveouttop {
        animation: moveOutTop var(--transition-duration, 0.5s) ease-in;
      }

      .transition-moveoutbottom {
        animation: moveOutBottom var(--transition-duration, 0.5s) ease-in;
      }

      /* Transiciones de zoom */
      .transition-zoomin {
        animation: zoomIn var(--transition-duration, 0.5s) ease-out;
      }

      .transition-zoomout {
        animation: zoomOut var(--transition-duration, 0.5s) ease-in;
      }

      .transition-zoominout {
        animation: zoomInOut var(--transition-duration, 0.5s) ease-in-out;
      }

      /* Transiciones de efecto */
      .transition-vpunch {
        animation: vPunch var(--transition-duration, 0.25s) ease-in-out;
      }

      .transition-hpunch {
        animation: hPunch var(--transition-duration, 0.25s) ease-in-out;
      }

      .transition-blinds {
        animation: blinds var(--transition-duration, 1.0s) ease-in-out;
      }

      .transition-squares {
        animation: squares var(--transition-duration, 1.0s) ease-in-out;
      }

      /* Transiciones de deslizamiento */
      .transition-wipeleft {
        animation: wipeLeft var(--transition-duration, 0.5s) ease-in-out;
      }

      .transition-wiperight {
        animation: wipeRight var(--transition-duration, 0.5s) ease-in-out;
      }

      .transition-wipeup {
        animation: wipeUp var(--transition-duration, 0.5s) ease-in-out;
      }

      .transition-wipedown {
        animation: wipeDown var(--transition-duration, 0.5s) ease-in-out;
      }

      .transition-slideleft {
        animation: slideLeft var(--transition-duration, 0.5s) ease-in-out;
      }

      .transition-slideright {
        animation: slideRight var(--transition-duration, 0.5s) ease-in-out;
      }

      .transition-slideup {
        animation: slideUp var(--transition-duration, 0.5s) ease-in-out;
      }

      .transition-slidedown {
        animation: slideDown var(--transition-duration, 0.5s) ease-in-out;
      }

      /* Transiciones de empuje */
      .transition-pushright {
        animation: pushRight var(--transition-duration, 0.5s) ease-in-out;
      }

      .transition-pushleft {
        animation: pushLeft var(--transition-duration, 0.5s) ease-in-out;
      }

      .transition-pushup {
        animation: pushUp var(--transition-duration, 0.5s) ease-in-out;
      }

      .transition-pushdown {
        animation: pushDown var(--transition-duration, 0.5s) ease-in-out;
      }

      /* Transiciones de iris */
      .transition-irisin {
        animation: irisIn var(--transition-duration, 0.5s) ease-in-out;
      }

      .transition-irisout {
        animation: irisOut var(--transition-duration, 0.5s) ease-in-out;
      }

      /* Keyframes para animaciones */
      @keyframes dissolve {
        0% { opacity: 1; }
        50% { opacity: 0.5; }
        100% { opacity: 1; }
      }

      @keyframes fade {
        0% { opacity: 1; }
        50% { opacity: 0; }
        100% { opacity: 1; }
      }

      @keyframes pixellate {
        0% { filter: blur(0px); }
        50% { filter: blur(2px); }
        100% { filter: blur(0px); }
      }

      @keyframes moveInRight {
        0% { transform: translateX(100%); }
        100% { transform: translateX(0); }
      }

      @keyframes moveInLeft {
        0% { transform: translateX(-100%); }
        100% { transform: translateX(0); }
      }

      @keyframes moveInTop {
        0% { transform: translateY(-100%); }
        100% { transform: translateY(0); }
      }

      @keyframes moveInBottom {
        0% { transform: translateY(100%); }
        100% { transform: translateY(0); }
      }

      @keyframes moveOutRight {
        0% { transform: translateX(0); }
        100% { transform: translateX(100%); }
      }

      @keyframes moveOutLeft {
        0% { transform: translateX(0); }
        100% { transform: translateX(-100%); }
      }

      @keyframes moveOutTop {
        0% { transform: translateY(0); }
        100% { transform: translateY(-100%); }
      }

      @keyframes moveOutBottom {
        0% { transform: translateY(0); }
        100% { transform: translateY(100%); }
      }

      @keyframes zoomIn {
        0% { transform: scale(1); }
        100% { transform: scale(1.2); }
      }

      @keyframes zoomOut {
        0% { transform: scale(1); }
        100% { transform: scale(0.8); }
      }

      @keyframes zoomInOut {
        0% { transform: scale(1); }
        50% { transform: scale(1.2); }
        100% { transform: scale(1); }
      }

      @keyframes vPunch {
        0%, 100% { transform: translateY(0); }
        25% { transform: translateY(-10px); }
        75% { transform: translateY(10px); }
      }

      @keyframes hPunch {
        0%, 100% { transform: translateX(0); }
        25% { transform: translateX(-10px); }
        75% { transform: translateX(10px); }
      }

      @keyframes blinds {
        0% { clip-path: inset(0 0 100% 0); }
        100% { clip-path: inset(0 0 0 0); }
      }

      @keyframes squares {
        0% { clip-path: inset(0 0 100% 0); }
        100% { clip-path: inset(0 0 0 0); }
      }

      @keyframes wipeLeft {
        0% { clip-path: inset(0 0 0 100%); }
        100% { clip-path: inset(0 0 0 0); }
      }

      @keyframes wipeRight {
        0% { clip-path: inset(0 100% 0 0); }
        100% { clip-path: inset(0 0 0 0); }
      }

      @keyframes wipeUp {
        0% { clip-path: inset(100% 0 0 0); }
        100% { clip-path: inset(0 0 0 0); }
      }

      @keyframes wipeDown {
        0% { clip-path: inset(0 0 100% 0); }
        100% { clip-path: inset(0 0 0 0); }
      }

      @keyframes slideLeft {
        0% { transform: translateX(-100%); }
        100% { transform: translateX(0); }
      }

      @keyframes slideRight {
        0% { transform: translateX(100%); }
        100% { transform: translateX(0); }
      }

      @keyframes slideUp {
        0% { transform: translateY(-100%); }
        100% { transform: translateY(0); }
      }

      @keyframes slideDown {
        0% { transform: translateY(100%); }
        100% { transform: translateY(0); }
      }

      @keyframes pushRight {
        0% { transform: translateX(0); }
        100% { transform: translateX(100%); }
      }

      @keyframes pushLeft {
        0% { transform: translateX(0); }
        100% { transform: translateX(-100%); }
      }

      @keyframes pushUp {
        0% { transform: translateY(0); }
        100% { transform: translateY(-100%); }
      }

      @keyframes pushDown {
        0% { transform: translateY(0); }
        100% { transform: translateY(100%); }
      }

      @keyframes irisIn {
        0% { clip-path: circle(0% at 50% 50%); }
        100% { clip-path: circle(100% at 50% 50%); }
      }

      @keyframes irisOut {
        0% { clip-path: circle(100% at 50% 50%); }
        100% { clip-path: circle(0% at 50% 50%); }
      }
    `;
  }

  // Inyectar estilos en el documento
  static injectStyles() {
    if (document.getElementById('transition-styles')) {
      return; // Ya están inyectados
    }

    const style = document.createElement('style');
    style.id = 'transition-styles';
    style.textContent = this.getTransitionStyles();
    document.head.appendChild(style);
  }
}

// Hook para usar transiciones en componentes React
export const useTransition = () => {
  const [transitionManager] = useState(() => new TransitionManager());

  useEffect(() => {
    TransitionManager.injectStyles();
  }, []);

  const applyTransition = useCallback((transitionType, duration) => {
    return transitionManager.applyTransition(transitionType, duration);
  }, [transitionManager]);

  return { applyTransition };
};
