import React, { useState } from 'react';
import styled from 'styled-components';

const TroubleshootingContainer = styled.div`
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: var(--color-background);
  z-index: 3000;
  overflow-y: auto;
  transition: background-color 0.3s ease;
`;

const TroubleshootingHeader = styled.div`
  background-color: var(--color-surface);
  padding: 20px;
  border-bottom: 2px solid var(--color-border);
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: sticky;
  top: 0;
  z-index: 3001;
  transition: background-color 0.3s ease, border-color 0.3s ease;
`;

const TroubleshootingTitle = styled.h1`
  margin: 0;
  color: var(--color-success);
  font-size: 32px;
  font-weight: 800;
  display: flex;
  align-items: center;
  gap: 15px;
  transition: color 0.3s ease;
  text-shadow: 0 2px 4px var(--color-shadow);
  letter-spacing: 0.5px;
`;

const CloseButton = styled.button`
  background-color: var(--color-error);
  color: var(--color-textInverse);
  border: none;
  border-radius: 8px;
  padding: 12px 20px;
  cursor: pointer;
  font-size: 16px;
  font-weight: bold;
  transition: all 0.3s ease;
  box-shadow: 0 4px 12px var(--color-shadow);

  &:hover {
    background-color: var(--color-errorHover);
    transform: scale(1.05);
    box-shadow: 0 6px 16px var(--color-shadowHover);
  }
`;

const TroubleshootingContent = styled.div`
  padding: 30px;
  max-width: 1200px;
  margin: 0 auto;
`;

const SearchBox = styled.div`
  background-color: var(--color-surfaceSecondary);
  border: 2px solid var(--color-border);
  border-radius: 12px;
  padding: 25px;
  margin-bottom: 30px;
  transition: background-color 0.3s ease, border-color 0.3s ease;
  box-shadow: 0 4px 12px var(--color-shadow);

  &:hover {
    border-color: var(--color-primary);
    box-shadow: 0 6px 16px var(--color-shadowHover);
  }
`;

const SearchTitle = styled.h2`
  color: var(--color-success);
  margin: 0 0 20px 0;
  font-size: 22px;
  font-weight: 800;
  display: flex;
  align-items: center;
  gap: 12px;
  transition: color 0.3s ease;
  text-shadow: 0 1px 3px var(--color-shadow);
  letter-spacing: 0.3px;
`;

const SearchInput = styled.input`
  width: 100%;
  padding: 12px 16px;
  background-color: var(--color-backgroundSecondary);
  border: 2px solid var(--color-border);
  border-radius: 8px;
  color: var(--color-textPrimary);
  font-size: 16px;
  font-weight: 500;
  transition: all 0.3s ease;
  box-shadow: 0 2px 6px var(--color-shadow);

  &:focus {
    outline: none;
    border-color: var(--color-success);
    box-shadow: 0 4px 12px var(--color-shadowHover);
    transform: translateY(-1px);
  }

  &:hover {
    border-color: var(--color-primary);
    box-shadow: 0 3px 8px var(--color-shadowHover);
  }

  &::placeholder {
    color: var(--color-textTertiary);
    font-weight: 400;
  }
`;

const CategoryTabs = styled.div`
  display: flex;
  gap: 10px;
  margin-bottom: 30px;
  flex-wrap: wrap;
`;

const CategoryTab = styled.button`
  background-color: ${props => props.active ? 'var(--color-success)' : 'var(--color-surfaceSecondary)'};
  color: ${props => props.active ? 'var(--color-textInverse)' : 'var(--color-textPrimary)'};
  border: 2px solid ${props => props.active ? 'var(--color-success)' : 'var(--color-border)'};
  padding: 14px 22px;
  border-radius: 8px;
  cursor: pointer;
  font-size: 14px;
  font-weight: 700;
  transition: all 0.3s ease;
  box-shadow: 0 3px 8px var(--color-shadow);

  &:hover {
    background-color: ${props => props.active ? 'var(--color-successHover)' : 'var(--color-border)'};
    border-color: ${props => props.active ? 'var(--color-successHover)' : 'var(--color-primary)'};
    color: var(--color-textInverse);
    transform: translateY(-3px);
    box-shadow: 0 6px 16px var(--color-shadowHover);
  }

  &:active {
    transform: translateY(-1px);
    box-shadow: 0 4px 12px var(--color-shadowHover);
  }
`;

const ProblemGrid = styled.div`
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(400px, 1fr));
  gap: 25px;
  margin-bottom: 40px;
`;

const ProblemCard = styled.div`
  background-color: var(--color-surfaceSecondary);
  border: 2px solid var(--color-border);
  border-radius: 12px;
  padding: 24px;
  transition: all 0.3s ease;
  box-shadow: 0 4px 12px var(--color-shadow);

  &:hover {
    border-color: var(--color-success);
    background-color: var(--color-surfaceElevated);
    transform: translateY(-8px);
    box-shadow: 0 12px 32px var(--color-shadowHover);
  }
`;

const ProblemHeader = styled.div`
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  margin-bottom: 15px;
`;

const ProblemTitle = styled.h3`
  margin: 0;
  color: var(--color-success);
  font-size: 20px;
  font-weight: 800;
  display: flex;
  align-items: center;
  gap: 12px;
  transition: color 0.3s ease;
  text-shadow: 0 2px 4px var(--color-shadow);
  letter-spacing: 0.3px;
`;

const SeverityIndicator = styled.span`
  background-color: ${props => {
    switch(props.severity) {
      case 'critical': return 'var(--color-error)';
      case 'warning': return 'var(--color-warning)';
      case 'info': return 'var(--color-info)';
      case 'success': return 'var(--color-success)';
      default: return 'var(--color-borderSecondary)';
    }
  }};
  color: var(--color-textInverse);
  padding: 6px 12px;
  border-radius: 6px;
  font-size: 12px;
  font-weight: 700;
  transition: all 0.3s ease;
  box-shadow: 0 2px 6px var(--color-shadow);
  border: 1px solid rgba(255, 255, 255, 0.1);

  &:hover {
    transform: translateY(-1px);
    box-shadow: 0 4px 8px var(--color-shadowHover);
  }
`;

const ProblemDescription = styled.p`
  color: var(--color-textPrimary);
  margin: 0 0 18px 0;
  line-height: 1.7;
  font-size: 16px;
  font-weight: 600;
  transition: color 0.3s ease;
  text-shadow: 0 1px 2px var(--color-shadow);
`;

const SolutionBox = styled.div`
  background-color: var(--color-surfaceSecondary);
  border: 3px solid var(--color-success);
  border-radius: 14px;
  padding: 24px;
  margin: 24px 0;
  transition: all 0.3s ease;
  box-shadow: 0 6px 20px var(--color-shadow);
  position: relative;

  &:hover {
    border-color: var(--color-successHover);
    box-shadow: 0 8px 24px var(--color-shadowHover);
    transform: translateY(-3px);
  }

  &::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    height: 4px;
    background: linear-gradient(90deg, var(--color-success), var(--color-successHover));
    border-radius: 14px 14px 0 0;
  }
`;

const SolutionTitle = styled.div`
  color: var(--color-success);
  font-weight: 800;
  font-size: 18px;
  margin-bottom: 16px;
  display: flex;
  align-items: center;
  gap: 10px;
  transition: color 0.3s ease;
  text-shadow: 0 2px 4px var(--color-shadow);
  letter-spacing: 0.3px;
`;

const SolutionText = styled.div`
  color: var(--color-textPrimary);
  font-size: 16px;
  line-height: 1.7;
  font-weight: 600;
  transition: color 0.3s ease;
  text-shadow: 0 1px 2px var(--color-shadow);
`;

const CodeExample = styled.div`
  background-color: var(--color-backgroundSecondary);
  border: 3px solid var(--color-primary);
  border-radius: 10px;
  padding: 20px;
  margin: 18px 0;
  font-family: 'Courier New', monospace;
  font-size: 14px;
  font-weight: 600;
  color: var(--color-primary);
  overflow-x: auto;
  transition: all 0.3s ease;
  box-shadow: 0 4px 12px var(--color-shadow);
  position: relative;

  &:hover {
    border-color: var(--color-primaryHover);
    box-shadow: 0 6px 16px var(--color-shadowHover);
    transform: translateY(-2px);
  }

  &::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    height: 3px;
    background: linear-gradient(90deg, var(--color-primary), var(--color-primaryHover));
    border-radius: 10px 10px 0 0;
  }
`;

const WarningBox = styled.div`
  background-color: var(--color-surfaceSecondary);
  border: 3px solid var(--color-warning);
  border-radius: 14px;
  padding: 24px;
  margin: 24px 0;
  transition: all 0.3s ease;
  box-shadow: 0 6px 20px var(--color-shadow);
  position: relative;

  &:hover {
    border-color: var(--color-warningHover);
    box-shadow: 0 8px 24px var(--color-shadowHover);
    transform: translateY(-3px);
  }

  &::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    height: 4px;
    background: linear-gradient(90deg, var(--color-warning), var(--color-warningHover));
    border-radius: 14px 14px 0 0;
  }
`;

const WarningTitle = styled.div`
  color: var(--color-warning);
  font-weight: 800;
  font-size: 18px;
  margin-bottom: 16px;
  display: flex;
  align-items: center;
  gap: 10px;
  transition: color 0.3s ease;
  text-shadow: 0 2px 4px var(--color-shadow);
  letter-spacing: 0.3px;
`;

const WarningText = styled.div`
  color: var(--color-textPrimary);
  font-size: 16px;
  line-height: 1.7;
  font-weight: 600;
  transition: color 0.3s ease;
  text-shadow: 0 1px 2px var(--color-shadow);
`;

const ErrorBox = styled.div`
  background-color: var(--color-surfaceSecondary);
  border: 3px solid var(--color-error);
  border-radius: 14px;
  padding: 24px;
  margin: 24px 0;
  transition: all 0.3s ease;
  box-shadow: 0 6px 20px var(--color-shadow);
  position: relative;

  &:hover {
    border-color: var(--color-errorHover);
    box-shadow: 0 8px 24px var(--color-shadowHover);
    transform: translateY(-3px);
  }

  &::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    height: 4px;
    background: linear-gradient(90deg, var(--color-error), var(--color-errorHover));
    border-radius: 14px 14px 0 0;
  }
`;

const ErrorTitle = styled.div`
  color: var(--color-error);
  font-weight: 800;
  font-size: 18px;
  margin-bottom: 16px;
  display: flex;
  align-items: center;
  gap: 10px;
  transition: color 0.3s ease;
  text-shadow: 0 2px 4px var(--color-shadow);
  letter-spacing: 0.3px;
`;

const ErrorText = styled.div`
  color: var(--color-textPrimary);
  font-size: 16px;
  line-height: 1.7;
  font-weight: 600;
  transition: color 0.3s ease;
  text-shadow: 0 1px 2px var(--color-shadow);
`;

const InfoBox = styled.div`
  background-color: var(--color-surfaceSecondary);
  border: 3px solid var(--color-info);
  border-radius: 14px;
  padding: 24px;
  margin: 24px 0;
  transition: all 0.3s ease;
  box-shadow: 0 6px 20px var(--color-shadow);
  position: relative;

  &:hover {
    border-color: var(--color-infoHover);
    box-shadow: 0 8px 24px var(--color-shadowHover);
    transform: translateY(-3px);
  }

  &::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    height: 4px;
    background: linear-gradient(90deg, var(--color-info), var(--color-infoHover));
    border-radius: 14px 14px 0 0;
  }
`;

const InfoTitle = styled.div`
  color: var(--color-info);
  font-weight: 800;
  font-size: 18px;
  margin-bottom: 16px;
  display: flex;
  align-items: center;
  gap: 10px;
  transition: color 0.3s ease;
  text-shadow: 0 2px 4px var(--color-shadow);
  letter-spacing: 0.3px;
`;

const InfoText = styled.div`
  color: var(--color-textPrimary);
  font-size: 16px;
  line-height: 1.7;
  font-weight: 600;
  transition: color 0.3s ease;
  text-shadow: 0 1px 2px var(--color-shadow);
`;

function TroubleshootingPage({ onClose }) {
  const [activeCategory, setActiveCategory] = useState('common');
  const [searchTerm, setSearchTerm] = useState('');

  const troubleshootingData = {
    common: [
      {
        id: 'script-errors',
        title: 'Errores de Script',
        icon: '📝',
        severity: 'critical',
        description: 'Errores comunes en la sintaxis del script que impiden que el juego funcione correctamente.',
        solution: 'Verifica la sintaxis de Ren\'Py, asegúrate de que todos los bloques estén correctamente cerrados y que las variables estén definidas.',
        codeExample: `# Error común: falta de indentación
label start:
    "Hola mundo"
    menu:
        "Opción 1":
            jump option1
        "Opción 2":
            jump option2

# Solución: indentación correcta
label start:
    "Hola mundo"
    menu:
        "Opción 1":
            jump option1
        "Opción 2":
            jump option2`,
        warning: 'Usa siempre la indentación correcta en Ren\'Py. Los bloques deben estar alineados correctamente.'
      },
      {
        id: 'image-not-found',
        title: 'Imagen No Encontrada',
        icon: '🖼️',
        severity: 'warning',
        description: 'El juego no puede encontrar las imágenes especificadas en el script.',
        solution: 'Verifica que las imágenes estén en la carpeta correcta (images/) y que los nombres coincidan exactamente.',
        codeExample: `# Error: imagen no encontrada
show character happy

# Solución: verificar ruta y nombre
show eileen happy
# O usar ruta completa
show "images/characters/eileen_happy.png"`,
        info: 'Ren\'Py busca imágenes en la carpeta images/ por defecto. Los nombres deben coincidir exactamente.'
      },
      {
        id: 'audio-issues',
        title: 'Problemas de Audio',
        icon: '🔊',
        severity: 'warning',
        description: 'La música o efectos de sonido no se reproducen correctamente.',
        solution: 'Verifica que los archivos de audio estén en formatos compatibles (OGG, MP3) y en la carpeta correcta.',
        codeExample: `# Configuración correcta de audio
play music "audio/bgm/main_theme.ogg"
play sound "audio/sfx/click.ogg"
play voice "audio/voice/character_001.ogg"`,
        info: 'Ren\'Py soporta formatos OGG, MP3 y WAV. OGG es recomendado para mejor compatibilidad.'
      }
    ],
    syntax: [
      {
        id: 'indentation-errors',
        title: 'Errores de Indentación',
        icon: '📐',
        severity: 'critical',
        description: 'Errores de indentación que causan fallos en la compilación del script.',
        solution: 'Usa espacios consistentes para la indentación. Ren\'Py es sensible a la indentación.',
        codeExample: `# Error: indentación inconsistente
label start:
    "Texto"
  menu:
    "Opción 1":
        jump option1

# Solución: indentación consistente
label start:
    "Texto"
    menu:
        "Opción 1":
            jump option1`,
        warning: 'Nunca mezcles espacios y tabs. Usa solo espacios para la indentación.'
      },
      {
        id: 'variable-errors',
        title: 'Errores de Variables',
        icon: '🔢',
        severity: 'warning',
        description: 'Variables no definidas o usadas incorrectamente en el script.',
        solution: 'Define todas las variables antes de usarlas con la declaración default o define.',
        codeExample: `# Error: variable no definida
if player_name == "Juan":
    "Hola Juan"

# Solución: definir variable
default player_name = "Juan"
if player_name == "Juan":
    "Hola Juan"`,
        info: 'Usa default para variables que pueden cambiar durante el juego, define para constantes.'
      },
      {
        id: 'label-errors',
        title: 'Errores de Labels',
        icon: '🏷️',
        severity: 'critical',
        description: 'Labels no definidos o referencias incorrectas a labels.',
        solution: 'Asegúrate de que todos los labels estén definidos y que las referencias sean correctas.',
        codeExample: `# Error: label no definido
jump non_existent_label

# Solución: definir label
label non_existent_label:
    "Este label existe"
    return`,
        warning: 'Los nombres de labels deben ser únicos y no pueden contener espacios.'
      }
    ],
    performance: [
      {
        id: 'slow-loading',
        title: 'Carga Lenta',
        icon: '⏳',
        severity: 'warning',
        description: 'El juego tarda mucho en cargar o tiene problemas de rendimiento.',
        solution: 'Optimiza las imágenes, reduce el tamaño de archivos y usa formatos eficientes.',
        codeExample: `# Optimización de imágenes
# Usa formatos eficientes
image bg room = "images/bg_room.webp"
# O comprime imágenes
image bg room = "images/bg_room.jpg"`,
        info: 'WebP y JPEG son formatos eficientes para imágenes. PNG solo cuando necesites transparencia.'
      },
      {
        id: 'memory-issues',
        title: 'Problemas de Memoria',
        icon: '💾',
        severity: 'warning',
        description: 'El juego consume mucha memoria RAM o se cierra inesperadamente.',
        solution: 'Libera memoria limpiando imágenes no usadas y optimizando el código.',
        codeExample: `# Liberar memoria
hide eileen
hide bg room
# O usar scene para limpiar todo
scene black`,
        info: 'Usa hide para ocultar elementos específicos, scene para limpiar toda la pantalla.'
      },
      {
        id: 'audio-performance',
        title: 'Problemas de Audio',
        icon: '🎵',
        severity: 'info',
        description: 'El audio se corta o tiene problemas de reproducción.',
        solution: 'Verifica la configuración de audio y los formatos de archivo.',
        codeExample: `# Configuración de audio
define config.has_sound = True
define config.has_music = True
define config.has_voice = True`,
        info: 'Asegúrate de que los archivos de audio no sean demasiado grandes.'
      }
    ],
    deployment: [
      {
        id: 'build-errors',
        title: 'Errores de Compilación',
        icon: '🔨',
        severity: 'critical',
        description: 'El juego no se compila correctamente para distribución.',
        solution: 'Verifica que todos los archivos estén en su lugar y que no haya errores de sintaxis.',
        codeExample: `# Verificar archivos necesarios
# options.rpy - configuración del juego
# script.rpy - script principal
# gui.rpy - configuración de GUI
# screens.rpy - pantallas del juego`,
        warning: 'Siempre verifica el script con "Check Script (Lint)" antes de compilar.'
      },
      {
        id: 'platform-issues',
        title: 'Problemas de Plataforma',
        icon: '🖥️',
        severity: 'warning',
        description: 'El juego no funciona en ciertas plataformas o sistemas operativos.',
        solution: 'Prueba el juego en diferentes plataformas y verifica la compatibilidad.',
        codeExample: `# Configuración para múltiples plataformas
define config.screen_width = 1920
define config.screen_height = 1080
define config.window = "auto"`,
        info: 'Usa config.window = "auto" para que el juego se adapte a diferentes tamaños de pantalla.'
      },
      {
        id: 'distribution-issues',
        title: 'Problemas de Distribución',
        icon: '📦',
        severity: 'warning',
        description: 'Problemas al crear archivos de distribución del juego.',
        solution: 'Verifica que todos los archivos estén incluidos y que la configuración sea correcta.',
        codeExample: `# Configuración de distribución
define config.name = _("Mi Juego")
define config.version = "1.0"
define config.build.classify_renpy = False`,
        info: 'Usa "Build Distributions" en el launcher para crear archivos de distribución.'
      }
    ]
  };

  const getCurrentProblems = () => {
    let currentProblems = troubleshootingData[activeCategory] || [];
    
    if (searchTerm) {
      currentProblems = currentProblems.filter(problem =>
        problem.title.toLowerCase().includes(searchTerm.toLowerCase()) ||
        problem.description.toLowerCase().includes(searchTerm.toLowerCase()) ||
        problem.solution.toLowerCase().includes(searchTerm.toLowerCase())
      );
    }
    
    return currentProblems;
  };

  const getSeverityText = (severity) => {
    switch(severity) {
      case 'critical': return 'Crítico';
      case 'warning': return 'Advertencia';
      case 'info': return 'Información';
      case 'success': return 'Éxito';
      default: return 'Otro';
    }
  };

  return (
    <TroubleshootingContainer>
      <TroubleshootingHeader>
        <TroubleshootingTitle>
          🔧 Solución de Problemas
        </TroubleshootingTitle>
        <CloseButton onClick={onClose}>
          ✕ Cerrar
        </CloseButton>
      </TroubleshootingHeader>

      <TroubleshootingContent>
        <SearchBox>
          <SearchTitle>
            🔍 Buscar Problemas
          </SearchTitle>
          <SearchInput
            type="text"
            placeholder="Busca problemas específicos o errores..."
            value={searchTerm}
            onChange={(e) => setSearchTerm(e.target.value)}
          />
        </SearchBox>

        <CategoryTabs>
          <CategoryTab 
            active={activeCategory === 'common'} 
            onClick={() => setActiveCategory('common')}
          >
            🔧 Comunes
          </CategoryTab>
          <CategoryTab 
            active={activeCategory === 'syntax'} 
            onClick={() => setActiveCategory('syntax')}
          >
            📝 Sintaxis
          </CategoryTab>
          <CategoryTab 
            active={activeCategory === 'performance'} 
            onClick={() => setActiveCategory('performance')}
          >
            ⚡ Rendimiento
          </CategoryTab>
          <CategoryTab 
            active={activeCategory === 'deployment'} 
            onClick={() => setActiveCategory('deployment')}
          >
            📦 Distribución
          </CategoryTab>
        </CategoryTabs>

        <ProblemGrid>
          {getCurrentProblems().map((problem) => (
            <ProblemCard key={problem.id}>
              <ProblemHeader>
                <ProblemTitle>
                  {problem.icon} {problem.title}
                </ProblemTitle>
                <SeverityIndicator severity={problem.severity}>
                  {getSeverityText(problem.severity)}
                </SeverityIndicator>
              </ProblemHeader>
              
              <ProblemDescription>
                {problem.description}
              </ProblemDescription>

              <SolutionBox>
                <SolutionTitle>
                  ✅ Solución
                </SolutionTitle>
                <SolutionText>
                  {problem.solution}
                </SolutionText>
              </SolutionBox>

              {problem.codeExample && (
                <CodeExample>
                  {problem.codeExample}
                </CodeExample>
              )}

              {problem.warning && (
                <WarningBox>
                  <WarningTitle>
                    ⚠️ Advertencia
                  </WarningTitle>
                  <WarningText>
                    {problem.warning}
                  </WarningText>
                </WarningBox>
              )}

              {problem.info && (
                <InfoBox>
                  <InfoTitle>
                    ℹ️ Información
                  </InfoTitle>
                  <InfoText>
                    {problem.info}
                  </InfoText>
                </InfoBox>
              )}
            </ProblemCard>
          ))}
        </ProblemGrid>

        {getCurrentProblems().length === 0 && searchTerm && (
          <div style={{ 
            textAlign: 'center', 
            color: 'var(--color-textTertiary)', 
            fontSize: '18px',
            fontWeight: '600',
            marginTop: '50px',
            textShadow: '0 1px 2px var(--color-shadow)'
          }}>
            No se encontraron problemas que coincidan con "{searchTerm}"
          </div>
        )}

        <ErrorBox>
          <ErrorTitle>
            🚨 Problemas Críticos
          </ErrorTitle>
          <ErrorText>
            Los problemas marcados como "Críticos" pueden impedir que el juego funcione. 
            Resuélvelos antes de continuar con el desarrollo.
          </ErrorText>
        </ErrorBox>

        <WarningBox>
          <WarningTitle>
            ⚠️ Problemas de Advertencia
          </WarningTitle>
          <WarningText>
            Los problemas de advertencia pueden causar comportamientos inesperados. 
            Es recomendable solucionarlos para una mejor experiencia de usuario.
          </WarningText>
        </WarningBox>

        <InfoBox>
          <InfoTitle>
            📚 Recursos Adicionales
          </InfoTitle>
          <InfoText>
            Para más información sobre solución de problemas, consulta:
            <br />• <a href="https://www.renpy.org/doc/html/" target="_blank" rel="noopener noreferrer" style={{color: 'var(--color-primary)', fontWeight: '600', textShadow: '0 1px 2px var(--color-shadow)'}}>Documentación Oficial de Ren'Py</a>
            <br />• <a href="https://www.renpy.org/doc/html/quickstart.html" target="_blank" rel="noopener noreferrer" style={{color: 'var(--color-primary)', fontWeight: '600', textShadow: '0 1px 2px var(--color-shadow)'}}>Guía de Inicio Rápido</a>
            <br />• <a href="https://lemmasoft.renai.us/forums/viewforum.php?f=51" target="_blank" rel="noopener noreferrer" style={{color: 'var(--color-primary)', fontWeight: '600', textShadow: '0 1px 2px var(--color-shadow)'}}>Ren'Py Cookbook</a>
          </InfoText>
        </InfoBox>
      </TroubleshootingContent>
    </TroubleshootingContainer>
  );
}

export default TroubleshootingPage;