// Hook para integrar sincronización en tiempo real con VisualNovelContext
import { useEffect, useRef, useCallback } from 'react';
import { useVisualNovel } from '../context/VisualNovelContext';
import { syncManager } from '../utils/syncManager';

export const useCollaborationSync = (isEnabled, userId) => {
  const { state, dispatch } = useVisualNovel();
  const previousStateRef = useRef(null);
  const isInitializedRef = useRef(false);

  // Inicializar/desactivar sincronización
  useEffect(() => {
    if (isEnabled && userId) {
      syncManager.initialize(userId);
      isInitializedRef.current = true;
    } else if (isInitializedRef.current) {
      syncManager.disable();
      isInitializedRef.current = false;
    }

    return () => {
      if (isInitializedRef.current) {
        syncManager.disable();
        isInitializedRef.current = false;
      }
    };
  }, [isEnabled, userId]);

  // Manejar eventos de sincronización entrantes
  useEffect(() => {
    if (!isEnabled) return;

    const handleCollaborationSync = (event) => {
      const { operation, data, metadata } = event.detail;
      
      console.log('🔄 Sincronización recibida:', operation, data);

      // Aplicar cambios según el tipo de operación
      switch (operation) {
        case 'project_update':
          handleProjectUpdate(data);
          break;
        case 'character_add':
          handleCharacterAdd(data);
          break;
        case 'character_edit':
          handleCharacterEdit(data);
          break;
        case 'character_delete':
          handleCharacterDelete(data.id);
          break;
        case 'script_element_add':
          handleScriptElementAdd(data);
          break;
        case 'script_element_edit':
          handleScriptElementEdit(data);
          break;
        case 'script_element_delete':
          handleScriptElementDelete(data.id);
          break;
        case 'script_element_move':
          handleScriptElementMove(data.id, data.newIndex);
          break;
        case 'scene_add':
          handleSceneAdd(data);
          break;
        case 'scene_edit':
          handleSceneEdit(data);
          break;
        case 'scene_delete':
          handleSceneDelete(data.id);
          break;
        case 'protagonist_config':
          handleProtagonistConfig(data);
          break;
        default:
          console.warn('⚠️ Operación de sincronización desconocida:', operation);
      }
    };

    window.addEventListener('collaborationSync', handleCollaborationSync);

    return () => {
      window.removeEventListener('collaborationSync', handleCollaborationSync);
    };
  }, [isEnabled, dispatch]);

  // Funciones para manejar diferentes tipos de sincronización
  const handleProjectUpdate = useCallback((projectData) => {
    dispatch({ type: 'LOAD_PROJECT', payload: projectData });
  }, [dispatch]);

  const handleCharacterAdd = useCallback((characterData) => {
    dispatch({ type: 'ADD_CHARACTER', payload: characterData });
  }, [dispatch]);

  const handleCharacterEdit = useCallback((characterData) => {
    dispatch({ type: 'UPDATE_CHARACTER', payload: characterData });
  }, [dispatch]);

  const handleCharacterDelete = useCallback((characterId) => {
    dispatch({ type: 'DELETE_CHARACTER', payload: characterId });
  }, [dispatch]);

  const handleScriptElementAdd = useCallback((elementData) => {
    dispatch({ type: 'ADD_SCRIPT_ELEMENT', payload: elementData });
  }, [dispatch]);

  const handleScriptElementEdit = useCallback((elementData) => {
    dispatch({ type: 'UPDATE_SCRIPT_ELEMENT', payload: elementData });
  }, [dispatch]);

  const handleScriptElementDelete = useCallback((elementId) => {
    dispatch({ type: 'DELETE_SCRIPT_ELEMENT', payload: elementId });
  }, [dispatch]);

  const handleScriptElementMove = useCallback((elementId, newIndex) => {
    dispatch({ type: 'MOVE_SCRIPT_ELEMENT', payload: { id: elementId, newIndex } });
  }, [dispatch]);

  const handleSceneAdd = useCallback((sceneData) => {
    dispatch({ type: 'ADD_SCENE', payload: sceneData });
  }, [dispatch]);

  const handleSceneEdit = useCallback((sceneData) => {
    dispatch({ type: 'UPDATE_SCENE', payload: sceneData });
  }, [dispatch]);

  const handleSceneDelete = useCallback((sceneId) => {
    dispatch({ type: 'DELETE_SCENE', payload: sceneId });
  }, [dispatch]);

  const handleProtagonistConfig = useCallback((configData) => {
    dispatch({ type: 'SET_PROTAGONIST_CONFIG', payload: configData });
  }, [dispatch]);

  // Detectar cambios en el estado y sincronizar
  useEffect(() => {
    if (!isEnabled || !isInitializedRef.current) return;

    const currentState = state;
    const previousState = previousStateRef.current;

    if (previousState) {
      // Detectar cambios en personajes
      if (JSON.stringify(currentState.characters) !== JSON.stringify(previousState.characters)) {
        console.log('🔄 Cambios detectados en personajes');
        
        // Sincronizar cambios específicos
        const currentChars = currentState.characters;
        const previousChars = previousState.characters;

        // Personajes agregados
        const addedChars = currentChars.filter(char => 
          !previousChars.find(pChar => pChar.id === char.id)
        );
        addedChars.forEach(char => {
          syncManager.syncCharacterAdd(char);
        });

        // Personajes editados
        currentChars.forEach(char => {
          const prevChar = previousChars.find(pChar => pChar.id === char.id);
          if (prevChar && JSON.stringify(char) !== JSON.stringify(prevChar)) {
            syncManager.syncCharacterEdit(char);
          }
        });

        // Personajes eliminados
        const deletedChars = previousChars.filter(pChar => 
          !currentChars.find(char => char.id === pChar.id)
        );
        deletedChars.forEach(char => {
          syncManager.syncCharacterDelete(char.id);
        });
      }

      // Detectar cambios en elementos del script
      if (JSON.stringify(currentState.script) !== JSON.stringify(previousState.script)) {
        console.log('🔄 Cambios detectados en script');
        
        const currentScript = currentState.script;
        const previousScript = previousState.script;

        // Elementos agregados
        const addedElements = currentScript.filter(element => 
          !previousScript.find(pElement => pElement.id === element.id)
        );
        addedElements.forEach(element => {
          syncManager.syncScriptElementAdd(element);
        });

        // Elementos editados
        currentScript.forEach(element => {
          const prevElement = previousScript.find(pElement => pElement.id === element.id);
          if (prevElement && JSON.stringify(element) !== JSON.stringify(prevElement)) {
            syncManager.syncScriptElementEdit(element);
          }
        });

        // Elementos eliminados
        const deletedElements = previousScript.filter(pElement => 
          !currentScript.find(element => element.id === pElement.id)
        );
        deletedElements.forEach(element => {
          syncManager.syncScriptElementDelete(element.id);
        });

        // Elementos movidos (cambios de orden)
        if (currentScript.length === previousScript.length) {
          currentScript.forEach((element, index) => {
            const prevIndex = previousScript.findIndex(pElement => pElement.id === element.id);
            if (prevIndex !== -1 && prevIndex !== index) {
              syncManager.syncScriptElementMove(element.id, index);
            }
          });
        }
      }

      // Detectar cambios en escenas
      if (JSON.stringify(currentState.scenes) !== JSON.stringify(previousState.scenes)) {
        console.log('🔄 Cambios detectados en escenas');
        
        const currentScenes = currentState.scenes;
        const previousScenes = previousState.scenes;

        // Escenas agregadas
        const addedScenes = currentScenes.filter(scene => 
          !previousScenes.find(pScene => pScene.id === scene.id)
        );
        addedScenes.forEach(scene => {
          syncManager.syncSceneAdd(scene);
        });

        // Escenas editadas
        currentScenes.forEach(scene => {
          const prevScene = previousScenes.find(pScene => pScene.id === scene.id);
          if (prevScene && JSON.stringify(scene) !== JSON.stringify(prevScene)) {
            syncManager.syncSceneEdit(scene);
          }
        });

        // Escenas eliminadas
        const deletedScenes = previousScenes.filter(pScene => 
          !currentScenes.find(scene => scene.id === pScene.id)
        );
        deletedScenes.forEach(scene => {
          syncManager.syncSceneDelete(scene.id);
        });
      }

      // Detectar cambios en configuración del protagonista
      if (JSON.stringify(currentState.protagonistConfig) !== JSON.stringify(previousState.protagonistConfig)) {
        console.log('🔄 Cambios detectados en configuración del protagonista');
        syncManager.syncProtagonistConfig(currentState.protagonistConfig);
      }
    }

    // Actualizar referencia del estado anterior
    previousStateRef.current = JSON.parse(JSON.stringify(currentState));
  }, [state, isEnabled]);

  // Función para sincronizar manualmente el proyecto completo
  const syncFullProject = useCallback(() => {
    if (isEnabled && isInitializedRef.current) {
      console.log('🔄 Sincronización manual del proyecto completo');
      syncManager.syncProjectUpdate(state.project);
    }
  }, [state.project, isEnabled]);

  return {
    syncFullProject,
    isSyncEnabled: isEnabled && isInitializedRef.current
  };
};

export default useCollaborationSync;
