import React, { useState, useEffect, useRef } from 'react';
import styled, { keyframes, css } from 'styled-components';
import { useVisualNovel } from '../context/VisualNovelContext';
import CharacterPanel from './panels/CharacterPanel';
import ScenePanel from './panels/ScenePanel';
import ScriptPanel from './panels/ScriptPanel';
import ProjectPanel from './panels/ProjectPanel';
import AdvancedConfigPanel from './panels/AdvancedConfigPanel';
import CollaborationPanel from './panels/CollaborationPanel';

// Keyframes definidos fuera de los styled components
const pulse = keyframes`
  0%, 100% { transform: translateY(-50%) scale(1); }
  50% { transform: translateY(-50%) scale(1.05); }
`;

const modalSlideIn = keyframes`
  from {
    transform: translateX(-100%);
    opacity: 0;
  }
  to {
    transform: translateX(0);
    opacity: 1;
  }
`;

const modalSlideInVertical = keyframes`
  from {
    opacity: 0;
    transform: translateY(-20px) scale(0.95);
  }
  to {
    opacity: 1;
    transform: translateY(0) scale(1);
  }
`;

const SidebarContainer = styled.div`
  position: fixed;
  left: 0;
  top: 80px; /* Más abajo para no tapar el logo */
  bottom: 0;
  width: ${props => {
    if (props.activePanel) return '420px'; // Más ancho para mejor trabajo
    return props.expanded ? '280px' : '60px';
  }};
  background-color: var(--color-surface);
  border-right: 1px solid var(--color-border);
  display: flex;
  flex-direction: column;
  overflow: hidden;
  transition: all 0.4s cubic-bezier(0.4, 0, 0.2, 1);
  z-index: 100;
  box-shadow: ${props => props.expanded ? '0 4px 20px var(--color-shadow)' : '0 2px 8px var(--color-shadow)'};
  
  /* Esquinas redondeadas dinámicas */
  border-radius: ${props => {
    if (props.activePanel) return '0 16px 16px 0'; // Más redondeado cuando está expandido
    return props.expanded ? '0 12px 12px 0' : '0 8px 8px 0'; // Diferentes niveles
  }};
  
  /* Efecto de suavizado en los bordes */
  backdrop-filter: blur(8px);
  -webkit-backdrop-filter: blur(8px);
`;

const MenuToggle = styled.div`
  position: absolute;
  top: 0px;
  right: 0px;
  width: 32px;
  height: 32px;
  background: linear-gradient(135deg, var(--color-primary) 0%, var(--color-primaryHover) 100%);
  border: 3px solid var(--color-surface);
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  color: var(--color-textInverse);
  font-size: 14px;
  font-weight: bold;
  transition: all 0.4s cubic-bezier(0.4, 0, 0.2, 1);
  z-index: 101;
  box-shadow: 0 4px 16px rgba(74, 158, 255, 0.3);
  
  /* Efecto de brillo sutil */
  &::before {
    content: '';
    position: absolute;
    top: 2px;
    left: 2px;
    right: 2px;
    height: 40%;
    background: linear-gradient(180deg, rgba(255, 255, 255, 0.2) 0%, transparent 100%);
    border-radius: 50% 50% 0 0;
    pointer-events: none;
  }

  &:hover {
    background: linear-gradient(135deg, var(--color-primaryHover) 0%, var(--color-primary) 100%);
    transform: scale(1.15) rotate(5deg);
    box-shadow: 0 6px 20px rgba(74, 158, 255, 0.4);
    border-width: 4px;
  }

  &:active {
    transform: scale(1.05) rotate(2deg);
    box-shadow: 0 2px 12px rgba(74, 158, 255, 0.3);
  }
`;

const TabsContainer = styled.div`
  display: flex;
  flex-direction: column;
  padding: 16px 0; // Reducir padding para mejor aprovechamiento del espacio
  gap: 8px; // Aumentar gap para mejor separación
  transition: all 0.3s ease;
`;

const SidebarLayout = styled.div`
  display: flex;
  height: 100%;
  flex: 1;
`;

const TabsSection = styled.div`
  width: ${props => props.activePanel ? '60px' : '100%'};
  transition: all 0.4s cubic-bezier(0.4, 0, 0.2, 1);
  display: flex;
  flex-direction: column;
  border-right: ${props => props.activePanel ? '1px solid var(--color-border)' : 'none'};
`;

const ContentSection = styled.div`
  flex: 1;
  display: ${props => props.activePanel ? 'flex' : 'none'};
  flex-direction: column;
  overflow: hidden;
  position: relative;
  height: 100%;
`;

const Tab = styled.button`
  display: flex;
  align-items: center;
  padding: ${props => props.compact ? '10px' : '12px 16px'}; // Padding uniforme para tabs compactos
  background-color: ${props => props.active ? 'var(--color-primary)' : 'transparent'};
  color: ${props => props.active ? 'var(--color-textInverse)' : 'var(--color-textPrimary)'};
  border: none;
  cursor: pointer;
  font-size: ${props => props.compact ? '12px' : '14px'};
  font-weight: 500;
  transition: all 0.3s ease;
  border-radius: ${props => {
    if (props.compact) return '50%';
    return props.expanded ? '0 20px 20px 0' : '50%';
  }};
  margin: ${props => {
    if (props.compact) return '0 auto';
    return props.expanded ? '0 8px 0 0' : '0 auto';
  }};
  width: ${props => {
    if (props.compact) return '44px'; // Aumentar de 36px a 44px para mejor visibilidad
    return props.expanded ? 'auto' : '44px';
  }};
  height: ${props => props.compact ? '44px' : '44px'}; // Mantener altura consistente
  justify-content: ${props => props.expanded && !props.compact ? 'flex-start' : 'center'};
  position: relative;
  overflow: hidden;

  &:hover {
    background-color: ${props => props.active ? 'var(--color-primaryHover)' : 'var(--color-backgroundSecondary)'};
    transform: ${props => {
      if (props.compact) return 'scale(1.1)';
      return props.expanded ? 'translateX(4px)' : 'scale(1.1)';
    }};
  }

  &:active {
    transform: ${props => {
      if (props.compact) return 'scale(0.95)';
      return props.expanded ? 'translateX(2px)' : 'scale(0.95)';
    }};
  }
`;

const TabIcon = styled.span`
  font-size: ${props => props.compact ? '18px' : '18px'}; // Mantener tamaño consistente para mejor visibilidad
  min-width: ${props => props.compact ? '20px' : '20px'};
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all 0.3s ease;
  line-height: 1;
`;

const TabLabel = styled.span`
  margin-left: 12px;
  opacity: ${props => (props.expanded && !props.compact) ? 1 : 0};
  transform: translateX(${props => (props.expanded && !props.compact) ? '0' : '-10px'});
  transition: all 0.3s ease;
  white-space: nowrap;
  display: ${props => props.compact ? 'none' : 'block'};
`;

const PanelContainer = styled.div`
  flex: 1;
  overflow-y: auto;
  overflow-x: hidden;
  padding: 16px;
  background-color: var(--color-backgroundSecondary);
  border-left: 1px solid var(--color-border);
  position: relative;
  height: 100%;
  
  /* Scroll suave y personalizado */
  scroll-behavior: auto; /* Cambiar a auto para mejor control manual */
  scrollbar-width: thin;
  scrollbar-color: var(--color-border) transparent;
  
  &::-webkit-scrollbar {
    width: 6px;
  }
  
  &::-webkit-scrollbar-track {
    background: transparent;
  }
  
  &::-webkit-scrollbar-thumb {
    background-color: var(--color-border);
    border-radius: 3px;
    transition: background-color 0.3s ease;
  }
  
  &::-webkit-scrollbar-thumb:hover {
    background-color: var(--color-primary);
  }
  
  /* Estilos para mejor legibilidad */
  h3 {
    font-size: 16px;
    margin-bottom: 16px;
    color: var(--color-primary);
    font-weight: 600;
  }
  
  /* Mejorar spacing en formularios */
  .form-group {
    margin-bottom: 16px;
  }
  
  label {
    display: block;
    font-size: 13px;
    font-weight: 500;
    margin-bottom: 6px;
    color: var(--color-textSecondary);
  }
  
  input, textarea, select {
    width: 100%;
    font-size: 13px;
    padding: 8px 12px;
  }
  
  button {
    font-size: 12px;
    padding: 6px 12px;
  }
`;

const Tooltip = styled.div`
  position: absolute;
  left: 60px;
  top: 50%;
  transform: translateY(-50%);
  background-color: var(--color-backgroundElevated);
  color: var(--color-textPrimary);
  padding: 8px 12px;
  border-radius: 6px;
  font-size: 12px;
  font-weight: 500;
  white-space: nowrap;
  opacity: 0;
  visibility: hidden;
  transition: all 0.2s ease;
  pointer-events: none;
  z-index: 1000;
  border: 1px solid var(--color-border);
  box-shadow: 0 4px 12px var(--color-shadow);

  &::before {
    content: '';
    position: absolute;
    right: 100%;
    top: 50%;
    transform: translateY(-50%);
    border: 5px solid transparent;
    border-right-color: var(--color-border);
  }

  &::after {
    content: '';
    position: absolute;
    right: 100%;
    top: 50%;
    transform: translateY(-50%) translateX(1px);
    border: 4px solid transparent;
    border-right-color: var(--color-backgroundElevated);
  }

  ${Tab}:hover & {
    opacity: ${props => props.expanded ? 0 : 1};
    visibility: ${props => props.expanded ? 'hidden' : 'visible'};
  }
`;

const SidebarOverlay = styled.div`
  position: fixed;
  top: 72px;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.08);
  opacity: ${props => props.visible ? 1 : 0};
  visibility: ${props => props.visible ? 'visible' : 'hidden'};
  transition: all 0.3s ease;
  z-index: 99;
  pointer-events: none;
`;

const ScrollIndicator = styled.div`
  position: absolute;
  top: 50%;
  right: 8px;
  transform: translateY(-50%);
  background: linear-gradient(135deg, var(--color-primary) 0%, var(--color-primaryHover) 100%);
  color: var(--color-textInverse);
  padding: 6px 12px;
  border-radius: 16px;
  font-size: 11px;
  font-weight: 600;
  opacity: ${props => props.visible ? 0.9 : 0};
  visibility: ${props => props.visible ? 'visible' : 'hidden'};
  transition: all 0.4s cubic-bezier(0.4, 0, 0.2, 1);
  pointer-events: none;
  z-index: 102;
  white-space: nowrap;
  box-shadow: 0 4px 12px rgba(74, 158, 255, 0.3);
  border: 1px solid rgba(255, 255, 255, 0.1);
  
  /* Efecto de pulso sutil */
  animation: ${props => props.visible ? css`${pulse} 2s ease-in-out infinite` : 'none'};
`;

const ScrollHint = styled.div`
  position: absolute;
  bottom: 16px;
  left: 50%;
  transform: translateX(-50%);
  background: linear-gradient(135deg, var(--color-backgroundElevated) 0%, var(--color-surface) 100%);
  color: var(--color-textSecondary);
  padding: 10px 14px;
  border-radius: 20px;
  font-size: 11px;
  font-weight: 500;
  border: 1px solid var(--color-border);
  opacity: ${props => props.visible ? 0.95 : 0};
  visibility: ${props => props.visible ? 'visible' : 'hidden'};
  transition: all 0.4s cubic-bezier(0.4, 0, 0.2, 1);
  pointer-events: none;
  z-index: 102;
  white-space: nowrap;
  box-shadow: 0 4px 16px var(--color-shadow);
  backdrop-filter: blur(8px);
  -webkit-backdrop-filter: blur(8px);
  
  &::before {
    content: '🖱️';
    margin-right: 8px;
    font-size: 12px;
  }
  
  /* Animación de entrada suave */
  transform: ${props => props.visible 
    ? 'translateX(-50%) translateY(0) scale(1)' 
    : 'translateX(-50%) translateY(10px) scale(0.9)'};
`;

const SubmenuContainer = styled.div`
  display: flex;
  flex-direction: column;
  background-color: var(--color-surface);
  border: 1px solid var(--color-border);
  border-radius: 8px;
  margin: 8px 12px;
  padding: 8px;
  gap: 4px;
  box-shadow: 0 4px 12px var(--color-shadow);
`;

const SubmenuButton = styled.button`
  display: flex;
  align-items: center;
  padding: 10px 14px;
  background-color: ${props => props.active ? 'var(--color-primary)' : 'var(--color-surfaceSecondary)'};
  color: ${props => props.active ? 'var(--color-textInverse)' : 'var(--color-textPrimary)'};
  border: 1px solid ${props => props.active ? 'var(--color-primary)' : 'var(--color-border)'};
  border-radius: 6px;
  cursor: pointer;
  font-size: 12px;
  font-weight: 500;
  transition: all 0.3s ease;
  text-align: left;
  
  &:hover {
    background-color: ${props => props.active ? 'var(--color-primaryHover)' : 'var(--color-surfaceElevated)'};
    border-color: ${props => props.active ? 'var(--color-primaryHover)' : 'var(--color-borderSecondary)'};
    transform: translateY(-1px);
    box-shadow: 0 2px 8px var(--color-shadow);
    color: ${props => props.active ? 'var(--color-textInverse)' : 'var(--color-textPrimary)'};
  }
  
  &:active {
    transform: scale(0.98);
  }
`;

const SubmenuIcon = styled.span`
  font-size: 14px;
  margin-right: 8px;
  min-width: 16px;
  display: flex;
  align-items: center;
  justify-content: center;
`;

const SubmenuLabel = styled.span`
  font-size: 11px;
  font-weight: 500;
`;

// Estilos para el modal
const ModalOverlay = styled.div`
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.6);
  display: ${props => props.visible ? 'flex' : 'none'};
  align-items: center;
  justify-content: center;
  z-index: 1000;
  backdrop-filter: blur(4px);
  -webkit-backdrop-filter: blur(4px);
`;

const ModalContainer = styled.div`
  background-color: var(--color-surface);
  border-radius: 16px;
  border: 1px solid var(--color-border);
  box-shadow: 0 20px 40px rgba(0, 0, 0, 0.3);
  max-width: 400px;
  width: 90%;
  padding: 24px;
  animation: ${css`${modalSlideInVertical} 0.3s ease-out`};
`;

const ModalTitle = styled.h3`
  margin: 0 0 16px 0;
  color: var(--color-textPrimary);
  font-size: 18px;
  font-weight: 600;
  display: flex;
  align-items: center;
  gap: 8px;
`;

const ModalMessage = styled.p`
  margin: 0 0 20px 0;
  color: var(--color-textSecondary);
  font-size: 14px;
  line-height: 1.5;
`;

const ModalButtons = styled.div`
  display: flex;
  gap: 12px;
  justify-content: flex-end;
`;

const ModalButton = styled.button`
  padding: 10px 20px;
  border-radius: 8px;
  border: none;
  font-size: 14px;
  font-weight: 500;
  cursor: pointer;
  transition: all 0.2s ease;
  
  &.primary {
    background-color: var(--color-primary);
    color: var(--color-textInverse);
    
    &:hover {
      background-color: var(--color-primaryHover);
      transform: translateY(-1px);
    }
  }
  
  &.secondary {
    background-color: transparent;
    color: var(--color-textSecondary);
    border: 1px solid var(--color-border);
    
    &:hover {
      background-color: var(--color-backgroundSecondary);
      color: var(--color-textPrimary);
    }
  }
`;

function Sidebar() {
  const { storage } = useVisualNovel();
  const [activeTab, setActiveTab] = useState(() => {
    // Cargar la última pestaña activa guardada
    return storage.getLastActiveTab();
  });
  const [expanded, setExpanded] = useState(false);
  const [isHovering, setIsHovering] = useState(false);
  const [autoCollapseTimer, setAutoCollapseTimer] = useState(null);
  const [activePanel, setActivePanel] = useState(null); // Panel actualmente seleccionado
  const [activeSubmenu, setActiveSubmenu] = useState(null); // Submenú actualmente activo
  const [showScrollHint, setShowScrollHint] = useState(false);
  const [isScrolling, setIsScrolling] = useState(false);
  const [showMirrorModal, setShowMirrorModal] = useState(false);
  const sidebarRef = useRef(null);
  const panelRef = useRef(null);

  // Escuchar evento para abrir sidebar automáticamente
  useEffect(() => {
    const handleOpenSidebarForElementCreation = (event) => {
      const { type, sceneFileId, insertIndex } = event.detail;
      console.log('Sidebar: Recibido evento para abrir sidebar:', { type, sceneFileId, insertIndex });
      
      // Abrir el sidebar y activar el panel de Script
      setExpanded(true);
      setActivePanel('script');
      setActiveTab('script');
      
      // Esperar un poco para que el sidebar se abra y luego preparar el formulario
      setTimeout(() => {
        if (window.addElementToScene) {
          window.addElementToScene(type, sceneFileId, insertIndex);
        }
      }, 200);
    };

    window.addEventListener('openSidebarForElementCreation', handleOpenSidebarForElementCreation);
    document.addEventListener('openSidebarForElementCreation', handleOpenSidebarForElementCreation);

    return () => {
      window.removeEventListener('openSidebarForElementCreation', handleOpenSidebarForElementCreation);
      document.removeEventListener('openSidebarForElementCreation', handleOpenSidebarForElementCreation);
    };
  }, []);
  const scrollHintTimer = useRef(null);

  // Configuración de tabs
  const tabs = [
    { id: 'project', label: 'Proyecto', icon: '📁' },
    { id: 'characters', label: 'Personajes', icon: '👥' },
    { id: 'scenes', label: 'Escenas', icon: '🎭' },
    { id: 'script', label: 'Script', icon: '📝' },
    { id: 'collaboration', label: 'Colaboración', icon: '🤝' },
    { id: 'tools', label: 'Herramientas', icon: '🔧', hasSubmenu: true },
    { id: 'downloads', label: 'Descargas', icon: '⬇️', hasSubmenu: true },
    { id: 'config', label: 'Configuración', icon: '⚙️' }
  ];

  // Manejar cambio de pestaña
  const handleTabChange = (tabId) => {
    const tab = tabs.find(t => t.id === tabId);
    
    setActiveTab(tabId);
    storage.setLastActiveTab(tabId);
    
    // Si es la misma pestaña, toggle del panel
    if (activePanel === tabId) {
      setActivePanel(null);
      setActiveSubmenu(null);
      setExpanded(false);
    } else {
      // Activar panel y expandir
      setActivePanel(tabId);
      setExpanded(true);
      
      // Si tiene submenú, mostrar el primer submenú por defecto
      if (tab?.hasSubmenu) {
        setActiveSubmenu(getDefaultSubmenu(tabId));
      } else {
        setActiveSubmenu(null);
      }
    }
    
    // Limpiar timer de auto-colapso
    if (autoCollapseTimer) {
      clearTimeout(autoCollapseTimer);
      setAutoCollapseTimer(null);
    }
  };

  // Función para obtener el submenú por defecto
  const getDefaultSubmenu = (tabId) => {
    switch (tabId) {
      case 'tools':
        return 'converter';
      case 'downloads':
        return 'templates';
      default:
        return null;
    }
  };

  // Manejar cambio de submenú
  const handleSubmenuChange = (submenuId) => {
    setActiveSubmenu(submenuId);
  };

  // Manejar hover del sidebar
  const handleMouseEnter = () => {
    setIsHovering(true);
    
    // Solo expandir para hover si no hay panel activo
    if (!activePanel) {
      setExpanded(true);
    }
    
    // Limpiar timer de auto-colapso
    if (autoCollapseTimer) {
      clearTimeout(autoCollapseTimer);
      setAutoCollapseTimer(null);
    }
  };

  const handleMouseLeave = () => {
    setIsHovering(false);
    
    // Solo auto-colapsar si no hay panel activo
    if (!activePanel) {
      const timer = setTimeout(() => {
        if (!isHovering && !activePanel) {
          setExpanded(false);
        }
      }, 1500);
      
      setAutoCollapseTimer(timer);
    }
  };

  // Toggle manual del sidebar
  const toggleSidebar = () => {
    if (activePanel) {
      // Si hay panel activo, cerrarlo
      setActivePanel(null);
      setExpanded(false);
    } else {
      // Si no hay panel, solo toggle de expansión
      setExpanded(!expanded);
    }
    
    // Limpiar timer si se hace toggle manual
    if (autoCollapseTimer) {
      clearTimeout(autoCollapseTimer);
      setAutoCollapseTimer(null);
    }
  };

  // Mostrar hint de scroll cuando se abre un panel
  useEffect(() => {
    if (activePanel && panelRef.current) {
      // Verificar si hay contenido que hacer scroll
      const hasScrollableContent = panelRef.current.scrollHeight > panelRef.current.clientHeight;
      
      if (hasScrollableContent) {
        setShowScrollHint(true);
        
        // Ocultar hint después de 3 segundos
        scrollHintTimer.current = setTimeout(() => {
          setShowScrollHint(false);
        }, 3000);
      }
    } else {
      setShowScrollHint(false);
    }

    return () => {
      if (scrollHintTimer.current) {
        clearTimeout(scrollHintTimer.current);
      }
    };
  }, [activePanel]);

  // Manejar scroll con rueda del mouse - Approach directo en el panel
  useEffect(() => {
    const handlePanelWheel = (e) => {
      const panelElement = panelRef.current;
      if (!panelElement) return;
      
      // Verificar si hay contenido scrollable
      const hasScrollableContent = panelElement.scrollHeight > panelElement.clientHeight;
      
      if (hasScrollableContent) {
        // Mostrar indicador de scroll
        setIsScrolling(true);
        setShowScrollHint(false);
        
        // El scroll nativo ya funciona, solo agregamos feedback visual
        setTimeout(() => {
          setIsScrolling(false);
        }, 1000);
      }
    };

    // Agregar listener directamente al panel
    const panelElement = panelRef.current;
    if (activePanel && panelElement) {
      panelElement.addEventListener('wheel', handlePanelWheel);
    }

    return () => {
      if (panelElement) {
        panelElement.removeEventListener('wheel', handlePanelWheel);
      }
    };
  }, [activePanel]);

  // Limpiar timer al desmontar
  useEffect(() => {
    return () => {
      if (autoCollapseTimer) {
        clearTimeout(autoCollapseTimer);
      }
    };
  }, [autoCollapseTimer]);

  // Configuración de submenús
  const getSubmenus = (tabId) => {
    switch (tabId) {
      case 'tools':
        return [
          { id: 'converter', label: 'Convertidor', icon: '🔄' },
          { id: 'validator', label: 'Validador', icon: '✅' },
          { id: 'optimizer', label: 'Optimizador', icon: '⚡' },
          { id: 'backup', label: 'Respaldo', icon: '💾' },
          { id: 'analytics', label: 'Análisis', icon: '📊' }
        ];
      case 'downloads':
        return [
          { id: 'templates', label: 'Plantillas', icon: '📄' },
          { id: 'assets', label: 'Recursos', icon: '🎨' },
          { id: 'scripts', label: 'Scripts', icon: '📜' },
          { id: 'extensions', label: 'Extensiones', icon: '🔌' },
          { id: 'updates', label: 'Actualizaciones/Descargas', icon: '🔄' }
        ];
      default:
        return [];
    }
  };

  // Renderizar submenú
  const renderSubmenu = (tabId) => {
    const submenus = getSubmenus(tabId);
    if (submenus.length === 0) return null;

    return (
      <SubmenuContainer>
        {submenus.map(submenu => (
          <SubmenuButton
            key={submenu.id}
            active={activeSubmenu === submenu.id}
            onClick={() => handleSubmenuChange(submenu.id)}
          >
            <SubmenuIcon>{submenu.icon}</SubmenuIcon>
            <SubmenuLabel>{submenu.label}</SubmenuLabel>
          </SubmenuButton>
        ))}
      </SubmenuContainer>
    );
  };

  // Renderizar panel activo
  const renderPanel = () => {
    switch (activeTab) {
      case 'project':
        return <ProjectPanel />;
      case 'characters':
        return <CharacterPanel />;
      case 'scenes':
        return <ScenePanel />;
      case 'script':
        return <ScriptPanel />;
      case 'collaboration':
        return <CollaborationPanel />;
      case 'tools':
        return renderToolPanel();
      case 'downloads':
        return renderDownloadPanel();
      case 'config':
        return <AdvancedConfigPanel />;
      default:
        return <ProjectPanel />;
    }
  };

  // Renderizar panel de herramientas
  const renderToolPanel = () => {
    return (
      <div style={{ padding: '16px', textAlign: 'center' }}>
        <h3 style={{ marginBottom: '16px', color: 'var(--color-primary)' }}>
          🔧 Herramientas
        </h3>
        <div style={{ 
          padding: '20px', 
          backgroundColor: 'var(--color-backgroundTertiary)', 
          borderRadius: '8px',
          border: '1px solid var(--color-border)'
        }}>
          <p style={{ color: 'var(--color-textSecondary)', marginBottom: '16px' }}>
            Selecciona una herramienta del submenú para comenzar
          </p>
          {activeSubmenu && (
            <div style={{ 
              padding: '12px', 
              backgroundColor: 'var(--color-primary)', 
              color: 'var(--color-textInverse)',
              borderRadius: '6px',
              fontSize: '12px'
            }}>
              {getSubmenus('tools').find(s => s.id === activeSubmenu)?.label} - Próximamente
            </div>
          )}
        </div>
      </div>
    );
  };

  // Renderizar panel de descargas
  const renderDownloadPanel = () => {
    // Renderizar contenido específico según el submenú
    if (activeSubmenu === 'updates') {
      return renderUpdatesPanel();
    }

    return (
      <div style={{ padding: '16px', textAlign: 'center' }}>
        <h3 style={{ marginBottom: '16px', color: 'var(--color-primary)' }}>
          ⬇️ Descargas
        </h3>
        <div style={{ 
          padding: '20px', 
          backgroundColor: 'var(--color-backgroundTertiary)', 
          borderRadius: '8px',
          border: '1px solid var(--color-border)'
        }}>
          <p style={{ color: 'var(--color-textSecondary)', marginBottom: '16px' }}>
            Selecciona una categoría del submenú para descargar recursos
          </p>
          {activeSubmenu && (
            <div style={{ 
              padding: '12px', 
              backgroundColor: 'var(--color-primary)', 
              color: 'var(--color-textInverse)',
              borderRadius: '6px',
              fontSize: '12px'
            }}>
              {getSubmenus('downloads').find(s => s.id === activeSubmenu)?.label} - Próximamente
            </div>
          )}
        </div>
      </div>
    );
  };

  // Renderizar panel de actualizaciones
  const renderUpdatesPanel = () => {
    return (
      <div style={{ padding: '16px' }}>
        <h3 style={{ marginBottom: '16px', color: 'var(--color-primary)' }}>
          🔄 Actualizaciones
        </h3>
        
        {/* Mini Post de Descarga Offline */}
        <div style={{
          backgroundColor: 'var(--color-backgroundTertiary)',
          borderRadius: '12px',
          border: '1px solid var(--color-border)',
          overflow: 'hidden',
          marginBottom: '16px',
          boxShadow: '0 2px 8px var(--color-shadow)'
        }}>
          {/* Header del Post */}
          <div style={{
            background: 'linear-gradient(135deg, var(--color-primary) 0%, var(--color-primaryHover) 100%)',
            padding: '12px 16px',
            color: 'var(--color-textInverse)',
            display: 'flex',
            alignItems: 'center',
            gap: '8px'
          }}>
            <span style={{ fontSize: '16px' }}>💻</span>
            <div>
              <h4 style={{ margin: 0, fontSize: '14px', fontWeight: '600' }}>
                Editor Offline
              </h4>
              <p style={{ margin: 0, fontSize: '11px', opacity: 0.9 }}>
                Versión v1.0.0 - Última actualización
              </p>
            </div>
          </div>

          {/* Contenido del Post */}
          <div style={{ padding: '16px' }}>
            <p style={{ 
              color: 'var(--color-textSecondary)', 
              fontSize: '12px', 
              marginBottom: '16px',
              lineHeight: '1.4'
            }}>
              Descarga RynEditor para usar sin conexión a internet. 
              Incluye todas las funcionalidades del editor web.
            </p>

            {/* Información del archivo */}
            <div style={{
              backgroundColor: 'var(--color-background)',
              padding: '12px',
              borderRadius: '8px',
              border: '1px solid var(--color-borderSecondary)',
              marginBottom: '16px'
            }}>
              <div style={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', marginBottom: '8px' }}>
                <span style={{ fontSize: '11px', color: 'var(--color-textSecondary)' }}>
                  📦 Archivo: RynEditor_v1.0.0.zip
                </span>
                <span style={{ fontSize: '11px', color: 'var(--color-primary)', fontWeight: '500' }}>
                  31.1 MB
                </span>
              </div>
              <div style={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center' }}>
                <span style={{ fontSize: '11px', color: 'var(--color-textSecondary)' }}>
                  🖥️ Compatible: Windows, macOS, Linux
                </span>
                <span style={{ fontSize: '11px', color: 'var(--color-success)', fontWeight: '500' }}>
                  ✅ Listo
                </span>
              </div>
            </div>

            {/* Botones de descarga */}
            <div style={{ display: 'flex', flexDirection: 'column', gap: '8px' }}>
              <button
                onClick={() => handleDownloadOffline('mirror')}
                style={{
                  width: '100%',
                  padding: '10px 16px',
                  backgroundColor: 'var(--color-primary)',
                  color: 'var(--color-textInverse)',
                  border: 'none',
                  borderRadius: '8px',
                  fontSize: '12px',
                  fontWeight: '600',
                  cursor: 'pointer',
                  transition: 'all 0.3s ease',
                  display: 'flex',
                  alignItems: 'center',
                  justifyContent: 'center',
                  gap: '8px'
                }}
                onMouseEnter={(e) => {
                  e.target.style.backgroundColor = 'var(--color-primaryHover)';
                  e.target.style.transform = 'translateY(-1px)';
                }}
                onMouseLeave={(e) => {
                  e.target.style.backgroundColor = 'var(--color-primary)';
                  e.target.style.transform = 'translateY(0)';
                }}
              >
                <span>⬇️</span>
                Descargar (Mirror Principal)
              </button>

              <button
                onClick={() => handleDownloadOffline('external')}
                style={{
                  width: '100%',
                  padding: '10px 16px',
                  backgroundColor: 'transparent',
                  color: 'var(--color-primary)',
                  border: '1px solid var(--color-primary)',
                  borderRadius: '8px',
                  fontSize: '12px',
                  fontWeight: '500',
                  cursor: 'pointer',
                  transition: 'all 0.3s ease',
                  display: 'flex',
                  alignItems: 'center',
                  justifyContent: 'center',
                  gap: '8px'
                }}
                onMouseEnter={(e) => {
                  e.target.style.backgroundColor = 'var(--color-primary)';
                  e.target.style.color = 'var(--color-textInverse)';
                  e.target.style.transform = 'translateY(-1px)';
                }}
                onMouseLeave={(e) => {
                  e.target.style.backgroundColor = 'transparent';
                  e.target.style.color = 'var(--color-primary)';
                  e.target.style.transform = 'translateY(0)';
                }}
              >
                <span>🌐</span>
                Descargar (Enlace Externo)
              </button>
            </div>

            {/* Información adicional */}
            <div style={{
              marginTop: '12px',
              padding: '10px',
              backgroundColor: 'var(--color-background)',
              borderRadius: '6px',
              border: '1px solid var(--color-borderSecondary)'
            }}>
              <div style={{ display: 'flex', alignItems: 'center', gap: '6px', marginBottom: '6px' }}>
                <span style={{ fontSize: '12px' }}>ℹ️</span>
                <span style={{ fontSize: '11px', color: 'var(--color-textSecondary)', fontWeight: '500' }}>
                  Instrucciones:
                </span>
              </div>
              <ul style={{ 
                margin: 0, 
                paddingLeft: '16px', 
                fontSize: '10px', 
                color: 'var(--color-textTertiary)',
                lineHeight: '1.3'
              }}>
                <li>Extrae el archivo ZIP en cualquier carpeta</li>
                <li>Ejecuta el archivo index.html en tu navegador</li>
                <li>¡Listo! El editor funcionará sin internet</li>
              </ul>
            </div>
          </div>
        </div>

        {/* Otras actualizaciones (placeholder) */}
        <div style={{
          backgroundColor: 'var(--color-backgroundTertiary)',
          borderRadius: '8px',
          border: '1px solid var(--color-border)',
          padding: '12px',
          textAlign: 'center'
        }}>
          <p style={{ color: 'var(--color-textSecondary)', fontSize: '11px', margin: 0 }}>
            🔄 Más actualizaciones próximamente...
          </p>
        </div>
      </div>
    );
  };

  // Manejar descarga offline
  const handleDownloadOffline = (source) => {
    if (source === 'mirror') {
      // Mostrar modal explicativo para mirror
      setShowMirrorModal(true);
    } else if (source === 'external') {
      // Descarga desde enlace externo (alternativo)
      window.open('https://bit.ly/46kUtwr', '_blank');
    }
  };

  // Confirmar descarga desde mirror
  const confirmMirrorDownload = () => {
    window.open('https://github.com/ryn-editor/editor.github.io/releases/latest', '_blank');
    setShowMirrorModal(false);
  };

  return (
    <>
      <SidebarOverlay visible={expanded && activePanel} />
      <SidebarContainer
        ref={sidebarRef}
        expanded={expanded}
        activePanel={activePanel}
        onMouseEnter={handleMouseEnter}
        onMouseLeave={handleMouseLeave}
      >
        <MenuToggle onClick={toggleSidebar}>
          {activePanel ? '×' : (expanded ? '‹' : '›')}
        </MenuToggle>

        <SidebarLayout>
          <TabsSection activePanel={activePanel}>
            <TabsContainer>
        {tabs.map(tab => {
          // Lógica corregida: compact cuando no está expandido O cuando hay panel activo pero no es este tab
          const isCompact = !expanded || (activePanel && activePanel !== tab.id);
          
          return (
          <Tab
            key={tab.id}
            active={activeTab === tab.id}
              expanded={expanded}
              compact={isCompact}
            onClick={() => handleTabChange(tab.id)}
            >
              <TabIcon compact={isCompact}>
                {tab.icon}
              </TabIcon>
              <TabLabel 
                expanded={expanded} 
                compact={isCompact}
              >
                {tab.label}
              </TabLabel>
              {isCompact && (
                <Tooltip expanded={expanded}>
            {tab.label}
                </Tooltip>
              )}
          </Tab>
          );
        })}
            </TabsContainer>
          </TabsSection>
      
          <ContentSection activePanel={activePanel}>
            {/* Renderizar submenú si existe */}
            {activePanel && renderSubmenu(activePanel)}
            
            <PanelContainer ref={panelRef}>
        {renderPanel()}
      </PanelContainer>
            
            {/* Indicadores de scroll */}
            <ScrollIndicator visible={isScrolling}>
              Desplazando...
            </ScrollIndicator>
            
            <ScrollHint visible={showScrollHint}>
              Usa la rueda del mouse para navegar
            </ScrollHint>
          </ContentSection>
        </SidebarLayout>
    </SidebarContainer>

    {/* Modal para descarga desde mirror */}
    <ModalOverlay 
      visible={showMirrorModal}
      onClick={() => setShowMirrorModal(false)}
    >
      <ModalContainer onClick={(e) => e.stopPropagation()}>
        <ModalTitle>
          🔗 Enlace Externo
        </ModalTitle>
        <ModalMessage>
          También puedes descargar RynEditor desde nuestro enlace externo que nos ayuda con los acortadores. 
          Este enlace te llevará directamente a GitHub Releases donde podrás descargar la última versión del editor.
        </ModalMessage>
        <ModalButtons>
          <ModalButton 
            className="secondary"
            onClick={() => setShowMirrorModal(false)}
          >
            Cancelar
          </ModalButton>
          <ModalButton 
            className="primary"
            onClick={confirmMirrorDownload}
          >
            Continuar Descarga
          </ModalButton>
        </ModalButtons>
      </ModalContainer>
    </ModalOverlay>
    </>
  );
}

export default Sidebar;
