import React, { useState, useEffect, useCallback } from 'react';
import styled, { keyframes, css } from 'styled-components';
import { useVisualNovel } from '../../context/VisualNovelContext';
import { useModalContext } from '../../context/ModalContext';
import { useNotification } from '../../context/NotificationContext';
import { collaborationClient } from '../../utils/collaborationClient';
import { ipDetector } from '../../utils/ipDetector';
import { ipEncoder } from '../../utils/ipEncoder';
import CollaborationInstructions from '../CollaborationInstructions';
import CollaborationHelp from '../CollaborationHelp';
import FloatingChat from '../FloatingChat';
import { useCollaborationSync } from '../../hooks/useCollaborationSync';

// Keyframes definidos fuera de los styled components
const pulse = keyframes`
  0%, 100% { opacity: 1; }
  50% { opacity: 0.5; }
`;

const PanelContainer = styled.div`
  display: flex;
  flex-direction: column;
  gap: 16px;
  height: 100%;
  overflow-y: auto;
  
  /* Responsive para móviles */
  @media (max-width: 768px) {
    gap: 12px;
    padding: 8px;
  }
  
  @media (max-width: 480px) {
    gap: 8px;
    padding: 4px;
  }
`;

const Section = styled.div`
  background-color: var(--color-surfaceSecondary);
  border-radius: 8px;
  padding: 16px;
  transition: background-color 0.3s ease;
  
  /* Responsive para móviles */
  @media (max-width: 768px) {
    padding: 12px;
    border-radius: 6px;
  }
  
  @media (max-width: 480px) {
    padding: 8px;
    border-radius: 4px;
  }
`;

const SectionTitle = styled.h3`
  margin: 0 0 12px 0;
  font-size: 14px;
  color: var(--color-primary);
  border-bottom: 1px solid var(--color-border);
  padding-bottom: 8px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  transition: color 0.3s ease, border-color 0.3s ease;
  
  /* Responsive para móviles */
  @media (max-width: 768px) {
    font-size: 13px;
    margin: 0 0 8px 0;
    padding-bottom: 6px;
  }
  
  @media (max-width: 480px) {
    font-size: 12px;
    margin: 0 0 6px 0;
    padding-bottom: 4px;
  }
`;

const StatusIndicator = styled.div`
  display: flex;
  align-items: center;
  gap: 8px;
  padding: 8px 12px;
  border-radius: 6px;
  background-color: ${props => {
    if (props.connected) return 'var(--color-success)';
    if (props.connecting) return 'var(--color-warning)';
    return 'var(--color-error)';
  }};
  color: var(--color-textInverse);
  font-size: 12px;
  font-weight: 600;
  margin-bottom: 16px;
  
  .indicator-dot {
    width: 8px;
    height: 8px;
    border-radius: 50%;
    background-color: var(--color-textInverse);
    animation: ${props => props.connecting ? css`${pulse} 1.5s infinite` : 'none'};
  }
`;

const Button = styled.button`
  background-color: var(--color-primary);
  color: var(--color-textInverse);
  border: none;
  padding: 8px 16px;
  border-radius: 6px;
  cursor: pointer;
  font-size: 12px;
  font-weight: 600;
  transition: all 0.3s ease;
  width: 100%;
  margin-bottom: 8px;
  
  &:hover:not(:disabled) {
    background-color: var(--color-primaryHover);
    transform: translateY(-1px);
  }
  
  &:disabled {
    opacity: 0.6;
    cursor: not-allowed;
    transform: none;
  }
  
  &.secondary {
    background-color: var(--color-surfaceElevated);
    color: var(--color-textPrimary);
    border: 1px solid var(--color-border);
    
    &:hover:not(:disabled) {
      background-color: var(--color-borderSecondary);
    }
  }
  
  &.danger {
    background-color: var(--color-error);
    
    &:hover:not(:disabled) {
      background-color: var(--color-errorHover);
    }
  }
`;

const Input = styled.input`
  width: 100%;
  padding: 8px 12px;
  border: 1px solid var(--color-border);
  border-radius: 6px;
  background-color: var(--color-surface);
  color: var(--color-textPrimary);
  font-size: 12px;
  margin-bottom: 8px;
  transition: all 0.3s ease;
  
  &:focus {
    outline: none;
    border-color: var(--color-primary);
    box-shadow: 0 0 0 2px rgba(74, 158, 255, 0.2);
  }
  
  &::placeholder {
    color: var(--color-textTertiary);
  }
`;

const RoomCodeDisplay = styled.div`
  background-color: var(--color-background);
  border: 2px solid var(--color-primary);
  border-radius: 8px;
  padding: 12px;
  text-align: center;
  margin-bottom: 16px;
  
  .room-code {
    font-size: 24px;
    font-weight: 700;
    color: var(--color-primary);
    letter-spacing: 2px;
    margin-bottom: 8px;
  }
  
  .room-info {
    font-size: 11px;
    color: var(--color-textSecondary);
  }
`;

const UsersList = styled.div`
  max-height: 200px;
  overflow-y: auto;
  margin-bottom: 16px;
`;

const UserItem = styled.div`
  display: flex;
  align-items: center;
  gap: 8px;
  padding: 6px 8px;
  border-radius: 4px;
  background-color: ${props => props.isHost ? 'var(--color-primary)' : 'var(--color-background)'};
  color: ${props => props.isHost ? 'var(--color-textInverse)' : 'var(--color-textPrimary)'};
  font-size: 11px;
  margin-bottom: 4px;
  transition: all 0.3s ease;
  
  .user-avatar {
    width: 20px;
    height: 20px;
    border-radius: 50%;
    background-color: ${props => props.isHost ? 'var(--color-textInverse)' : 'var(--color-primary)'};
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 10px;
  }
  
  .user-name {
    flex: 1;
    font-weight: 500;
  }
  
  .user-status {
    font-size: 10px;
    opacity: 0.8;
  }
`;


const CopyButton = styled.button`
  background-color: var(--color-success);
  color: var(--color-textInverse);
  border: none;
  padding: 4px 8px;
  border-radius: 4px;
  cursor: pointer;
  font-size: 10px;
  margin-left: 8px;
  
  &:hover {
    background-color: var(--color-successHover);
  }
`;

const ServerInfoContainer = styled.div`
  background-color: var(--color-background);
  border: 2px solid var(--color-primary);
  border-radius: 8px;
  padding: 12px;
  margin-bottom: 16px;
  
  .server-title {
    font-size: 12px;
    font-weight: 600;
    color: var(--color-primary);
    margin-bottom: 8px;
    display: flex;
    align-items: center;
    gap: 6px;
  }
  
  .server-ip {
    font-size: 14px;
    font-weight: 700;
    color: var(--color-textPrimary);
    font-family: 'Courier New', monospace;
    margin-bottom: 4px;
  }
  
  .server-type {
    font-size: 10px;
    color: var(--color-textSecondary);
    margin-bottom: 8px;
  }
  
  .server-actions {
    display: flex;
    gap: 8px;
  }
`;

const DetectButton = styled.button`
  background-color: var(--color-warning);
  color: var(--color-textInverse);
  border: none;
  padding: 6px 12px;
  border-radius: 4px;
  cursor: pointer;
  font-size: 11px;
  font-weight: 600;
  
  &:hover:not(:disabled) {
    background-color: var(--color-warningHover);
  }
  
  &:disabled {
    opacity: 0.6;
    cursor: not-allowed;
  }
`;

function CollaborationPanel() {
  const { state, dispatch } = useVisualNovel();
  const { showAlert, showConfirm } = useModalContext();
  const { showServerStatus, showSuccess, showError, showWarning, showInfo } = useNotification();
  
  // Estados locales
  const [isConnected, setIsConnected] = useState(false);
  const [isConnecting, setIsConnecting] = useState(false);
  const [roomCode, setRoomCode] = useState('');
  const [isHost, setIsHost] = useState(false);
  const [users, setUsers] = useState([]);
  const [chatMessages, setChatMessages] = useState([]);
  const [chatInput, setChatInput] = useState('');
  const [username, setUsername] = useState('Usuario');
  const [serverUrl, setServerUrl] = useState('ws://localhost:3002/collaboration');
  const [collaborationEnabled, setCollaborationEnabled] = useState(() => {
    return localStorage.getItem('collaborationEnabled') === 'true';
  });
  const [publicIP, setPublicIP] = useState(null);
  const [localIP, setLocalIP] = useState(null);
  const [isDetectingIP, setIsDetectingIP] = useState(false);
  const [serverInfo, setServerInfo] = useState(null);
  const [connectionCode, setConnectionCode] = useState(null);

  // Inicializar sincronización en tiempo real
  const { syncFullProject, isSyncEnabled } = useCollaborationSync(
    collaborationEnabled && isConnected, 
    username
  );

  // Conectar al servidor
  const handleConnect = useCallback(async () => {
    try {
      setIsConnecting(true);
      await collaborationClient.connect(serverUrl);
      setIsConnected(true);
      setIsConnecting(false);
      showServerStatus('Conectado al servidor de colaboración', 'success', '✅');
    } catch (error) {
      setIsConnecting(false);
      showServerStatus('Error conectando al servidor: ' + error.message, 'error', '❌');
    }
  }, [serverUrl, showServerStatus]);

  // Desconectar del servidor
  const handleDisconnect = useCallback(() => {
    collaborationClient.disconnect();
    setIsConnected(false);
    setRoomCode('');
    setIsHost(false);
    setUsers([]);
    setChatMessages([]);
    showServerStatus('Desconectado del servidor', 'info', '🔌');
  }, [showServerStatus]);

  // Crear sala
  const handleCreateRoom = useCallback(() => {
    try {
      collaborationClient.createRoom(state.project, username);
      showServerStatus('Creando sala de colaboración...', 'info', '🏠');
    } catch (error) {
      showServerStatus('Error creando sala: ' + error.message, 'error', '❌');
    }
  }, [state.project, username, showServerStatus]);

  // Unirse a sala
  const handleJoinRoom = useCallback(() => {
    if (!roomCode.trim()) {
      showWarning('Ingresa un código de sala', 'Código Requerido', '⚠️');
      return;
    }

    const trimmedCode = roomCode.trim();
    
    // Verificar si es un código de conexión codificado
    if (ipEncoder.validateCodeFormat(trimmedCode)) {
      const decodedInfo = ipEncoder.decodeConnectionInfo(trimmedCode);
      
      if (decodedInfo.valid) {
        // Usar la IP decodificada para conectar
        const decodedServerUrl = `ws://${decodedInfo.ip}:${decodedInfo.port}/collaboration`;
        setServerUrl(decodedServerUrl);
        showServerStatus(`Código decodificado. Conectando a servidor...`, 'info', '🔐');
        
        // Conectar al servidor decodificado
        handleConnect();
        return;
      } else {
        showWarning(`Código inválido: ${decodedInfo.error}`, 'Error de Código', '❌');
        return;
      }
    }
    
    // Si no es un código codificado, proceder como antes (código de sala normal)
    try {
      collaborationClient.joinRoom(trimmedCode, username);
      showServerStatus('Uniéndose a la sala...', 'info', '👤');
    } catch (error) {
      showServerStatus('Error uniéndose a la sala: ' + error.message, 'error', '❌');
    }
  }, [roomCode, username, showWarning, showServerStatus, handleConnect]);

  // Abandonar sala
  const handleLeaveRoom = useCallback(() => {
    collaborationClient.leaveRoom();
    setRoomCode('');
    setIsHost(false);
    setUsers([]);
    setChatMessages([]);
    showInfo('Abandonaste la sala', 'Sala Abandonada', '👋');
  }, [showInfo]);

  // Copiar código de sala
  const handleCopyRoomCode = useCallback(async () => {
    try {
      await navigator.clipboard.writeText(collaborationClient.getRoomCode());
      showSuccess('Código copiado al portapapeles', 'Código Copiado', '📋');
    } catch (error) {
      showError('Error copiando código', 'Error', '❌');
    }
  }, [showSuccess, showError]);

  // Enviar mensaje de chat
  const handleSendChat = useCallback((message) => {
    if (!message || !message.trim()) return;
    
    collaborationClient.sendChatMessage(message.trim());
  }, []);


  // Detectar IPs automáticamente
  const handleDetectIPs = useCallback(async () => {
    try {
      setIsDetectingIP(true);
      showServerStatus('Detectando IPs...', 'info', '🔍');
      
      const ips = await ipDetector.detectAllIPs();
      setPublicIP(ips.public);
      setLocalIP(ips.local);
      
      // Generar información del servidor
      const serverURL = ipDetector.generateServerURL(3002);
      setServerInfo(serverURL);
      
      // Generar código de conexión codificado
      if (ips.public) {
        const encodedCode = ipEncoder.encodeConnectionInfo(ips.public, 3002);
        setConnectionCode(encodedCode);
        showServerStatus(`IP pública detectada y codificada`, 'success', '🌐');
      } else if (ips.local) {
        const encodedCode = ipEncoder.encodeConnectionInfo(ips.local, 3002);
        setConnectionCode(encodedCode);
        showServerStatus(`IP local detectada y codificada`, 'success', '🏠');
      } else {
        showServerStatus('No se pudo detectar IP automáticamente', 'warning', '⚠️');
      }
    } catch (error) {
      console.error('Error detectando IPs:', error);
      showServerStatus('Error detectando IPs', 'error', '❌');
    } finally {
      setIsDetectingIP(false);
    }
  }, [showServerStatus]);

  // Copiar información del servidor
  const handleCopyServerInfo = useCallback(async () => {
    if (!serverInfo) return;
    
    const textToCopy = serverInfo.display;
    try {
      await navigator.clipboard.writeText(textToCopy);
      showSuccess(`${textToCopy} copiado al portapapeles`, 'IP Copiada', '📋');
    } catch (error) {
      showServerStatus('Error copiando información del servidor', 'error', '❌');
    }
  }, [serverInfo, showSuccess, showServerStatus]);

  // Copiar código de conexión codificado
  const handleCopyConnectionCode = useCallback(async () => {
    if (!connectionCode) return;
    
    try {
      await navigator.clipboard.writeText(connectionCode);
      showSuccess(`Código de conexión copiado: ${connectionCode}`, 'Código Copiado', '🔐');
    } catch (error) {
      showServerStatus('Error copiando código de conexión', 'error', '❌');
    }
  }, [connectionCode, showSuccess, showServerStatus]);


  // Iniciar servidor real de colaboración
  const handleStartRealServer = useCallback(async () => {
    try {
      showServerStatus('Iniciando servidor real de colaboración...', 'info', '🏠');
      
      // Crear una ventana modal con instrucciones
      const confirmed = await showConfirm(
        'Para iniciar el servidor real, necesitas ejecutar un comando en la terminal.\n\n' +
        '¿Quieres que te muestre las instrucciones paso a paso?',
        'Iniciar Servidor Real',
        '🏠'
      );
      
      if (confirmed) {
        // Mostrar instrucciones detalladas
        const instructions = `🚀 GUÍA COMPLETA PARA INICIAR SERVIDOR REAL:

📁 PASO 1 - Abrir Terminal en la Carpeta Correcta:
   • Presiona Win + R, escribe "cmd" y presiona Enter
   • O busca "Símbolo del sistema" en el menú inicio
   • Navega a la carpeta del editor:
     cd C:\\Users\\[TuUsuario]\\Documents\\editor20212
   • O si descargaste el editor, ve a donde lo descomprimiste

📁 PASO 2 - Verificar que estás en la carpeta correcta:
   • Debes ver archivos como: package.json, collaboration-server.js
   • Si ves "El sistema no puede encontrar la ruta", revisa la ubicación

🚀 PASO 3 - Ejecutar el servidor:
   npm run server:collaboration

🔧 PASO 4 - Alternativa (si npm no funciona):
   node collaboration-server.js

✅ PASO 5 - Verificar que funciona:
   • Debes ver: "🚀 Servidor de colaboración iniciado en 0.0.0.0:3002"
   • Si hay errores, revisa que Node.js esté instalado

💡 IMPORTANTE: Mantén esta ventana abierta mientras colaboras`;
        
        showAlert(instructions, 'Instrucciones del Servidor', '📋');
        
        // También copiar el comando al portapapeles
        try {
          await navigator.clipboard.writeText('npm run server:collaboration');
          showSuccess('Comando copiado al portapapeles', 'Comando Copiado', '📋');
        } catch (error) {
          console.log('No se pudo copiar al portapapeles');
        }
      }
      
    } catch (error) {
      console.error('Error iniciando servidor real:', error);
      showServerStatus('Error mostrando instrucciones del servidor', 'error', '❌');
    }
  }, [showServerStatus, showConfirm]);

  // Activar/desactivar colaboración
  const handleToggleCollaboration = useCallback(async () => {
    const newState = !collaborationEnabled;
    setCollaborationEnabled(newState);
    
    // Guardar en localStorage
    localStorage.setItem('collaborationEnabled', newState.toString());
    
    if (newState) {
      showServerStatus('Colaboración activada - Usa "Iniciar Servidor Real" para comenzar', 'info', '🤝');
      // Detectar IPs automáticamente cuando se activa
      handleDetectIPs();
    } else {
      // Desconectar si está conectado
      if (isConnected) {
        handleDisconnect();
      }
      showServerStatus('Colaboración desactivada', 'info', '🚫');
    }
  }, [collaborationEnabled, isConnected, handleDisconnect, showServerStatus, handleDetectIPs]);

  // Configurar event listeners
  useEffect(() => {
    const handleRoomCreated = (data) => {
      setRoomCode(data.roomCode);
      setIsHost(true);
      showSuccess(`Sala ${data.roomCode} creada exitosamente`, 'Sala Creada', '🏠');
    };

    const handleRoomJoined = (data) => {
      setRoomCode(data.roomCode);
      setIsHost(false);
      setUsers(data.users);
      showSuccess(`Te uniste a la sala ${data.roomCode}`, 'Sala Unida', '👤');
    };

    const handleUserJoined = (data) => {
      setUsers(prev => [...prev, data.user]);
      setChatMessages(prev => [...prev, {
        type: 'system',
        user: 'Sistema',
        text: `${data.user.username} se unió a la sala`,
        timestamp: Date.now()
      }]);
    };

    const handleUserLeft = (data) => {
      setUsers(prev => prev.filter(user => user.id !== data.user.id));
      setChatMessages(prev => [...prev, {
        type: 'system',
        user: 'Sistema',
        text: `${data.user.username} abandonó la sala`,
        timestamp: Date.now()
      }]);
    };

    const handleChatMessage = (data) => {
      setChatMessages(prev => [...prev, {
        type: 'user',
        user: data.username,
        text: data.message,
        timestamp: data.timestamp
      }]);
    };

    const handleSyncOperation = (data) => {
      // El nuevo sistema de sincronización maneja esto automáticamente
      // a través del hook useCollaborationSync
      console.log('🔄 Operación de sincronización recibida:', data.operation, data.data);
      
      // Mostrar notificación de sincronización
      if (data.metadata && data.metadata.description) {
        showServerStatus(`Sincronizado: ${data.metadata.description}`, 'info', '🔄');
      }
    };

    const handleError = (data) => {
      showError('Error: ' + data.message, 'Error de Colaboración', '❌');
    };

    const handleDisconnected = () => {
      setIsConnected(false);
      setRoomCode('');
      setIsHost(false);
      setUsers([]);
      setChatMessages([]);
    };

    // Registrar event listeners
    collaborationClient.on('roomCreated', handleRoomCreated);
    collaborationClient.on('roomJoined', handleRoomJoined);
    collaborationClient.on('userJoined', handleUserJoined);
    collaborationClient.on('userLeft', handleUserLeft);
    collaborationClient.on('chatMessage', handleChatMessage);
    collaborationClient.on('syncOperation', handleSyncOperation);
    collaborationClient.on('error', handleError);
    collaborationClient.on('disconnected', handleDisconnected);

    // Cleanup
    return () => {
      collaborationClient.off('roomCreated', handleRoomCreated);
      collaborationClient.off('roomJoined', handleRoomJoined);
      collaborationClient.off('userJoined', handleUserJoined);
      collaborationClient.off('userLeft', handleUserLeft);
      collaborationClient.off('chatMessage', handleChatMessage);
      collaborationClient.off('syncOperation', handleSyncOperation);
      collaborationClient.off('error', handleError);
      collaborationClient.off('disconnected', handleDisconnected);
    };
  }, [showSuccess, showError]);

  return (
    <PanelContainer>
      <Section>
        <SectionTitle>
          🤝 Colaboración
          <Button 
            onClick={handleToggleCollaboration}
            className={collaborationEnabled ? 'danger' : 'secondary'}
            style={{ 
              width: 'auto', 
              padding: '4px 8px', 
              fontSize: '10px',
              margin: 0,
              minHeight: 'auto'
            }}
          >
            {collaborationEnabled ? '🚫 Desactivar' : '✅ Activar'}
          </Button>
        </SectionTitle>
        
        {collaborationEnabled ? (
          <>
            
            <StatusIndicator 
              connected={isConnected} 
              connecting={isConnecting}
            >
              <div className="indicator-dot" />
              {isConnecting ? 'Conectando...' : 
               isConnected ? 'Conectado al servidor' : 'Desconectado del servidor'}
            </StatusIndicator>
          </>
        ) : (
          <div style={{
            padding: '12px',
            backgroundColor: 'var(--color-backgroundSecondary)',
            borderRadius: '6px',
            textAlign: 'center',
            color: 'var(--color-textSecondary)',
            fontSize: '12px',
            border: '1px dashed var(--color-border)'
          }}>
            <div style={{ marginBottom: '8px', fontSize: '16px' }}>🤝</div>
            <div style={{ fontWeight: '600', marginBottom: '4px' }}>
              Colaboración Desactivada
            </div>
            <div style={{ fontSize: '11px', opacity: 0.8, marginBottom: '8px' }}>
              Activa la colaboración para trabajar en equipo
            </div>
            <div style={{ fontSize: '10px', opacity: 0.7, fontStyle: 'italic', marginBottom: '8px' }}>
              💡 Para colaboración real, activa la colaboración y usa "Iniciar Servidor Real"
            </div>
            <div style={{ fontSize: '9px', opacity: 0.6, fontStyle: 'italic' }}>
              🔧 Usuarios avanzados: npm run server:collaboration
            </div>
          </div>
        )}

        {collaborationEnabled && (
          <>
            {/* Guía de ayuda para usuarios nuevos */}
            <CollaborationHelp onStartRealServer={handleStartRealServer} />
            
            {/* Información del servidor */}
            {serverInfo && (
              <ServerInfoContainer>
                <div className="server-title">
                  🌐 Servidor de Colaboración
                </div>
                <div className="server-ip">
                  {serverInfo.display}
                </div>
                <div className="server-type">
                  {publicIP ? 'IP Pública' : localIP ? 'IP Local' : 'Localhost'}
                </div>
                <div className="server-actions">
                  <CopyButton onClick={handleCopyServerInfo}>
                    📋 Copiar IP
                  </CopyButton>
                  <DetectButton 
                    onClick={handleDetectIPs} 
                    disabled={isDetectingIP}
                  >
                    {isDetectingIP ? '🔍 Detectando...' : '🔄 Actualizar IP'}
                  </DetectButton>
                </div>
                
                {/* Código de conexión seguro */}
                {connectionCode && (
                  <>
                    <div style={{ 
                      borderTop: '1px solid var(--color-border)', 
                      marginTop: '12px', 
                      paddingTop: '12px' 
                    }}>
                      <div className="server-title">
                        🔐 Código de Conexión Seguro
                      </div>
                      <div style={{ 
                        fontFamily: 'monospace', 
                        fontSize: '14px', 
                        fontWeight: 'bold',
                        color: 'var(--color-success)',
                        marginBottom: '8px'
                      }}>
                        {connectionCode}
                      </div>
                      <div style={{ 
                        fontSize: '11px', 
                        opacity: 0.8, 
                        marginBottom: '8px',
                        fontStyle: 'italic'
                      }}>
                        💡 Comparte este código en lugar de la IP real
                      </div>
                      <CopyButton onClick={handleCopyConnectionCode}>
                        🔐 Copiar Código Seguro
                      </CopyButton>
                    </div>
                  </>
                )}
              </ServerInfoContainer>
            )}

            {/* Controles del servidor */}
            {collaborationEnabled && (
              <div style={{ display: 'flex', gap: '8px', marginBottom: '16px', flexWrap: 'wrap' }}>
                <Button 
                  onClick={handleStartRealServer}
                  className="primary"
                  style={{ backgroundColor: 'var(--color-success)' }}
                >
                  🏠 Iniciar Servidor Real
                </Button>
                
                <Button 
                  onClick={handleDetectIPs} 
                  disabled={isDetectingIP}
                  className="secondary"
                >
                  {isDetectingIP ? '🔍 Detectando...' : '🌐 Detectar IP'}
                </Button>
              </div>
            )}

            {/* Instrucciones de conexión */}
            {serverInfo && (
              <CollaborationInstructions 
                serverInfo={serverInfo} 
                isHost={false} 
              />
            )}

            {!isConnected ? (
          <>
            <Input
              type="text"
              placeholder="URL del servidor (ej: ws://localhost:3002/collaboration)"
              value={serverUrl}
              onChange={(e) => setServerUrl(e.target.value)}
            />
            <Input
              type="text"
              placeholder="Tu nombre de usuario"
              value={username}
              onChange={(e) => setUsername(e.target.value)}
            />
            <Button onClick={handleConnect} disabled={isConnecting}>
              {isConnecting ? 'Conectando...' : 'Conectar'}
            </Button>
          </>
        ) : (
          <>
            {!roomCode ? (
              <>
                <Input
                  type="text"
                  placeholder="Código de sala o código seguro"
                  value={roomCode}
                  onChange={(e) => setRoomCode(e.target.value)}
                />
                <div style={{ 
                  fontSize: '10px', 
                  opacity: 0.7, 
                  marginTop: '4px',
                  marginBottom: '8px',
                  fontStyle: 'italic'
                }}>
                  💡 Acepta códigos normales (ABC123) o códigos seguros (ABC123-XYZ789)
                </div>
                <Button onClick={handleCreateRoom}>
                  🏠 Crear Sala
                </Button>
                <Button className="secondary" onClick={handleJoinRoom}>
                  👤 Unirse a Sala
                </Button>
              </>
            ) : (
              <>
                <RoomCodeDisplay>
                  <div className="room-code">{roomCode}</div>
                  <div className="room-info">
                    {isHost ? 'Sala creada por ti' : 'Sala de colaboración'}
                  </div>
                </RoomCodeDisplay>
                
                <Button className="secondary" onClick={handleCopyRoomCode}>
                  📋 Copiar Código
                </Button>
                
                <UsersList>
                  {users.map(user => (
                    <UserItem key={user.id} isHost={user.isHost}>
                      <div className="user-avatar">
                        {user.username.charAt(0).toUpperCase()}
                      </div>
                      <div className="user-name">{user.username}</div>
                      {user.isHost && <div className="user-status">Host</div>}
                    </UserItem>
                  ))}
                </UsersList>


                <Button className="danger" onClick={handleLeaveRoom}>
                  🚪 Abandonar Sala
                </Button>
              </>
            )}
            
            {/* Indicador y botón de sincronización */}
            {isSyncEnabled && (
              <div style={{ 
                display: 'flex', 
                alignItems: 'center', 
                gap: '8px',
                marginTop: '8px',
                padding: '8px',
                backgroundColor: 'rgba(74, 158, 255, 0.1)',
                borderRadius: '6px',
                border: '1px solid rgba(74, 158, 255, 0.3)'
              }}>
                <div style={{ 
                  width: '8px', 
                  height: '8px', 
                  borderRadius: '50%',
                  backgroundColor: '#4ade80',
                  animation: 'pulse 2s infinite'
                }}></div>
                <span style={{ 
                  fontSize: '12px', 
                  color: 'var(--color-primary)',
                  fontWeight: '600'
                }}>
                  Sincronización Activa
                </span>
                <Button 
                  onClick={syncFullProject}
                  style={{ 
                    fontSize: '10px',
                    padding: '4px 8px',
                    backgroundColor: 'var(--color-primary)',
                    color: 'white'
                  }}
                >
                  🔄 Sincronizar
                </Button>
              </div>
            )}
            
            <Button className="danger" onClick={handleDisconnect}>
              🔌 Desconectar
            </Button>
          </>
        )}
          </>
        )}
      </Section>

      {/* Chat flotante - solo se muestra cuando hay una sala activa */}
      {(roomCode || isHost) && (
        <FloatingChat
          messages={chatMessages}
          onSendMessage={handleSendChat}
          currentUser={username}
          isConnected={isConnected}
          roomCode={roomCode}
        />
      )}
    </PanelContainer>
  );
}

export default CollaborationPanel;
