import React, { useState } from 'react';
import styled from 'styled-components';

const GuideContainer = styled.div`
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: var(--color-background);
  z-index: 3000;
  overflow-y: auto;
  transition: background-color 0.3s ease;
`;

const GuideHeader = styled.div`
  background-color: var(--color-surface);
  padding: 20px;
  border-bottom: 2px solid var(--color-border);
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: sticky;
  top: 0;
  z-index: 3001;
  transition: background-color 0.3s ease, border-color 0.3s ease;
`;

const GuideTitle = styled.h1`
  margin: 0;
  color: var(--color-primary);
  font-size: 32px;
  font-weight: 800;
  display: flex;
  align-items: center;
  gap: 15px;
  transition: color 0.3s ease;
  text-shadow: 0 2px 4px var(--color-shadow);
  letter-spacing: 0.5px;
`;

const CloseButton = styled.button`
  background-color: var(--color-error);
  color: var(--color-textInverse);
  border: none;
  border-radius: 8px;
  padding: 12px 20px;
  cursor: pointer;
  font-size: 16px;
  font-weight: bold;
  transition: all 0.3s ease;
  box-shadow: 0 4px 12px var(--color-shadow);

  &:hover {
    background-color: var(--color-errorHover);
    transform: scale(1.05);
    box-shadow: 0 6px 16px var(--color-shadowHover);
  }
`;

const GuideContent = styled.div`
  padding: 30px;
  max-width: 1200px;
  margin: 0 auto;
`;

const ProgressContainer = styled.div`
  background-color: var(--color-surfaceSecondary);
  border-radius: 12px;
  padding: 24px;
  margin-bottom: 30px;
  border: 2px solid var(--color-border);
  transition: all 0.3s ease;
  box-shadow: 0 4px 12px var(--color-shadow);

  &:hover {
    border-color: var(--color-primary);
    box-shadow: 0 6px 16px var(--color-shadowHover);
  }
`;

const ProgressTitle = styled.h2`
  color: var(--color-primary);
  margin: 0 0 20px 0;
  font-size: 22px;
  font-weight: 800;
  display: flex;
  align-items: center;
  gap: 12px;
  transition: color 0.3s ease;
  text-shadow: 0 1px 3px var(--color-shadow);
  letter-spacing: 0.3px;
`;

const ProgressBar = styled.div`
  background-color: var(--color-backgroundSecondary);
  border-radius: 10px;
  height: 24px;
  overflow: hidden;
  margin-bottom: 12px;
  border: 2px solid var(--color-border);
  box-shadow: 0 2px 6px var(--color-shadow);
`;

const ProgressFill = styled.div`
  background: linear-gradient(90deg, var(--color-primary), var(--color-primaryHover));
  height: 100%;
  width: ${props => props.progress}%;
  transition: width 0.3s ease;
  box-shadow: 0 2px 4px var(--color-shadow);
`;

const ProgressText = styled.div`
  color: var(--color-textPrimary);
  font-size: 16px;
  font-weight: 600;
  text-align: center;
  transition: color 0.3s ease;
  text-shadow: 0 1px 2px var(--color-shadow);
`;

const ChapterTabs = styled.div`
  display: flex;
  gap: 10px;
  margin-bottom: 30px;
  flex-wrap: wrap;
`;

const ChapterTab = styled.button`
  background-color: ${props => props.active ? 'var(--color-primary)' : 'var(--color-surfaceSecondary)'};
  color: ${props => props.active ? 'var(--color-textInverse)' : 'var(--color-textPrimary)'};
  border: 2px solid ${props => props.active ? 'var(--color-primary)' : 'var(--color-border)'};
  padding: 14px 22px;
  border-radius: 8px;
  cursor: pointer;
  font-size: 14px;
  font-weight: 700;
  transition: all 0.3s ease;
  box-shadow: 0 3px 8px var(--color-shadow);

  &:hover {
    background-color: ${props => props.active ? 'var(--color-primaryHover)' : 'var(--color-border)'};
    border-color: ${props => props.active ? 'var(--color-primaryHover)' : 'var(--color-primary)'};
    color: var(--color-textInverse);
    transform: translateY(-3px);
    box-shadow: 0 6px 16px var(--color-shadowHover);
  }

  &:active {
    transform: translateY(-1px);
    box-shadow: 0 4px 12px var(--color-shadowHover);
  }
`;

const ChapterContent = styled.div`
  background-color: var(--color-surfaceSecondary);
  border-radius: 12px;
  padding: 30px;
  border: 2px solid var(--color-border);
  margin-bottom: 30px;
  transition: all 0.3s ease;
  box-shadow: 0 4px 12px var(--color-shadow);

  &:hover {
    border-color: var(--color-primary);
    box-shadow: 0 6px 16px var(--color-shadowHover);
  }
`;

const ChapterTitle = styled.h2`
  color: var(--color-primary);
  margin: 0 0 25px 0;
  font-size: 26px;
  font-weight: 800;
  display: flex;
  align-items: center;
  gap: 15px;
  transition: color 0.3s ease;
  text-shadow: 0 2px 4px var(--color-shadow);
  letter-spacing: 0.3px;
`;

const SectionTitle = styled.h3`
  color: var(--color-error);
  margin: 25px 0 18px 0;
  font-size: 20px;
  font-weight: 800;
  display: flex;
  align-items: center;
  gap: 12px;
  transition: color 0.3s ease;
  text-shadow: 0 2px 4px var(--color-shadow);
  letter-spacing: 0.3px;
`;

const StepContainer = styled.div`
  background-color: var(--color-backgroundSecondary);
  border: 2px solid var(--color-border);
  border-radius: 10px;
  padding: 24px;
  margin: 18px 0;
  position: relative;
  transition: all 0.3s ease;
  box-shadow: 0 4px 12px var(--color-shadow);

  &:hover {
    border-color: var(--color-primary);
    box-shadow: 0 6px 16px var(--color-shadowHover);
    transform: translateY(-2px);
  }
`;

const StepNumber = styled.div`
  position: absolute;
  top: -12px;
  left: 24px;
  background-color: var(--color-primary);
  color: var(--color-textInverse);
  width: 32px;
  height: 32px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: 800;
  font-size: 14px;
  transition: all 0.3s ease;
  box-shadow: 0 4px 12px var(--color-shadow);
  border: 2px solid var(--color-primaryHover);

  &:hover {
    background-color: var(--color-primaryHover);
    transform: scale(1.1);
    box-shadow: 0 6px 16px var(--color-shadowHover);
  }
`;

const StepTitle = styled.h4`
  color: var(--color-primary);
  margin: 12px 0 12px 0;
  font-size: 18px;
  font-weight: 800;
  transition: color 0.3s ease;
  text-shadow: 0 1px 3px var(--color-shadow);
  letter-spacing: 0.2px;
`;

const StepDescription = styled.p`
  color: var(--color-textPrimary);
  margin: 0 0 18px 0;
  line-height: 1.7;
  font-size: 16px;
  font-weight: 600;
  transition: color 0.3s ease;
  text-shadow: 0 1px 2px var(--color-shadow);
`;

const CodeExample = styled.div`
  background-color: var(--color-backgroundSecondary);
  border: 3px solid var(--color-primary);
  border-radius: 10px;
  padding: 20px;
  margin: 18px 0;
  font-family: 'Courier New', monospace;
  font-size: 14px;
  font-weight: 600;
  color: var(--color-primary);
  overflow-x: auto;
  position: relative;
  transition: all 0.3s ease;
  box-shadow: 0 4px 12px var(--color-shadow);

  &:hover {
    border-color: var(--color-primaryHover);
    box-shadow: 0 6px 16px var(--color-shadowHover);
    transform: translateY(-2px);
  }

  &::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    height: 3px;
    background: linear-gradient(90deg, var(--color-primary), var(--color-primaryHover));
    border-radius: 10px 10px 0 0;
  }
`;

const CodeLabel = styled.div`
  position: absolute;
  top: -12px;
  left: 20px;
  background-color: var(--color-success);
  color: var(--color-textInverse);
  padding: 6px 12px;
  font-size: 12px;
  border-radius: 6px;
  font-weight: 700;
  transition: all 0.3s ease;
  box-shadow: 0 2px 6px var(--color-shadow);
  border: 1px solid var(--color-successHover);

  &:hover {
    background-color: var(--color-successHover);
    transform: translateY(-1px);
    box-shadow: 0 4px 8px var(--color-shadowHover);
  }
`;

const InfoBox = styled.div`
  background-color: var(--color-surfaceSecondary);
  border: 3px solid var(--color-info);
  border-radius: 14px;
  padding: 24px;
  margin: 24px 0;
  transition: all 0.3s ease;
  box-shadow: 0 6px 20px var(--color-shadow);
  position: relative;

  &:hover {
    border-color: var(--color-infoHover);
    box-shadow: 0 8px 24px var(--color-shadowHover);
    transform: translateY(-3px);
  }

  &::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    height: 4px;
    background: linear-gradient(90deg, var(--color-info), var(--color-infoHover));
    border-radius: 14px 14px 0 0;
  }
`;

const InfoTitle = styled.div`
  color: var(--color-info);
  font-weight: 800;
  font-size: 18px;
  margin-bottom: 16px;
  display: flex;
  align-items: center;
  gap: 10px;
  transition: color 0.3s ease;
  text-shadow: 0 2px 4px var(--color-shadow);
  letter-spacing: 0.3px;
`;

const InfoText = styled.div`
  color: var(--color-textPrimary);
  font-size: 16px;
  line-height: 1.7;
  font-weight: 600;
  transition: color 0.3s ease;
  text-shadow: 0 1px 2px var(--color-shadow);
`;

const TipBox = styled.div`
  background-color: var(--color-surfaceSecondary);
  border: 3px solid var(--color-warning);
  border-radius: 14px;
  padding: 24px;
  margin: 24px 0;
  transition: all 0.3s ease;
  box-shadow: 0 6px 20px var(--color-shadow);
  position: relative;

  &:hover {
    border-color: var(--color-warningHover);
    box-shadow: 0 8px 24px var(--color-shadowHover);
    transform: translateY(-3px);
  }

  &::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    height: 4px;
    background: linear-gradient(90deg, var(--color-warning), var(--color-warningHover));
    border-radius: 14px 14px 0 0;
  }
`;

const TipTitle = styled.div`
  color: var(--color-warning);
  font-weight: 800;
  font-size: 18px;
  margin-bottom: 16px;
  display: flex;
  align-items: center;
  gap: 10px;
  transition: color 0.3s ease;
  text-shadow: 0 2px 4px var(--color-shadow);
  letter-spacing: 0.3px;
`;

const TipText = styled.div`
  color: var(--color-textPrimary);
  font-size: 16px;
  line-height: 1.7;
  font-weight: 600;
  transition: color 0.3s ease;
  text-shadow: 0 1px 2px var(--color-shadow);
`;

