// Hook personalizado para monitorear memoria
const useMemoryMonitor = () => {
  const [memoryInfo, setMemoryInfo] = useState(null);
  
  const getMemoryInfo = useCallback(() => {
    if ('memory' in performance) {
      const memory = performance.memory;
      return {
        usedJSHeapSize: memory.usedJSHeapSize,
        totalJSHeapSize: memory.totalJSHeapSize,
        jsHeapSizeLimit: memory.jsHeapSizeLimit,
        usedMB: (memory.usedJSHeapSize / 1024 / 1024).toFixed(2),
        totalMB: (memory.totalJSHeapSize / 1024 / 1024).toFixed(2),
        limitMB: (memory.jsHeapSizeLimit / 1024 / 1024).toFixed(2)
      };
    }
    return null;
  }, []);
  
  useEffect(() => {
    const updateMemoryInfo = () => {
      const info = getMemoryInfo();
      if (info) {
        setMemoryInfo(info);
        
        // Advertir si el uso de memoria es alto
        const usagePercent = (info.usedJSHeapSize / info.jsHeapSizeLimit) * 100;
        if (usagePercent > 80) {
          console.warn(`PreviewPanel: Uso de memoria alto: ${usagePercent.toFixed(1)}%`);
        }
      }
    };
    
    // Actualizar cada 30 segundos
    const interval = setInterval(updateMemoryInfo, 30000);
    updateMemoryInfo(); // Actualizar inmediatamente
    
    return () => clearInterval(interval);
  }, [getMemoryInfo]);
  
  return { memoryInfo, getMemoryInfo };
};

