// Componente mejorado para mostrar sprites - Memoizado para evitar re-renders innecesarios
const CharacterSpriteComponent = React.memo(({ characterId, spriteName, visible, position, characterName, loadedSprites, setLoadedSprites, fileManager, state }) => {
  const [spriteUrl, setSpriteUrl] = useState(null);
  const [isLoading, setIsLoading] = useState(false);
  
  // Memoizar la función de carga de sprite para evitar recreaciones innecesarias
  const loadSprite = useCallback(async () => {
      // Verificar que state y state.project existan
      if (!state || !state.project || !state.project.characters) {
        setSpriteUrl(null);
        return;
      }
      
      const character = state.project.characters.find(char => char.id === characterId);
      
      if (!character || !character.sprites || character.sprites.length === 0) {
        setSpriteUrl(null);
        return;
      }
      
      const characterSprites = loadedSprites[characterId] || {};
      
      let targetSprite = null;
      let url = null;
      
      if (spriteName) {
        targetSprite = character.sprites.find(s => s.name === spriteName);
        if (targetSprite) {
          url = characterSprites[spriteName];
        }
      } else {
        targetSprite = character.sprites[0];
        url = characterSprites[targetSprite.name];
      }
      
      // Si no tenemos la URL cargada, intentar cargarla ahora
      if (!url && targetSprite) {
        setIsLoading(true);
        try {
          url = await fileManager.getImageUrl(targetSprite.path);
          
          // Actualizar el estado de sprites cargados
          if (url) {
            setLoadedSprites(prev => ({
              ...prev,
              [characterId]: {
                ...prev[characterId],
                [targetSprite.name]: url
              }
            }));
          }
        } catch (error) {
          console.error('Error loading sprite URL:', error);
        } finally {
          setIsLoading(false);
        }
      }
      
      // Si aún no tenemos URL, usar la ruta como fallback
      if (!url && targetSprite) {
        url = targetSprite.path;
      }
      
      setSpriteUrl(url);
  }, [characterId, spriteName, state?.project?.characters, loadedSprites, fileManager, setLoadedSprites]);
    
  useEffect(() => {
    loadSprite();
  }, [loadSprite]);
  
  return (
    <CharacterSprite
      visible={visible}
      sprite={spriteUrl}
      position={position}
    >
      {isLoading && '⏳'}
      {!spriteUrl && !isLoading && characterName}
      {!spriteUrl && !isLoading && !characterName && '🎭'}
    </CharacterSprite>
  );
});

