// Componente para manejar la carga de videos con URL desde IndexedDB
const VideoPlayerWithUrl = React.memo(({ element, onPlay, onPause, onEnded, onTimeUpdate, onLoadedMetadata, onError, controls, loop, autoPlay, muted }) => {
  const [videoUrl, setVideoUrl] = useState(null);
  const [isLoading, setIsLoading] = useState(true);
  const [isPlaying, setIsPlaying] = useState(false);
  const videoRef = useRef(null);
  const abortControllerRef = useRef(null);

  // Cleanup de recursos al desmontar o cambiar elemento
  useEffect(() => {
    return () => {
      // Limpiar video
      if (videoRef.current) {
        videoRef.current.pause();
        videoRef.current.src = '';
        videoRef.current.load();
        videoRef.current = null;
      }
      
      // Cancelar operaciones asíncronas pendientes
      if (abortControllerRef.current) {
        abortControllerRef.current.abort();
      }
      
      // Limpiar estados
      setVideoUrl(null);
      setIsLoading(true);
      setIsPlaying(false);
    };
  }, [element.id]); // Solo limpiar cuando cambie el elemento

  useEffect(() => {
    const loadVideoUrl = async () => {
      try {
        if (!element.file) return;
        
        // Crear nuevo AbortController para esta operación
        abortControllerRef.current = new AbortController();
        
        // Extraer el nombre del archivo de la ruta
        const fileName = element.file.split('/').pop();
        
        // Acceder directamente a IndexedDB
        const assetStorage = new AssetStorage();
        const files = await assetStorage.getAllFiles();
        
        // Verificar si la operación fue cancelada
        if (abortControllerRef.current.signal.aborted) {
          return;
        }
        
        const file = files.find(f => f.name === fileName && f.category === 'videos');
        
        if (file && file.data) {
          console.log('Archivo encontrado en IndexedDB:', fileName);
          setVideoUrl(file.data);
        } else {
          console.log('No se encontró el archivo en IndexedDB:', fileName);
          console.log('Archivos disponibles:', files.map(f => ({ name: f.name, category: f.category })));
        }
      } catch (error) {
        if (error.name !== 'AbortError') {
          console.error('Error al cargar URL del video:', error);
        }
      } finally {
        if (!abortControllerRef.current?.signal.aborted) {
          setIsLoading(false);
        }
      }
    };

    loadVideoUrl();
  }, [element.file]);

  // Intentar reproducir automáticamente cuando el video se carga
  useEffect(() => {
    if (videoUrl && videoRef.current && !isPlaying) {
      const attemptPlay = async () => {
        try {
          console.log('Intentando reproducir video automáticamente...');
          await videoRef.current.play();
          setIsPlaying(true);
          console.log('Video reproducido automáticamente');
        } catch (error) {
          console.log('No se pudo reproducir automáticamente:', error.message);
          // Si falla la reproducción automática, mostrar botón de play
        }
      };
      
      // Pequeño delay para asegurar que el video esté listo
      setTimeout(attemptPlay, 100);
    }
  }, [videoUrl, isPlaying]);

  const handlePlayClick = async () => {
    if (videoRef.current) {
      try {
        await videoRef.current.play();
        setIsPlaying(true);
      } catch (error) {
        console.error('Error al reproducir video:', error);
      }
    }
  };

  if (isLoading) {
    return (
      <div style={{
        position: 'absolute',
        top: '50%',
        left: '50%',
        transform: 'translate(-50%, -50%)',
        background: 'rgba(0, 0, 0, 0.8)',
        color: '#ffffff',
        padding: '20px',
        borderRadius: '8px',
        textAlign: 'center',
        fontSize: '14px',
        display: 'flex',
        flexDirection: 'column',
        alignItems: 'center',
        gap: '8px'
      }}>
        <div style={{ fontSize: '32px' }}>🎬</div>
        <div style={{ fontWeight: 'bold' }}>Cargando video...</div>
        <div style={{ fontSize: '12px', opacity: 0.8 }}>
          {element.file.split('/').pop()}
        </div>
      </div>
    );
  }

  if (!videoUrl) {
    return (
      <div style={{
        position: 'absolute',
        top: '50%',
        left: '50%',
        transform: 'translate(-50%, -50%)',
        background: 'rgba(0, 0, 0, 0.8)',
        color: '#ffffff',
        padding: '20px',
        borderRadius: '8px',
        textAlign: 'center',
        fontSize: '14px',
        display: 'flex',
        flexDirection: 'column',
        alignItems: 'center',
        gap: '8px'
      }}>
        <div style={{ fontSize: '32px' }}>🎬</div>
        <div style={{ fontWeight: 'bold' }}>Video</div>
        <div style={{ fontSize: '12px', opacity: 0.8 }}>
          {element.file.split('/').pop()}
        </div>
        <div style={{ fontSize: '10px', opacity: 0.6 }}>
          Archivo no encontrado
        </div>
      </div>
    );
  }

  return (
    <div style={{ position: 'relative', width: '100%', height: '100%' }}>
      <VideoPlayer
        ref={videoRef}
        id={`video-${element.id}`}
        src={videoUrl}
        onPlay={(e) => {
          setIsPlaying(true);
          if (onPlay) onPlay(e);
        }}
        onPause={(e) => {
          setIsPlaying(false);
          if (onPause) onPause(e);
        }}
        onEnded={onEnded}
        onTimeUpdate={onTimeUpdate}
        onLoadedMetadata={onLoadedMetadata}
        onError={onError}
        controls={false}
        loop={loop}
        autoPlay={false}
        muted={muted}
        style={{
          background: 'linear-gradient(45deg, #1a1a1a, #2a2a2a)'
        }}
      />
      
      {/* Botón de play overlay cuando el video no está reproduciéndose */}
      {!isPlaying && (
        <div
          style={{
            position: 'absolute',
            top: '50%',
            left: '50%',
            transform: 'translate(-50%, -50%)',
            background: 'rgba(0, 0, 0, 0.7)',
            borderRadius: '50%',
            width: '60px',
            height: '60px',
            display: 'flex',
            alignItems: 'center',
            justifyContent: 'center',
            cursor: 'pointer',
            fontSize: '24px',
            color: 'white',
            zIndex: 10
          }}
          onClick={handlePlayClick}
        >
          ▶️
        </div>
      )}
    </div>
  );
});

function PreviewPanel() {
  const { state, dispatch } = useVisualNovel();
  const [currentElementIndex, setCurrentElementIndex] = useState(0);
  const [isPlaying, setIsPlaying] = useState(false);
  const [currentBackground, setCurrentBackground] = useState(null);
  const [visibleCharacters, setVisibleCharacters] = useState([]);
  const [characterSprites, setCharacterSprites] = useState({});
  const [isInteractive, setIsInteractive] = useState(false);
  const [selectedChoice, setSelectedChoice] = useState(null);
  const [videoStates, setVideoStates] = useState({});
  const [currentVideo, setCurrentVideo] = useState(null);
  const fileManager = new FileManager();
  
  // Monitoreo de memoria
  const { memoryInfo, getMemoryInfo } = useMemoryMonitor();

  // Hook para transiciones
  const { applyTransition } = useTransition();

  // Aplicar transiciones cuando se detecte un elemento de transición o cambio de fondo/sprite
  useEffect(() => {
    const previewElements = getPreviewElements();
    const currentElement = previewElements[currentElementIndex];
    
    if (currentElement) {
      // Transición independiente
      if (currentElement.type === 'transition') {
        const transitionType = currentElement.transitionType || 'dissolve';
        const transitionDuration = currentElement.transitionDuration || null;
        
        console.log('PreviewPanel: Aplicando transición independiente:', transitionType, transitionDuration);
        applyTransition(transitionType, transitionDuration);
      }
      
      // Transición de fondo en cambio de escena
      if (currentElement.type === 'scene' && currentElement.backgroundTransition) {
        const transitionType = currentElement.backgroundTransition;
        const transitionDuration = currentElement.backgroundTransitionDuration || null;
        
        console.log('PreviewPanel: Aplicando transición de fondo:', transitionType, transitionDuration);
        applyTransition(transitionType, transitionDuration);
      }
      
      // Transición de sprite en show_sprite
      if (currentElement.type === 'show_sprite' && currentElement.spriteTransition) {
        const transitionType = currentElement.spriteTransition;
        const transitionDuration = currentElement.spriteTransitionDuration || null;
        
        console.log('PreviewPanel: Aplicando transición de sprite:', transitionType, transitionDuration);
        applyTransition(transitionType, transitionDuration);
      }
    }
  }, [currentElementIndex, applyTransition]);

  // Escuchar eventos de navegación desde ScriptEditor
  useEffect(() => {
    const handleNavigateToElement = (event) => {
      const { elementIndex, elementId, sceneFileId } = event.detail;
      
      console.log('PreviewPanel: Evento recibido:', { elementIndex, elementId, sceneFileId, currentSceneFile: state.project.currentSceneFile, previewMode: state.previewMode });
      
      // Verificar que el evento sea para el archivo de escena actual
      if (sceneFileId === state.project.currentSceneFile) {
        console.log('PreviewPanel: Navegando a elemento desde ScriptEditor:', elementIndex, elementId);
        
        // Si la vista previa no está activa, activarla primero
        if (!state.previewMode) {
          console.log('PreviewPanel: Activando vista previa automáticamente');
          dispatch({ type: 'TOGGLE_PREVIEW' });
          
          // Esperar un momento para que se active la vista previa antes de navegar
          setTimeout(() => {
            const previewElements = getPreviewElements();
            console.log('PreviewPanel: Elementos de vista previa (después de activar):', previewElements.length, 'Índice solicitado:', elementIndex);
            
            if (elementIndex >= 0 && elementIndex < previewElements.length) {
              setCurrentElementIndex(elementIndex);
              console.log('PreviewPanel: Navegación exitosa al elemento (después de activar):', elementIndex);
            } else {
              console.warn('PreviewPanel: Índice de elemento inválido (después de activar):', elementIndex, 'de', previewElements.length);
            }
          }, 100);
        } else {
          // Si ya está activa, navegar directamente
          const previewElements = getPreviewElements();
          console.log('PreviewPanel: Elementos de vista previa:', previewElements.length, 'Índice solicitado:', elementIndex);
          
          if (elementIndex >= 0 && elementIndex < previewElements.length) {
            setCurrentElementIndex(elementIndex);
            console.log('PreviewPanel: Navegación exitosa al elemento:', elementIndex);
          } else {
            console.warn('PreviewPanel: Índice de elemento inválido:', elementIndex, 'de', previewElements.length);
          }
        }
      } else {
        console.log('PreviewPanel: Evento ignorado - archivo de escena diferente:', sceneFileId, 'vs', state.project.currentSceneFile);
      }
    };

    window.addEventListener('navigateToElement', handleNavigateToElement);
    document.addEventListener('navigateToElement', handleNavigateToElement);
    
    return () => {
      window.removeEventListener('navigateToElement', handleNavigateToElement);
      document.removeEventListener('navigateToElement', handleNavigateToElement);
    };
  }, [state.project.currentSceneFile, state.project.sceneFiles, state.previewMode, dispatch]);

  useEffect(() => {
    if (!state.previewMode) {
      setIsPlaying(false);
      setCurrentElementIndex(0);
    }
  }, [state.previewMode]);

  // Resetear vista previa cuando cambie el archivo de escena
  useEffect(() => {
    setCurrentElementIndex(0);
    setVisibleCharacters([]);
    setSelectedChoice(null);
    // No resetear el fondo aquí, se actualizará automáticamente
  }, [state.project.currentSceneFile]);

  // Función para actualizar personajes visibles basado en elementos del script
  const updateVisibleCharacters = useCallback(() => {
    const currentSceneFile = state.project.sceneFiles.find(
      file => file.id === state.project.currentSceneFile
    );
    
    if (!currentSceneFile) return;

    const visibleChars = new Set();
    const realIndex = getRealElementIndex(currentElementIndex);
    
    // Revisar todos los elementos hasta el elemento actual
    for (let i = 0; i <= realIndex; i++) {
      const element = currentSceneFile.elements[i];
      if (!element) continue;

      switch (element.type) {
        case 'show_sprite':
          if (element.character && element.spriteName) {
            visibleChars.add(element.character);
          }
          break;
        case 'hide_sprite':
          if (element.character) {
            visibleChars.delete(element.character);
          }
          break;
        case 'dialogue':
          // En diálogos, solo mostrar el personaje que está hablando si tiene sprite
          if (element.character && element.showSprite) {
            visibleChars.add(element.character);
          }
          break;
        case 'scene':
          // Las escenas pueden ocultar todos los personajes si no hay show_sprite
          // Esto se maneja por defecto ya que visibleChars empieza vacío
          break;
      }
    }

    setVisibleCharacters(Array.from(visibleChars));
  }, [currentElementIndex, state.project.currentSceneFile, state.project.sceneFiles]);

  // Actualizar personajes visibles cuando cambie el elemento actual
  useEffect(() => {
    updateVisibleCharacters();
  }, [updateVisibleCharacters]);

  // Función para obtener elementos que deben aparecer en la vista previa
  const getPreviewElements = () => {
    // Verificar que state y project existan
    if (!state || !state.project || !state.project.sceneFiles) {
      console.log('PreviewPanel - getPreviewElements: state o project no disponibles');
      return [];
    }
    
    // Obtener el archivo de escena actual
    const currentSceneFile = state.project.sceneFiles.find(
      file => file.id === state.project.currentSceneFile
    );
    
    if (!currentSceneFile) {
      console.log('PreviewPanel - getPreviewElements: archivo de escena actual no encontrado');
      return [];
    }
    
    console.log('PreviewPanel - getPreviewElements: archivo actual:', currentSceneFile.displayName, 'elementos totales:', currentSceneFile.elements.length);
    
    // Filtrar elementos del archivo actual
    const filteredElements = currentSceneFile.elements.filter(element => {
      // Filtrar fondos que no deben mostrarse como elementos en la vista previa
      if (element.type === 'scene' && !element.showAsElement) {
        return false;
      }
      // Filtrar labels - no deben aparecer en la vista previa
      if (element.type === 'label') {
        return false;
      }
      // Filtrar variables, condiciones y flags - son elementos de lógica interna
      if (element.type === 'variable' || element.type === 'condition' || element.type === 'flag') {
        return false;
      }
      return true;
    });
    
    console.log('PreviewPanel - getPreviewElements: elementos filtrados:', filteredElements.length);
    return filteredElements;
  };

  // Función para encontrar el índice de vista previa de un label
  const findPreviewIndexForLabel = (labelName) => {
    // Verificar que state y project existan
    if (!state || !state.project || !state.project.sceneFiles) {
      return -1;
    }
    
    // Buscar el label en todos los archivos de escena
    for (const sceneFile of state.project.sceneFiles) {
      const labelIndex = sceneFile.elements.findIndex(element => 
        element.type === 'label' && element.label === labelName
      );
      
      if (labelIndex !== -1) {
        // Si el label está en el archivo actual, calcular el índice de vista previa
        if (sceneFile.id === state.project.currentSceneFile) {
          let previewIndex = 0;
          for (let i = 0; i < labelIndex; i++) {
            const element = sceneFile.elements[i];
            if (element.type !== 'label' && !(element.type === 'scene' && !element.showAsElement)) {
              previewIndex++;
            }
          }
          return previewIndex;
        } else {
          // Si el label está en otro archivo, cambiar al archivo correcto
          // y navegar al primer elemento visible después del label
          let previewIndex = 0;
          for (let i = 0; i < labelIndex; i++) {
            const element = sceneFile.elements[i];
            if (element.type !== 'label' && !(element.type === 'scene' && !element.showAsElement)) {
              previewIndex++;
            }
          }
          
          // Cambiar al archivo correcto y navegar al primer elemento visible después del label
          setTimeout(() => {
            // Cambiar el archivo de escena actual
            dispatch({
              type: 'SET_CURRENT_SCENE_FILE',
              payload: sceneFile.id
            });
            
            // Navegar al primer elemento visible después del label
            setCurrentElementIndex(previewIndex);
            
            // Forzar actualización del elemento actual después del cambio
            setTimeout(() => {
              // Actualizar el fondo inmediatamente
              const newBackground = getCurrentBackground();
              setCurrentBackground(newBackground);
              console.log('PreviewPanel: Fondo actualizado después del cambio de archivo:', newBackground);
              
              // El re-render se maneja automáticamente por React cuando cambia el estado
              console.log('PreviewPanel: Elemento actual actualizado automáticamente');
              
              // Segunda actualización del fondo después de que el elemento se haya actualizado
              setTimeout(() => {
                const finalBackground = getCurrentBackground();
                setCurrentBackground(finalBackground);
                console.log('PreviewPanel: Fondo final actualizado:', finalBackground);
              }, 100);
            }, 50);
          }, 100);
          return -1; // Indicar que se está cambiando de archivo
        }
      }
    }
    
    return -1; // Label no encontrado
  };

  // Obtener el elemento actual basado en el índice de vista previa
  const getCurrentElement = () => {
    const previewElements = getPreviewElements();
    console.log('PreviewPanel - getCurrentElement:', {
      currentElementIndex,
      previewElementsLength: previewElements.length,
      currentSceneFile: state.project?.currentSceneFile
    });
    
    if (currentElementIndex >= 0 && currentElementIndex < previewElements.length) {
      const element = previewElements[currentElementIndex];
      console.log('PreviewPanel - getCurrentElement found:', element);
      return element;
    }
    
    console.log('PreviewPanel - getCurrentElement: no element found');
    return null;
  };

  const currentElement = React.useMemo(() => {
    return getCurrentElement();
  }, [currentElementIndex, state.project.currentSceneFile, state.project.sceneFiles]);

  // Función para obtener el fondo actual basado en el último elemento de escena
  const getCurrentBackground = () => {
    // Verificar que state y project existan
    if (!state || !state.project || !state.project.sceneFiles) {
      console.log('PreviewPanel - getCurrentBackground: state o project no disponibles');
      return null;
    }
    
    // Obtener el archivo de escena actual
    const currentSceneFile = state.project.sceneFiles.find(
      file => file.id === state.project.currentSceneFile
    );
    
    if (!currentSceneFile) {
      console.log('PreviewPanel - getCurrentBackground: archivo de escena actual no encontrado');
      return null;
    }
    
    console.log('PreviewPanel - getCurrentBackground: archivo actual:', currentSceneFile.displayName, 'índice:', currentElementIndex);
    
    // Obtener elementos de vista previa del archivo actual
    const previewElements = getPreviewElements();
    
    // PRIORIDAD 1: Si el elemento actual es una escena, usar su fondo inmediatamente
    if (currentElement && currentElement.type === 'scene' && currentElement.background) {
      console.log('PreviewPanel - getCurrentBackground (current scene):', currentElement.background);
      return currentElement.background;
    }
    
    // PRIORIDAD 2: Buscar basado en el índice actual (más confiable para cambios de archivo)
    if (currentElementIndex >= 0 && currentElementIndex < previewElements.length) {
      const targetElement = previewElements[currentElementIndex];
      const realIndex = currentSceneFile.elements.findIndex(scriptElement => scriptElement.id === targetElement.id);
      
      if (realIndex !== -1) {
        // Buscar el último elemento de escena o video antes del elemento objetivo
        for (let i = realIndex; i >= 0; i--) {
          const element = currentSceneFile.elements[i];
          if (element && element.type === 'scene' && element.background) {
            console.log('PreviewPanel - getCurrentBackground (scene, by index):', element.background);
            return element.background;
          }
          // También buscar videos como fondo
          if (element && (element.type === 'video' || element.type === 'movie_sprite' || element.type === 'movie_cutscene') && element.file) {
            console.log('PreviewPanel - getCurrentBackground (video, by index):', element.file);
            return { type: 'video', file: element.file, element: element };
          }
        }
      }
    }
    
    // PRIORIDAD 3: Si hay un elemento actual y está en el archivo correcto, usarlo
    if (currentElement && currentElement.id) {
      const realIndex = currentSceneFile.elements.findIndex(scriptElement => scriptElement.id === currentElement.id);
      
      if (realIndex !== -1) {
        // Buscar el último elemento de escena o video antes del elemento actual
        for (let i = realIndex; i >= 0; i--) {
          const element = currentSceneFile.elements[i];
          if (element && element.type === 'scene' && element.background) {
            console.log('PreviewPanel - getCurrentBackground (scene, with currentElement):', element.background);
            return element.background;
          }
          // También buscar videos como fondo
          if (element && (element.type === 'video' || element.type === 'movie_sprite' || element.type === 'movie_cutscene') && element.file) {
            console.log('PreviewPanel - getCurrentBackground (video, with currentElement):', element.file);
            return { type: 'video', file: element.file, element: element };
          }
        }
      }
    }
    
    // PRIORIDAD 4: Fallback - buscar el último fondo en todo el archivo
    for (let i = currentSceneFile.elements.length - 1; i >= 0; i--) {
      const element = currentSceneFile.elements[i];
      if (element && element.type === 'scene' && element.background) {
        console.log('PreviewPanel - getCurrentBackground (scene, fallback):', element.background);
        return element.background;
      }
      // También buscar videos como fondo
      if (element && (element.type === 'video' || element.type === 'movie_sprite' || element.type === 'movie_cutscene') && element.file) {
        console.log('PreviewPanel - getCurrentBackground (video, fallback):', element.file);
        return { type: 'video', file: element.file, element: element };
      }
    }
    
    console.log('PreviewPanel - getCurrentBackground: no background found');
    return null;
  };

  // Función para obtener el índice real del elemento actual en el archivo actual
  const getRealElementIndex = (previewIndex) => {
    const previewElements = getPreviewElements();
    if (previewIndex >= 0 && previewIndex < previewElements.length) {
      const element = previewElements[previewIndex];
      const currentSceneFile = state.project.sceneFiles.find(
        file => file.id === state.project.currentSceneFile
      );
      if (currentSceneFile) {
        return currentSceneFile.elements.findIndex(scriptElement => scriptElement.id === element.id);
      }
    }
    return 0;
  };

  // Funciones para manejar videos automáticamente con límite de memoria
  const getVideoState = (elementId) => {
    return videoStates[elementId] || { isPlaying: false, currentTime: 0, duration: 0, hasEnded: false };
  };

  const setVideoState = (elementId, newState) => {
    setVideoStates(prev => {
      const newStates = {
        ...prev,
        [elementId]: { ...prev[elementId], ...newState }
      };
      
      // Limitar el número de estados de video para evitar memory leak
      const stateKeys = Object.keys(newStates);
      if (stateKeys.length > 10) {
        // Mantener solo los 5 estados más recientes
        const sortedKeys = stateKeys.sort((a, b) => {
          const timeA = newStates[a].lastAccessed || 0;
          const timeB = newStates[b].lastAccessed || 0;
          return timeB - timeA;
        });
        
        const limitedStates = {};
        sortedKeys.slice(0, 5).forEach(key => {
          limitedStates[key] = newStates[key];
        });
        
        console.log('PreviewPanel: Limpiando estados de video antiguos para evitar memory leak');
        return limitedStates;
      }
      
      return newStates;
    });
  };

  // Limpiar estados de video antiguos
  const cleanupVideoStates = () => {
    setVideoStates(prev => {
      const now = Date.now();
      const cleanedStates = {};
      
      Object.entries(prev).forEach(([elementId, state]) => {
        // Mantener solo estados de los últimos 5 minutos
        if (state.lastAccessed && (now - state.lastAccessed) < 300000) {
          cleanedStates[elementId] = state;
        }
      });
      
      if (Object.keys(cleanedStates).length !== Object.keys(prev).length) {
        console.log('PreviewPanel: Limpiando estados de video antiguos');
      }
      
      return cleanedStates;
    });
  };

  const handleVideoPlay = (elementId) => {
    setVideoState(elementId, { isPlaying: true, hasEnded: false, lastAccessed: Date.now() });
    setCurrentVideo(elementId);
  };

  const handleVideoPause = (elementId) => {
    setVideoState(elementId, { isPlaying: false, lastAccessed: Date.now() });
  };

  const handleVideoEnded = (elementId) => {
    setVideoState(elementId, { isPlaying: false, currentTime: 0, hasEnded: true, lastAccessed: Date.now() });
    setCurrentVideo(null);
    
    // Avanzar automáticamente al siguiente elemento después del video
    setTimeout(() => {
      handleNext();
    }, 500);
  };

  const handleVideoTimeUpdate = (elementId, currentTime) => {
    setVideoState(elementId, { currentTime, lastAccessed: Date.now() });
  };

  const handleVideoLoadedMetadata = (elementId, duration) => {
    setVideoState(elementId, { duration, lastAccessed: Date.now() });
  };


  // Función para obtener la URL real del archivo desde IndexedDB
  const getFileUrl = async (filePath) => {
    try {
      if (!filePath) return null;
      
      // Extraer el nombre del archivo de la ruta
      const fileName = filePath.split('/').pop();
      
      // Buscar el archivo en IndexedDB
      const fileManager = new FileManager();
      const files = await fileManager.getFilesByCategory('videos');
      const file = files.find(f => f.name === fileName);
      
      if (file && file.data) {
        return file.data; // data:video/webm;base64,...
      }
      
      return null;
    } catch (error) {
      console.error('Error al obtener URL del archivo:', error);
      return null;
    }
  };

  // Función para reproducir video automáticamente cuando se llega a él
  const playVideoAutomatically = async (element) => {
    if (element && (element.type === 'video' || element.type === 'movie_sprite' || element.type === 'movie_cutscene')) {
      const timeoutId = setTimeout(async () => {
        const videoElement = document.getElementById(`video-${element.id}`);
        if (videoElement && element.file) {
          // Obtener la URL real del archivo
          const fileUrl = await getFileUrl(element.file);
          if (fileUrl) {
            videoElement.src = fileUrl;
            videoElement.play().catch(error => {
              console.log('Error al reproducir video:', error);
              // Si no se puede reproducir, avanzar automáticamente
              handleNext();
            });
          } else {
            console.log('No se pudo obtener la URL del archivo');
            handleNext();
          }
        } else {
          // Si no hay archivo, avanzar automáticamente
          handleNext();
        }
      }, 100);
      
      // Guardar el timeoutId para poder cancelarlo si es necesario
      return timeoutId;
    }
  };


  // Actualizar el fondo actual cuando cambien los elementos, índice o archivo de escena
  useEffect(() => {
    const background = getCurrentBackground();
    setCurrentBackground(background);
  }, [currentElementIndex, currentElement, state.project.currentSceneFile, state.project.sceneFiles]);

  // Efecto para verificar y actualizar el fondo cuando cambie la escena o elementos
  useEffect(() => {
    // Verificar si el fondo actual es correcto
    const expectedBackground = getCurrentBackground();
    if (expectedBackground !== currentBackground) {
      setCurrentBackground(expectedBackground);
    }
  }, [state.project.currentSceneFile, currentElementIndex, currentBackground]);

  // Manejar reproducción automática de videos
  useEffect(() => {
    let videoTimeoutId = null;
    
    if (currentElement && isPlaying) {
      // Pausar videos anteriores
      Object.keys(videoStates).forEach(elementId => {
        if (elementId !== currentElement.id) {
          const videoElement = document.getElementById(`video-${elementId}`);
          if (videoElement && !videoElement.paused) {
            videoElement.pause();
          }
        }
      });

      // Reproducir video actual si es un elemento de video
      videoTimeoutId = playVideoAutomatically(currentElement);
    }
    
    // Cleanup: cancelar timeout si el componente se desmonta o cambia el elemento
    return () => {
      if (videoTimeoutId) {
        clearTimeout(videoTimeoutId);
      }
    };
  }, [currentElement, isPlaying]);

  // Cleanup automático de memoria cada 2 minutos
  useEffect(() => {
    const cleanupInterval = setInterval(() => {
      cleanupVideoStates();
      
      // Limpiar videos que no están siendo usados
      Object.keys(videoStates).forEach(elementId => {
        const videoElement = document.getElementById(`video-${elementId}`);
        if (videoElement && elementId !== currentElement?.id) {
          // Si el video no está siendo usado actualmente, limpiarlo completamente
          if (videoElement.paused && videoElement.currentTime === 0) {
            videoElement.pause();
            videoElement.src = '';
            videoElement.load();
            // Remover event listeners si existen
            videoElement.removeEventListener('ended', () => {});
            videoElement.removeEventListener('error', () => {});
          }
        }
      });
      
      // Limpiar elementos DOM huérfanos
      const orphanVideos = document.querySelectorAll('video[id^="video-"]');
      orphanVideos.forEach(video => {
        const elementId = video.id.replace('video-', '');
        if (!videoStates[elementId] && elementId !== currentElement?.id) {
          video.pause();
          video.src = '';
          video.load();
          video.remove();
        }
      });
      
      // Forzar garbage collection si está disponible
      if (window.gc) {
        window.gc();
      }
      
      // Obtener información de memoria para logging
      const memInfo = getMemoryInfo();
      if (memInfo) {
        console.log(`PreviewPanel: Cleanup automático ejecutado - Memoria: ${memInfo.usedMB}MB/${memInfo.limitMB}MB`);
        
        // Advertir si el uso de memoria es muy alto
        const usagePercent = (memInfo.usedJSHeapSize / memInfo.jsHeapSizeLimit) * 100;
        if (usagePercent > 85) {
          console.warn(`PreviewPanel: ⚠️ Uso de memoria muy alto: ${usagePercent.toFixed(1)}%`);
          // Limpieza de emergencia
          cleanupVideoStates();
          Object.keys(videoStates).forEach(elementId => {
            const videoElement = document.getElementById(`video-${elementId}`);
            if (videoElement) {
              videoElement.pause();
              videoElement.src = '';
              videoElement.load();
            }
          });
        }
      } else {
        console.log('PreviewPanel: Cleanup automático de memoria ejecutado');
      }
    }, 120000); // Cada 2 minutos

    return () => clearInterval(cleanupInterval);
  }, [videoStates, currentElement, getMemoryInfo]);

  // Cleanup al desmontar el componente
  useEffect(() => {
    return () => {
      // Limpiar todos los videos
      Object.keys(videoStates).forEach(elementId => {
        const videoElement = document.getElementById(`video-${elementId}`);
        if (videoElement) {
          videoElement.pause();
          videoElement.src = '';
          videoElement.load();
          videoElement.removeEventListener('ended', () => {});
          videoElement.removeEventListener('error', () => {});
        }
      });
      
      // Limpiar todos los timeouts pendientes
      // Esto se maneja automáticamente por React, pero podemos agregar logging
      console.log('PreviewPanel: Componente desmontado, limpiando recursos');
      
      // Forzar garbage collection si está disponible
      if (window.gc) {
        window.gc();
      }
    };
  }, []);


  const handlePlay = () => {
    setIsPlaying(true);
  };

  const handlePause = () => {
    setIsPlaying(false);
  };

  const handleStop = () => {
    setIsPlaying(false);
    setCurrentElementIndex(0);
    setCurrentBackground(null);
    setVisibleCharacters([]);
    
    // Limpiar todos los videos al detener
    Object.keys(videoStates).forEach(elementId => {
      const videoElement = document.getElementById(`video-${elementId}`);
      if (videoElement) {
        videoElement.pause();
        videoElement.src = '';
        videoElement.load();
      }
    });
    
    // Limpiar estados de video
    setVideoStates({});
    
    // Forzar garbage collection
    if (window.gc) {
      window.gc();
    }
  };

  // Función de limpieza de emergencia para casos de memoria muy alta
  const emergencyCleanup = () => {
    console.warn('PreviewPanel: Ejecutando limpieza de emergencia');
    
    // Pausar todos los videos
    const allVideos = document.querySelectorAll('video');
    allVideos.forEach(video => {
      video.pause();
      video.src = '';
      video.load();
    });
    
    // Limpiar todos los estados
    setVideoStates({});
    setLoadedSprites({});
    setCurrentBackground(null);
    setVisibleCharacters([]);
    setSelectedChoice(null);
    
    // Forzar garbage collection múltiples veces
    if (window.gc) {
      window.gc();
      setTimeout(() => window.gc(), 1000);
      setTimeout(() => window.gc(), 3000);
    }
    
    console.log('PreviewPanel: Limpieza de emergencia completada');
  };

  const handleNext = () => {
    const previewElements = getPreviewElements();
    if (currentElementIndex < previewElements.length - 1) {
      setCurrentElementIndex(prev => prev + 1);
    }
  };

  const handlePrevious = () => {
    if (currentElementIndex > 0) {
      setCurrentElementIndex(prev => prev - 1);
    }
  };

  const handleElementClick = (index) => {
    setCurrentElementIndex(index);
  };

  const handlePreviewClick = () => {
    if (isInteractive && currentElement?.type !== 'choice') {
      const previewElements = getPreviewElements();
      if (currentElementIndex < previewElements.length - 1) {
        setCurrentElementIndex(prev => prev + 1);
      }
    }
  };

  const handleChoiceSelect = (choiceIndex) => {
    if (currentElement?.type === 'choice' && currentElement.choices) {
      const selectedChoice = currentElement.choices[choiceIndex];
      setSelectedChoice(selectedChoice);
      
      // Navegar al label correspondiente si existe
      if (selectedChoice.jump) {
        setTimeout(() => {
          const targetPreviewIndex = findPreviewIndexForLabel(selectedChoice.jump);
          
          if (targetPreviewIndex !== -1) {
            // Navegar al label encontrado
            setCurrentElementIndex(targetPreviewIndex);
          } else {
            // Si no se encuentra el label, mostrar mensaje de error
            console.error(`Label "${selectedChoice.jump}" no encontrado`);
            // Avanzar al siguiente elemento como fallback
            const previewElements = getPreviewElements();
            if (currentElementIndex < previewElements.length - 1) {
              setCurrentElementIndex(prev => prev + 1);
            }
          }
          setSelectedChoice(null);
        }, 500);
      } else {
        // Si no hay jump, avanzar al siguiente elemento
        setTimeout(() => {
          const previewElements = getPreviewElements();
          if (currentElementIndex < previewElements.length - 1) {
            setCurrentElementIndex(prev => prev + 1);
          }
          setSelectedChoice(null);
        }, 500);
      }
    }
  };

  // Estado para sprites cargados
  const [loadedSprites, setLoadedSprites] = useState({});

  // Lazy loading de sprites - solo cargar cuando sean necesarios
  const loadSpriteOnDemand = useCallback(async (characterId, spriteName) => {
    if (loadedSprites[characterId]?.[spriteName]) {
      return loadedSprites[characterId][spriteName];
    }

    try {
      const character = state.project.characters.find(char => char.id === characterId);
      if (!character || !character.sprites) return null;

      const sprite = character.sprites.find(s => s.name === spriteName);
      if (!sprite) return null;

              const imageUrl = await fileManager.getImageUrl(sprite.path);
      const url = imageUrl || sprite.path;

      // Actualizar el estado de sprites cargados
      setLoadedSprites(prev => ({
        ...prev,
        [characterId]: {
          ...prev[characterId],
          [spriteName]: url
        }
      }));

      return url;
      } catch (error) {
      console.error('Error loading sprite on demand:', error);
      return null;
    }
  }, [state.project.characters, fileManager, loadedSprites]);


  // Función síncrona para compatibilidad (deprecated)
  const getCharacterSprite = (characterId, spriteName = null) => {
    // Verificar que state y project existan
    if (!state || !state.project || !state.project.characters) {
      return null;
    }
    
    const character = state.project.characters.find(char => char.id === characterId);
    if (character && character.sprites && character.sprites.length > 0) {
      const characterSprites = loadedSprites[characterId] || {};
      
      if (spriteName && characterSprites[spriteName]) {
        return characterSprites[spriteName];
      }
      
      const firstSprite = character.sprites[0];
      return characterSprites[firstSprite.name] || firstSprite.path;
    }
    return null;
  };

  // Estado para fondos cargados
  const [loadedBackgrounds, setLoadedBackgrounds] = useState({});

  // Cargar fondos de forma asíncrona con cleanup
  useEffect(() => {
    let isMounted = true;
    
    const loadBackgrounds = async () => {
      try {
        const backgroundPaths = new Set();
        
        // Recopilar todos los fondos únicos del proyecto
        console.log('PreviewPanel - loadBackgrounds: iniciando carga de fondos...');
        state.project.sceneFiles.forEach(sceneFile => {
          if (!isMounted) return;
          
          sceneFile.elements.forEach(element => {
            if (element.background) {
              backgroundPaths.add(element.background);
              console.log('PreviewPanel - loadBackgrounds: encontrado fondo:', element.background);
            }
          });
        });

        console.log('PreviewPanel - loadBackgrounds: total de fondos encontrados:', backgroundPaths.size);
        console.log('PreviewPanel - loadBackgrounds: rutas de fondos:', Array.from(backgroundPaths));

        if (!isMounted) return;

        const backgroundPromises = Array.from(backgroundPaths).map(async (path) => {
          if (!isMounted) return null;
          
          console.log('PreviewPanel - loadBackgrounds: cargando fondo:', path);
          const imageUrl = await fileManager.getImageUrl(path);
          console.log('PreviewPanel - loadBackgrounds: fondo cargado:', path, '->', imageUrl);
          return { path, url: imageUrl || path };
        });

        const results = await Promise.all(backgroundPromises);
        
        if (isMounted) {
          const backgroundMap = {};
          results.forEach(result => {
            if (result) {
              backgroundMap[result.path] = result.url;
            }
          });
          console.log('PreviewPanel - loadBackgrounds: mapa de fondos final:', backgroundMap);
          setLoadedBackgrounds(backgroundMap);
        }
      } catch (error) {
        if (isMounted) {
          console.error('Error al cargar fondos:', error);
        }
      }
    };

    loadBackgrounds();
    
    return () => {
      isMounted = false;
    };
  }, [state.project.sceneFiles]);

  const getBackgroundImage = (backgroundPath) => {
    if (!backgroundPath) {
      console.log('PreviewPanel - getBackgroundImage: no background path provided');
      return null;
    }
    
    // Si es un objeto con tipo video, devolver null para que se maneje como video
    if (typeof backgroundPath === 'object' && backgroundPath.type === 'video') {
      console.log('PreviewPanel - getBackgroundImage: video background detected');
      return null;
    }
    
    const imageUrl = loadedBackgrounds[backgroundPath];
    
    // Si no está cargado, mostrar advertencia y usar fallback
    if (!imageUrl && backgroundPath) {
      console.warn('PreviewPanel - getBackgroundImage: Fondo no cargado:', backgroundPath);
      console.log('PreviewPanel - getBackgroundImage: Fondos disponibles:', Object.keys(loadedBackgrounds));
      // Usar la ruta original como fallback
      return backgroundPath;
    }
    
    console.log('PreviewPanel - getBackgroundImage:', {
      backgroundPath,
      loadedUrl: imageUrl,
      finalUrl: imageUrl,
      totalLoadedBackgrounds: Object.keys(loadedBackgrounds).length
    });
    
    return imageUrl;
  };

  const getBackgroundVideo = (backgroundPath) => {
    if (!backgroundPath) return null;
    
    // Si es un objeto con tipo video, devolver el elemento
    if (typeof backgroundPath === 'object' && backgroundPath.type === 'video') {
      return backgroundPath.element;
    }
    
    return null;
  };

  // Componente para renderizar el fondo (imagen o video)
  const renderBackground = (backgroundPath) => {
    const backgroundVideo = getBackgroundVideo(backgroundPath);
    
    if (backgroundVideo) {
      // Si hay un video de fondo, renderizarlo directamente
      return (
        <VideoBackground>
          <VideoPlayerWithUrl 
            element={backgroundVideo}
            onPlay={() => {}}
            onPause={() => {}}
            onEnded={() => {}}
            onTimeUpdate={() => {}}
            onLoadedMetadata={() => {}}
            onError={() => {}}
            controls={false}
            loop={true}
            autoPlay={false}
            muted={true}
          />
        </VideoBackground>
      );
    }
    
    return null;
  };

  const renderPreviewContent = () => {
    if (!currentElement) {
      return (
        <EmptyPreview>
          <EmptyIcon>🎮</EmptyIcon>
          <EmptyText>Vista Previa</EmptyText>
          <EmptySubtext>Selecciona un elemento del script para ver la vista previa</EmptySubtext>
        </EmptyPreview>
      );
    }

    switch (currentElement.type) {
      case 'dialogue':
        // Verificar que state y project existan
        if (!state || !state.project || !state.project.characters) {
          return null;
        }
        
        const character = state.project.characters.find(char => char.id === currentElement.character);
        return (
            <>
              <GameScreen background={getBackgroundImage(currentBackground)}>
                {renderBackground(currentBackground)}
                <CharacterSprites>
                {state.project.characters
                  .filter(char => visibleCharacters.includes(char.id)) // Solo mostrar personajes visibles
                  .map(char => {
                    const isCurrentCharacter = char.id === currentElement.character;
                    const spriteName = isCurrentCharacter && currentElement.showSprite ? currentElement.showSprite : null;
                    const spritePosition = isCurrentCharacter && currentElement.showSprite ? currentElement.spritePosition : 'center';
                    
                    return (
                      <CharacterSpriteComponent
                        key={char.id}
                        characterId={char.id}
                        spriteName={spriteName}
                        visible={true} // Ya filtrado arriba
                        position={spritePosition}
                        characterName={char.name}
                        loadedSprites={loadedSprites}
                        setLoadedSprites={setLoadedSprites}
                        fileManager={fileManager}
                        state={state}
                      />
                    );
                  })}
              </CharacterSprites>
            </GameScreen>
            <DialogueBox>
              <CharacterName color={character?.who_color || character?.color}>
                {character?.isProtagonist ? 'Protagonista' : (character?.name || currentElement.character)}
              </CharacterName>
              <DialogueText>
                {currentElement.text.replace(/\[mc_name\]/g, 'Protagonista')}
              </DialogueText>
            </DialogueBox>
          </>
        );

      case 'narration':
        return (
          <GameScreen background={getBackgroundImage(currentBackground)}>
            {renderBackground(currentBackground)}
            <NarrationText 
              textColor={currentElement.textColor}
              fontSize={currentElement.fontSize}
            >
              {currentElement.text.replace(/\[mc_name\]/g, 'Protagonista')}
            </NarrationText>
          </GameScreen>
        );

      case 'scene':
        return (
          <GameScreen background={getBackgroundImage(currentBackground)}>
            {renderBackground(currentBackground)}
            <CharacterSprites>
              {state && state.project && state.project.characters ? 
                state.project.characters
                  .filter(char => visibleCharacters.includes(char.id))
                  .map(char => (
                    <CharacterSpriteComponent
                      key={char.id}
                      characterId={char.id}
                      spriteName={null}
                      visible={true}
                      position="center"
                      characterName={char.name}
                      loadedSprites={loadedSprites}
                      setLoadedSprites={setLoadedSprites}
                      fileManager={fileManager}
                      state={state}
                    />
                  )) : null}
            </CharacterSprites>
            <div style={{
              position: 'absolute',
              top: '20px',
              left: '20px',
              background: 'linear-gradient(135deg, rgba(255, 158, 74, 0.9) 0%, rgba(255, 158, 74, 0.7) 100%)',
              color: '#ffffff',
              padding: '12px 16px',
              borderRadius: '12px',
              fontSize: '13px',
              fontWeight: '600',
              boxShadow: '0 4px 16px rgba(255, 158, 74, 0.3)',
              border: '2px solid rgba(255, 255, 255, 0.2)',
              backdropFilter: 'blur(8px)',
              display: 'flex',
              alignItems: 'center',
              gap: '8px'
            }}>
              <span style={{ fontSize: '16px' }}>🎭</span>
              <div>
                <div style={{ fontSize: '11px', opacity: 0.9 }}>Cambio de escena</div>
                <div style={{ fontSize: '13px', fontWeight: '700' }}>
                  {currentElement.background || 'Sin fondo'}
                </div>
              </div>
            </div>
          </GameScreen>
        );

      case 'transition':
        // Las transiciones no muestran contenido visual, solo aplican el efecto
        // El efecto se aplica automáticamente en el useEffect
        return (
          <GameScreen background={getBackgroundImage(currentBackground)} className="preview-transition-container">
            {renderBackground(currentBackground)}
            <div style={{
              position: 'absolute',
              top: '50%',
              left: '50%',
              transform: 'translate(-50%, -50%)',
              color: 'var(--color-textPrimary)',
              fontSize: '18px',
              textAlign: 'center',
              backgroundColor: 'rgba(0, 0, 0, 0.7)',
              padding: '20px',
              borderRadius: '8px',
              border: '2px solid var(--color-primary)'
            }}>
              <div style={{ fontSize: '24px', marginBottom: '8px' }}>✨</div>
              <div>Transición: {currentElement.transitionType || 'dissolve'}</div>
              {currentElement.transitionDuration && (
                <div style={{ fontSize: '14px', opacity: 0.8 }}>
                  Duración: {currentElement.transitionDuration}s
                </div>
              )}
            </div>
          </GameScreen>
        );

      case 'choice':
        return (
          <GameScreen background={getBackgroundImage(currentBackground)}>
            {renderBackground(currentBackground)}
            <CharacterSprites>
              {state && state.project && state.project.characters ? 
                state.project.characters
                  .filter(char => visibleCharacters.includes(char.id))
                  .map(char => (
                    <CharacterSpriteComponent
                      key={char.id}
                      characterId={char.id}
                      spriteName={null}
                      visible={true}
                      position="center"
                      characterName={char.name}
                      loadedSprites={loadedSprites}
                      setLoadedSprites={setLoadedSprites}
                      fileManager={fileManager}
                      state={state}
                    />
                  )) : null}
            </CharacterSprites>
            <ChoiceContainer>
              <ChoiceMenu>
                {currentElement.text && (
                  <ChoiceQuestion>{currentElement.text}</ChoiceQuestion>
                )}
                {currentElement.choices?.map((choice, index) => (
                  <ChoiceOption
                    key={choice.id || index}
                    onClick={() => handleChoiceSelect(index)}
                    style={{
                      opacity: selectedChoice === choice ? 0.7 : 1,
                      transform: selectedChoice === choice ? 'scale(0.95)' : 'scale(1)'
                    }}
                  >
                    {choice.text}
                  </ChoiceOption>
                ))}
              </ChoiceMenu>
            </ChoiceContainer>
          </GameScreen>
        );

      case 'label':
        // Los labels son invisibles en Ren'Py, solo marcan puntos de destino
        // Avanzar automáticamente al siguiente elemento
        return null;

      case 'jump':
        // Los jumps navegan al label correspondiente
        const targetPreviewIndex = findPreviewIndexForLabel(currentElement.jump);
        
        if (targetPreviewIndex !== -1) {
          // Navegar al label encontrado en el archivo actual
          setCurrentElementIndex(targetPreviewIndex);
          return null; // No mostrar nada, ya que navegamos al label
        } else {
          // Verificar si el label existe en algún archivo (incluso si está cambiando de archivo)
          const labelExists = state && state.project && state.project.sceneFiles && 
            state.project.sceneFiles.some(sceneFile => 
              sceneFile.elements.some(element => 
                element.type === 'label' && element.label === currentElement.jump
              )
            );
          
          if (labelExists) {
            // El label existe pero está en otro archivo, se está cambiando automáticamente
            // Mostrar un mensaje de transición temporal
            return (
              <GameScreen background={getBackgroundImage(currentBackground)}>
                {renderBackground(currentBackground)}
                <CharacterSprites>
                  {state && state.project && state.project.characters ? state.project.characters.map(char => (
                    <CharacterSpriteComponent
                      key={char.id}
                      characterId={char.id}
                      spriteName={null}
                      visible={visibleCharacters.includes(char.id)}
                      position="center"
                      characterName={char.name}
                    />
                  )) : null}
                </CharacterSprites>
                <div style={{
                  position: 'absolute',
                  top: '50%',
                  left: '50%',
                  transform: 'translate(-50%, -50%)',
                  background: 'rgba(0, 150, 255, 0.8)',
                  color: '#ffffff',
                  padding: '20px',
                  borderRadius: '8px',
                  fontSize: '16px',
                  textAlign: 'center',
                  border: '2px solid #0096ff'
                }}>
                  🔄 Navegando a "{currentElement.jump}"...
                </div>
              </GameScreen>
            );
          } else {
            // Si no se encuentra el label en ningún archivo, mostrar mensaje de error
            return (
              <GameScreen background={getBackgroundImage(currentBackground)}>
                {renderBackground(currentBackground)}
                <CharacterSprites>
                  {state && state.project && state.project.characters ? state.project.characters.map(char => (
                    <CharacterSpriteComponent
                      key={char.id}
                      characterId={char.id}
                      spriteName={null}
                      visible={visibleCharacters.includes(char.id)}
                      position="center"
                      characterName={char.name}
                    />
                  )) : null}
                </CharacterSprites>
                <div style={{
                  position: 'absolute',
                  top: '50%',
                  left: '50%',
                  transform: 'translate(-50%, -50%)',
                  background: 'rgba(255, 0, 0, 0.8)',
                  color: '#ffffff',
                  padding: '20px',
                  borderRadius: '8px',
                  fontSize: '16px',
                  textAlign: 'center',
                  border: '2px solid #ff0000'
                }}>
                  ❌ Error: Label "{currentElement.jump}" no encontrado
                </div>
              </GameScreen>
            );
          }
        }

      case 'video':
        return (
          <GameScreen>
            <VideoContainer>
              {currentElement.file ? (
                <VideoPlayerWithUrl
                  element={currentElement}
                  onPlay={() => handleVideoPlay(currentElement.id)}
                  onPause={() => handleVideoPause(currentElement.id)}
                  onEnded={() => handleVideoEnded(currentElement.id)}
                  onTimeUpdate={(e) => handleVideoTimeUpdate(currentElement.id, e.target.currentTime)}
                  onLoadedMetadata={(e) => handleVideoLoadedMetadata(currentElement.id, e.target.duration)}
                  onError={(e) => {
                    console.log('Error al cargar video:', e);
                    // Si hay error, avanzar automáticamente
                    setTimeout(() => handleNext(), 1000);
                  }}
                  controls={false}
                  loop={currentElement.loops > 0}
                  autoPlay={isPlaying}
                  muted={false}
                />
              ) : (
                <VideoPreview>
                  <VideoIcon>🎬</VideoIcon>
                  <VideoInfo>
                    <VideoTitle>Video</VideoTitle>
                    <VideoFile>Sin archivo</VideoFile>
                    <VideoType>Archivo no encontrado</VideoType>
                  </VideoInfo>
                </VideoPreview>
              )}
            </VideoContainer>
          </GameScreen>
        );

      case 'movie_sprite':
        return (
          <GameScreen background={getBackgroundImage(currentBackground)}>
            {renderBackground(currentBackground)}
            <CharacterSprites>
              {currentElement.file ? (
                <CharacterSprite
                  key={currentElement.id}
                  visible={true}
                  style={{
                    position: 'absolute',
                    top: '50%',
                    left: '50%',
                    transform: 'translate(-50%, -50%)',
                    width: '300px',
                    height: '400px',
                    overflow: 'hidden',
                    borderRadius: '8px',
                    border: '3px solid #4a9eff',
                    boxShadow: '0 4px 12px rgba(74, 158, 255, 0.3)',
                    background: 'linear-gradient(45deg, #1a1a1a, #2a2a2a)'
                  }}
                >
                  <VideoPlayer
                    id={`video-${currentElement.id}`}
                    src={currentElement.file}
                    onPlay={() => handleVideoPlay(currentElement.id)}
                    onPause={() => handleVideoPause(currentElement.id)}
                    onEnded={() => handleVideoEnded(currentElement.id)}
                    onTimeUpdate={(e) => handleVideoTimeUpdate(currentElement.id, e.target.currentTime)}
                    onLoadedMetadata={(e) => handleVideoLoadedMetadata(currentElement.id, e.target.duration)}
                    onError={(e) => {
                      console.log('Error al cargar movie sprite:', e);
                      // Si hay error, avanzar automáticamente
                      setTimeout(() => handleNext(), 1000);
                    }}
                    controls={false}
                    loop={true}
                    autoPlay={isPlaying}
                    muted={false}
                    style={{
                      width: '100%',
                      height: '100%',
                      objectFit: 'cover'
                    }}
                  />
                  {/* Fallback visual mientras se carga el video */}
                  <div style={{
                    position: 'absolute',
                    top: '50%',
                    left: '50%',
                    transform: 'translate(-50%, -50%)',
                    background: 'rgba(0, 0, 0, 0.8)',
                    color: '#ffffff',
                    padding: '16px',
                    borderRadius: '6px',
                    textAlign: 'center',
                    fontSize: '12px',
                    display: 'flex',
                    flexDirection: 'column',
                    alignItems: 'center',
                    gap: '6px'
                  }}>
                    <div style={{ fontSize: '24px' }}>🎭🎬</div>
                    <div style={{ fontWeight: 'bold' }}>Movie Sprite</div>
                    <div style={{ fontSize: '10px', opacity: 0.8 }}>
                      {currentElement.spriteName || 'Sin nombre'}
                    </div>
                    <div style={{ fontSize: '9px', opacity: 0.6 }}>
                      {currentElement.file.split('/').pop()}
                    </div>
                  </div>
                </CharacterSprite>
              ) : (
                <CharacterSprite
                  key={currentElement.id}
                  visible={true}
                  style={{
                    position: 'absolute',
                    top: '50%',
                    left: '50%',
                    transform: 'translate(-50%, -50%)',
                    width: '300px',
                    height: '400px',
                    background: 'linear-gradient(45deg, #4a9eff, #6bb6ff)',
                    border: '3px solid #4a9eff',
                    borderRadius: '8px',
                    display: 'flex',
                    flexDirection: 'column',
                    alignItems: 'center',
                    justifyContent: 'center',
                    color: '#ffffff',
                    fontSize: '14px',
                    textAlign: 'center',
                    boxShadow: '0 4px 12px rgba(74, 158, 255, 0.3)'
                  }}
                >
                  <div style={{ fontSize: '48px', marginBottom: '12px' }}>🎭🎬</div>
                  <div style={{ fontWeight: 'bold', marginBottom: '8px' }}>Movie Sprite</div>
                  <div style={{ fontSize: '12px', marginBottom: '4px' }}>
                    {currentElement.spriteName || 'Sin nombre'}
                  </div>
                  <div style={{ fontSize: '10px', opacity: 0.8 }}>
                    Sin archivo
                  </div>
                </CharacterSprite>
              )}
            </CharacterSprites>
          </GameScreen>
        );

      case 'movie_cutscene':
        return (
          <GameScreen>
            <VideoContainer>
              {currentElement.file ? (
                <>
                  <VideoPlayer
                    id={`video-${currentElement.id}`}
                    src={currentElement.file}
                    onPlay={() => handleVideoPlay(currentElement.id)}
                    onPause={() => handleVideoPause(currentElement.id)}
                    onEnded={() => handleVideoEnded(currentElement.id)}
                    onTimeUpdate={(e) => handleVideoTimeUpdate(currentElement.id, e.target.currentTime)}
                    onLoadedMetadata={(e) => handleVideoLoadedMetadata(currentElement.id, e.target.duration)}
                    onError={(e) => {
                      console.log('Error al cargar movie cutscene:', e);
                      // Si hay error, avanzar automáticamente
                      setTimeout(() => handleNext(), 1000);
                    }}
                    controls={false}
                    loop={currentElement.loops > 0}
                    autoPlay={isPlaying}
                    muted={false}
                    style={{
                      background: 'linear-gradient(45deg, #1a1a1a, #2a2a2a)'
                    }}
                  />
                  {/* Fallback visual mientras se carga el video */}
                  <div style={{
                    position: 'absolute',
                    top: '50%',
                    left: '50%',
                    transform: 'translate(-50%, -50%)',
                    background: 'rgba(0, 0, 0, 0.8)',
                    color: '#ffffff',
                    padding: '20px',
                    borderRadius: '8px',
                    textAlign: 'center',
                    fontSize: '14px',
                    display: 'flex',
                    flexDirection: 'column',
                    alignItems: 'center',
                    gap: '8px'
                  }}>
                    <div style={{ fontSize: '32px' }}>🎬🎭</div>
                    <div style={{ fontWeight: 'bold' }}>Movie Cutscene</div>
                    <div style={{ fontSize: '12px', opacity: 0.8 }}>
                      {currentElement.file.split('/').pop()}
                    </div>
                    <div style={{ fontSize: '10px', opacity: 0.6 }}>
                      Cinemática de pantalla completa
                    </div>
                  </div>
                </>
              ) : (
                <VideoPreview>
                  <VideoIcon>🎬🎭</VideoIcon>
                  <VideoInfo>
                    <VideoTitle>Movie Cutscene</VideoTitle>
                    <VideoFile>Sin archivo</VideoFile>
                    <VideoType>Archivo no encontrado</VideoType>
                  </VideoInfo>
                </VideoPreview>
              )}
            </VideoContainer>
          </GameScreen>
        );

      case 'show_sprite':
        return (
          <GameScreen background={getBackgroundImage(currentBackground)}>
            {renderBackground(currentBackground)}
            <CharacterSprites>
              {state && state.project && state.project.characters ? 
                state.project.characters
                  .filter(char => visibleCharacters.includes(char.id))
                  .map(char => (
                    <CharacterSpriteComponent
                      key={char.id}
                      characterId={char.id}
                      spriteName={char.id === currentElement.character ? currentElement.spriteName : null}
                      visible={true}
                      position={char.id === currentElement.character ? (currentElement.spritePosition || 'center') : 'center'}
                      characterName={char.name}
                      loadedSprites={loadedSprites}
                      setLoadedSprites={setLoadedSprites}
                      fileManager={fileManager}
                      state={state}
                    />
                  )) : null}
            </CharacterSprites>
            <div style={{
              position: 'absolute',
              top: '20px',
              left: '20px',
              background: 'linear-gradient(135deg, rgba(74, 158, 255, 0.9) 0%, rgba(74, 158, 255, 0.7) 100%)',
              color: '#ffffff',
              padding: '12px 16px',
              borderRadius: '12px',
              fontSize: '13px',
              fontWeight: '600',
              boxShadow: '0 4px 16px rgba(74, 158, 255, 0.3)',
              border: '2px solid rgba(255, 255, 255, 0.2)',
              backdropFilter: 'blur(8px)',
              display: 'flex',
              alignItems: 'center',
              gap: '8px'
            }}>
              <span style={{ fontSize: '16px' }}>👤</span>
              <div>
                <div style={{ fontSize: '11px', opacity: 0.9 }}>Mostrar Sprite</div>
                <div style={{ fontSize: '13px', fontWeight: '700' }}>
                  {currentElement.character} - {currentElement.spriteName || 'Sprite por defecto'}
                </div>
              </div>
            </div>
          </GameScreen>
        );

      case 'hide_sprite':
        return (
          <GameScreen background={getBackgroundImage(currentBackground)}>
            {renderBackground(currentBackground)}
            <CharacterSprites>
              {state && state.project && state.project.characters ? 
                state.project.characters
                  .filter(char => visibleCharacters.includes(char.id))
                  .map(char => (
                    <CharacterSpriteComponent
                      key={char.id}
                      characterId={char.id}
                      spriteName={null}
                      visible={true}
                      position="center"
                      characterName={char.name}
                      loadedSprites={loadedSprites}
                      setLoadedSprites={setLoadedSprites}
                      fileManager={fileManager}
                      state={state}
                    />
                  )) : null}
            </CharacterSprites>
            <div style={{
              position: 'absolute',
              top: '20px',
              left: '20px',
              background: 'linear-gradient(135deg, rgba(255, 107, 107, 0.9) 0%, rgba(255, 107, 107, 0.7) 100%)',
              color: '#ffffff',
              padding: '12px 16px',
              borderRadius: '12px',
              fontSize: '13px',
              fontWeight: '600',
              boxShadow: '0 4px 16px rgba(255, 107, 107, 0.3)',
              border: '2px solid rgba(255, 255, 255, 0.2)',
              backdropFilter: 'blur(8px)',
              display: 'flex',
              alignItems: 'center',
              gap: '8px'
            }}>
              <span style={{ fontSize: '16px' }}>👤❌</span>
              <div>
                <div style={{ fontSize: '11px', opacity: 0.9 }}>Ocultar Sprite</div>
                <div style={{ fontSize: '13px', fontWeight: '700' }}>
                  {currentElement.character}
                </div>
              </div>
            </div>
          </GameScreen>
        );

      case 'protagonist_setup':
        return (
          <GameScreen background={getBackgroundImage(currentBackground)}>
            {renderBackground(currentBackground)}
            
            {/* Simulación del input de Ren'Py */}
            <div style={{
              position: 'absolute',
              bottom: '120px',
              left: '50%',
              transform: 'translateX(-50%)',
              width: '80%',
              maxWidth: '600px'
            }}>
              <div style={{
                background: 'linear-gradient(135deg, rgba(0, 0, 0, 0.8) 0%, rgba(0, 0, 0, 0.9) 100%)',
                padding: '20px',
                borderRadius: '8px',
                border: '2px solid rgba(255, 255, 255, 0.2)',
                boxShadow: '0 4px 20px rgba(0, 0, 0, 0.5)'
              }}>
                <div style={{
                  fontSize: '16px',
                  color: '#ffffff',
                  marginBottom: '16px',
                  textAlign: 'center',
                  fontWeight: '500'
                }}>
                  {state.project.protagonistConfig?.promptText || 'Por favor, introduce tu nombre:'}
                </div>
                
                <input
                  type="text"
                  placeholder={state.project.protagonistConfig?.defaultName || 'Jugador'}
                  style={{
                    width: '100%',
                    padding: '12px 16px',
                    borderRadius: '4px',
                    border: '2px solid #4a9eff',
                    backgroundColor: 'rgba(255, 255, 255, 0.95)',
                    color: '#333',
                    fontSize: '16px',
                    textAlign: 'center',
                    outline: 'none',
                    fontFamily: 'inherit',
                    transition: 'all 0.2s ease',
                    boxSizing: 'border-box'
                  }}
                  onFocus={(e) => {
                    e.target.style.borderColor = '#60a5fa';
                    e.target.style.backgroundColor = '#ffffff';
                    e.target.style.boxShadow = '0 0 10px rgba(74, 158, 255, 0.3)';
                  }}
                  onBlur={(e) => {
                    e.target.style.borderColor = '#4a9eff';
                    e.target.style.backgroundColor = 'rgba(255, 255, 255, 0.95)';
                    e.target.style.boxShadow = 'none';
                  }}
                />
              </div>
            </div>

            {/* Simulación del menú de confirmación de Ren'Py */}
            <div style={{
              position: 'absolute',
              bottom: '20px',
              left: '50%',
              transform: 'translateX(-50%)',
              width: '80%',
              maxWidth: '600px'
            }}>
              <div style={{
                background: 'linear-gradient(135deg, rgba(0, 0, 0, 0.9) 0%, rgba(0, 0, 0, 0.95) 100%)',
                padding: '16px',
                borderRadius: '8px',
                border: '2px solid rgba(255, 255, 255, 0.2)',
                boxShadow: '0 4px 20px rgba(0, 0, 0, 0.5)'
              }}>
                <div style={{
                  fontSize: '16px',
                  color: '#ffffff',
                  marginBottom: '12px',
                  textAlign: 'center',
                  fontWeight: '500'
                }}>
                  ¿Estás seguro?
                </div>
                
                <div style={{
                  display: 'flex',
                  gap: '12px',
                  justifyContent: 'center'
                }}>
                  <button
                    style={{
                      padding: '8px 20px',
                      borderRadius: '4px',
                      border: '2px solid #4a9eff',
                      backgroundColor: 'rgba(74, 158, 255, 0.2)',
                      color: '#ffffff',
                      fontSize: '14px',
                      cursor: 'pointer',
                      transition: 'all 0.2s ease',
                      fontWeight: '500'
                    }}
                    onMouseOver={(e) => {
                      e.target.style.backgroundColor = 'rgba(74, 158, 255, 0.4)';
                      e.target.style.borderColor = '#60a5fa';
                      e.target.style.transform = 'translateY(-1px)';
                    }}
                    onMouseOut={(e) => {
                      e.target.style.backgroundColor = 'rgba(74, 158, 255, 0.2)';
                      e.target.style.borderColor = '#4a9eff';
                      e.target.style.transform = 'translateY(0)';
                    }}
                  >
                    Sí, continuar.
                  </button>
                  <button
                    style={{
                      padding: '8px 20px',
                      borderRadius: '4px',
                      border: '2px solid #ff6b6b',
                      backgroundColor: 'rgba(255, 107, 107, 0.2)',
                      color: '#ffffff',
                      fontSize: '14px',
                      cursor: 'pointer',
                      transition: 'all 0.2s ease',
                      fontWeight: '500'
                    }}
                    onMouseOver={(e) => {
                      e.target.style.backgroundColor = 'rgba(255, 107, 107, 0.4)';
                      e.target.style.borderColor = '#ff8a8a';
                      e.target.style.transform = 'translateY(-1px)';
                    }}
                    onMouseOut={(e) => {
                      e.target.style.backgroundColor = 'rgba(255, 107, 107, 0.2)';
                      e.target.style.borderColor = '#ff6b6b';
                      e.target.style.transform = 'translateY(0)';
                    }}
                  >
                    No, quiero cambiar mi nombre.
                  </button>
                </div>
              </div>
            </div>
          </GameScreen>
        );

      default:
        return (
          <EmptyPreview>
            <EmptyIcon>📄</EmptyIcon>
            <EmptyText>Tipo de elemento no soportado</EmptyText>
            <EmptySubtext>{currentElement.type}</EmptySubtext>
          </EmptyPreview>
        );
    }
  };

  if (!state.previewMode) {
    return <PreviewContainer visible={false} />;
  }

  return (
    <PreviewContainer visible={state.previewMode}>
      <PreviewHeader>
        <PreviewTitle>
          <span style={{ fontSize: '18px' }}>{PREVIEW_CONFIG.icon}</span>
          {PREVIEW_CONFIG.title}
        </PreviewTitle>
        <PreviewControls>
          {PREVIEW_CONFIG.controls.map(control => (
          <ControlButton 
              key={control.id}
              primary={control.primary}
              onClick={() => {
                switch (control.action) {
                  case 'navigatePrevious':
                    handlePrevious();
                    break;
                  case 'navigateNext':
                    handleNext();
                    break;
                  default:
                    break;
                }
              }}
              disabled={
                (control.id === 'previous' && currentElementIndex === 0) ||
                (control.id === 'next' && currentElementIndex >= getPreviewElements().length - 1)
              }
              title={control.label}
            >
              <span style={{ fontSize: '14px' }}>
                {control.icon}
              </span>
              <span style={{ fontSize: '11px', fontWeight: '600' }}>
                {control.label}
              </span>
          </ControlButton>
          ))}
          <ControlButton 
            primary={isInteractive}
            onClick={() => setIsInteractive(!isInteractive)}
            title={isInteractive ? 'Desactivar modo interactivo' : 'Activar modo interactivo'}
          >
            <span style={{ fontSize: '14px' }}>
            {isInteractive ? '🖱️' : '👆'}
            </span>
            <span style={{ fontSize: '11px', fontWeight: '600' }}>
              {isInteractive ? 'Interactivo' : 'Estático'}
            </span>
          </ControlButton>
        </PreviewControls>
      </PreviewHeader>

      <PreviewContent 
        onClick={handlePreviewClick}
        style={{ 
          cursor: isInteractive ? 'pointer' : 'default',
          position: 'relative'
        }}
      >
        {renderPreviewContent()}
        {isInteractive && (
          <div style={{
            position: 'absolute',
            bottom: '10px',
            right: '10px',
            background: 'rgba(0, 0, 0, 0.7)',
            color: '#ffffff',
            padding: '5px 10px',
            borderRadius: '4px',
            fontSize: '12px',
            pointerEvents: 'none'
          }}>
            {currentElement?.type === 'choice' ? '🎯 Selecciona una opción' : '👆 Click para avanzar'}
          </div>
        )}
      </PreviewContent>

      <PreviewInfo>
        {(() => {
          const currentSceneFile = state.project.sceneFiles.find(
            file => file.id === state.project.currentSceneFile
          );
          const sceneFileName = currentSceneFile ? currentSceneFile.displayName : 'Sin archivo';
          return `${sceneFileName} • Elemento ${currentElementIndex + 1} de ${getPreviewElements().length}`;
        })()}
        {currentElement && ` • ${currentElement.type}`}
        {isInteractive && ` • 🖱️ Interactivo`}
        {memoryInfo && ` • 💾 ${memoryInfo.usedMB}MB`}
        {memoryInfo && (memoryInfo.usedJSHeapSize / memoryInfo.jsHeapSizeLimit) > 0.8 && (
          <button 
            onClick={emergencyCleanup}
            style={{
              marginLeft: '10px',
              padding: '4px 8px',
              backgroundColor: '#ff4444',
              color: 'white',
              border: 'none',
              borderRadius: '4px',
              fontSize: '10px',
              cursor: 'pointer'
            }}
            title="Limpieza de emergencia - Uso de memoria muy alto"
          >
            🧹 Limpiar
          </button>
        )}
        
        <button
          onClick={() => {
            console.log('=== DEBUG FONDOS ===');
            console.log('loadedBackgrounds:', loadedBackgrounds);
            console.log('currentBackground:', currentBackground);
            console.log('currentElement:', currentElement);
            console.log('state.project.sceneFiles:', state.project.sceneFiles);
            
            // Mostrar todas las escenas con fondos
            state.project.sceneFiles.forEach(sceneFile => {
              console.log(`Archivo: ${sceneFile.displayName}`);
              sceneFile.elements.forEach((element, index) => {
                if (element.type === 'scene' && element.background) {
                  console.log(`  Escena ${index}: ${element.background}`);
                }
              });
            });
          }}
          style={{
            backgroundColor: 'var(--color-info)',
            color: 'white',
            border: 'none',
            padding: '4px 8px',
            borderRadius: '4px',
            fontSize: '10px',
            cursor: 'pointer',
            marginLeft: '8px'
          }}
          title="Debug: Mostrar información de fondos en consola"
        >
          🐛 Debug
        </button>
        
        <button
          onClick={() => {
            // Crear una ventana modal para mostrar todas las imágenes de fondo
            const modal = document.createElement('div');
            modal.style.cssText = `
              position: fixed;
              top: 0;
              left: 0;
              width: 100%;
              height: 100%;
              background-color: rgba(0, 0, 0, 0.8);
              z-index: 10000;
              display: flex;
              flex-direction: column;
              align-items: center;
              padding: 20px;
              overflow-y: auto;
            `;
            
            const title = document.createElement('h2');
            title.textContent = 'Galería de Fondos Cargados';
            title.style.cssText = `
              color: white;
              margin-bottom: 20px;
              text-align: center;
            `;
            
            const closeBtn = document.createElement('button');
            closeBtn.textContent = 'Cerrar';
            closeBtn.style.cssText = `
              position: absolute;
              top: 20px;
              right: 20px;
              background-color: #ff4444;
              color: white;
              border: none;
              padding: 10px 20px;
              border-radius: 4px;
              cursor: pointer;
              font-size: 14px;
            `;
            closeBtn.onclick = () => modal.remove();
            
            const gallery = document.createElement('div');
            gallery.style.cssText = `
              display: grid;
              grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
              gap: 20px;
              max-width: 1200px;
              width: 100%;
            `;
            
            Object.entries(loadedBackgrounds).forEach(([path, url]) => {
              const item = document.createElement('div');
              item.style.cssText = `
                background-color: white;
                border-radius: 8px;
                padding: 10px;
                text-align: center;
              `;
              
              const img = document.createElement('img');
              img.src = url;
              img.style.cssText = `
                width: 100%;
                height: 150px;
                object-fit: cover;
                border-radius: 4px;
                margin-bottom: 10px;
              `;
              
              const label = document.createElement('div');
              label.textContent = path;
              label.style.cssText = `
                font-size: 12px;
                color: #333;
                word-break: break-all;
              `;
              
              item.appendChild(img);
              item.appendChild(label);
              gallery.appendChild(item);
            });
            
            modal.appendChild(title);
            modal.appendChild(closeBtn);
            modal.appendChild(gallery);
            document.body.appendChild(modal);
          }}
          style={{
            backgroundColor: 'var(--color-success)',
            color: 'white',
            border: 'none',
            padding: '4px 8px',
            borderRadius: '4px',
            fontSize: '10px',
            cursor: 'pointer',
            marginLeft: '8px'
          }}
          title="Ver galería de todas las imágenes de fondo cargadas"
        >
          🖼️ Galería
        </button>
      </PreviewInfo>
    </PreviewContainer>
  );
}

export default PreviewPanel;
