// Función para obtener el tema actual
export const getCurrentTheme = (themeId) => {
  return THEMES[themeId] || THEMES['dark-default'];
};

// Función para aplicar el tema al documento
export const applyTheme = (themeId) => {
  const theme = getCurrentTheme(themeId);
  const root = document.documentElement;
  
  // Aplicar variables CSS del tema
  Object.entries(theme.colors).forEach(([key, value]) => {
    root.style.setProperty(`--color-${key}`, value);
  });
  
  Object.entries(theme.typography).forEach(([key, value]) => {
    if (typeof value === 'object') {
      Object.entries(value).forEach(([subKey, subValue]) => {
        root.style.setProperty(`--font-${key}-${subKey}`, subValue);
      });
    } else {
      root.style.setProperty(`--font-${key}`, value);
    }
  });
  
  Object.entries(theme.spacing).forEach(([key, value]) => {
    root.style.setProperty(`--spacing-${key}`, value);
  });
  
  Object.entries(theme.borderRadius).forEach(([key, value]) => {
    root.style.setProperty(`--radius-${key}`, value);
  });
  
  // Aplicar clase del tema al body
  document.body.className = `theme-${theme.type}`;
  
  // Guardar tema en localStorage
  localStorage.setItem('editor-theme', themeId);
};

// Función para obtener el tema guardado
export const getSavedTheme = () => {
  return localStorage.getItem('editor-theme') || 'dark-default';
};

// Función para inicializar el tema
export const initializeTheme = () => {
  const savedTheme = getSavedTheme();
  applyTheme(savedTheme);
  return savedTheme;
};
