function DecisionMapPanel() {
  const { state } = useVisualNovel();
  const [selectedDecision, setSelectedDecision] = useState(null);
  const [filter, setFilter] = useState('all'); // 'all', 'withJumps', 'withoutJumps'
  const [showScrollHint, setShowScrollHint] = useState(false);
  const [isScrolling, setIsScrolling] = useState(false);
  const [showGuide, setShowGuide] = useState(false);
  const panelRef = useRef(null);

  // Obtener todas las decisiones del proyecto
  const decisions = useMemo(() => {
    const allDecisions = [];
    
    // Buscar en todos los archivos de escenas
    state.project.sceneFiles.forEach(sceneFile => {
      sceneFile.elements.forEach(element => {
        if (element.type === 'choice') {
          allDecisions.push({
            ...element,
            sceneFile: sceneFile.displayName,
            sceneFileId: sceneFile.id
          });
        }
      });
    });

    return allDecisions;
  }, [state.project.sceneFiles]);

  // Obtener todas las etiquetas (labels) del proyecto
  const labels = useMemo(() => {
    const allLabels = [];
    
    // Agregar labels manuales
    state.project.sceneFiles.forEach(sceneFile => {
      sceneFile.elements.forEach(element => {
        if (element.type === 'label') {
          allLabels.push({
            name: element.label || element.name,
            type: 'manual',
            sceneFile: sceneFile.displayName
          });
        }
      });
    });

    // Agregar IDs de archivos de escena como labels automáticos
    state.project.sceneFiles.forEach(sceneFile => {
      if (!sceneFile.isMain) { // No incluir el archivo principal
        allLabels.push({
          name: sceneFile.id,
          type: 'sceneFile',
          sceneFile: sceneFile.displayName
        });
      }
    });

    return allLabels;
  }, [state.project.sceneFiles]);

  // Filtrar decisiones
  const filteredDecisions = useMemo(() => {
    switch (filter) {
      case 'withJumps':
        return decisions.filter(decision => 
          decision.choices?.some(choice => choice.jump && choice.jump.trim())
        );
      case 'withoutJumps':
        return decisions.filter(decision => 
          !decision.choices?.some(choice => choice.jump && choice.jump.trim())
        );
      default:
        return decisions;
    }
  }, [decisions, filter]);

  // Estadísticas
  const stats = useMemo(() => {
    const totalDecisions = decisions.length;
    const decisionsWithJumps = decisions.filter(decision => 
      decision.choices?.some(choice => choice.jump && choice.jump.trim())
    ).length;
    const totalJumps = decisions.reduce((count, decision) => 
      count + (decision.choices?.filter(choice => choice.jump && choice.jump.trim()).length || 0), 0
    );
    const orphanJumps = decisions.reduce((count, decision) => 
      count + (decision.choices?.filter(choice => 
        choice.jump && choice.jump.trim() && !labels.includes(choice.jump)
      ).length || 0), 0
    );

    return {
      totalDecisions,
      decisionsWithJumps,
      totalJumps,
      orphanJumps
    };
  }, [decisions, labels]);

  const handleDecisionClick = (decision) => {
    setSelectedDecision(selectedDecision?.id === decision.id ? null : decision);
  };

  const checkJumpExists = (jumpLabel) => {
    // Verificar si existe en la lista de labels (manuales o automáticos)
    const labelExists = labels.some(label => 
      label.name === jumpLabel
    );
    
    if (labelExists) {
      console.log(`✅ Jump "${jumpLabel}" válido`);
      return true;
    }
    
    return false;
  };

  // Función para encontrar la ubicación de un label
  const findLabelLocation = (labelName) => {
    // Buscar como label manual primero
    for (const sceneFile of state.project.sceneFiles) {
      const labelElement = sceneFile.elements.find(element => 
        element.type === 'label' && element.label === labelName
      );
      if (labelElement) {
        return {
          sceneFile: sceneFile.displayName,
          sceneFileId: sceneFile.id,
          exists: true,
          type: 'manual'
        };
      }
    }
    
    // Buscar como ID de archivo de escena
    const sceneFile = state.project.sceneFiles.find(file => file.id === labelName);
    if (sceneFile) {
      console.log(`📍 Label "${labelName}" encontrado como archivo de escena: ${sceneFile.displayName}`);
      return {
        sceneFile: sceneFile.displayName,
        sceneFileId: sceneFile.id,
        exists: true,
        type: 'sceneFile'
      };
    }
    
    return { exists: false };
  };

  // Efecto para mostrar hint de scroll cuando se carga el componente
  useEffect(() => {
    if (decisions.length > 0) {
      const timer = setTimeout(() => {
        setShowScrollHint(true);
      }, 1000);
      
      const hideTimer = setTimeout(() => {
        setShowScrollHint(false);
      }, 4000);
      
      return () => {
        clearTimeout(timer);
        clearTimeout(hideTimer);
      };
    }
  }, [decisions.length]);

  // Efecto para manejar el scroll del panel principal
  useEffect(() => {
    const panelElement = panelRef.current;
    if (!panelElement) return;

    const handleWheel = (e) => {
      e.preventDefault();
      
      const scrollAmount = e.deltaY * 4.0;
      panelElement.scrollBy({
        top: scrollAmount,
        behavior: 'smooth'
      });
      
      setIsScrolling(true);
      clearTimeout(window.scrollTimeout);
      window.scrollTimeout = setTimeout(() => {
        setIsScrolling(false);
      }, 200);
    };

    const handleScroll = (e) => {
      setIsScrolling(true);
      clearTimeout(window.scrollTimeout);
      window.scrollTimeout = setTimeout(() => {
        setIsScrolling(false);
      }, 200);
    };

    const handleKeyDown = (e) => {
      if (e.key === 'ArrowDown' || e.key === 'ArrowUp') {
        e.preventDefault();
        
        const scrollAmount = e.key === 'ArrowDown' ? 300 : -300;
        panelElement.scrollBy({
          top: scrollAmount,
          behavior: 'smooth'
        });
        
        setIsScrolling(true);
        clearTimeout(window.scrollTimeout);
        window.scrollTimeout = setTimeout(() => {
          setIsScrolling(false);
        }, 200);
      }
      
      if (e.key === 'PageDown' || e.key === 'PageUp') {
        e.preventDefault();
        
        const scrollAmount = e.key === 'PageDown' ? 600 : -600;
        panelElement.scrollBy({
          top: scrollAmount,
          behavior: 'smooth'
        });
        
        setIsScrolling(true);
        clearTimeout(window.scrollTimeout);
        window.scrollTimeout = setTimeout(() => {
          setIsScrolling(false);
        }, 200);
      }
      
      if (e.key === 'Home') {
        e.preventDefault();
        panelElement.scrollTo({
          top: 0,
          behavior: 'smooth'
        });
        setIsScrolling(true);
        clearTimeout(window.scrollTimeout);
        window.scrollTimeout = setTimeout(() => {
          setIsScrolling(false);
        }, 200);
      }
      
      if (e.key === 'End') {
        e.preventDefault();
        panelElement.scrollTo({
          top: panelElement.scrollHeight,
          behavior: 'smooth'
        });
        setIsScrolling(true);
        clearTimeout(window.scrollTimeout);
        window.scrollTimeout = setTimeout(() => {
          setIsScrolling(false);
        }, 200);
      }
      
      if (e.key === ' ') {
        e.preventDefault();
        panelElement.scrollBy({
          top: 400,
          behavior: 'smooth'
        });
        setIsScrolling(true);
        clearTimeout(window.scrollTimeout);
        window.scrollTimeout = setTimeout(() => {
          setIsScrolling(false);
        }, 200);
      }
      
      if (e.key === ' ' && e.shiftKey) {
        e.preventDefault();
        panelElement.scrollBy({
          top: -400,
          behavior: 'smooth'
        });
        setIsScrolling(true);
        clearTimeout(window.scrollTimeout);
        window.scrollTimeout = setTimeout(() => {
          setIsScrolling(false);
        }, 200);
      }
    };

    panelElement.addEventListener('wheel', handleWheel, { passive: false });
    panelElement.addEventListener('scroll', handleScroll);
    panelElement.addEventListener('keydown', handleKeyDown);
    
    panelElement.setAttribute('tabindex', '0');
    
    return () => {
      panelElement.removeEventListener('wheel', handleWheel);
      panelElement.removeEventListener('scroll', handleScroll);
      panelElement.removeEventListener('keydown', handleKeyDown);
      clearTimeout(window.scrollTimeout);
    };
  }, []);

  // Función para renderizar la guía del mapa de decisiones
  const renderDecisionMapGuide = () => (
    <GuideSection>
      <GuideTitle>
        🗺️ Guía del Mapa de Decisiones
      </GuideTitle>
      <GuideText>
        El Mapa de Decisiones te ayuda a visualizar y gestionar todas las decisiones de tu novela visual:
      </GuideText>
      <GuideList>
        <GuideListItem>
          <strong>Estadísticas:</strong> Ve el resumen general de tus decisiones y jumps
        </GuideListItem>
        <GuideListItem>
          <strong>Labels Disponibles:</strong> Lista de todos los puntos de salto definidos
        </GuideListItem>
        <GuideListItem>
          <strong>Filtros:</strong> Organiza las decisiones por tipo (Todas, Con Jumps, Sin Jumps)
        </GuideListItem>
        <GuideListItem>
          <strong>Validación:</strong> Los jumps huérfanos se marcan en rojo (❌)
        </GuideListItem>
        <GuideListItem>
          <strong>Navegación:</strong> Haz clic en cualquier decisión para expandirla
        </GuideListItem>
      </GuideList>
      <GuideText>
        <strong>💡 Consejos:</strong> Revisa regularmente los jumps huérfanos para mantener la coherencia narrativa.
      </GuideText>
    </GuideSection>
  );

  if (decisions.length === 0) {
    return (
      <PanelContainer ref={panelRef}>
        <PanelTitle>
          🗺️ Mapa de Decisiones
        </PanelTitle>
        <EmptyState>
          <EmptyIcon>🎯</EmptyIcon>
          <EmptyText>No hay decisiones creadas</EmptyText>
          <EmptySubtext>Crea elementos de elección en el script para ver el mapa de decisiones</EmptySubtext>
        </EmptyState>
        {renderDecisionMapGuide()}
      </PanelContainer>
    );
  }

  return (
    <PanelContainer ref={panelRef}>
      <ScrollHint visible={showScrollHint}>
        📜 Mouse: rueda | Teclado: ↑↓, Page Up/Down, Home/End, Espacio
      </ScrollHint>
      
      <PanelTitle>
        🗺️ Mapa de Decisiones
        <div style={{ display: 'flex', gap: '8px', marginLeft: 'auto' }}>
          <button
            onClick={() => setShowGuide(!showGuide)}
            style={{
              background: 'linear-gradient(135deg, var(--color-primary) 0%, var(--color-primaryHover) 100%)',
              color: 'white',
              border: '2px solid var(--color-primary)',
              padding: '8px 12px',
              borderRadius: '8px',
              cursor: 'pointer',
              fontSize: '12px',
              fontWeight: '600',
              transition: 'all 0.3s ease'
            }}
          >
            {showGuide ? '❌ Ocultar Guía' : '❓ Ver Guía'}
          </button>
        </div>
      </PanelTitle>

      {showGuide && renderDecisionMapGuide()}

      <StatsContainer>
        <StatItem>
          <StatNumber>{stats.totalDecisions}</StatNumber>
          <StatLabel>Decisiones</StatLabel>
        </StatItem>
        <StatItem>
          <StatNumber>{stats.decisionsWithJumps}</StatNumber>
          <StatLabel>Con Jumps</StatLabel>
        </StatItem>
        <StatItem>
          <StatNumber>{stats.totalJumps}</StatNumber>
          <StatLabel>Total Jumps</StatLabel>
        </StatItem>
        <StatItem>
          <StatNumber style={{ color: stats.orphanJumps > 0 ? '#ff6b6b' : '#4a9eff' }}>
            {stats.orphanJumps}
          </StatNumber>
          <StatLabel>Jumps Huérfanos</StatLabel>
        </StatItem>
      </StatsContainer>

      {labels.length > 0 && (
        <LabelsSection>
          <LabelsTitle>
            🏷️ Labels Disponibles ({labels.length})
          </LabelsTitle>
          <LabelsList>
            {labels.map((label, index) => {
              return (
                <LabelTag key={index}>
                  {label.name}
                  <LabelLocation>
                    {label.sceneFile} 
                    {label.type === 'sceneFile' && ' (Archivo)'}
                  </LabelLocation>
                </LabelTag>
              );
            })}
          </LabelsList>
        </LabelsSection>
      )}

      <FilterContainer>
        <FilterButton 
          active={filter === 'all'} 
          onClick={() => setFilter('all')}
        >
          Todas ({decisions.length})
        </FilterButton>
        <FilterButton 
          active={filter === 'withJumps'} 
          onClick={() => setFilter('withJumps')}
        >
          Con Jumps ({stats.decisionsWithJumps})
        </FilterButton>
        <FilterButton 
          active={filter === 'withoutJumps'} 
          onClick={() => setFilter('withoutJumps')}
        >
          Sin Jumps ({stats.totalDecisions - stats.decisionsWithJumps})
        </FilterButton>
      </FilterContainer>

      {filteredDecisions.map((decision, index) => (
        <DecisionNode
          key={decision.id}
          isSelected={selectedDecision?.id === decision.id}
          onClick={() => handleDecisionClick(decision)}
        >
          <DecisionHeader>
            <DecisionTitle>
              Decisión #{index + 1} - {decision.sceneFile}
            </DecisionTitle>
            <DecisionType>CHOICE</DecisionType>
          </DecisionHeader>

          {decision.text && (
            <DecisionQuestion>
              "{decision.text}"
            </DecisionQuestion>
          )}

          <ChoicesList>
            {decision.choices?.map((choice, choiceIndex) => {
              const jumpExists = checkJumpExists(choice.jump);
              const labelLocation = findLabelLocation(choice.jump);
              
              return (
                <div key={choice.id || choiceIndex}>
                  <ChoiceItem>
                    <ChoiceText>
                      {choiceIndex + 1}. {choice.text}
                    </ChoiceText>
                    {choice.condition && (
                      <ConditionLabel>
                        🔀 Si: {choice.condition}
                      </ConditionLabel>
                    )}
                    <JumpLabel exists={jumpExists}>
                      {choice.jump || 'Sin jump'}
                    </JumpLabel>
                  </ChoiceItem>
                  
                  {choice.jump && (
                    <JumpDetails>
                      <JumpDestination exists={jumpExists}>
                        {jumpExists ? `→ ${choice.jump}` : `→ ${choice.jump} (NO ENCONTRADO)`}
                      </JumpDestination>
                      {jumpExists && (
                        <JumpLocation>
                          📁 Ubicación: {labelLocation.sceneFile}
                        </JumpLocation>
                      )}
                    </JumpDetails>
                  )}
                </div>
              );
            })}
          </ChoicesList>
        </DecisionNode>
      ))}
      
      <ScrollIndicator visible={isScrolling}>
        🖱️ Navegando por el mapa...
      </ScrollIndicator>
    </PanelContainer>
  );
}

export default DecisionMapPanel;
