import { useState, useEffect, useCallback } from 'react';
import { collaborationClient } from '../utils/collaborationClient';

// Hook para manejar la colaboración en tiempo real
export function useCollaboration() {
  const [isConnected, setIsConnected] = useState(false);
  const [roomCode, setRoomCode] = useState(null);
  const [isHost, setIsHost] = useState(false);
  const [users, setUsers] = useState([]);
  const [cursorPositions, setCursorPositions] = useState(new Map());

  // Conectar al servidor
  const connect = useCallback(async (serverUrl) => {
    try {
      await collaborationClient.connect(serverUrl);
      setIsConnected(true);
      return true;
    } catch (error) {
      console.error('Error conectando:', error);
      return false;
    }
  }, []);

  // Desconectar
  const disconnect = useCallback(() => {
    collaborationClient.disconnect();
    setIsConnected(false);
    setRoomCode(null);
    setIsHost(false);
    setUsers([]);
    setCursorPositions(new Map());
  }, []);

  // Crear sala
  const createRoom = useCallback((projectData, username) => {
    collaborationClient.createRoom(projectData, username);
  }, []);

  // Unirse a sala
  const joinRoom = useCallback((roomCode, username) => {
    collaborationClient.joinRoom(roomCode, username);
  }, []);

  // Abandonar sala
  const leaveRoom = useCallback(() => {
    collaborationClient.leaveRoom();
    setRoomCode(null);
    setIsHost(false);
    setUsers([]);
    setCursorPositions(new Map());
  }, []);

  // Enviar operación de sincronización
  const syncOperation = useCallback((operation, data) => {
    collaborationClient.sendSyncOperation(operation, data);
  }, []);

  // Actualizar cursor
  const updateCursor = useCallback((cursor) => {
    collaborationClient.updateCursor(cursor);
  }, []);

  // Enviar mensaje de chat
  const sendChatMessage = useCallback((message) => {
    collaborationClient.sendChatMessage(message);
  }, []);

  // Actualizar nombre de usuario
  const updateUsername = useCallback((username) => {
    collaborationClient.updateUsername(username);
  }, []);

  // Configurar event listeners
  useEffect(() => {
    const handleRoomCreated = (data) => {
      setRoomCode(data.roomCode);
      setIsHost(true);
    };

    const handleRoomJoined = (data) => {
      setRoomCode(data.roomCode);
      setIsHost(false);
      setUsers(data.users || []);
    };

    const handleUserJoined = (data) => {
      setUsers(prev => [...prev, data.user]);
    };

    const handleUserLeft = (data) => {
      setUsers(prev => prev.filter(user => user.id !== data.user.id));
      setCursorPositions(prev => {
        const newMap = new Map(prev);
        newMap.delete(data.user.id);
        return newMap;
      });
    };

    const handleCursorUpdate = (data) => {
      setCursorPositions(prev => {
        const newMap = new Map(prev);
        newMap.set(data.userId, {
          ...data.cursor,
          username: data.username
        });
        return newMap;
      });
    };

    const handleHostTransferred = (data) => {
      if (data.newHost.id === collaborationClient.getUserId()) {
        setIsHost(true);
      }
    };

    const handleDisconnected = () => {
      setIsConnected(false);
      setRoomCode(null);
      setIsHost(false);
      setUsers([]);
      setCursorPositions(new Map());
    };

    // Registrar event listeners
    collaborationClient.on('roomCreated', handleRoomCreated);
    collaborationClient.on('roomJoined', handleRoomJoined);
    collaborationClient.on('userJoined', handleUserJoined);
    collaborationClient.on('userLeft', handleUserLeft);
    collaborationClient.on('cursorUpdate', handleCursorUpdate);
    collaborationClient.on('hostTransferred', handleHostTransferred);
    collaborationClient.on('disconnected', handleDisconnected);

    // Cleanup
    return () => {
      collaborationClient.off('roomCreated', handleRoomCreated);
      collaborationClient.off('roomJoined', handleRoomJoined);
      collaborationClient.off('userJoined', handleUserJoined);
      collaborationClient.off('userLeft', handleUserLeft);
      collaborationClient.off('cursorUpdate', handleCursorUpdate);
      collaborationClient.off('hostTransferred', handleHostTransferred);
      collaborationClient.off('disconnected', handleDisconnected);
    };
  }, []);

  return {
    // Estado
    isConnected,
    roomCode,
    isHost,
    users,
    cursorPositions: Array.from(cursorPositions.values()),
    
    // Acciones
    connect,
    disconnect,
    createRoom,
    joinRoom,
    leaveRoom,
    syncOperation,
    updateCursor,
    sendChatMessage,
    updateUsername,
    
    // Utilidades
    getUserId: () => collaborationClient.getUserId(),
    getServerIP: () => collaborationClient.getServerIP()
  };
}
