import React, { createContext, useContext, useState } from 'react';
import styled, { keyframes, css } from 'styled-components';

// Animaciones
const fadeIn = keyframes`
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
`;

const slideIn = keyframes`
  from {
    opacity: 0;
    transform: translateY(-20px) scale(0.95);
  }
  to {
    opacity: 1;
    transform: translateY(0) scale(1);
  }
`;

// Styled Components
const ModalOverlay = styled.div`
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.6);
  backdrop-filter: blur(8px);
  z-index: 10000;
  display: flex;
  align-items: center;
  justify-content: center;
  animation: ${css`${fadeIn} 0.3s ease-out`};
`;

const ModalWindow = styled.div`
  background: #2a2a2a;
  border: 2px solid var(--color-primary);
  border-radius: 16px;
  box-shadow: 0 20px 40px rgba(0, 0, 0, 0.5);
  padding: 0;
  max-width: 500px;
  width: 90%;
  max-height: 80vh;
  overflow: hidden;
  animation: ${css`${slideIn} 0.4s ease-out`};
  position: relative;
`;

const ModalHeader = styled.div`
  background: linear-gradient(135deg, var(--color-primary) 0%, var(--color-primaryHover) 100%);
  color: white;
  padding: 20px 24px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-bottom: 2px solid var(--color-primary);
`;

const ModalTitle = styled.h3`
  margin: 0;
  font-size: 18px;
  font-weight: 700;
  color: #ffffff;
  text-shadow: 0 2px 8px rgba(0, 0, 0, 0.8);
  display: flex;
  align-items: center;
  gap: 8px;
`;

const ModalCloseButton = styled.button`
  background: rgba(255, 255, 255, 0.2);
  border: none;
  color: white;
  width: 32px;
  height: 32px;
  border-radius: 50%;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 18px;
  font-weight: bold;
  transition: all 0.3s ease;

  &:hover {
    background: rgba(255, 255, 255, 0.3);
    transform: scale(1.1);
  }
`;

const ModalContent = styled.div`
  padding: 24px;
  color: var(--color-textPrimary);
  line-height: 1.6;
  white-space: pre-line;
`;

const ModalActions = styled.div`
  display: flex;
  gap: 12px;
  justify-content: flex-end;
  padding: 20px 24px;
  border-top: 1px solid var(--color-border);
  background: #2a2a2a;
`;

const ModalButton = styled.button`
  padding: 12px 24px;
  border: 2px solid ${props => props.primary ? 'var(--color-primary)' : 'var(--color-border)'};
  border-radius: 8px;
  background: ${props => props.primary 
    ? 'linear-gradient(135deg, var(--color-primary) 0%, var(--color-primaryHover) 100%)' 
    : props.danger
    ? 'linear-gradient(135deg, var(--color-danger) 0%, var(--color-dangerHover) 100%)'
    : 'var(--color-surface)'};
  color: ${props => props.primary || props.danger ? 'white' : 'var(--color-textPrimary)'};
  cursor: pointer;
  font-weight: 600;
  transition: all 0.3s ease;
  font-size: 14px;
  min-width: 80px;

  &:hover {
    transform: translateY(-2px);
    box-shadow: 0 6px 16px ${props => props.primary 
      ? 'rgba(74, 158, 255, 0.3)' 
      : props.danger
      ? 'rgba(255, 99, 99, 0.3)'
      : 'rgba(0, 0, 0, 0.1)'};
  }

  &:active {
    transform: translateY(-1px);
  }

  &:disabled {
    opacity: 0.5;
    cursor: not-allowed;
    transform: none;
    box-shadow: none;
  }
`;

const ModalInput = styled.input`
  width: 100%;
  padding: 12px 16px;
  border: 2px solid var(--color-border);
  border-radius: 8px;
  background: var(--color-surface);
  color: var(--color-textPrimary);
  font-size: 14px;
  margin-top: 12px;
  transition: all 0.3s ease;

  &:focus {
    outline: none;
    border-color: var(--color-primary);
    box-shadow: 0 0 0 3px rgba(74, 158, 255, 0.1);
  }

  &::placeholder {
    color: var(--color-textSecondary);
  }
`;

const ModalTextarea = styled.textarea`
  width: 100%;
  padding: 12px 16px;
  border: 2px solid var(--color-border);
  border-radius: 8px;
  background: var(--color-surface);
  color: var(--color-textPrimary);
  font-size: 14px;
  margin-top: 12px;
  min-height: 100px;
  resize: vertical;
  font-family: inherit;
  transition: all 0.3s ease;

  &:focus {
    outline: none;
    border-color: var(--color-primary);
    box-shadow: 0 0 0 3px rgba(74, 158, 255, 0.1);
  }

  &::placeholder {
    color: var(--color-textSecondary);
  }
`;

const ModalContext = createContext();

export const useModalContext = () => {
  const context = useContext(ModalContext);
  if (!context) {
    throw new Error('useModalContext debe ser usado dentro de un ModalProvider');
  }
  return context;
};

// Componente de Modal
const Modal = ({ isOpen, onClose, title, children, icon = null }) => {
  React.useEffect(() => {
    const handleEscape = (e) => {
      if (e.key === 'Escape' && isOpen) {
        onClose();
      }
    };

    if (isOpen) {
      document.addEventListener('keydown', handleEscape);
      document.body.style.overflow = 'hidden';
    }

    return () => {
      document.removeEventListener('keydown', handleEscape);
      document.body.style.overflow = 'unset';
    };
  }, [isOpen, onClose]);

  if (!isOpen) return null;

  return (
    <ModalOverlay onClick={onClose}>
      <ModalWindow onClick={(e) => e.stopPropagation()}>
        <ModalHeader>
          <ModalTitle>
            {icon && <span>{icon}</span>}
            {title}
          </ModalTitle>
          <ModalCloseButton onClick={onClose}>
            ×
          </ModalCloseButton>
        </ModalHeader>
        {children}
      </ModalWindow>
    </ModalOverlay>
  );
};

// Componente para Prompt Modal
const PromptModal = ({ modal }) => {
  const [value, setValue] = useState(modal.defaultValue);
  const inputRef = React.useRef(null);

  React.useEffect(() => {
    if (inputRef.current) {
      inputRef.current.focus();
      inputRef.current.select();
    }
  }, []);

  const handleSubmit = () => {
    modal.onClose(value);
  };

  const handleKeyPress = (e) => {
    if (e.key === 'Enter' && !modal.multiline) {
      handleSubmit();
    }
  };

  return (
    <Modal
      isOpen={true}
      onClose={() => modal.onClose(null)}
      title={modal.title}
      icon={modal.icon}
    >
      <ModalContent>
        {modal.message}
        {modal.multiline ? (
          <ModalTextarea
            ref={inputRef}
            value={value}
            onChange={(e) => setValue(e.target.value)}
            onKeyPress={handleKeyPress}
            placeholder="Escribe tu respuesta aquí..."
          />
        ) : (
          <ModalInput
            ref={inputRef}
            type="text"
            value={value}
            onChange={(e) => setValue(e.target.value)}
            onKeyPress={handleKeyPress}
            placeholder="Escribe tu respuesta aquí..."
          />
        )}
      </ModalContent>
      <ModalActions>
        <ModalButton onClick={() => modal.onClose(null)}>
          Cancelar
        </ModalButton>
        <ModalButton primary onClick={handleSubmit}>
          Aceptar
        </ModalButton>
      </ModalActions>
    </Modal>
  );
};

export const ModalProvider = ({ children }) => {
  const [modals, setModals] = useState([]);

  const showAlert = (message, title = 'Información', icon = 'ℹ️') => {
    return new Promise((resolve) => {
      const modalId = Date.now() + Math.random();
      const modal = {
        id: modalId,
        type: 'alert',
        title,
        message,
        icon,
        onClose: () => {
          setModals(prev => prev.filter(m => m.id !== modalId));
          resolve();
        }
      };
      setModals(prev => [...prev, modal]);
    });
  };

  const showConfirm = (message, title = 'Confirmar', icon = '❓') => {
    return new Promise((resolve) => {
      const modalId = Date.now() + Math.random();
      const modal = {
        id: modalId,
        type: 'confirm',
        title,
        message,
        icon,
        onClose: (result) => {
          setModals(prev => prev.filter(m => m.id !== modalId));
          resolve(result);
        }
      };
      setModals(prev => [...prev, modal]);
    });
  };

  const showPrompt = (message, defaultValue = '', title = 'Entrada', icon = '✏️', multiline = false) => {
    return new Promise((resolve) => {
      const modalId = Date.now() + Math.random();
      const modal = {
        id: modalId,
        type: 'prompt',
        title,
        message,
        defaultValue,
        icon,
        multiline,
        onClose: (result) => {
          setModals(prev => prev.filter(m => m.id !== modalId));
          resolve(result);
        }
      };
      setModals(prev => [...prev, modal]);
    });
  };

  const renderModals = () => {
    return modals.map(modal => {
      switch (modal.type) {
        case 'alert':
          return (
            <Modal
              key={modal.id}
              isOpen={true}
              onClose={modal.onClose}
              title={modal.title}
              icon={modal.icon}
            >
              <ModalContent>
                {modal.message}
              </ModalContent>
              <ModalActions>
                <ModalButton primary onClick={() => modal.onClose()}>
                  Aceptar
                </ModalButton>
              </ModalActions>
            </Modal>
          );

        case 'confirm':
          return (
            <Modal
              key={modal.id}
              isOpen={true}
              onClose={() => modal.onClose(false)}
              title={modal.title}
              icon={modal.icon}
            >
              <ModalContent>
                {modal.message}
              </ModalContent>
              <ModalActions>
                <ModalButton onClick={() => modal.onClose(false)}>
                  Cancelar
                </ModalButton>
                <ModalButton primary onClick={() => modal.onClose(true)}>
                  Confirmar
                </ModalButton>
              </ModalActions>
            </Modal>
          );

        case 'prompt':
          return (
            <PromptModal
              key={modal.id}
              modal={modal}
            />
          );

        default:
          return null;
      }
    });
  };

  const modalContextValue = {
    showAlert,
    showConfirm,
    showPrompt,
    renderModals
  };

  return (
    <ModalContext.Provider value={modalContextValue}>
      {children}
      {renderModals()}
    </ModalContext.Provider>
  );
};
