import React, { useState } from 'react';
import styled from 'styled-components';
import { useVisualNovel } from '../context/VisualNovelContext';

const TemplatesContainer = styled.div`
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: var(--color-background);
  z-index: 3000;
  overflow-y: auto;
  transition: background-color 0.3s ease;
`;

const TemplatesHeader = styled.div`
  background-color: var(--color-surface);
  padding: 20px;
  border-bottom: 2px solid var(--color-border);
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: sticky;
  top: 0;
  z-index: 3001;
  transition: background-color 0.3s ease, border-color 0.3s ease;
`;

const TemplatesTitle = styled.h1`
  margin: 0;
  color: var(--color-primary);
  font-size: 28px;
  display: flex;
  align-items: center;
  gap: 15px;
  transition: color 0.3s ease;
`;

const CloseButton = styled.button`
  background-color: var(--color-error);
  color: var(--color-textInverse);
  border: none;
  border-radius: 8px;
  padding: 12px 20px;
  cursor: pointer;
  font-size: 16px;
  font-weight: bold;
  transition: all 0.3s ease;

  &:hover {
    background-color: var(--color-errorHover);
    transform: scale(1.05);
  }
`;

const TemplatesContent = styled.div`
  padding: 30px;
  max-width: 1200px;
  margin: 0 auto;
`;

const CategoryTabs = styled.div`
  display: flex;
  gap: 10px;
  margin-bottom: 30px;
  flex-wrap: wrap;
`;

const CategoryTab = styled.button`
  background-color: ${props => props.active ? 'var(--color-primary)' : 'var(--color-surfaceSecondary)'};
  color: ${props => props.active ? 'var(--color-textInverse)' : 'var(--color-textPrimary)'};
  border: 2px solid ${props => props.active ? 'var(--color-primary)' : 'var(--color-border)'};
  padding: 14px 22px;
  border-radius: 8px;
  cursor: pointer;
  font-size: 14px;
  font-weight: 700;
  transition: all 0.3s ease;
  box-shadow: 0 3px 8px var(--color-shadow);

  &:hover {
    background-color: ${props => props.active ? 'var(--color-primaryHover)' : 'var(--color-border)'};
    border-color: ${props => props.active ? 'var(--color-primaryHover)' : 'var(--color-primary)'};
    color: var(--color-textInverse);
    transform: translateY(-3px);
    box-shadow: 0 6px 16px var(--color-shadowHover);
  }

  &:active {
    transform: translateY(-1px);
    box-shadow: 0 4px 12px var(--color-shadowHover);
  }
`;

const TemplatesGrid = styled.div`
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(350px, 1fr));
  gap: 25px;
  margin-bottom: 40px;
`;

const TemplateCard = styled.div`
  background-color: var(--color-surfaceSecondary);
  border: 2px solid var(--color-border);
  border-radius: 12px;
  padding: 24px;
  transition: all 0.3s ease;
  box-shadow: 0 4px 12px var(--color-shadow);

  &:hover {
    border-color: var(--color-primary);
    background-color: var(--color-surfaceElevated);
    transform: translateY(-8px);
    box-shadow: 0 12px 32px var(--color-shadowHover);
  }
`;

const TemplateHeader = styled.div`
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  margin-bottom: 15px;
`;

const TemplateTitle = styled.h3`
  margin: 0;
  color: var(--color-primary);
  font-size: 18px;
  font-weight: 700;
  display: flex;
  align-items: center;
  gap: 10px;
  transition: color 0.3s ease;
  text-shadow: 0 1px 2px var(--color-shadow);
`;

const TemplateBadge = styled.span`
  background-color: var(--color-success);
  color: var(--color-textInverse);
  padding: 6px 12px;
  border-radius: 6px;
  font-size: 12px;
  font-weight: 700;
  transition: all 0.3s ease;
  box-shadow: 0 2px 6px var(--color-shadow);
  border: 1px solid rgba(255, 255, 255, 0.1);

  &:hover {
    transform: translateY(-1px);
    box-shadow: 0 4px 8px var(--color-shadowHover);
  }
`;

const TemplateDescription = styled.p`
  color: var(--color-textSecondary);
  margin: 0 0 15px 0;
  line-height: 1.6;
  font-size: 15px;
  font-weight: 500;
  transition: color 0.3s ease;
`;

const CodePreview = styled.div`
  background-color: var(--color-backgroundSecondary);
  border: 2px solid var(--color-border);
  border-radius: 12px;
  padding: 20px;
  margin: 20px 0;
  font-family: 'Courier New', monospace;
  font-size: 13px;
  font-weight: 500;
  color: var(--color-primary);
  overflow-x: auto;
  position: relative;
  transition: all 0.3s ease;
  box-shadow: 0 4px 12px var(--color-shadow);

  &:hover {
    border-color: var(--color-primary);
    box-shadow: 0 6px 16px var(--color-shadowHover);
  }
`;

const CodeLabel = styled.div`
  position: absolute;
  top: -12px;
  left: 20px;
  background-color: var(--color-surfaceSecondary);
  color: var(--color-textTertiary);
  padding: 4px 10px;
  font-size: 11px;
  font-weight: 600;
  border-radius: 6px;
  transition: all 0.3s ease;
  box-shadow: 0 2px 6px var(--color-shadow);
  border: 1px solid var(--color-border);
`;

const TemplateActions = styled.div`
  display: flex;
  gap: 10px;
  margin-top: 15px;
`;

const ActionButton = styled.button`
  background-color: ${props => props.primary ? 'var(--color-primary)' : 'var(--color-surfaceSecondary)'};
  color: ${props => props.primary ? 'var(--color-textInverse)' : 'var(--color-textPrimary)'};
  border: 2px solid ${props => props.primary ? 'var(--color-primary)' : 'var(--color-border)'};
  border-radius: 8px;
  padding: 12px 20px;
  cursor: pointer;
  font-size: 14px;
  font-weight: 700;
  transition: all 0.3s ease;
  box-shadow: 0 3px 8px var(--color-shadow);

  &:hover {
    background-color: ${props => props.primary ? 'var(--color-primaryHover)' : 'var(--color-border)'};
    border-color: ${props => props.primary ? 'var(--color-primaryHover)' : 'var(--color-primary)'};
    color: var(--color-textInverse);
    transform: translateY(-2px);
    box-shadow: 0 6px 16px var(--color-shadowHover);
  }

  &:active {
    transform: translateY(0);
    box-shadow: 0 3px 8px var(--color-shadow);
  }
`;

const TutorialSection = styled.div`
  background-color: var(--color-surfaceSecondary);
  border: 2px solid var(--color-border);
  border-radius: 12px;
  padding: 30px;
  margin-bottom: 30px;
  transition: all 0.3s ease;
  box-shadow: 0 4px 16px var(--color-shadow);

  &:hover {
    border-color: var(--color-primary);
    box-shadow: 0 6px 20px var(--color-shadowHover);
    transform: translateY(-2px);
  }
`;

const TutorialTitle = styled.h2`
  color: var(--color-warning);
  margin: 0 0 20px 0;
  font-size: 22px;
  font-weight: 700;
  display: flex;
  align-items: center;
  gap: 10px;
  transition: color 0.3s ease;
  text-shadow: 0 1px 2px var(--color-shadow);
`;

const TutorialContent = styled.div`
  color: var(--color-textPrimary);
  line-height: 1.6;
  font-size: 15px;
  font-weight: 500;
  transition: color 0.3s ease;

  h4 {
    color: var(--color-primary);
    margin: 20px 0 10px 0;
    font-weight: 700;
    font-size: 16px;
    transition: color 0.3s ease;
    text-shadow: 0 1px 2px var(--color-shadow);
  }

  ul {
    margin: 10px 0;
    padding-left: 20px;
  }

  li {
    margin: 8px 0;
    color: var(--color-textSecondary);
    font-weight: 500;
    transition: color 0.3s ease;
  }
`;

const ImportModal = styled.div`
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: var(--color-shadow);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 4000;
  opacity: ${props => props.isOpen ? 1 : 0};
  visibility: ${props => props.isOpen ? 'visible' : 'hidden'};
  transition: all 0.3s ease;
`;

const ImportModalContent = styled.div`
  background-color: var(--color-surfaceSecondary);
  border-radius: 16px;
  padding: 35px;
  max-width: 600px;
  width: 90%;
  border: 2px solid var(--color-primary);
  box-shadow: 0 20px 40px var(--color-shadow);
  transform: ${props => props.isOpen ? 'scale(1)' : 'scale(0.8)'};
  transition: all 0.3s ease;

  &:hover {
    border-color: var(--color-primaryHover);
    box-shadow: 0 25px 50px var(--color-shadowHover);
  }
`;

const ImportModalTitle = styled.h2`
  color: var(--color-primary);
  margin: 0 0 20px 0;
  font-size: 24px;
  font-weight: 700;
  display: flex;
  align-items: center;
  gap: 10px;
  transition: color 0.3s ease;
  text-shadow: 0 1px 2px var(--color-shadow);
`;

const ImportModalText = styled.p`
  color: var(--color-textSecondary);
  margin: 0 0 20px 0;
  line-height: 1.6;
  font-size: 15px;
  font-weight: 500;
  transition: color 0.3s ease;
`;

const ImportModalActions = styled.div`
  display: flex;
  gap: 15px;
  justify-content: flex-end;
`;

function TemplatesPage({ onClose }) {
  const { dispatch } = useVisualNovel();
  const [activeCategory, setActiveCategory] = useState('basic');
  const [importModal, setImportModal] = useState({ isOpen: false, template: null });

  const templates = {
    basic: [
      {
        id: 'simple-dialogue',
        title: 'Diálogo Simple',
        icon: '💬',
        description: 'Plantilla básica para un diálogo entre dos personajes',
        code: `# Diálogo simple
label start:
    scene bg room
    show character1 happy
    
    character1 "¡Hola! ¿Cómo estás?"
    
    show character2 smile
    character2 "¡Hola! Muy bien, gracias."
    
    character1 "Me alegra escuchar eso."
    
    return`,
        renpyExplanation: 'Esta plantilla muestra los elementos básicos: scene (fondo), show (personaje), y diálogos con comillas.'
      },
      {
        id: 'character-introduction',
        title: 'Introducción de Personaje',
        icon: '👋',
        description: 'Presentar un nuevo personaje en la historia',
        code: `# Introducción de personaje
label meet_new_character:
    scene bg school
    
    "Un nuevo estudiante entra al salón de clases."
    
    show new_character neutral at center
    with dissolve
    
    new_character "Hola, soy [name]. ¿Puedo sentarme aquí?"
    
    show main_character surprised
    main_character "¡Por supuesto! Soy [player_name]."
    
    show new_character happy
    new_character "¡Mucho gusto!"`,
        renpyExplanation: 'Usa "with dissolve" para transiciones suaves y posicionamiento con "at center".'
      },
      {
        id: 'scene-change',
        title: 'Cambio de Escena',
        icon: '🎭',
        description: 'Transición entre diferentes ubicaciones',
        code: `# Cambio de escena
label change_scene:
    scene bg park
    with fade
    
    "Nos dirigimos al parque."
    
    show character1 happy at left
    show character2 smile at right
    
    character1 "¡Qué hermoso día!"
    character2 "Sí, perfecto para caminar."
    
    scene bg sunset
    with dissolve
    
    "El sol comienza a ponerse."`,
        renpyExplanation: '"with fade" y "with dissolve" crean transiciones visuales entre escenas.'
      },
      {
        id: 'character-emotions',
        title: 'Cambio de Emociones',
        icon: '😊',
        description: 'Cambiar las emociones de un personaje durante el diálogo',
        code: `# Cambio de emociones
label character_emotions:
    scene bg classroom
    show main_character neutral
    
    main_character "Hola, ¿cómo estás?"
    
    show main_character happy
    main_character "¡Me alegra verte!"
    
    show main_character sad
    main_character "Pero tengo que contarte algo triste..."
    
    show main_character surprised
    main_character "¡¿En serio?! ¡No puedo creerlo!"
    
    show main_character angry
    main_character "¡Esto es inaceptable!"`,
        renpyExplanation: 'Usa "show character emotion" para cambiar las expresiones faciales de los personajes.'
      },
      {
        id: 'multiple-characters',
        title: 'Múltiples Personajes',
        icon: '👥',
        description: 'Mostrar varios personajes en pantalla simultáneamente',
        code: `# Múltiples personajes
label multiple_characters:
    scene bg park
    
    "Un grupo de amigos se reúne en el parque."
    
    show character1 happy at left
    show character2 smile at center
    show character3 neutral at right
    
    character1 "¡Qué hermoso día!"
    character2 "Sí, perfecto para un picnic."
    character3 "Aunque hace un poco de calor..."
    
    hide character3
    show character4 excited at right
    
    character4 "¡Hola a todos! ¿Qué se me perdió?"`,
        renpyExplanation: 'Usa "at left/center/right" para posicionar personajes y "hide" para ocultarlos.'
      }
    ],
    choices: [
      {
        id: 'simple-choice',
        title: 'Decisión Simple',
        icon: '🎯',
        description: 'Plantilla para una decisión con dos opciones',
        code: `# Decisión simple
label make_choice:
    scene bg crossroads
    
    "Te encuentras en una encrucijada."
    
    menu:
        "¿Qué camino eliges?"
        
        "Ir a la izquierda":
            jump left_path
            
        "Ir a la derecha":
            jump right_path

label left_path:
    "Decides ir por el camino izquierdo."
    scene bg forest
    "Llegas a un bosque misterioso."
    return

label right_path:
    "Decides ir por el camino derecho."
    scene bg city
    "Llegas a una ciudad bulliciosa."
    return`,
        renpyExplanation: 'Las decisiones usan "menu:" seguido de opciones con "jump" para navegar.'
      },
      {
        id: 'multiple-choice',
        title: 'Decisión Múltiple',
        icon: '🔀',
        description: 'Decisión con tres o más opciones',
        code: `# Decisión múltiple
label complex_choice:
    scene bg school_cafeteria
    
    "Es hora del almuerzo. ¿Qué decides hacer?"
    
    menu:
        "¿Qué haces?"
        
        "Comer solo":
            jump eat_alone
            
        "Buscar a tus amigos":
            jump find_friends
            
        "Ir a la biblioteca":
            jump go_library
            
        "Salir de la escuela":
            jump leave_school

label eat_alone:
    "Decides comer solo en una mesa."
    # Continúa la historia...
    return`,
        renpyExplanation: 'Puedes tener tantas opciones como necesites en un menú.'
      },
      {
        id: 'choice-with-consequences',
        title: 'Decisión con Consecuencias',
        icon: '⚖️',
        description: 'Decisión que afecta variables y tiene consecuencias posteriores',
        code: `# Decisión con consecuencias
label choice_with_consequences:
    scene bg school_hallway
    
    "Te encuentras con un compañero que parece triste."
    
    menu:
        "¿Qué haces?"
        
        "Preguntarle qué le pasa":
            $ friendship_points += 2
            $ kindness_points += 1
            jump comfort_friend
            
        "Ignorarlo y seguir caminando":
            $ friendship_points -= 1
            $ kindness_points -= 2
            jump ignore_friend
            
        "Hacer una broma para animarlo":
            $ humor_points += 1
            jump try_humor

label comfort_friend:
    friend "Gracias por preocuparte por mí..."
    friend "Realmente lo aprecio."
    "Tu amistad se fortalece."
    return

label ignore_friend:
    "Pasas de largo sin decir nada."
    "Sientes que tal vez deberías haber hecho algo diferente."
    return

label try_humor:
    friend "Jaja, gracias por intentar animarme."
    "Aunque no funcionó completamente, tu esfuerzo fue apreciado."
    return`,
        renpyExplanation: 'Las decisiones pueden modificar variables que afectan el desarrollo de la historia.'
      },
      {
        id: 'conditional-choice',
        title: 'Decisión Condicional',
        icon: '🔀',
        description: 'Menú que cambia según el estado del juego',
        code: `# Decisión condicional
label conditional_choice:
    scene bg library
    
    if friendship_points >= 5:
        "Tu amigo te ve y se acerca sonriendo."
        friend "¡Hola! ¿Quieres que estudiemos juntos?"
        
        menu:
            "¿Qué respondes?"
            
            "¡Por supuesto!":
                jump study_together
                
            "Tal vez otro día":
                jump decline_politely
                
    elif friendship_points >= 2:
        "Tu amigo te saluda de lejos."
        friend "Hola..."
        
        menu:
            "¿Qué haces?"
            
            "Acercarte y saludar":
                $ friendship_points += 1
                jump approach_friend
                
            "Responder desde lejos":
                jump distant_greeting
                
    else:
        "Tu amigo parece evitar el contacto visual contigo."
        "Tal vez deberías intentar mejorar la relación primero."
        return`,
        renpyExplanation: 'Los menús pueden cambiar completamente basándose en variables y condiciones previas.'
      }
    ],
    advanced: [
      {
        id: 'variables-usage',
        title: 'Uso de Variables',
        icon: '🔢',
        description: 'Implementar variables para recordar decisiones',
        code: `# Uso de variables
default friendship_points = 0
default player_name = ""

label start:
    $ player_name = renpy.input("¿Cuál es tu nombre?")
    
    scene bg school
    show friend neutral
    
    friend "Hola, [player_name]!"
    
    menu:
        "¿Cómo respondes?"
        
        "¡Hola! ¿Cómo estás?":
            $ friendship_points += 2
            friend "¡Muy bien! Me alegra verte."
            
        "Hola...":
            $ friendship_points += 1
            friend "Oh, hola."
            
        "No respondas":
            $ friendship_points -= 1
            friend "Hmm... ¿todo bien?"
    
    if friendship_points >= 3:
        "Tu amistad se fortalece."
    elif friendship_points >= 0:
        "Mantienes una relación neutral."
    else:
        "Tu amistad se deteriora."`,
        renpyExplanation: '"default" define variables, "$" las modifica, y "if/elif/else" las evalúa.'
      },
      {
        id: 'conditional-dialogue',
        title: 'Diálogo Condicional',
        icon: '🎭',
        description: 'Diálogos que cambian según el estado del juego',
        code: `# Diálogo condicional
label conditional_dialogue:
    scene bg classroom
    
    if friendship_points >= 5:
        show character very_happy
        character "¡[player_name]! ¡Qué alegría verte!"
        character "¿Quieres que almorcemos juntos?"
        
    elif friendship_points >= 2:
        show character happy
        character "Hola, [player_name]. ¿Cómo estás?"
        
    elif friendship_points >= 0:
        show character neutral
        character "Hola."
        
    else:
        show character sad
        character "..."
        "Parece que no quiere hablar contigo."`,
        renpyExplanation: 'Los diálogos pueden cambiar completamente basándose en variables y condiciones.'
      },
      {
        id: 'character-relationships',
        title: 'Sistema de Relaciones',
        icon: '💕',
        description: 'Sistema completo de relaciones entre personajes',
        code: `# Sistema de relaciones
default relationship_points = {
    "friend1": 0,
    "friend2": 0,
    "romantic_interest": 0
}

label relationship_system:
    scene bg school_cafeteria
    
    "Es hora del almuerzo y ves a tus amigos."
    
    # Verificar relaciones
    if relationship_points["friend1"] >= 10:
        show friend1 very_happy
        friend1 "¡Mi mejor amigo! ¡Ven aquí!"
        
    elif relationship_points["friend1"] >= 5:
        show friend1 happy
        friend1 "¡Hola! ¿Cómo estás?"
        
    elif relationship_points["friend1"] >= 0:
        show friend1 neutral
        friend1 "Oh, hola."
        
    else:
        show friend1 sad
        friend1 "..."
        "Parece que hay tensión entre ustedes."
    
    # Menú basado en relaciones
    if relationship_points["friend1"] >= 5:
        menu:
            "¿Qué quieres hacer?"
            
            "Almorzar juntos":
                $ relationship_points["friend1"] += 1
                jump lunch_together
                
            "Invitar a más amigos":
                jump invite_others
                
            "Hablar en privado":
                jump private_talk
    else:
        menu:
            "¿Qué intentas hacer?"
            
            "Acercarte cuidadosamente":
                $ relationship_points["friend1"] += 1
                jump careful_approach
                
            "Dar espacio":
                jump give_space
                
            "Intentar disculparte":
                $ relationship_points["friend1"] += 2
                jump try_apologize`,
        renpyExplanation: 'Los diccionarios permiten manejar múltiples relaciones y estados complejos.'
      },
      {
        id: 'inventory-system',
        title: 'Sistema de Inventario',
        icon: '🎒',
        description: 'Sistema básico de inventario con objetos',
        code: `# Sistema de inventario
default inventory = []
default money = 100

label inventory_system:
    scene bg store
    
    "Entras a una tienda local."
    clerk "¡Bienvenido! ¿Qué puedo hacer por ti?"
    
    menu:
        "¿Qué quieres hacer?"
        
        "Ver inventario" if len(inventory) > 0:
            jump show_inventory
            
        "Comprar algo":
            jump buy_items
            
        "Vender objetos" if len(inventory) > 0:
            jump sell_items
            
        "Salir de la tienda":
            jump leave_store

label show_inventory:
    "Tus objetos actuales:"
    if len(inventory) > 0:
        $ i = 0
        while i < len(inventory):
            "- [inventory[i]]"
            $ i += 1
    else:
        "No tienes objetos en tu inventario."
    "Dinero disponible: $[money]"
    return

label buy_items:
    menu:
        "¿Qué quieres comprar?"
        
        "Libro ($20)" if money >= 20:
            $ money -= 20
            $ inventory.append("Libro")
            "Compraste un libro."
            
        "Comida ($10)" if money >= 10:
            $ money -= 10
            $ inventory.append("Comida")
            "Compraste comida."
            
        "Regresar":
            jump inventory_system`,
        renpyExplanation: 'Las listas y bucles while permiten manejar inventarios dinámicos y mostrar objetos de forma compatible con Ren\'Py.'
      }
    ],
    examples: [
      {
        id: 'complete-scene',
        title: 'Escena Completa',
        icon: '🎬',
        description: 'Ejemplo de una escena completa con todos los elementos',
        code: `# Escena completa
label complete_example:
    # Configuración inicial
    scene bg library
    with fade
    
    "La biblioteca está tranquila esta tarde."
    
    # Entrada del personaje
    show librarian smile at center
    with dissolve
    
    librarian "Bienvenido a la biblioteca. ¿En qué puedo ayudarte?"
    
    # Decisión del jugador
    menu:
        "¿Qué haces?"
        
        "Buscar un libro":
            jump search_book
            
        "Preguntar por información":
            jump ask_info
            
        "Irse":
            jump leave_library

label search_book:
    librarian "¿Qué tipo de libro buscas?"
    
    menu:
        "¿Qué género prefieres?"
        
        "Fantasía":
            $ book_genre = "fantasy"
            
        "Misterio":
            $ book_genre = "mystery"
            
        "Romance":
            $ book_genre = "romance"
    
    librarian "Perfecto, te ayudo a encontrar un buen libro de [book_genre]."
    return

label ask_info:
    librarian "¿Qué información necesitas?"
    # Continúa la conversación...
    return

label leave_library:
    librarian "Que tengas un buen día."
    scene black
    with fade
    return`,
        renpyExplanation: 'Esta plantilla combina todos los elementos: escenas, personajes, decisiones y variables.'
      }
    ],
    specialized: [
      {
        id: 'minigame-template',
        title: 'Minijuego Simple',
        icon: '🎮',
        description: 'Plantilla para crear minijuegos básicos',
        code: `# Minijuego simple - Adivinanza
label minigame_guessing:
    scene bg classroom
    
    teacher "Vamos a jugar un juego de adivinanzas."
    teacher "Tengo un número del 1 al 10 en mente. ¿Puedes adivinarlo?"
    
    $ secret_number = renpy.random.randint(1, 10)
    $ attempts = 0
    $ max_attempts = 3
    
    while attempts < max_attempts:
        $ guess_text = renpy.input("¿Cuál crees que es el número? (1-10)")
        $ guess = int(guess_text) if guess_text.isdigit() else 0
        $ attempts += 1
        
        if guess == secret_number:
            teacher "¡Correcto! ¡Felicitaciones!"
            $ score += 10
            return
            
        elif attempts < max_attempts:
            if guess < secret_number:
                teacher "Más alto..."
            else:
                teacher "Más bajo..."
    
    teacher "Se acabaron los intentos. El número era [secret_number]."
    return`,
        renpyExplanation: 'Los bucles while y renpy.input() permiten crear mecánicas de juego interactivas como minijuegos.'
      },
      {
        id: 'save-load-system',
        title: 'Sistema de Guardado',
        icon: '💾',
        description: 'Implementar sistema de guardado y carga',
        code: `# Sistema de guardado
label save_load_system:
    scene bg main_menu
    
    "¿Qué quieres hacer?"
    
    menu:
        "¿Qué opción eliges?"
        
        "Nueva Partida":
            $ friendship_points = 0
            $ money = 100
            $ inventory = []
            jump start_game
            
        "Cargar Partida":
            jump load_game_menu
            
        "Guardar Partida":
            jump save_game_menu
            
        "Configuración":
            jump settings_menu

label save_game_menu:
    menu:
        "¿En qué slot quieres guardar?"
        
        "Slot 1":
            $ renpy.save("save1")
            "Juego guardado en el slot 1."
            
        "Slot 2":
            $ renpy.save("save2")
            "Juego guardado en el slot 2."
            
        "Slot 3":
            $ renpy.save("save3")
            "Juego guardado en el slot 3."
            
        "Cancelar":
            return

label load_game_menu:
    menu:
        "¿Qué partida quieres cargar?"
        
        "Slot 1" if renpy.can_load("save1"):
            $ renpy.load("save1")
            
        "Slot 2" if renpy.can_load("save2"):
            $ renpy.load("save2")
            
        "Slot 3" if renpy.can_load("save3"):
            $ renpy.load("save3")
            
        "Cancelar":
            return`,
        renpyExplanation: 'Ren\'Py tiene funciones integradas para manejar guardados automáticamente.'
      },
      {
        id: 'time-system',
        title: 'Sistema de Tiempo',
        icon: '⏰',
        description: 'Sistema básico de días y horarios',
        code: `# Sistema de tiempo
default current_day = 1
default current_time = "morning"
default day_names = ["Lunes", "Martes", "Miércoles", "Jueves", "Viernes", "Sábado", "Domingo"]

label time_system:
    scene bg school
    
    "Es [day_names[current_day % 7 - 1]], [current_time]."
    
    if current_time == "morning":
        "Las clases están por comenzar."
        jump morning_scene
        
    elif current_time == "afternoon":
        "Es hora del almuerzo."
        jump afternoon_scene
        
    elif current_time == "evening":
        "Las clases han terminado."
        jump evening_scene

label advance_time:
    if current_time == "morning":
        $ current_time = "afternoon"
    elif current_time == "afternoon":
        $ current_time = "evening"
    elif current_time == "evening":
        $ current_time = "morning"
        $ current_day += 1
    
    "El tiempo avanza..."
    jump time_system`,
        renpyExplanation: 'Los sistemas de tiempo permiten crear historias con estructura temporal y rutinas diarias.'
      },
      {
        id: 'achievement-system',
        title: 'Sistema de Logros',
        icon: '🏆',
        description: 'Sistema de logros y recompensas',
        code: `# Sistema de logros
default achievements = {
    "first_friend": False,
    "social_butterfly": False,
    "good_student": False,
    "romance_master": False
}

label achievement_system:
    scene bg school
    
    # Verificar logros
    if friendship_points >= 5 and not achievements["first_friend"]:
        $ achievements["first_friend"] = True
        $ renpy.notify("¡Logro desbloqueado: Primer Amigo!")
        
    if friendship_points >= 20 and not achievements["social_butterfly"]:
        $ achievements["social_butterfly"] = True
        $ renpy.notify("¡Logro desbloqueado: Mariposa Social!")
        
    if relationship_points.get("romantic_interest", 0) >= 15 and not achievements["romance_master"]:
        $ achievements["romance_master"] = True
        $ renpy.notify("¡Logro desbloqueado: Maestro del Romance!")
    
    # Mostrar logros obtenidos
    if achievements["first_friend"]:
        "¡Has hecho tu primer amigo!"
    if achievements["social_butterfly"]:
        "¡Eres muy popular!"
    if achievements["romance_master"]:
        "¡Eres un experto en romance!"
    
    return

label show_achievements:
    "Logros obtenidos:"
    
    if achievements["first_friend"]:
        "✓ Primer Amigo"
    else:
        "✗ Primer Amigo"
        
    if achievements["social_butterfly"]:
        "✓ Mariposa Social"
    else:
        "✗ Mariposa Social"
        
    if achievements["good_student"]:
        "✓ Buen Estudiante"
    else:
        "✗ Buen Estudiante"
        
    if achievements["romance_master"]:
        "✓ Maestro del Romance"
    else:
        "✗ Maestro del Romance"
    
    return`,
        renpyExplanation: 'Los sistemas de logros con renpy.notify() motivan a los jugadores y añaden metas adicionales a la historia.'
      }
    ]
  };

  const handleImportTemplate = (template) => {
    setImportModal({ isOpen: true, template });
  };

  const confirmImport = () => {
    // Aquí se implementaría la lógica para importar la plantilla
    alert(`Plantilla "${importModal.template.title}" importada exitosamente.\n\nEsta funcionalidad estará disponible en futuras versiones.`);
    setImportModal({ isOpen: false, template: null });
  };

  const cancelImport = () => {
    setImportModal({ isOpen: false, template: null });
  };

  const getCurrentTemplates = () => {
    return templates[activeCategory] || [];
  };

  return (
    <>
      <TemplatesContainer>
        <TemplatesHeader>
          <TemplatesTitle>
            📚 Plantillas y Tutoriales de Ren'Py
          </TemplatesTitle>
          <CloseButton onClick={onClose}>
            ✕ Cerrar
          </CloseButton>
        </TemplatesHeader>

        <TemplatesContent>
          <CategoryTabs>
            <CategoryTab 
              active={activeCategory === 'basic'} 
              onClick={() => setActiveCategory('basic')}
            >
              🔰 Básico
            </CategoryTab>
            <CategoryTab 
              active={activeCategory === 'choices'} 
              onClick={() => setActiveCategory('choices')}
            >
              🎯 Decisiones
            </CategoryTab>
            <CategoryTab 
              active={activeCategory === 'advanced'} 
              onClick={() => setActiveCategory('advanced')}
            >
              🚀 Avanzado
            </CategoryTab>
            <CategoryTab 
              active={activeCategory === 'examples'} 
              onClick={() => setActiveCategory('examples')}
            >
              🎬 Ejemplos
            </CategoryTab>
            <CategoryTab 
              active={activeCategory === 'specialized'} 
              onClick={() => setActiveCategory('specialized')}
            >
              ⚡ Especializadas
            </CategoryTab>
          </CategoryTabs>

          {activeCategory === 'basic' && (
            <TutorialSection>
              <TutorialTitle>🔰 Conceptos Básicos de Ren'Py</TutorialTitle>
              <TutorialContent>
                <h4>Elementos Fundamentales:</h4>
                <ul>
                  <li><strong>label:</strong> Puntos de destino en tu historia</li>
                  <li><strong>scene:</strong> Cambia el fondo de la pantalla</li>
                  <li><strong>show:</strong> Muestra un personaje o imagen</li>
                  <li><strong>hide:</strong> Oculta un personaje o imagen</li>
                  <li><strong>Diálogos:</strong> Texto entre comillas para que hablen los personajes</li>
                </ul>
                
                <h4>Comandos de Transición:</h4>
                <ul>
                  <li><strong>with fade:</strong> Transición de desvanecimiento</li>
                  <li><strong>with dissolve:</strong> Transición suave</li>
                  <li><strong>with pixellate:</strong> Efecto pixelado</li>
                </ul>
              </TutorialContent>
            </TutorialSection>
          )}

          {activeCategory === 'choices' && (
            <TutorialSection>
              <TutorialTitle>🎯 Sistema de Decisiones</TutorialTitle>
              <TutorialContent>
                <h4>Creando Decisiones:</h4>
                <ul>
                  <li><strong>menu:</strong> Inicia un menú de opciones</li>
                  <li><strong>jump:</strong> Salta a un label específico</li>
                  <li><strong>return:</strong> Termina el label actual</li>
                </ul>
                
                <h4>Estructura de Decisiones:</h4>
                <ul>
                  <li>Pregunta del menú</li>
                  <li>Opciones con texto y destino</li>
                  <li>Labels de destino para cada opción</li>
                </ul>
              </TutorialContent>
            </TutorialSection>
          )}

          {activeCategory === 'advanced' && (
            <TutorialSection>
              <TutorialTitle>🚀 Funciones Avanzadas</TutorialTitle>
              <TutorialContent>
                <h4>Variables:</h4>
                <ul>
                  <li><strong>default:</strong> Define variables con valor inicial</li>
                  <li><strong>$:</strong> Modifica variables durante el juego</li>
                  <li><strong>if/elif/else:</strong> Condiciones basadas en variables</li>
                </ul>
                
                <h4>Flujo de Control:</h4>
                <ul>
                  <li>Condiciones para cambiar el diálogo</li>
                  <li>Variables para recordar decisiones</li>
                  <li>Múltiples rutas en la historia</li>
                </ul>
              </TutorialContent>
            </TutorialSection>
          )}

          {activeCategory === 'specialized' && (
            <TutorialSection>
              <TutorialTitle>⚡ Plantillas Especializadas</TutorialTitle>
              <TutorialContent>
                <h4>Sistemas Avanzados:</h4>
                <ul>
                  <li><strong>Minijuegos:</strong> Mecánicas de juego interactivas</li>
                  <li><strong>Guardado/Carga:</strong> Sistema de persistencia de datos</li>
                  <li><strong>Tiempo:</strong> Estructura temporal y rutinas</li>
                  <li><strong>Logros:</strong> Sistema de recompensas y metas</li>
                </ul>
                
                <h4>Conceptos Avanzados:</h4>
                <ul>
                  <li><strong>Bucles while:</strong> Para mecánicas repetitivas compatibles con Ren'Py</li>
                  <li><strong>Diccionarios:</strong> Para datos complejos y sistemas de relaciones</li>
                  <li><strong>Listas:</strong> Para inventarios dinámicos con bucles while</li>
                  <li><strong>Funciones Ren'Py:</strong> renpy.input(), renpy.notify(), renpy.save(), renpy.load()</li>
                </ul>
              </TutorialContent>
            </TutorialSection>
          )}

          <TemplatesGrid>
            {getCurrentTemplates().map((template) => (
              <TemplateCard key={template.id}>
                <TemplateHeader>
                  <TemplateTitle>
                    {template.icon} {template.title}
                  </TemplateTitle>
                  <TemplateBadge>Plantilla</TemplateBadge>
                </TemplateHeader>
                
                <TemplateDescription>
                  {template.description}
                </TemplateDescription>

                <CodePreview>
                  <CodeLabel>Código Ren'Py</CodeLabel>
                  <pre>{template.code}</pre>
                </CodePreview>

                {template.renpyExplanation && (
                  <div style={{ 
                    backgroundColor: 'var(--color-surfaceSecondary)', 
                    padding: '12px', 
                    borderRadius: '6px', 
                    marginTop: '15px',
                    color: 'var(--color-warning)',
                    fontSize: '13px',
                    lineHeight: '1.4'
                  }}>
                    <strong>💡 Explicación:</strong> {template.renpyExplanation}
                  </div>
                )}

                <TemplateActions>
                  <ActionButton 
                    primary 
                    onClick={() => handleImportTemplate(template)}
                  >
                    📥 Importar
                  </ActionButton>
                  <ActionButton>
                    👁️ Ver Detalles
                  </ActionButton>
                </TemplateActions>
              </TemplateCard>
            ))}
          </TemplatesGrid>
        </TemplatesContent>
      </TemplatesContainer>

      <ImportModal isOpen={importModal.isOpen}>
        <ImportModalContent isOpen={importModal.isOpen}>
          <ImportModalTitle>
            📥 Importar Plantilla
          </ImportModalTitle>
          <ImportModalText>
            ¿Estás seguro de que quieres importar la plantilla <strong>"{importModal.template?.title}"</strong>?
            <br/><br/>
            Esta acción agregará el código de la plantilla a tu proyecto actual. Puedes modificarlo después según tus necesidades.
          </ImportModalText>
          <ImportModalActions>
            <ActionButton onClick={cancelImport}>
              ❌ Cancelar
            </ActionButton>
            <ActionButton primary onClick={confirmImport}>
              ✅ Importar
            </ActionButton>
          </ImportModalActions>
        </ImportModalContent>
      </ImportModal>
    </>
  );
}

export default TemplatesPage;
