import { useState, useEffect } from 'react';
import { useVisualNovel } from '../context/VisualNovelContext';
import { applyTheme, getCurrentTheme, initializeTheme } from '../utils/themeManager';

export const useTheme = () => {
  const { state } = useVisualNovel();
  const [currentTheme, setCurrentTheme] = useState('dark-default');

  // Inicializar tema al cargar
  useEffect(() => {
    const savedTheme = initializeTheme();
    setCurrentTheme(savedTheme);
  }, []);

  // Aplicar tema cuando cambie el estado
  useEffect(() => {
    if (state.theme && state.theme !== currentTheme) {
      applyTheme(state.theme);
      setCurrentTheme(state.theme);
    }
  }, [state.theme, currentTheme]);

  // Función para cambiar tema
  const changeTheme = (themeId) => {
    applyTheme(themeId);
    setCurrentTheme(themeId);
  };

  // Obtener información del tema actual
  const getThemeInfo = () => {
    return getCurrentTheme(currentTheme);
  };

  // Obtener colores del tema actual
  const getThemeColors = () => {
    const theme = getCurrentTheme(currentTheme);
    return theme.colors;
  };

  // Obtener variables CSS del tema actual
  const getCSSVariables = () => {
    const theme = getCurrentTheme(currentTheme);
    const variables = {};
    
    // Colores
    Object.entries(theme.colors).forEach(([key, value]) => {
      variables[`--color-${key}`] = value;
    });
    
    // Tipografía
    Object.entries(theme.typography).forEach(([key, value]) => {
      if (typeof value === 'object') {
        Object.entries(value).forEach(([subKey, subValue]) => {
          variables[`--font-${key}-${subKey}`] = subValue;
        });
      } else {
        variables[`--font-${key}`] = value;
      }
    });
    
    // Espaciado
    Object.entries(theme.spacing).forEach(([key, value]) => {
      variables[`--spacing-${key}`] = value;
    });
    
    // Bordes redondeados
    Object.entries(theme.borderRadius).forEach(([key, value]) => {
      variables[`--radius-${key}`] = value;
    });
    
    return variables;
  };

  return {
    currentTheme,
    changeTheme,
    getThemeInfo,
    getThemeColors,
    getCSSVariables,
    isLight: currentTheme.includes('light'),
    isDark: currentTheme.includes('dark')
  };
};
