import React, { useState, useRef } from 'react';
import styled from 'styled-components';
import { useVisualNovel } from '../context/VisualNovelContext';
import { RynBackupManager } from '../utils/rynBackupManager';
import { ProjectExporter } from '../utils/projectExporter';
import BackupInfo from './BackupInfo';

// Componentes estilizados
const BackupContainer = styled.div`
  background: var(--color-gradientSecondary);
  border-radius: 12px;
  padding: 24px;
  margin: 20px 0;
  border: 1px solid var(--color-border);
  box-shadow: 0 8px 32px var(--color-shadowStrong);
  transition: background 0.3s ease, border-color 0.3s ease;
`;

const BackupTitle = styled.h2`
  color: var(--color-textPrimary);
  font-size: 24px;
  margin-bottom: 8px;
  display: flex;
  align-items: center;
  gap: 12px;
  transition: color 0.3s ease;
`;

const BackupSubtitle = styled.p`
  color: var(--color-textSecondary);
  font-size: 14px;
  margin-bottom: 24px;
  transition: color 0.3s ease;
`;

const BackupSection = styled.div`
  margin-bottom: 24px;
`;

const SectionTitle = styled.h3`
  color: var(--color-primary);
  font-size: 18px;
  margin-bottom: 12px;
  display: flex;
  align-items: center;
  gap: 8px;
  transition: color 0.3s ease;
`;

const ButtonGroup = styled.div`
  display: flex;
  gap: 12px;
  flex-wrap: wrap;
`;

const BackupButton = styled.button`
  background: linear-gradient(135deg, #4a9eff 0%, #3a8eef 100%);
  color: #ffffff;
  border: none;
  border-radius: 8px;
  padding: 12px 20px;
  font-size: 14px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  display: flex;
  align-items: center;
  gap: 8px;
  min-width: 140px;
  justify-content: center;

  &:hover {
    transform: translateY(-2px);
    box-shadow: 0 8px 25px rgba(74, 158, 255, 0.3);
  }

  &:active {
    transform: translateY(0);
  }

  &:disabled {
    opacity: 0.6;
    cursor: not-allowed;
    transform: none;
  }
`;

const RestoreButton = styled(BackupButton)`
  background: linear-gradient(135deg, #28a745 0%, #218838 100%);
  
  &:hover {
    box-shadow: 0 8px 25px rgba(40, 167, 69, 0.3);
  }
`;

const ImportButton = styled(BackupButton)`
  background: linear-gradient(135deg, #ffc107 0%, #e0a800 100%);
  color: #333;
  
  &:hover {
    box-shadow: 0 8px 25px rgba(255, 193, 7, 0.3);
  }
`;

const FileInput = styled.input`
  display: none;
`;

const StatusMessage = styled.div`
  padding: 12px 16px;
  border-radius: 8px;
  margin-top: 16px;
  font-size: 14px;
  font-weight: 500;
  display: flex;
  align-items: center;
  gap: 8px;
`;

const SuccessMessage = styled(StatusMessage)`
  background: rgba(40, 167, 69, 0.1);
  border: 1px solid rgba(40, 167, 69, 0.3);
  color: #28a745;
`;

const ErrorMessage = styled(StatusMessage)`
  background: rgba(220, 53, 69, 0.1);
  border: 1px solid rgba(220, 53, 69, 0.3);
  color: #dc3545;
`;

const InfoMessage = styled(StatusMessage)`
  background: rgba(23, 162, 184, 0.1);
  border: 1px solid rgba(23, 162, 184, 0.3);
  color: #17a2b8;
`;

const ProjectInfo = styled.div`
  background: rgba(74, 158, 255, 0.1);
  border: 1px solid rgba(74, 158, 255, 0.3);
  border-radius: 8px;
  padding: 16px;
  margin-bottom: 20px;
`;

const InfoRow = styled.div`
  display: flex;
  justify-content: space-between;
  margin-bottom: 8px;
  
  &:last-child {
    margin-bottom: 0;
  }
`;

const InfoLabel = styled.span`
  color: #cccccc;
  font-weight: 500;
`;

const InfoValue = styled.span`
  color: #ffffff;
  font-weight: 600;
`;

const ProgressBar = styled.div`
  width: 100%;
  height: 8px;
  background: rgba(255, 255, 255, 0.1);
  border-radius: 4px;
  overflow: hidden;
  margin: 12px 0;
`;

const ProgressFill = styled.div`
  height: 100%;
  background: linear-gradient(90deg, #4a9eff, #3a8eef);
  width: ${props => props.progress}%;
  transition: width 0.3s ease;
`;

function BackupManager() {
  const { state, dispatch } = useVisualNovel();
  const [isExporting, setIsExporting] = useState(false);
  const [isImporting, setIsImporting] = useState(false);
  const [statusMessage, setStatusMessage] = useState(null);
  const [exportProgress, setExportProgress] = useState(0);
  const fileInputRef = useRef(null);
  const backupManager = new RynBackupManager();
  const projectExporter = new ProjectExporter();

  // Exportar proyecto como .ryn
  const handleExportProject = async () => {
    try {
      setIsExporting(true);
      setStatusMessage({ type: 'info', text: '🔄 Preparando exportación...' });
      setExportProgress(10);

      setExportProgress(30);
      setStatusMessage({ type: 'info', text: '📦 Empaquetando proyecto...' });

      // Usar el ProjectExporter para exportar como .ryn
      const filename = await projectExporter.exportProjectAsRyn(state.project, state);
      
      setExportProgress(70);
      setStatusMessage({ type: 'info', text: '💾 Generando archivo .ryn...' });

      setExportProgress(100);
      setStatusMessage({ 
        type: 'success', 
        text: `✅ Proyecto exportado exitosamente: ${filename}` 
      });
    } catch (error) {
      setStatusMessage({ 
        type: 'error', 
        text: `❌ Error inesperado: ${error.message}` 
      });
    } finally {
      setIsExporting(false);
      setTimeout(() => setExportProgress(0), 2000);
    }
  };

  // Importar proyecto desde .ryn
  const handleImportProject = () => {
    fileInputRef.current?.click();
  };

  const handleFileSelected = async (event) => {
    const file = event.target.files[0];
    if (!file) return;

    if (!file.name.endsWith('.ryn')) {
      setStatusMessage({ 
        type: 'error', 
        text: '❌ Por favor selecciona un archivo .ryn válido' 
      });
      return;
    }

    try {
      setIsImporting(true);
      setStatusMessage({ type: 'info', text: '🔄 Cargando archivo .ryn...' });

      const result = await backupManager.importProjectFromRyn(file);
      
      if (result.success) {
        setStatusMessage({ 
          type: 'success', 
          text: `✅ Proyecto importado exitosamente: ${result.metadata.projectName}` 
        });

        // Restaurar estado completo
        dispatch({
          type: 'LOAD_FULL_STATE',
          payload: result.fullState
        });

        // Restaurar archivos multimedia
        if (result.mediaFiles) {
          // Aquí se restaurarían los archivos multimedia
          console.log('Archivos multimedia restaurados:', result.mediaFiles);
        }
      } else {
        setStatusMessage({ 
          type: 'error', 
          text: `❌ Error al importar: ${result.error}` 
        });
      }
    } catch (error) {
      setStatusMessage({ 
        type: 'error', 
        text: `❌ Error inesperado: ${error.message}` 
      });
    } finally {
      setIsImporting(false);
    }
  };

  // Obtener información del proyecto
  const getProjectInfo = () => {
    const characters = state.project.characters?.length || 0;
    const sceneFiles = state.project.sceneFiles?.length || 0;
    const totalElements = state.project.sceneFiles?.reduce((total, file) => total + (file.elements?.length || 0), 0) || 0;
    const variables = state.project.variables?.length || 0;
    const flags = state.project.flags?.length || 0;

    return { characters, sceneFiles, totalElements, variables, flags };
  };

  const projectInfo = getProjectInfo();

  return (
    <BackupContainer>
      <BackupTitle>
        💾 Sistema de Backup .ryn
      </BackupTitle>
      <BackupSubtitle>
        Guarda y restaura tus proyectos completos con todos los archivos multimedia
      </BackupSubtitle>

      {/* Información del proyecto actual */}
      <BackupSection>
        <SectionTitle>📊 Información del Proyecto Actual</SectionTitle>
        <ProjectInfo>
          <InfoRow>
            <InfoLabel>Nombre:</InfoLabel>
            <InfoValue>{state.project.name}</InfoValue>
          </InfoRow>
          <InfoRow>
            <InfoLabel>Versión:</InfoLabel>
            <InfoValue>{state.project.version}</InfoValue>
          </InfoRow>
          <InfoRow>
            <InfoLabel>Personajes:</InfoLabel>
            <InfoValue>{projectInfo.characters}</InfoValue>
          </InfoRow>
          <InfoRow>
            <InfoLabel>Archivos de escena:</InfoLabel>
            <InfoValue>{projectInfo.sceneFiles}</InfoValue>
          </InfoRow>
          <InfoRow>
            <InfoLabel>Elementos de script:</InfoLabel>
            <InfoValue>{projectInfo.totalElements}</InfoValue>
          </InfoRow>
          <InfoRow>
            <InfoLabel>Variables:</InfoLabel>
            <InfoValue>{projectInfo.variables}</InfoValue>
          </InfoRow>
          <InfoRow>
            <InfoLabel>Flags:</InfoLabel>
            <InfoValue>{projectInfo.flags}</InfoValue>
          </InfoRow>
        </ProjectInfo>
      </BackupSection>

      {/* Exportar proyecto */}
      <BackupSection>
        <SectionTitle>📤 Exportar Proyecto</SectionTitle>
        <ButtonGroup>
          <BackupButton 
            onClick={handleExportProject}
            disabled={isExporting || isImporting}
          >
            {isExporting ? '🔄 Exportando...' : '💾 Exportar como .ryn'}
          </BackupButton>
        </ButtonGroup>
        
        {isExporting && (
          <ProgressBar>
            <ProgressFill progress={exportProgress} />
          </ProgressBar>
        )}
      </BackupSection>

      {/* Importar proyecto */}
      <BackupSection>
        <SectionTitle>📥 Importar Proyecto</SectionTitle>
        <ButtonGroup>
          <ImportButton 
            onClick={handleImportProject}
            disabled={isExporting || isImporting}
          >
            {isImporting ? '🔄 Importando...' : '📁 Seleccionar archivo .ryn'}
          </ImportButton>
        </ButtonGroup>
        
        <FileInput
          ref={fileInputRef}
          type="file"
          accept=".ryn"
          onChange={handleFileSelected}
        />
      </BackupSection>

      {/* Mensajes de estado */}
      {statusMessage && (
        <>
          {statusMessage.type === 'success' && (
            <SuccessMessage>
              {statusMessage.text}
            </SuccessMessage>
          )}
          {statusMessage.type === 'error' && (
            <ErrorMessage>
              {statusMessage.text}
            </ErrorMessage>
          )}
          {statusMessage.type === 'info' && (
            <InfoMessage>
              {statusMessage.text}
            </InfoMessage>
          )}
        </>
      )}

      {/* Información sobre archivos .ryn */}
      <BackupInfo />
    </BackupContainer>
  );
}

export default BackupManager;
