import React from 'react';
import styled from 'styled-components';
import { useVisualNovel } from '../../context/VisualNovelContext';
import BackupManager from '../BackupManager';

const PanelContainer = styled.div`
  display: flex;
  flex-direction: column;
  gap: 16px;
`;

const Section = styled.div`
  background-color: var(--color-surfaceSecondary);
  border-radius: 8px;
  padding: 16px;
  transition: background-color 0.3s ease;
`;

const SectionTitle = styled.h3`
  margin: 0 0 12px 0;
  font-size: 14px;
  color: var(--color-primary);
  border-bottom: 1px solid var(--color-border);
  padding-bottom: 8px;
  transition: color 0.3s ease, border-color 0.3s ease;
`;

const FormGroup = styled.div`
  margin-bottom: 12px;
`;

const Label = styled.label`
  display: block;
  margin-bottom: 4px;
  font-size: 12px;
  color: var(--color-textSecondary);
  transition: color 0.3s ease;
`;

const Input = styled.input`
  width: 100%;
  background-color: var(--color-border);
  border: 1px solid var(--color-borderSecondary);
  color: var(--color-textPrimary);
  padding: 8px;
  border-radius: 4px;
  font-size: 14px;
  transition: all 0.3s ease;

  &:focus {
    outline: none;
    border-color: var(--color-primary);
  }
`;

const TextArea = styled.textarea`
  width: 100%;
  background-color: var(--color-surfaceSecondary);
  border: 1px solid var(--color-border);
  color: var(--color-textPrimary);
  padding: 8px;
  border-radius: 4px;
  font-size: 14px;
  min-height: 80px;
  resize: vertical;
  transition: all 0.3s ease;

  &:focus {
    outline: none;
    border-color: var(--color-primary);
  }
`;

const StatsContainer = styled.div`
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 8px;
`;

const StatsRow = styled.div`
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  gap: 6px;
  margin-bottom: 8px;
  
  &:last-child {
    margin-bottom: 0;
  }
`;

const StatItem = styled.div`
  background-color: var(--color-surfaceSecondary);
  border: 1px solid var(--color-border);
  padding: 8px;
  border-radius: 6px;
  text-align: center;
  min-height: 60px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  overflow: hidden;
  transition: all 0.3s ease;
  box-shadow: 0 2px 4px var(--color-shadow);

  &:hover {
    background-color: var(--color-border);
    transform: translateY(-1px);
    box-shadow: 0 4px 8px var(--color-shadowHover);
  }
`;

const StatValue = styled.div`
  font-size: 18px;
  font-weight: bold;
  color: var(--color-primary);
  line-height: 1.2;
  transition: color 0.3s ease;
`;

const StatLabel = styled.div`
  font-size: 10px;
  color: var(--color-textSecondary);
  margin-top: 4px;
  line-height: 1.2;
  word-wrap: break-word;
  overflow-wrap: break-word;
  hyphens: auto;
  transition: color 0.3s ease;
`;

function ProjectPanel() {
  const { state, dispatch } = useVisualNovel();
  
  // Calcular estadísticas en tiempo real
  const getProjectStats = () => {
    const characters = state.project.characters?.length || 0;
    const sceneFiles = state.project.sceneFiles?.length || 0;
    const totalElements = state.project.sceneFiles?.reduce((total, file) => total + (file.elements?.length || 0), 0) || 0;
    const dialogues = state.project.sceneFiles?.reduce((total, file) => 
      total + (file.elements?.filter(el => el.type === 'dialogue').length || 0), 0) || 0;
    const choices = state.project.sceneFiles?.reduce((total, file) => 
      total + (file.elements?.filter(el => el.type === 'choice').length || 0), 0) || 0;
    const sceneChanges = state.project.sceneFiles?.reduce((total, file) => 
      total + (file.elements?.filter(el => el.type === 'scene').length || 0), 0) || 0;
    const variables = state.project.variables?.length || 0;
    const flags = state.project.flags?.length || 0;
    
    return {
      characters,
      sceneFiles,
      totalElements,
      dialogues,
      choices,
      sceneChanges,
      variables,
      flags
    };
  };
  
  const stats = getProjectStats();

  // Obtener información del estado actual del proyecto
  const getProjectStatus = () => {
    const currentSceneFile = state.project.sceneFiles?.find(file => 
      file.id === state.project.currentSceneFile
    );
    
    const lastModified = state.project.lastModified ? 
      new Date(state.project.lastModified).toLocaleString() : 
      'No disponible';
    
    const autoSaveStatus = state.autoSaveEnabled ? 'Activo' : 'Inactivo';
    
    const currentElement = state.selectedElement ? 
      `${state.selectedElement.type} (línea ${state.selectedElementIndex + 1})` : 
      'Ninguno';
    
    return {
      currentSceneFile: currentSceneFile?.displayName || 'Ninguna',
      currentSceneFileId: currentSceneFile?.id || null,
      lastModified,
      autoSaveStatus,
      currentElement,
      totalSceneFiles: state.project.sceneFiles?.length || 0,
      currentSceneFileElements: currentSceneFile?.elements?.length || 0
    };
  };
  
  const projectStatus = getProjectStatus();

  const handleAuthorChange = (e) => {
    dispatch({
      type: 'SET_PROJECT_AUTHOR',
      payload: e.target.value
    });
  };

  const handleDescriptionChange = (e) => {
    dispatch({
      type: 'SET_PROJECT_DESCRIPTION',
      payload: e.target.value
    });
  };

  const handleVersionChange = (e) => {
    dispatch({
      type: 'SET_PROJECT_VERSION',
      payload: e.target.value
    });
  };

  return (
    <PanelContainer>
      <Section>
        <SectionTitle>Información del Proyecto</SectionTitle>
        
        <FormGroup>
          <Label>Autor</Label>
          <Input
            value={state.project.author}
            onChange={handleAuthorChange}
            placeholder="Nombre del autor"
          />
        </FormGroup>

        <FormGroup>
          <Label>Versión</Label>
          <Input
            value={state.project.version}
            onChange={handleVersionChange}
            placeholder="1.0.0"
          />
        </FormGroup>

        <FormGroup>
          <Label>Descripción</Label>
          <TextArea
            value={state.project.description}
            onChange={handleDescriptionChange}
            placeholder="Descripción de la novela visual..."
          />
        </FormGroup>
      </Section>

      <Section>
        <SectionTitle>
          📊 Estadísticas del Proyecto
          <span style={{ 
            fontSize: '10px', 
            color: 'var(--color-primary)', 
            marginLeft: '8px',
            fontWeight: 'normal'
          }}>
            (Actualización en tiempo real)
          </span>
        </SectionTitle>
        <StatsRow>
          <StatItem>
            <StatValue>{stats.characters}</StatValue>
            <StatLabel>Personajes</StatLabel>
          </StatItem>
          <StatItem>
            <StatValue>{stats.sceneFiles}</StatValue>
            <StatLabel>Archivos de Escena</StatLabel>
          </StatItem>
          <StatItem>
            <StatValue>{stats.totalElements}</StatValue>
            <StatLabel>Elementos</StatLabel>
          </StatItem>
          <StatItem>
            <StatValue>{stats.dialogues}</StatValue>
            <StatLabel>Diálogos</StatLabel>
          </StatItem>
        </StatsRow>
        <StatsRow>
          <StatItem>
            <StatValue>{stats.choices}</StatValue>
            <StatLabel>Decisiones</StatLabel>
          </StatItem>
          <StatItem>
            <StatValue>{stats.sceneChanges}</StatValue>
            <StatLabel>Cambios de Escena</StatLabel>
          </StatItem>
          <StatItem>
            <StatValue>{stats.variables}</StatValue>
            <StatLabel>Variables</StatLabel>
          </StatItem>
          <StatItem>
            <StatValue>{stats.flags}</StatValue>
            <StatLabel>Flags</StatLabel>
          </StatItem>
        </StatsRow>
      </Section>

      <Section>
        <SectionTitle>
          🔄 Estado del Proyecto
          <span style={{ 
            fontSize: '10px', 
            color: 'var(--color-primary)', 
            marginLeft: '8px',
            fontWeight: 'normal'
          }}>
            (Sincronizado en tiempo real)
          </span>
        </SectionTitle>
        <div style={{ fontSize: '12px', color: 'var(--color-textSecondary)', lineHeight: '1.6' }}>
          <div style={{ marginBottom: '8px', padding: '8px', backgroundColor: 'var(--color-border)', borderRadius: '4px' }}>
            <strong style={{ color: 'var(--color-primary)' }}>📝 Modo de Trabajo:</strong> {state.previewMode ? 'Vista Previa' : 'Edición'}
          </div>
          
          <div style={{ marginBottom: '8px', padding: '8px', backgroundColor: 'var(--color-border)', borderRadius: '4px' }}>
            <strong style={{ color: 'var(--color-primary)' }}>📄 Archivo de Escena Actual:</strong> {projectStatus.currentSceneFile}
            {projectStatus.currentSceneFileElements > 0 && (
              <span style={{ color: 'var(--color-success)', marginLeft: '8px' }}>
                ({projectStatus.currentSceneFileElements} elementos)
              </span>
            )}
          </div>
          
          <div style={{ marginBottom: '8px', padding: '8px', backgroundColor: 'var(--color-border)', borderRadius: '4px' }}>
            <strong style={{ color: 'var(--color-primary)' }}>🎯 Elemento Seleccionado:</strong> {projectStatus.currentElement}
          </div>
          
          <div style={{ marginBottom: '8px', padding: '8px', backgroundColor: 'var(--color-border)', borderRadius: '4px' }}>
            <strong style={{ color: 'var(--color-primary)' }}>💾 Auto-guardado:</strong> 
            <span style={{ color: projectStatus.autoSaveStatus === 'Activo' ? 'var(--color-success)' : 'var(--color-error)', marginLeft: '8px' }}>
              {projectStatus.autoSaveStatus}
            </span>
          </div>
          
          <div style={{ marginBottom: '8px', padding: '8px', backgroundColor: 'var(--color-border)', borderRadius: '4px' }}>
            <strong style={{ color: 'var(--color-primary)' }}>⏰ Última Modificación:</strong> {projectStatus.lastModified}
          </div>
          
          <div style={{ padding: '8px', backgroundColor: 'var(--color-border)', borderRadius: '4px' }}>
            <strong style={{ color: 'var(--color-primary)' }}>📊 Progreso del Proyecto:</strong> 
            <span style={{ marginLeft: '8px' }}>
              {projectStatus.totalSceneFiles} archivos de escena, {stats.totalElements} elementos totales
            </span>
          </div>
        </div>
      </Section>

      {/* Sistema de Backup .ryn */}
      <BackupManager />
    </PanelContainer>
  );
}

export default ProjectPanel;
