import React, { useState, useRef, useEffect } from 'react';
import styled, { keyframes, css } from 'styled-components';

// Animaciones
const fadeIn = keyframes`
  from {
    opacity: 0;
    transform: scale(0.8);
  }
  to {
    opacity: 1;
    transform: scale(1);
  }
`;

const slideIn = keyframes`
  from {
    opacity: 0;
    transform: translateY(20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
`;

const pulse = keyframes`
  0% {
    transform: scale(1);
  }
  50% {
    transform: scale(1.1);
  }
  100% {
    transform: scale(1);
  }
`;

// Styled Components
const FloatingChatContainer = styled.div`
  position: fixed;
  bottom: 20px;
  right: 20px;
  z-index: 1000;
  
  @media (max-width: 768px) {
    bottom: 10px;
    right: 10px;
  }
`;

const ChatToggleButton = styled.button`
  width: 60px;
  height: 60px;
  border-radius: 50%;
  background: linear-gradient(135deg, var(--color-primary) 0%, var(--color-primaryHover) 100%);
  border: 3px solid var(--color-surface);
  color: var(--color-textInverse);
  font-size: 24px;
  cursor: pointer;
  box-shadow: 0 8px 24px var(--color-shadowElevated);
  transition: all 0.3s ease;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  
  &:hover {
    transform: translateY(-3px) scale(1.05);
    box-shadow: 0 12px 32px var(--color-shadowHover);
  }
  
  &:active {
    transform: translateY(-1px) scale(1.02);
  }
  
  @media (max-width: 768px) {
    width: 50px;
    height: 50px;
    font-size: 20px;
  }
  
  ${props => props.hasNewMessages && css`
    animation: ${pulse} 2s infinite;
  `}
`;

const NewMessageIndicator = styled.div`
  position: absolute;
  top: -5px;
  right: -5px;
  width: 20px;
  height: 20px;
  background: var(--color-error);
  color: var(--color-textInverse);
  border-radius: 50%;
  font-size: 12px;
  font-weight: bold;
  display: flex;
  align-items: center;
  justify-content: center;
  border: 2px solid var(--color-surface);
  
  @media (max-width: 768px) {
    width: 16px;
    height: 16px;
    font-size: 10px;
  }
`;

const ChatWindow = styled.div`
  position: absolute;
  bottom: 70px;
  right: 0;
  width: 350px;
  height: 400px;
  background: var(--color-surface);
  border: 2px solid var(--color-primary);
  border-radius: 16px;
  box-shadow: 0 20px 40px var(--color-shadowElevated);
  display: flex;
  flex-direction: column;
  animation: ${css`${slideIn} 0.3s ease-out`};
  overflow: hidden;
  
  @media (max-width: 768px) {
    width: 280px;
    height: 350px;
    bottom: 60px;
    right: -10px;
  }
  
  @media (max-width: 480px) {
    width: calc(100vw - 20px);
    height: 300px;
    right: 10px;
    left: 10px;
    bottom: 60px;
  }
`;

const ChatHeader = styled.div`
  background: var(--color-primary);
  color: var(--color-textInverse);
  padding: 12px 16px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  font-weight: 600;
  font-size: 14px;
`;

const ChatCloseButton = styled.button`
  background: none;
  border: none;
  color: var(--color-textInverse);
  font-size: 18px;
  cursor: pointer;
  padding: 4px;
  border-radius: 4px;
  transition: background-color 0.2s ease;
  
  &:hover {
    background: rgba(255, 255, 255, 0.2);
  }
`;

const ChatMessages = styled.div`
  flex: 1;
  padding: 12px;
  overflow-y: auto;
  display: flex;
  flex-direction: column;
  gap: 8px;
  
  &::-webkit-scrollbar {
    width: 6px;
  }
  
  &::-webkit-scrollbar-track {
    background: var(--color-backgroundSecondary);
    border-radius: 3px;
  }
  
  &::-webkit-scrollbar-thumb {
    background: var(--color-border);
    border-radius: 3px;
  }
  
  &::-webkit-scrollbar-thumb:hover {
    background: var(--color-borderSecondary);
  }
`;

const Message = styled.div`
  display: flex;
  flex-direction: column;
  gap: 4px;
  animation: ${css`${fadeIn} 0.3s ease-out`};
`;

const MessageHeader = styled.div`
  display: flex;
  align-items: center;
  gap: 8px;
  font-size: 11px;
  color: var(--color-textSecondary);
`;

const MessageAuthor = styled.span`
  font-weight: 600;
  color: ${props => props.isOwn ? 'var(--color-primary)' : 'var(--color-textSecondary)'};
`;

const MessageTime = styled.span`
  font-size: 10px;
  opacity: 0.7;
`;

const MessageContent = styled.div`
  background: ${props => props.isOwn 
    ? 'linear-gradient(135deg, var(--color-primary) 0%, var(--color-primaryHover) 100%)' 
    : 'var(--color-surfaceSecondary)'};
  color: ${props => props.isOwn ? 'var(--color-textInverse)' : 'var(--color-textPrimary)'};
  padding: 8px 12px;
  border-radius: 12px;
  font-size: 13px;
  line-height: 1.4;
  max-width: 80%;
  align-self: ${props => props.isOwn ? 'flex-end' : 'flex-start'};
  word-wrap: break-word;
  box-shadow: ${props => props.isOwn 
    ? '0 2px 8px rgba(0, 0, 0, 0.1)' 
    : '0 1px 4px rgba(0, 0, 0, 0.1)'};
`;

const ChatInput = styled.div`
  padding: 12px;
  border-top: 1px solid var(--color-border);
  display: flex;
  gap: 8px;
  align-items: center;
`;

const MessageInput = styled.input`
  flex: 1;
  padding: 8px 12px;
  border: 1px solid var(--color-border);
  border-radius: 20px;
  background: var(--color-backgroundSecondary);
  color: var(--color-textPrimary);
  font-size: 13px;
  outline: none;
  transition: border-color 0.2s ease;
  
  &:focus {
    border-color: var(--color-primary);
  }
  
  &::placeholder {
    color: var(--color-textTertiary);
  }
`;

const SendButton = styled.button`
  width: 36px;
  height: 36px;
  border-radius: 50%;
  background: var(--color-primary);
  color: var(--color-textInverse);
  border: none;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 16px;
  transition: all 0.2s ease;
  
  &:hover:not(:disabled) {
    background: var(--color-primaryHover);
    transform: scale(1.05);
  }
  
  &:disabled {
    opacity: 0.5;
    cursor: not-allowed;
  }
`;

const EmptyState = styled.div`
  flex: 1;
  display: flex;
  align-items: center;
  justify-content: center;
  color: var(--color-textSecondary);
  font-size: 13px;
  text-align: center;
  padding: 20px;
`;

function FloatingChat({ 
  messages = [], 
  onSendMessage, 
  currentUser = 'Usuario',
  isConnected = false,
  roomCode = ''
}) {
  const [isOpen, setIsOpen] = useState(false);
  const [inputValue, setInputValue] = useState('');
  const [hasNewMessages, setHasNewMessages] = useState(false);
  const messagesEndRef = useRef(null);
  const chatMessagesRef = useRef(null);

  // Auto-scroll to bottom when new messages arrive
  useEffect(() => {
    if (messagesEndRef.current) {
      messagesEndRef.current.scrollIntoView({ behavior: 'smooth' });
    }
  }, [messages]);

  // Show new message indicator when messages arrive and chat is closed
  useEffect(() => {
    if (messages.length > 0 && !isOpen) {
      setHasNewMessages(true);
    }
  }, [messages, isOpen]);

  // Clear new message indicator when chat is opened
  useEffect(() => {
    if (isOpen) {
      setHasNewMessages(false);
    }
  }, [isOpen]);

  const handleSendMessage = () => {
    if (inputValue.trim() && onSendMessage) {
      onSendMessage(inputValue.trim());
      setInputValue('');
    }
  };

  const handleKeyPress = (e) => {
    if (e.key === 'Enter' && !e.shiftKey) {
      e.preventDefault();
      handleSendMessage();
    }
  };

  const formatTime = (timestamp) => {
    return new Date(timestamp).toLocaleTimeString('es-ES', {
      hour: '2-digit',
      minute: '2-digit'
    });
  };

  if (!isConnected && !roomCode) {
    return null; // Don't show chat if not in a room
  }

  return (
    <FloatingChatContainer>
      <ChatToggleButton 
        onClick={() => setIsOpen(!isOpen)}
        hasNewMessages={hasNewMessages}
        title={isOpen ? 'Cerrar chat' : 'Abrir chat'}
      >
        💬
        {hasNewMessages && (
          <NewMessageIndicator>
            {messages.length}
          </NewMessageIndicator>
        )}
      </ChatToggleButton>

      {isOpen && (
        <ChatWindow>
          <ChatHeader>
            <span>💬 Chat - Sala {roomCode}</span>
            <ChatCloseButton onClick={() => setIsOpen(false)}>
              ×
            </ChatCloseButton>
          </ChatHeader>

          <ChatMessages ref={chatMessagesRef}>
            {messages.length === 0 ? (
              <EmptyState>
                <div>
                  <div style={{ fontSize: '24px', marginBottom: '8px' }}>💬</div>
                  <div>¡Empieza la conversación!</div>
                  <div style={{ fontSize: '11px', opacity: 0.7, marginTop: '4px' }}>
                    Escribe un mensaje para tus colaboradores
                  </div>
                </div>
              </EmptyState>
            ) : (
              messages.map((message, index) => (
                <Message key={index}>
                  <MessageHeader>
                    <MessageAuthor isOwn={message.user === currentUser}>
                      {message.user === currentUser ? 'Tú' : message.user}
                    </MessageAuthor>
                    <MessageTime>
                      {formatTime(message.timestamp)}
                    </MessageTime>
                  </MessageHeader>
                  <MessageContent isOwn={message.user === currentUser}>
                    {message.text}
                  </MessageContent>
                </Message>
              ))
            )}
            <div ref={messagesEndRef} />
          </ChatMessages>

          <ChatInput>
            <MessageInput
              value={inputValue}
              onChange={(e) => setInputValue(e.target.value)}
              onKeyPress={handleKeyPress}
              placeholder="Escribe un mensaje..."
              disabled={!isConnected}
            />
            <SendButton 
              onClick={handleSendMessage}
              disabled={!inputValue.trim() || !isConnected}
            >
              ➤
            </SendButton>
          </ChatInput>
        </ChatWindow>
      )}
    </FloatingChatContainer>
  );
}

export default FloatingChat;
