// Generador de scripts Ren'Py
export class RenPyGenerator {
  constructor() {
    this.script = '';
    this.characters = new Map();
    this.images = new Set();
    this.audio = new Set();
    this.assetStorage = null;
  }

  // Configurar AssetStorage para acceso a archivos
  setAssetStorage(assetStorage) {
    this.assetStorage = assetStorage;
  }

  // Configurar proyecto para acceso a personajes
  setProject(project) {
    this.project = project;
  }

  // Función para obtener el nombre de imagen definido
  async getImageDefinitionName(fileName, category) {
    if (!this.assetStorage) return null;
    
    try {
      console.log('getImageDefinitionName - fileName:', fileName);
      console.log('getImageDefinitionName - category:', category);
      
      // Extraer solo el nombre del archivo de la ruta completa
      let name = fileName;
      
      // Si es una ruta completa, extraer solo el nombre del archivo
      if (fileName.includes('/')) {
        name = fileName.split('/').pop(); // Obtener la última parte de la ruta
      }
      
      console.log('getImageDefinitionName - nombre extraído:', name);
      
      // Remover extensión
      name = name.replace(/\.[^/.]+$/, "");
      
      console.log('getImageDefinitionName - sin extensión:', name);
      
      // Para personajes, buscar el personaje correspondiente en el proyecto
      if (category === 'characters' && this.project) {
        const character = this.project.characters.find(char => 
          char.sprites && char.sprites.some(sprite => 
            sprite.name.toLowerCase() === name.toLowerCase()
          )
        );
        
        if (character) {
          const spriteName = name.toLowerCase();
          const finalName = `${character.id} ${spriteName}`;
          console.log('getImageDefinitionName - personaje encontrado:', character.id);
          console.log('getImageDefinitionName - nombre final:', finalName);
          return finalName;
        }
      }
      
      // Convertir a minúsculas y reemplazar espacios y caracteres especiales
      name = name.toLowerCase()
        .replace(/\s+/g, '_')
        .replace(/[^a-z0-9_]/g, '')
        .replace(/_+/g, '_')
        .replace(/^_|_$/g, '');
      
      console.log('getImageDefinitionName - nombre procesado:', name);
      
      // Agregar prefijo según categoría
      let finalName;
      switch (category) {
        case 'characters':
          finalName = `char_${name}`;
          break;
        case 'backgrounds':
          finalName = `bg_${name}`;
          break;
        case 'gui':
          finalName = `gui_${name}`;
          break;
        case 'effects':
          finalName = `fx_${name}`;
          break;
        default:
          finalName = name;
      }
      
      console.log('getImageDefinitionName - nombre final:', finalName);
      return finalName;
    } catch (error) {
      console.error('Error al generar nombre de imagen:', error);
      return null;
    }
  }

  // Formatear texto de narración con estilos de Ren'Py
  formatNarrationText(element) {
    let text = element.text || '';
    
    // Aplicar estilos si están definidos
    if (element.fontSize || element.textColor) {
      let styledText = text;
      
      // Aplicar tamaño de fuente usando text tags de Ren'Py
      if (element.fontSize) {
        const fontSizeMap = {
          'small': '12',
          'medium': '16', 
          'large': '20',
          'xlarge': '24'
        };
        const size = fontSizeMap[element.fontSize] || '16';
        styledText = `{size=${size}}${styledText}{/size}`;
      }
      
      // Aplicar color usando text tags de Ren'Py
      if (element.textColor) {
        styledText = `{color=${element.textColor}}${styledText}{/color}`;
      }
      
      return styledText;
    }
    
    return text;
  }

  // Generar el script principal de Ren'Py
  async generateScript(project) {
    this.script = '';
    this.characters.clear();
    this.images.clear();
    this.audio.clear();

    // Asegurar que el proyecto esté configurado
    this.project = project;
    
    console.log('🔍 generateScript - Proyecto configurado:', this.project);
    console.log('🔍 generateScript - protagonistConfig:', this.project?.protagonistConfig);

    // Generar encabezado del script
    this.generateHeader(project);
    
    // Definir personajes
    this.generateCharacters(project.characters);
    
    // Definir variables y flags
    this.generateVariablesAndFlags(project.variables, project.flags);
    
    // Generar escenas
    this.generateScenes(project.scenes);
    
    // Generar definiciones de videos
    this.generateVideos(project);
    
    // Generar script principal (usar elementos del archivo principal)
    const mainSceneFile = project.sceneFiles.find(file => file.isMain);
    if (mainSceneFile) {
      await this.generateMainScript(mainSceneFile.elements);
    }

    return this.script;
  }

  // Generar archivos de escenas separados
  async generateSceneFiles(project) {
    const sceneFiles = {};
    
    // Primero, analizar todos los jumps para identificar referencias a archivos de escena
    const requiredLabels = this.analyzeJumpReferences(project);
    console.log('🔍 Labels requeridos por jumps:', requiredLabels);
    
    // Usar for...of con await para manejar funciones asíncronas
    for (const sceneFile of project.sceneFiles) {
      this.script = '';
      this.characters.clear();
      this.images.clear();
      this.audio.clear();

      if (sceneFile.isMain) {
        // Para el archivo principal, generar como script principal
        await this.generateMainScript(sceneFile.elements);
      } else {
        // Para archivos de escena, generar como archivos separados
        this.generateSceneFileHeader(sceneFile);
        
        // Verificar si este archivo necesita labels adicionales por jumps
        const fileRequiredLabels = requiredLabels.filter(label => 
          label.sceneFileId === sceneFile.id
        );
        
        // Generar labels requeridos por jumps si no existen
        this.generateRequiredLabels(sceneFile, fileRequiredLabels);
        
        await this.generateSceneFileElements(sceneFile.elements);
      }

      sceneFiles[sceneFile.name] = this.script;
    }

    return sceneFiles;
  }

  // Generar un archivo de escena específico
  async generateSceneFile(sceneFile) {
    this.script = '';
    this.characters.clear();
    this.images.clear();
    this.audio.clear();

    if (sceneFile.isMain) {
      // Si es el archivo principal, generar como script principal
      await this.generateMainScript(sceneFile.elements);
    } else {
      // Si es un archivo de escena, generar como archivo separado
      // Verificar si hay elementos de tipo 'label' en todo el archivo
      const hasAnyManualLabel = sceneFile.elements.some(element => element.type === 'label');
      console.log(`🔍 Generando archivo de escena: ${sceneFile.name}`);
      console.log(`🔍 Elementos totales: ${sceneFile.elements.length}`);
      console.log(`🔍 Tiene labels manuales: ${hasAnyManualLabel}`);
      console.log(`🔍 Generará label automático: ${!hasAnyManualLabel}`);
      
      this.generateSceneFileHeader(sceneFile, !hasAnyManualLabel);
      await this.generateSceneFileElements(sceneFile.elements);
    }

    return this.script;
  }

  // Analizar todas las referencias de jump en el proyecto
  analyzeJumpReferences(project) {
    const requiredLabels = [];
    const sceneFileIds = project.sceneFiles.map(file => file.id);
    
    console.log('🔍 Analizando referencias de jump en el proyecto...');
    console.log('📁 IDs de archivos de escena:', sceneFileIds);
    
    // Analizar todos los elementos de todos los archivos de escena
    for (const sceneFile of project.sceneFiles) {
      console.log(`🔍 Analizando archivo: ${sceneFile.name} (${sceneFile.elements.length} elementos)`);
      
      for (const element of sceneFile.elements) {
        // Buscar jumps en elementos de tipo 'jump'
        if (element.type === 'jump' && element.jump) {
          const jumpTarget = element.jump;
          console.log(`🔍 Jump encontrado: ${jumpTarget} en ${sceneFile.name}`);
          
          // Verificar si el jump target coincide con un ID de archivo de escena
          if (sceneFileIds.includes(jumpTarget)) {
            console.log(`✅ Jump target ${jumpTarget} coincide con archivo de escena`);
            requiredLabels.push({
              labelName: jumpTarget,
              sceneFileId: jumpTarget,
              requiredBy: {
                file: sceneFile.name,
                element: element.id
              }
            });
          }
        }
        
        // Buscar jumps en elementos de tipo 'choice'
        if (element.type === 'choice' && element.choices) {
          for (const choice of element.choices) {
            if (choice.jump && sceneFileIds.includes(choice.jump)) {
              console.log(`✅ Choice jump target ${choice.jump} coincide con archivo de escena`);
              requiredLabels.push({
                labelName: choice.jump,
                sceneFileId: choice.jump,
                requiredBy: {
                  file: sceneFile.name,
                  element: element.id,
                  choice: choice.text
                }
              });
            }
          }
        }
      }
    }
    
    return requiredLabels;
  }

  // Generar labels requeridos por jumps
  generateRequiredLabels(sceneFile, requiredLabels) {
    if (requiredLabels.length === 0) return;
    
    console.log(`🏷️ Generando labels requeridos para ${sceneFile.name}:`, requiredLabels);
    
    for (const requiredLabel of requiredLabels) {
      // Verificar si el label ya existe en los elementos del archivo
      const labelExists = sceneFile.elements.some(element => 
        element.type === 'label' && element.label === requiredLabel.labelName
      );
      
      if (!labelExists) {
        console.log(`✅ Generando label automático: ${requiredLabel.labelName} (requerido por ${requiredLabel.requiredBy.file})`);
        this.script += `label ${requiredLabel.labelName}:\n\n`;
      } else {
        console.log(`⏭️ Label ya existe: ${requiredLabel.labelName}`);
      }
    }
  }

  // Generar encabezado para archivo de escena
  generateSceneFileHeader(sceneFile, shouldGenerateAutoLabel = true) {
    this.script += `# Generado por RynEditor - Gracias por confiar en nosotros\n\n`;
    
    // Agregar label para archivos de escena (no para el archivo principal)
    // Solo si no se va a generar un label manualmente desde los elementos
    if (!sceneFile.isMain && shouldGenerateAutoLabel) {
      // Crear un nombre de label basado en el ID o nombre del archivo
      const labelName = sceneFile.id || sceneFile.name.replace('.rpy', '').replace(/\s+/g, '_').toLowerCase();
      console.log(`🤖 Generando label automático: ${labelName}`);
      this.script += `label ${labelName}:\n\n`;
    } else if (!sceneFile.isMain) {
      console.log(`⏭️ Saltando generación de label automático para ${sceneFile.name}`);
    }
  }

  // Generar elementos de archivo de escena
  async generateSceneFileElements(elements) {
    if (elements.length === 0) {
      this.script += `# Esta escena está vacía\n`;
      return;
    }

    // Usar for...of con await para manejar funciones asíncronas
    for (let index = 0; index < elements.length; index++) {
      await this.generateScriptElement(elements[index], index);
    }
  }

  // Generar encabezado del proyecto
  generateHeader(project) {
    this.script += `# Generado por RynEditor - Gracias por confiar en nosotros\n\n`;
    
    if (project.name) {
    this.script += `# ${project.name}\n`;
    }
    
    if (project.description) {
      this.script += `# ${project.description}\n`;
    }
    
    this.script += `\n`;
  }

  // Definir personajes
  generateCharacters(characters) {
    if (characters.length === 0) return;

    this.script += `# Definición de personajes\n`;
    
    characters.forEach(character => {
      this.characters.set(character.id, character.name);
      this.script += `define ${character.id} = Character("${character.name}"`;
      
      if (character.color) {
        this.script += `, color="${character.color}"`;
      }
      
      if (character.who_color) {
        this.script += `, who_color="${character.who_color}"`;
      }
      
      if (character.what_color) {
        this.script += `, what_color="${character.what_color}"`;
      }
      
      this.script += `)\n`;
      
      // Generar sprites del personaje
      if (character.sprites && character.sprites.length > 0) {
        character.sprites.forEach(sprite => {
          // Convertir ruta a formato Ren'Py (sin game/ prefix)
          let renpyPath = sprite.path;
          
          // Limpiar rutas para Ren'Py
          if (renpyPath.startsWith('images/')) {
            renpyPath = renpyPath.replace('images/', '');
          }
          if (renpyPath.startsWith('game/')) {
            renpyPath = renpyPath.replace('game/', '');
          }
          
          // Generar nombre limpio para el sprite
          const spriteName = sprite.name.toLowerCase()
            .replace(/\s+/g, '_')
            .replace(/[^a-z0-9_]/g, '');
          
          this.script += `image ${character.id} ${spriteName} = "${renpyPath}"\n`;
          this.images.add(sprite.path);
        });
      }
    });
    
    this.script += `\n`;
  }

  // Generar variables y flags
  generateVariablesAndFlags(variables, flags) {
    // Verificar que las variables y flags existan y sean arrays
    const safeVariables = Array.isArray(variables) ? variables : [];
    const safeFlags = Array.isArray(flags) ? flags : [];
    
    if (safeVariables.length === 0 && safeFlags.length === 0) return;

    this.script += `# Variables y Flags del proyecto\n`;
    
    // Generar variables
    safeVariables.forEach(variable => {
      if (variable && variable.name) {
        let value = variable.value;
        if (variable.type === 'string') {
          value = `"${value}"`;
        } else if (variable.type === 'boolean') {
          value = value ? 'True' : 'False';
        }
        this.script += `default ${variable.name} = ${value}\n`;
      }
    });
    
    // Generar flags
    safeFlags.forEach(flag => {
      if (flag && flag.name) {
        const value = flag.value ? 'True' : 'False';
        this.script += `default ${flag.name} = ${value}\n`;
      }
    });
    
    this.script += `\n`;
  }

  // Generar escenas
  generateScenes(scenes) {
    if (scenes.length === 0) return;

    this.script += `# Definición de escenas\n`;
    
    scenes.forEach(scene => {
      this.script += `# Escena: ${scene.name}\n`;
      
      if (scene.background) {
        // Convertir ruta a formato Ren'Py (sin game/ prefix)
        const renpyPath = scene.background.replace('images/', '');
        this.script += `image ${scene.id} = "${renpyPath}"\n`;
        this.images.add(scene.background);
      }
      
      if (scene.music) {
        // Convertir ruta a formato Ren'Py (sin game/ prefix)
        const renpyPath = scene.music.replace('audio/', '');
        this.script += `define audio.${scene.id}_music = "${renpyPath}"\n`;
        this.audio.add(scene.music);
      }
    });
    
    this.script += `\n`;
  }

  // Generar definiciones de videos
  generateVideos(project) {
    // Buscar todos los videos usados en el proyecto
    const videos = new Set();
    const movieSprites = new Map();
    
    // Buscar en todos los archivos de escenas
    project.sceneFiles.forEach(sceneFile => {
      sceneFile.elements.forEach(element => {
        if ((element.type === 'video' || element.type === 'movie_sprite' || element.type === 'movie_cutscene') && element.file) {
          if (element.type === 'movie_sprite' && element.spriteName) {
            // Para movie sprites, usar el nombre personalizado
            movieSprites.set(element.spriteName, element.file);
          } else {
            // Para videos normales y cutscenes
            videos.add(element.file);
          }
        }
      });
    });

    if (videos.size === 0 && movieSprites.size === 0) return;

    this.script += `# Definición de videos\n`;
    
    // Generar definiciones de videos normales
    videos.forEach(videoPath => {
      // Convertir ruta a formato Ren'Py (sin game/ prefix)
      const renpyPath = videoPath.replace('videos/', '');
      const videoName = renpyPath.replace(/\.[^/.]+$/, "").replace(/[^a-zA-Z0-9_]/g, '_');
      
      // Generar definición de video
      this.script += `image ${videoName} = Movie(play="${renpyPath}")\n`;
    });
    
    // Generar definiciones de movie sprites
    movieSprites.forEach((videoPath, spriteName) => {
      const renpyPath = videoPath.replace('videos/', '');
      this.script += `image ${spriteName} = Movie(play="${renpyPath}")\n`;
    });
    
    this.script += `\n`;
  }

  // Generar script principal
  async generateMainScript(scriptElements) {
    this.script += `label start:\n\n`;

    // Usar for...of con await para manejar funciones asíncronas
    for (let index = 0; index < scriptElements.length; index++) {
      await this.generateScriptElement(scriptElements[index], index);
    }

    // Agregar saltos a archivos de escenas
    this.generateSceneJumps();

    this.script += `\n    return\n`;
  }

  // Generar setup del protagonista
  generateProtagonistSetup() {
    console.log('🔧 generateProtagonistSetup - Iniciando generación...');
    const config = this.project.protagonistConfig;
    const defaultName = config.defaultName || 'Jugador';
    const promptText = config.promptText || 'Por favor, introduce tu nombre:';
    
    console.log('🔧 Configuración:', { defaultName, promptText });
    
    this.script += `# Configuración del protagonista\n`;
    this.script += `label setup_protagonist:\n`;
    this.script += `    python:\n`;
    this.script += `        mc_name = renpy.input("${promptText}")\n`;
    this.script += `        mc_name = mc_name.strip() or "${defaultName}"\n\n`;
    this.script += `    "Tu nombre es [mc_name]."\n`;
    this.script += `    menu:\n`;
    this.script += `        "¿Estás seguro?"\n`;
    this.script += `        "Sí, continuar.":\n`;
    this.script += `            "¡Perfecto! Ahora comienza tu historia..."\n`;
    this.script += `            jump main_story\n`;
    this.script += `        "No, quiero cambiar mi nombre.":\n`;
    this.script += `            jump setup_protagonist\n\n`;
    
    console.log('✅ generateProtagonistSetup - Código generado exitosamente');
  }

  // Generar saltos a archivos de escenas
  generateSceneJumps() {
    // Esto se completará cuando se implemente la lógica de saltos
    // Por ahora, se puede agregar manualmente en el script principal
  }

  // Generar elemento individual del script
  async generateScriptElement(element, index) {
    const indent = '    ';

    switch (element.type) {
      case 'dialogue':
        // Mostrar sprite si está especificado
        if (element.showSprite && element.character) {
          const position = element.spritePosition || 'center';
          const positionMap = {
            'left': 'at left',
            'center': 'at center',
            'right': 'at right'
          };
          
          // Limpiar nombre del sprite para coincidir con la definición
          const cleanSpriteName = element.showSprite.toLowerCase()
            .replace(/\s+/g, '_')
            .replace(/[^a-z0-9_]/g, '');
          
          this.script += `${indent}show ${element.character} ${cleanSpriteName} ${positionMap[position]}\n`;
        }
        
        // Generar el diálogo
        if (element.character && this.characters.has(element.character)) {
          // Verificar si es el protagonista
          const character = this.project.characters.find(char => char.id === element.character);
          if (character && character.isProtagonist) {
            // Para el protagonista, usar mc_name y reemplazar [mc_name] con mc_name
            const dialogueText = element.text.replace(/\[mc_name\]/g, '[mc_name]');
            this.script += `${indent}mc_name "${dialogueText}"\n`;
          } else {
            // Para otros personajes, usar el ID del personaje
            this.script += `${indent}${element.character} "${element.text}"\n`;
          }
        } else {
          this.script += `${indent}'${element.text}'\n`;
        }
        break;

      case 'narration':
        // Aplicar estilos de Ren'Py si están definidos
        const narrationText = this.formatNarrationText(element);
        this.script += `${indent}'${narrationText}'\n`;
        break;

      case 'scene':
        if (element.background) {
          // Obtener el nombre de imagen definido
          const imageName = await this.getImageDefinitionName(element.background, 'backgrounds');
          if (imageName) {
            this.script += `${indent}scene ${imageName}`;
          } else {
            // Fallback: usar ruta directa
            let renpyPath = element.background.replace('images/', '');
            renpyPath = renpyPath.replace('game/', '');
            this.script += `${indent}scene ${renpyPath}`;
          }
          
          // Agregar transición si está definida
          if (element.backgroundTransition) {
            const transitionType = element.backgroundTransition;
            const transitionDuration = element.backgroundTransitionDuration || '';
            
            if (transitionDuration) {
              this.script += ` with ${transitionType}(${transitionDuration})\n`;
            } else {
              this.script += ` with ${transitionType}\n`;
            }
          } else {
            this.script += '\n';
          }
        }
        // Manejar show/hide de personajes en elementos de escena
        if (element.show && Array.isArray(element.show)) {
          element.show.forEach(characterInfo => {
            if (typeof characterInfo === 'string') {
              // Formato simple: solo nombre del personaje
              this.script += `${indent}show ${characterInfo}\n`;
            } else if (characterInfo.character && characterInfo.sprite) {
              // Formato completo: personaje con sprite y posición
              const position = characterInfo.position || 'center';
              const positionMap = {
                'left': 'at left',
                'center': 'at center',
                'right': 'at right'
              };
              
              const cleanSpriteName = characterInfo.sprite.toLowerCase()
                .replace(/\s+/g, '_')
                .replace(/[^a-z0-9_]/g, '');
              
              this.script += `${indent}show ${characterInfo.character} ${cleanSpriteName} ${positionMap[position]}\n`;
            }
          });
        }
        
        if (element.hide && Array.isArray(element.hide)) {
          element.hide.forEach(character => {
            this.script += `${indent}hide ${character}\n`;
          });
        }
        break;

      case 'transition':
        // Generar comando de transición
        const transitionType = element.transitionType || 'dissolve';
        const transitionDuration = element.transitionDuration || '';
        
        if (transitionDuration) {
          // Usar duración personalizada
          this.script += `${indent}with ${transitionType}(${transitionDuration})\n`;
        } else {
          // Usar duración por defecto
          this.script += `${indent}with ${transitionType}\n`;
        }
        break;

      case 'choice':
        if (element.text) {
          this.script += `${indent}'${element.text}'\n`;
        }
        this.script += `${indent}menu:\n\n`;
        element.choices.forEach(choice => {
          // Agregar condición si existe
          const conditionText = choice.condition ? ` if ${choice.condition}` : '';
          this.script += `${indent}    "${choice.text}"${conditionText}:\n\n`;
          if (choice.jump) {
            this.script += `${indent}        jump ${choice.jump}\n`;
          }
        });
        break;

      case 'jump':
        this.script += `${indent}jump ${element.jump}\n\n`;
        break;

      case 'label':
        console.log(`🏷️ Generando label manual: ${element.label}`);
        this.script += `label ${element.label}:\n\n`;
        break;

      case 'condition':
        this.script += `${indent}if ${element.condition}:\n`;
        if (element.text) {
          this.script += `${indent}    '${element.text}'\n`;
        }
        break;

      case 'variable':
        this.script += `${indent}$ ${element.variableName} = ${element.variableValue}\n`;
        break;

      case 'flag':
        this.script += `${indent}$ ${element.flagName} = ${element.flagValue}\n`;
        break;

      case 'music':
        if (element.action === 'play') {
          // Convertir ruta a formato Ren'Py (sin game/ prefix)
          const renpyPath = element.file.replace('audio/', '');
          this.script += `${indent}play music "${renpyPath}"\n`;
        } else if (element.action === 'stop') {
          this.script += `${indent}stop music\n`;
        }
        this.audio.add(element.file);
        break;

      case 'sound':
        // Convertir ruta a formato Ren'Py (sin game/ prefix)
        const renpyPath = element.file.replace('audio/', '');
        this.script += `${indent}play sound "${renpyPath}"\n`;
        this.audio.add(element.file);
        break;

      case 'pause':
        this.script += `${indent}$ renpy.pause(${element.duration || 1.0})\n`;
        break;

      case 'show_sprite':
        if (element.character && element.spriteName) {
          // Agregar posición si está definida
          const position = element.spritePosition || 'center';
          const positionMap = {
            'left': 'at left',
            'center': 'at center', 
            'right': 'at right'
          };
          
          this.script += `${indent}show ${element.character} ${element.spriteName} ${positionMap[position]}`;
          
          // Agregar transición si está definida
          if (element.spriteTransition) {
            const transitionType = element.spriteTransition;
            const transitionDuration = element.spriteTransitionDuration || '';
            
            if (transitionDuration) {
              this.script += ` with ${transitionType}(${transitionDuration})\n`;
            } else {
              this.script += ` with ${transitionType}\n`;
            }
          } else {
            this.script += '\n';
          }
        } else if (element.character) {
          // Si no hay sprite específico, mostrar el personaje por defecto
          const position = element.spritePosition || 'center';
          const positionMap = {
            'left': 'at left',
            'center': 'at center',
            'right': 'at right'
          };
          
          this.script += `${indent}show ${element.character} ${positionMap[position]}\n`;
        }
        break;

      case 'hide_sprite':
        if (element.character) {
          // Agregar transición si está definida
          if (element.spriteTransition) {
            const transitionType = element.spriteTransition;
            const transitionDuration = element.spriteTransitionDuration || '';
            
            if (transitionDuration) {
              this.script += `${indent}hide ${element.character} with ${transitionType}(${transitionDuration})\n`;
            } else {
              this.script += `${indent}hide ${element.character} with ${transitionType}\n`;
            }
          } else {
            this.script += `${indent}hide ${element.character}\n`;
          }
        }
        break;

      case 'video':
        if (element.file) {
          // Convertir ruta a formato Ren'Py (sin game/ prefix)
          const renpyPath = element.file.replace('videos/', '');
          if (element.displayType === 'fullscreen') {
            this.script += `${indent}$ renpy.movie_cutscene("${renpyPath}"`;
            if (element.delay !== undefined && element.delay !== '') {
              this.script += `, delay=${element.delay}`;
            }
            if (element.loops !== undefined && element.loops !== 0) {
              this.script += `, loops=${element.loops}`;
            }
            if (element.stopMusic !== undefined && element.stopMusic === false) {
              this.script += `, stop_music=False`;
            }
            this.script += `)\n`;
          } else {
            // Video como displayable - usar el nombre generado en generateVideos
            const videoName = renpyPath.replace(/\.[^/.]+$/, "").replace(/[^a-zA-Z0-9_]/g, '_');
            this.script += `${indent}show ${videoName}\n`;
          }
        }
        break;

      case 'movie_sprite':
        if (element.file) {
          const renpyPath = element.file.replace('videos/', '');
          const spriteName = element.spriteName || 'movie_sprite';
          this.script += `${indent}show ${spriteName}\n`;
        }
        break;

      case 'movie_cutscene':
        if (element.file) {
          const renpyPath = element.file.replace('videos/', '');
          this.script += `${indent}$ renpy.movie_cutscene("${renpyPath}"`;
          if (element.delay !== undefined && element.delay !== '') {
            this.script += `, delay=${element.delay}`;
          }
          if (element.loops !== undefined && element.loops !== 0) {
            this.script += `, loops=${element.loops}`;
          }
          if (element.stopMusic !== undefined && element.stopMusic === false) {
            this.script += `, stop_music=False`;
          }
          this.script += `)\n`;
        }
        break;

      case 'protagonist_setup':
        // Generar setup del protagonista
        console.log('🎮 Generando setup del protagonista desde elemento...');
        this.generateProtagonistSetup();
        // Generar label main_story después del setup
        this.script += `label main_story:\n\n`;
        break;

      default:
        // Elemento desconocido - no generar nada
        break;
    }

    // Solo agregar salto de línea si el elemento no es un label o jump (ya tienen su propio espaciado)
    if (element.type !== 'label' && element.type !== 'jump') {
    this.script += `\n`;
    }
  }

  // Generar archivo de configuración options.rpy
  generateOptions(project) {
    return `# Configuración del juego
define config.name = _("${project.name}")

define gui.show_name = True

define config.version = "${project.version}"

define gui.about = _p("""
${project.description || 'Una novela visual creada con Visual Novel Creator'}
""")

define build.name = "${project.name.replace(/\s+/g, '_')}"

# Configuración de pantalla
define config.screen_width = 1920
define config.screen_height = 1080

# Configuración de audio
define config.has_sound = True
define config.has_music = True
define config.has_voice = True

# Configuración de texto
define config.enter_transition = dissolve
define config.exit_transition = dissolve
define config.after_load_transition = None
define config.end_game_transition = fade

# Configuración de ventana
define config.window = "auto"

# Configuración de archivos
define config.archives = None
`;
  }

  // Generar archivo de guión script.rpy
  async generateScriptFile(project) {
    return await this.generateScript(project);
  }

  // Obtener lista de recursos necesarios
  getResources() {
    return {
      images: Array.from(this.images),
      audio: Array.from(this.audio)
    };
  }
}
