import React, { useState, useEffect } from 'react';
import styled from 'styled-components';

const InfoContent = styled.div`
  color: var(--color-textPrimary);
  line-height: 1.6;
`;

const NavigationContainer = styled.div`
  display: flex;
  flex-wrap: wrap;
  gap: 8px;
  margin-bottom: 24px;
  padding: 16px;
  background: linear-gradient(135deg, var(--color-backgroundSecondary) 0%, var(--color-surface) 100%);
  border-radius: 12px;
  border: 1px solid var(--color-border);
`;

const NavTitle = styled.h4`
  width: 100%;
  margin: 0 0 12px 0;
  color: var(--color-primary);
  font-weight: 600;
`;

const NavButton = styled.button`
  background: linear-gradient(135deg, var(--color-primary) 0%, var(--color-primaryHover) 100%);
  color: var(--color-textInverse);
  border: none;
  padding: 6px 12px;
  border-radius: 8px;
  cursor: pointer;
  font-size: 12px;
  font-weight: 500;
  transition: all 0.3s ease;

  &:hover {
    transform: translateY(-2px);
    box-shadow: 0 4px 12px rgba(74, 158, 255, 0.3);
  }

  &:active {
    transform: translateY(0);
  }
`;

const InfoSection = styled.div`
  margin-bottom: 24px;
  padding: 20px;
  background: linear-gradient(135deg, var(--color-backgroundElevated) 0%, var(--color-surface) 100%);
  border-radius: 12px;
  border-left: 4px solid var(--color-primary);
`;

const SectionTitle = styled.h3`
  color: var(--color-primary);
  margin-bottom: 12px;
  font-weight: 600;
  display: flex;
  align-items: center;
  gap: 8px;
`;

const BackToTopButton = styled.button`
  position: sticky;
  bottom: 20px;
  left: 100%;
  background: linear-gradient(135deg, var(--color-warning) 0%, var(--color-warningHover) 100%);
  color: var(--color-textInverse);
  border: none;
  border-radius: 50%;
  width: 40px;
  height: 40px;
  cursor: pointer;
  font-size: 16px;
  display: flex;
  align-items: center;
  justify-content: center;
  box-shadow: 0 4px 12px var(--color-shadow);
  transition: all 0.3s ease;
  z-index: 2001;
  opacity: ${props => props.visible ? 1 : 0};
  visibility: ${props => props.visible ? 'visible' : 'hidden'};
  transform: ${props => props.visible ? 'scale(1)' : 'scale(0.8)'};
  margin-top: 20px;

  &:hover {
    transform: ${props => props.visible ? 'scale(1.1)' : 'scale(0.8)'};
    box-shadow: 0 6px 16px var(--color-shadowHover);
  }
`;

function InfoModalContent() {
  const [showBackToTop, setShowBackToTop] = useState(false);

  const scrollToSection = (sectionId) => {
    const element = document.getElementById(sectionId);
    if (element) {
      element.scrollIntoView({ 
        behavior: 'smooth',
        block: 'start'
      });
    }
  };

  const scrollToTop = () => {
    const modalContent = document.querySelector('.modal-content');
    if (modalContent) {
      modalContent.scrollTo({
        top: 0,
        behavior: 'smooth'
      });
    }
  };

  useEffect(() => {
    const handleScroll = (e) => {
      const target = e.target;
      if (target.classList.contains('modal-content')) {
        setShowBackToTop(target.scrollTop > 300);
      }
    };

    const modalContent = document.querySelector('.modal-content');
    if (modalContent) {
      modalContent.addEventListener('scroll', handleScroll);
      return () => modalContent.removeEventListener('scroll', handleScroll);
    }
  }, []);

  return (
    <InfoContent>
      <NavigationContainer>
        <NavTitle>📋 Navegación Rápida:</NavTitle>
        <NavButton onClick={() => scrollToSection('info-overview')}>Vista General</NavButton>
        <NavButton onClick={() => scrollToSection('info-interface')}>Interfaz</NavButton>
        <NavButton onClick={() => scrollToSection('info-projects')}>Proyectos</NavButton>
        <NavButton onClick={() => scrollToSection('info-characters')}>Personajes</NavButton>
        <NavButton onClick={() => scrollToSection('info-scenes')}>Escenas</NavButton>
        <NavButton onClick={() => scrollToSection('info-export')}>Exportar</NavButton>
        <NavButton onClick={() => scrollToSection('info-tips')}>Consejos</NavButton>
      </NavigationContainer>

      <InfoSection id="info-overview">
        <SectionTitle>🎯 Vista General del Editor</SectionTitle>
        <p>
          <strong>RynEditor</strong> es un editor visual completo para crear novelas visuales 
          compatibles con Ren'Py. Te permite crear historias interactivas sin necesidad 
          de programar directamente.
        </p>
        <ul>
          <li><strong>Interfaz Intuitiva:</strong> Diseño moderno y fácil de usar</li>
          <li><strong>Vista Previa en Tiempo Real:</strong> Ve cómo se ve tu novela mientras la creas</li>
          <li><strong>Compatibilidad Ren'Py:</strong> Exporta proyectos listos para usar</li>
          <li><strong>Gestión de Recursos:</strong> Organiza imágenes, audio y más</li>
        </ul>
      </InfoSection>

      <InfoSection id="info-interface">
        <SectionTitle>🖥️ Interfaz del Editor</SectionTitle>
        <h4>Componentes Principales:</h4>
        <ul>
          <li><strong>Sidebar Izquierdo:</strong> Herramientas principales (Proyecto, Personajes, Escenas, Script, Configuración)</li>
          <li><strong>Editor Central:</strong> Área de trabajo donde creas tu contenido</li>
          <li><strong>Panel de Vista Previa:</strong> Muestra cómo se verá tu novela</li>
          <li><strong>Barra Superior:</strong> Acciones rápidas y navegación</li>
          <li><strong>Botones Flotantes:</strong> Acceso rápido a ayuda y guías</li>
        </ul>
        
        <h4>Navegación:</h4>
        <ul>
          <li>Usa el <strong>sidebar</strong> para acceder a las diferentes herramientas</li>
          <li>Los <strong>tooltips</strong> te ayudan a entender cada función</li>
          <li>El <strong>scroll</strong> funciona con rueda del mouse en todos los paneles</li>
        </ul>
      </InfoSection>

      <InfoSection id="info-projects">
        <SectionTitle>📁 Gestión de Proyectos</SectionTitle>
        <h4>Crear un Nuevo Proyecto:</h4>
        <ol>
          <li>Ve al panel <strong>"📁 Proyecto"</strong> en el sidebar</li>
          <li>Haz clic en <strong>"Nuevo Proyecto"</strong></li>
          <li>Completa la información básica (nombre, descripción, autor)</li>
          <li>¡Tu proyecto está listo para comenzar!</li>
        </ol>
        
        <h4>Configuración del Proyecto:</h4>
        <ul>
          <li><strong>Información Básica:</strong> Título, descripción, versión</li>
          <li><strong>Configuración Visual:</strong> Resolución, colores, fuentes</li>
          <li><strong>Metadatos:</strong> Autor, etiquetas, categorías</li>
        </ul>
      </InfoSection>

      <InfoSection id="info-characters">
        <SectionTitle>👥 Sistema de Personajes</SectionTitle>
        <h4>Crear Personajes:</h4>
        <ul>
          <li><strong>Nombre:</strong> Identificador único para el personaje</li>
          <li><strong>Color:</strong> Color distintivo para los diálogos</li>
          <li><strong>Sprites:</strong> Imágenes del personaje en diferentes poses</li>
          <li><strong>Expresiones:</strong> Diferentes emociones (feliz, triste, enojado, etc.)</li>
        </ul>
        
        <h4>Buenas Prácticas:</h4>
        <ul>
          <li>Usa nombres descriptivos y únicos</li>
          <li>Mantén consistencia en los colores</li>
          <li>Organiza sprites por emociones</li>
          <li>Usa formatos PNG para transparencia</li>
        </ul>
      </InfoSection>

      <InfoSection id="info-scenes">
        <SectionTitle>🎭 Creación de Escenas</SectionTitle>
        <h4>Elementos de una Escena:</h4>
        <ul>
          <li><strong>Fondo:</strong> Imagen de ambiente donde ocurre la acción</li>
          <li><strong>Personajes:</strong> Sprites que aparecen en la escena</li>
          <li><strong>Diálogos:</strong> Conversaciones entre personajes</li>
          <li><strong>Narración:</strong> Texto descriptivo del narrador</li>
          <li><strong>Decisiones:</strong> Opciones que el jugador puede elegir</li>
        </ul>
        
        <h4>Flujo de Trabajo:</h4>
        <ol>
          <li>Selecciona o crea un fondo para la escena</li>
          <li>Añade personajes y posiciónalos</li>
          <li>Escribe diálogos y narración</li>
          <li>Agrega decisiones si es necesario</li>
          <li>Previsualiza el resultado</li>
        </ol>
      </InfoSection>

      <InfoSection id="info-export">
        <SectionTitle>📤 Exportar tu Proyecto</SectionTitle>
        <h4>Opciones de Exportación:</h4>
        <ul>
          <li><strong>Script Ren'Py:</strong> Archivo .rpy listo para usar</li>
          <li><strong>Proyecto Completo:</strong> Carpeta con todos los archivos</li>
          <li><strong>Recursos:</strong> Solo imágenes y audio organizados</li>
        </ul>
        
        <h4>Proceso de Exportación:</h4>
        <ol>
          <li>Ve a <strong>"⚙️ Configuración"</strong> en el sidebar</li>
          <li>Revisa la configuración de exportación</li>
          <li>Haz clic en <strong>"Exportar Proyecto"</strong></li>
          <li>Selecciona la ubicación de destino</li>
          <li>¡Tu proyecto está listo para Ren'Py!</li>
        </ol>
      </InfoSection>

      <InfoSection id="info-tips">
        <SectionTitle>💡 Consejos y Trucos</SectionTitle>
        <h4>Productividad:</h4>
        <ul>
          <li>Usa <strong>plantillas</strong> para acelerar la creación</li>
          <li>Organiza tus recursos en <strong>carpetas</strong> desde el inicio</li>
          <li>Guarda frecuentemente - el editor tiene <strong>autoguardado</strong></li>
          <li>Usa la <strong>vista previa</strong> constantemente para verificar el resultado</li>
        </ul>
        
        <h4>Recursos:</h4>
        <ul>
          <li>Mantén un <strong>estilo artístico consistente</strong></li>
          <li>Usa <strong>resoluciones estándar</strong> (1920x1080 recomendado)</li>
          <li>Comprime imágenes para <strong>reducir el tamaño</strong> del proyecto</li>
          <li>Nombra archivos de forma <strong>descriptiva</strong></li>
        </ul>
        
        <h4>Escritura:</h4>
        <ul>
          <li>Planifica la <strong>estructura</strong> de tu historia antes de empezar</li>
          <li>Usa <strong>labels</strong> para organizar capítulos y secciones</li>
          <li>Prueba diferentes <strong>rutas de decisión</strong></li>
          <li>Mantén los diálogos <strong>naturales y concisos</strong></li>
        </ul>
      </InfoSection>

      <BackToTopButton
        visible={showBackToTop}
        onClick={scrollToTop}
        title="Volver arriba"
      >
        ↑
      </BackToTopButton>
    </InfoContent>
  );
}

export default InfoModalContent;
