// Sistema de almacenamiento de assets híbrido (IndexedDB + localStorage)
export class AssetStorage {
  constructor() {
    this.dbName = 'VisualNovelAssets';
    this.dbVersion = 1;
    this.db = null;
    this.metadataKey = 'visual_novel_files_metadata';
  }

  // Inicializar IndexedDB
  async init() {
    return new Promise((resolve, reject) => {
      const request = indexedDB.open(this.dbName, this.dbVersion);

      request.onerror = () => reject(request.error);
      request.onsuccess = () => {
        this.db = request.result;
        resolve();
      };

      request.onupgradeneeded = (event) => {
        const db = event.target.result;
        
        // Crear store para archivos
        if (!db.objectStoreNames.contains('files')) {
          const fileStore = db.createObjectStore('files', { keyPath: 'id' });
          fileStore.createIndex('category', 'category', { unique: false });
          fileStore.createIndex('name', 'name', { unique: false });
        }
      };
    });
  }

  // Guardar archivo en IndexedDB con reintentos
  async saveFile(fileData, retryCount = 0) {
    if (!this.db) {
      await this.init();
    }

    return new Promise((resolve, reject) => {
      const transaction = this.db.transaction(['files'], 'readwrite');
      const store = transaction.objectStore('files');
      
      const fileRecord = {
        id: fileData.id,
        name: fileData.name,
        category: fileData.category,
        data: fileData.data,
        folderPath: fileData.folderPath,
        filePath: fileData.filePath,
        uploadedAt: new Date().toISOString()
      };

      console.log(`=== AssetStorage: Guardando archivo ===`);
      console.log(`AssetStorage: "${fileData.name}" con categoría "${fileData.category}"`);
      console.log(`AssetStorage: ID: ${fileData.id}`);
      console.log(`AssetStorage: Intento: ${retryCount + 1}`);

      const request = store.put(fileRecord);
      
      request.onsuccess = () => {
        console.log(`AssetStorage: Archivo guardado exitosamente con ID: ${fileData.id}`);
        // Actualizar metadatos en localStorage
        this.updateMetadata(fileData);
        resolve(fileData.id);
      };
      
      request.onerror = () => {
        console.error('AssetStorage: Error al guardar archivo:', request.error);
        
        // Reintentar hasta 3 veces
        if (retryCount < 2) {
          console.log(`AssetStorage: Reintentando guardado (${retryCount + 1}/3)...`);
          setTimeout(() => {
            this.saveFile(fileData, retryCount + 1)
              .then(resolve)
              .catch(reject);
          }, 1000 * (retryCount + 1)); // Delay incremental
        } else {
          reject(request.error);
        }
      };
    });
  }

  // Obtener archivo de IndexedDB
  async getFile(id) {
    if (!this.db) {
      await this.init();
    }

    return new Promise((resolve, reject) => {
      const transaction = this.db.transaction(['files'], 'readonly');
      const store = transaction.objectStore('files');
      const request = store.get(id);

      request.onsuccess = () => {
        if (request.result) {
          resolve(request.result);
        } else {
          resolve(null);
        }
      };

      request.onerror = () => reject(request.error);
    });
  }

  // Obtener todos los archivos de una categoría
  async getFilesByCategory(category) {
    if (!this.db) {
      await this.init();
    }

    return new Promise((resolve, reject) => {
      const transaction = this.db.transaction(['files'], 'readonly');
      const store = transaction.objectStore('files');
      const index = store.index('category');
      const request = index.getAll(category);

      request.onsuccess = () => {
        resolve(request.result || []);
      };

      request.onerror = () => reject(request.error);
    });
  }

  // Obtener todos los archivos
  async getAllFiles() {
    if (!this.db) {
      await this.init();
    }

    return new Promise((resolve, reject) => {
      const transaction = this.db.transaction(['files'], 'readonly');
      const store = transaction.objectStore('files');
      const request = store.getAll();

      request.onsuccess = () => {
        console.log('AssetStorage: getAllFiles - request.result:', request.result);
        resolve(request.result || []);
      };

      request.onerror = () => {
        console.error('AssetStorage: Error en getAllFiles:', request.error);
        reject(request.error);
      };
    });
  }

  // Eliminar archivo
  async deleteFile(id) {
    if (!this.db) {
      await this.init();
    }

    return new Promise((resolve, reject) => {
      const transaction = this.db.transaction(['files'], 'readwrite');
      const store = transaction.objectStore('files');
      const request = store.delete(id);

      request.onsuccess = () => {
        // Actualizar metadatos en localStorage
        this.removeFromMetadata(id);
        resolve();
      };

      request.onerror = () => reject(request.error);
    });
  }

  // Eliminar todos los archivos
  async clearAllFiles() {
    if (!this.db) {
      await this.init();
    }

    return new Promise((resolve, reject) => {
      const transaction = this.db.transaction(['files'], 'readwrite');
      const store = transaction.objectStore('files');
      const request = store.clear();

      request.onsuccess = () => {
        // Limpiar metadatos en localStorage
        localStorage.removeItem(this.metadataKey);
        console.log('🗑️ Todos los archivos eliminados de IndexedDB');
        resolve();
      };

      request.onerror = () => reject(request.error);
    });
  }

  // Actualizar metadatos en localStorage
  updateMetadata(fileData) {
    try {
      const metadata = this.getMetadata();
      metadata[fileData.id] = {
        name: fileData.name,
        category: fileData.category,
        folderPath: fileData.folderPath,
        filePath: fileData.filePath,
        uploadedAt: new Date().toISOString(),
        lastSync: new Date().toISOString()
      };
      localStorage.setItem(this.metadataKey, JSON.stringify(metadata));
      console.log(`AssetStorage: Metadatos actualizados para ${fileData.name}`);
    } catch (error) {
      console.warn('No se pudieron actualizar metadatos en localStorage:', error);
    }
  }

  // Remover de metadatos
  removeFromMetadata(id) {
    try {
      const metadata = this.getMetadata();
      delete metadata[id];
      localStorage.setItem(this.metadataKey, JSON.stringify(metadata));
    } catch (error) {
      console.warn('No se pudieron actualizar metadatos en localStorage:', error);
    }
  }

  // Obtener metadatos
  getMetadata() {
    try {
      const metadata = localStorage.getItem(this.metadataKey);
      return metadata ? JSON.parse(metadata) : {};
    } catch (error) {
      console.warn('Error al obtener metadatos:', error);
      return {};
    }
  }

  // Obtener URL de imagen
  async getImageUrl(imagePath) {
    if (!imagePath) return null;

    // Buscar en IndexedDB
    const files = await this.getAllFiles();
    const file = files.find(f => f.folderPath === imagePath || f.name === imagePath.split('/').pop());
    
    if (file) {
      return file.data; // Data URL
    }

    return null;
  }

  // Obtener archivos por categoría (compatible con FileManager existente)
  async getFilesByCategoryCompat(category) {
    console.log(`=== AssetStorage: Buscando archivos en categoría "${category}" ===`);
    
    try {
      // Usar siempre filtrado manual para evitar problemas con el índice
      console.log(`AssetStorage: Filtrando manualmente archivos para categoría "${category}"...`);
      const allFiles = await this.getAllFiles();
      console.log(`AssetStorage: Total de archivos en DB: ${allFiles.length}`);
      
      const filteredFiles = allFiles.filter(file => {
        const matches = file.category === category;
        console.log(`AssetStorage: Archivo "${file.name}" - categoría: "${file.category}", coincide: ${matches}`);
        return matches;
      });
      
      console.log(`AssetStorage: Archivos filtrados para "${category}": ${filteredFiles.length}`);
      
      return filteredFiles.map(file => ({
        id: file.id,
        name: file.name,
        category: file.category,
        folderPath: file.folderPath,
        data: file.data,
        url: file.data
      }));
      
    } catch (error) {
      console.error('AssetStorage: Error al buscar archivos por categoría:', error);
      return [];
    }
  }

  // Limpiar todos los archivos
  async clearAll() {
    if (!this.db) {
      await this.init();
    }

    return new Promise((resolve, reject) => {
      const transaction = this.db.transaction(['files'], 'readwrite');
      const store = transaction.objectStore('files');
      const request = store.clear();

      request.onsuccess = () => {
        // Limpiar metadatos
        localStorage.removeItem(this.metadataKey);
        resolve();
      };

      request.onerror = () => reject(request.error);
    });
  }

  // Obtener estadísticas de almacenamiento
  async getStorageStats() {
    console.log('=== AssetStorage: Obteniendo estadísticas ===');
    const files = await this.getAllFiles();
    console.log('AssetStorage: Archivos encontrados:', files.length);
    console.log('AssetStorage: Archivos:', files.map(f => ({ id: f.id, name: f.name, category: f.category })));
    
    const totalSize = files.reduce((sum, file) => sum + (file.data ? file.data.length : 0), 0);
    
    const stats = {
      totalFiles: files.length,
      totalSize: totalSize,
      totalSizeMB: (totalSize / (1024 * 1024)).toFixed(2),
      categories: [...new Set(files.map(f => f.category))]
    };
    
    console.log('AssetStorage: Estadísticas calculadas:', stats);
    console.log('=== AssetStorage: Fin estadísticas ===');
    
    return stats;
  }
}
