const VariablesPanel = () => {
  const { state, dispatch } = useVisualNovel();
  const [activeTab, setActiveTab] = useState('variables');
  const [showForm, setShowForm] = useState(false);
  const [editingItem, setEditingItem] = useState(null);
  const [formData, setFormData] = useState({
    name: '',
    value: '',
    type: 'number',
    description: ''
  });
  const [showScrollHint, setShowScrollHint] = useState(false);
  const [isScrolling, setIsScrolling] = useState(false);
  const panelRef = useRef(null);

  // Función para renderizar la guía de variables
  const renderVariableGuide = () => (
    <GuideSection>
      <GuideTitle>
        <span>📊</span>
        Guía de Variables
      </GuideTitle>
      <GuideText>
        Las <strong>variables</strong> almacenan datos que pueden cambiar durante el juego. Son perfectas para sistemas de puntuación, inventario, o cualquier valor numérico o de texto.
      </GuideText>
      <GuideList>
        <GuideListItem><strong>Número:</strong> Para puntuaciones, dinero, estadísticas (ej: <GuideCode>player_money = 100</GuideCode>)</GuideListItem>
        <GuideListItem><strong>Texto:</strong> Para nombres, descripciones, mensajes (ej: <GuideCode>player_name = "Alex"</GuideCode>)</GuideListItem>
        <GuideListItem><strong>Booleano:</strong> Para estados simples (ej: <GuideCode>has_key = true</GuideCode>)</GuideListItem>
      </GuideList>
      <GuideText>
        <strong>💡 Tip:</strong> Usa nombres descriptivos con guiones bajos: <GuideCode>love_points</GuideCode>, <GuideCode>current_chapter</GuideCode>
      </GuideText>
    </GuideSection>
  );

  // Función para renderizar la guía de flags
  const renderFlagGuide = () => (
    <GuideSection>
      <GuideTitle>
        <span>🚩</span>
        Guía de Flags
      </GuideTitle>
      <GuideText>
        Los <strong>flags</strong> son variables booleanas (true/false) que recuerdan eventos o estados específicos del juego. Son ideales para desbloquear contenido o rastrear progreso.
      </GuideText>
      <GuideList>
        <GuideListItem><strong>Eventos:</strong> Si el jugador conoció a un personaje (ej: <GuideCode>met_alice = true</GuideCode>)</GuideListItem>
        <GuideListItem><strong>Ubicaciones:</strong> Si visitó un lugar (ej: <GuideCode>visited_school = false</GuideCode>)</GuideListItem>
        <GuideListItem><strong>Logros:</strong> Si completó una tarea (ej: <GuideCode>found_secret = true</GuideCode>)</GuideListItem>
        <GuideListItem><strong>Rutas:</strong> Para desbloquear finales alternativos</GuideListItem>
      </GuideList>
      <GuideText>
        <strong>💡 Tip:</strong> Los flags se usan en condiciones para mostrar contenido diferente: <GuideCode>if met_alice: "Hola Alice!"</GuideCode>
      </GuideText>
    </GuideSection>
  );

  // Cargar variables y flags del proyecto
  const variables = state.project.variables || [];
  const flags = state.project.flags || [];

  // Efecto para mostrar hint de scroll cuando se abre el formulario
  useEffect(() => {
    if (showForm) {
      const timer = setTimeout(() => {
        setShowScrollHint(true);
      }, 1000);
      
      const hideTimer = setTimeout(() => {
        setShowScrollHint(false);
      }, 4000);
      
      return () => {
        clearTimeout(timer);
        clearTimeout(hideTimer);
      };
    } else {
      setShowScrollHint(false);
    }
  }, [showForm]);

  // Efecto para manejar el scroll del panel principal
  useEffect(() => {
    const panelElement = panelRef.current;
    if (!panelElement) return;

    const handleWheel = (e) => {
      e.preventDefault();
      
      // Aumentar significativamente la sensibilidad del scroll
      const scrollAmount = e.deltaY * 4.0; // Multiplicar por 4.0 para scroll mucho más rápido
      panelElement.scrollBy({
        top: scrollAmount,
        behavior: 'smooth'
      });
      
      setIsScrolling(true);
      clearTimeout(window.scrollTimeout);
      window.scrollTimeout = setTimeout(() => {
        setIsScrolling(false);
      }, 200);
    };

    const handleScroll = (e) => {
      setIsScrolling(true);
      clearTimeout(window.scrollTimeout);
      window.scrollTimeout = setTimeout(() => {
        setIsScrolling(false);
      }, 200);
    };

    const handleKeyDown = (e) => {
      if (e.key === 'ArrowDown' || e.key === 'ArrowUp') {
        e.preventDefault();
        
        // Scroll mucho más rápido con teclado
        const scrollAmount = e.key === 'ArrowDown' ? 300 : -300;
        panelElement.scrollBy({
          top: scrollAmount,
          behavior: 'smooth'
        });
        
        setIsScrolling(true);
        clearTimeout(window.scrollTimeout);
        window.scrollTimeout = setTimeout(() => {
          setIsScrolling(false);
        }, 200);
      }
      
      // Agregar soporte para Page Up/Page Down para scroll aún más rápido
      if (e.key === 'PageDown' || e.key === 'PageUp') {
        e.preventDefault();
        
        const scrollAmount = e.key === 'PageDown' ? 600 : -600;
        panelElement.scrollBy({
          top: scrollAmount,
          behavior: 'smooth'
        });
        
        setIsScrolling(true);
        clearTimeout(window.scrollTimeout);
        window.scrollTimeout = setTimeout(() => {
          setIsScrolling(false);
        }, 200);
      }
      
      // Agregar soporte para Home/End para ir al inicio/final
      if (e.key === 'Home') {
        e.preventDefault();
        panelElement.scrollTo({
          top: 0,
          behavior: 'smooth'
        });
        setIsScrolling(true);
        clearTimeout(window.scrollTimeout);
        window.scrollTimeout = setTimeout(() => {
          setIsScrolling(false);
        }, 200);
      }
      
      if (e.key === 'End') {
        e.preventDefault();
        panelElement.scrollTo({
          top: panelElement.scrollHeight,
          behavior: 'smooth'
        });
        setIsScrolling(true);
        clearTimeout(window.scrollTimeout);
        window.scrollTimeout = setTimeout(() => {
          setIsScrolling(false);
        }, 200);
      }
      
      // Agregar soporte para barra espaciadora (scroll hacia abajo)
      if (e.key === ' ') {
        e.preventDefault();
        panelElement.scrollBy({
          top: 400,
          behavior: 'smooth'
        });
        setIsScrolling(true);
        clearTimeout(window.scrollTimeout);
        window.scrollTimeout = setTimeout(() => {
          setIsScrolling(false);
        }, 200);
      }
      
      // Agregar soporte para Shift + barra espaciadora (scroll hacia arriba)
      if (e.key === ' ' && e.shiftKey) {
        e.preventDefault();
        panelElement.scrollBy({
          top: -400,
          behavior: 'smooth'
        });
        setIsScrolling(true);
        clearTimeout(window.scrollTimeout);
        window.scrollTimeout = setTimeout(() => {
          setIsScrolling(false);
        }, 200);
      }
    };

    panelElement.addEventListener('wheel', handleWheel, { passive: false });
    panelElement.addEventListener('scroll', handleScroll);
    panelElement.addEventListener('keydown', handleKeyDown);
    
    // Hacer el panel focusable para recibir eventos de teclado
    panelElement.setAttribute('tabindex', '0');
    
    return () => {
      panelElement.removeEventListener('wheel', handleWheel);
      panelElement.removeEventListener('scroll', handleScroll);
      panelElement.removeEventListener('keydown', handleKeyDown);
      clearTimeout(window.scrollTimeout);
    };
  }, []);

  const handleFormChange = (field, value) => {
    setFormData(prev => ({
      ...prev,
      [field]: value
    }));
  };

  const handleAddItem = (type) => {
    setActiveTab(type);
    setFormData({
      name: '',
      value: type === 'flags' ? false : '',
      type: type === 'variables' ? 'number' : 'boolean',
      description: ''
    });
    setEditingItem(null);
    setShowForm(true);
  };

  const handleEditItem = (item, type) => {
    setActiveTab(type);
    setFormData({
      name: item.name,
      value: item.value,
      type: item.type || (type === 'flags' ? 'boolean' : 'number'),
      description: item.description || ''
    });
    setEditingItem(item);
    setShowForm(true);
  };

  const handleSaveItem = () => {
    if (!formData.name.trim()) {
      alert('El nombre es requerido');
      return;
    }

    const itemData = {
      id: editingItem ? editingItem.id : uuidv4(),
      name: formData.name.trim(),
      value: formData.value,
      type: formData.type,
      description: formData.description.trim()
    };

    if (activeTab === 'variables') {
      if (editingItem) {
        dispatch({
          type: 'UPDATE_VARIABLE',
          payload: itemData
        });
      } else {
        dispatch({
          type: 'ADD_VARIABLE',
          payload: itemData
        });
      }
    } else {
      if (editingItem) {
        dispatch({
          type: 'UPDATE_FLAG',
          payload: itemData
        });
      } else {
        dispatch({
          type: 'ADD_FLAG',
          payload: itemData
        });
      }
    }

    setShowForm(false);
    setEditingItem(null);
    setFormData({
      name: '',
      value: '',
      type: 'number',
      description: ''
    });
  };

  const handleDeleteItem = (item, type) => {
    if (window.confirm(`¿Estás seguro de que quieres eliminar ${item.name}?`)) {
      if (type === 'variables') {
        dispatch({
          type: 'DELETE_VARIABLE',
          payload: item.id
        });
      } else {
        dispatch({
          type: 'DELETE_FLAG',
          payload: item.id
        });
      }
    }
  };

  const renderItemValue = (item) => {
    if (item.type === 'boolean') {
      return item.value ? 'Activado (True)' : 'Desactivado (False)';
    } else if (item.type === 'string') {
      return `"${item.value}"`;
    } else {
      return item.value;
    }
  };

  return (
    <PanelContainer ref={panelRef}>
      <ScrollHint visible={showScrollHint}>
        📜 Mouse: rueda | Teclado: ↑↓, Page Up/Down, Home/End, Espacio
      </ScrollHint>
      
      <Section>
        <SectionTitle>
          📊 Variables y Flags
          <div style={{ display: 'flex', gap: '8px' }}>
            <Button onClick={() => handleAddItem('variables')}>
              + Variable
            </Button>
            <Button onClick={() => handleAddItem('flags')}>
              + Flag
            </Button>
          </div>
        </SectionTitle>

        <TabsContainer>
          <Tab 
            active={activeTab === 'variables'} 
            onClick={() => setActiveTab('variables')}
          >
            <span style={{ fontSize: '16px' }}>📊</span>
            <div>
              <div style={{ fontSize: '13px', fontWeight: '700' }}>Variables</div>
              <div style={{ fontSize: '10px', opacity: 0.8, marginTop: '2px' }}>
                Datos dinámicos ({variables.length})
              </div>
            </div>
          </Tab>
          <Tab 
            active={activeTab === 'flags'} 
            onClick={() => setActiveTab('flags')}
          >
            <span style={{ fontSize: '16px' }}>🚩</span>
            <div>
              <div style={{ fontSize: '13px', fontWeight: '700' }}>Flags</div>
              <div style={{ fontSize: '10px', opacity: 0.8, marginTop: '2px' }}>
                Estados del juego ({flags.length})
              </div>
            </div>
          </Tab>
        </TabsContainer>

        {activeTab === 'variables' && (
          <>
            <ItemList>
              {variables.length === 0 ? (
                <EmptyState>
                  <div style={{ fontSize: '48px', marginBottom: '16px' }}>📊</div>
                  <div style={{ fontSize: '16px', fontWeight: '600', marginBottom: '8px' }}>No hay variables definidas</div>
                  <div style={{ fontSize: '14px' }}>Crea tu primera variable para comenzar a personalizar tu juego</div>
                  <div style={{ fontSize: '12px', marginTop: '8px', opacity: 0.7 }}>
                    Las variables son perfectas para sistemas de puntuación, inventario y estadísticas
                  </div>
                </EmptyState>
              ) : (
                variables.map(variable => (
                  <ItemCard key={variable.id}>
                    <ItemInfo>
                      <ItemName>{variable.name}</ItemName>
                      <ItemDetails>
                        {renderItemValue(variable)} ({variable.type})
                        {variable.description && ` - ${variable.description}`}
                      </ItemDetails>
                    </ItemInfo>
                    <ItemActions>
                      <ActionButton onClick={() => handleEditItem(variable, 'variables')}>
                        ✏️
                      </ActionButton>
                      <ActionButton 
                        danger 
                        onClick={() => handleDeleteItem(variable, 'variables')}
                      >
                        🗑️
                      </ActionButton>
                    </ItemActions>
                  </ItemCard>
                ))
              )}
            </ItemList>
          </>
        )}

        {activeTab === 'flags' && (
          <>
            <ItemList>
              {flags.length === 0 ? (
                <EmptyState>
                  <div style={{ fontSize: '48px', marginBottom: '16px' }}>🚩</div>
                  <div style={{ fontSize: '16px', fontWeight: '600', marginBottom: '8px' }}>No hay flags definidos</div>
                  <div style={{ fontSize: '14px' }}>Crea tu primer flag para comenzar a rastrear eventos y estados</div>
                  <div style={{ fontSize: '12px', marginTop: '8px', opacity: 0.7 }}>
                    Los flags son ideales para recordar encuentros, logros y progreso del juego
                  </div>
                </EmptyState>
              ) : (
                flags.map(flag => (
                  <ItemCard key={flag.id}>
                    <ItemInfo>
                      <ItemName>{flag.name}</ItemName>
                      <ItemDetails>
                        {renderItemValue(flag)}
                        {flag.description && ` - ${flag.description}`}
                      </ItemDetails>
                    </ItemInfo>
                    <ItemActions>
                      <ActionButton onClick={() => handleEditItem(flag, 'flags')}>
                        ✏️
                      </ActionButton>
                      <ActionButton 
                        danger 
                        onClick={() => handleDeleteItem(flag, 'flags')}
                      >
                        🗑️
                      </ActionButton>
                    </ItemActions>
                  </ItemCard>
                ))
              )}
            </ItemList>
          </>
        )}

        {showForm && (
          <FormContainer>
            
            <h4 style={{ margin: '0 0 20px 0', color: 'var(--color-primary)', fontSize: '18px', fontWeight: '700' }}>
              {editingItem ? '✏️ Editar' : '➕ Agregar'} {activeTab === 'variables' ? 'Variable' : 'Flag'}
            </h4>
            
            {/* Mostrar guía contextual */}
            {activeTab === 'variables' ? renderVariableGuide() : renderFlagGuide()}
            
            <FormGroup>
              <Label>Nombre de la {activeTab === 'variables' ? 'Variable' : 'Flag'}</Label>
              <Input
                value={formData.name}
                onChange={(e) => handleFormChange('name', e.target.value)}
                placeholder={activeTab === 'variables' ? "player_money, love_points, current_chapter" : "met_alice, visited_school, found_secret"}
              />
              <div style={{ marginTop: '6px', fontSize: '11px', color: 'var(--color-textTertiary)', fontStyle: 'italic' }}>
                {activeTab === 'variables' 
                  ? "Usa nombres descriptivos con guiones bajos (sin espacios ni caracteres especiales)"
                  : "Nombre que identifique claramente el evento o estado que representa"
                }
              </div>
            </FormGroup>

            {activeTab === 'variables' && (
              <FormGroup>
                <Label>Tipo de Variable</Label>
                <Select
                  value={formData.type}
                  onChange={(e) => handleFormChange('type', e.target.value)}
                >
                  <option value="number">🔢 Número (puntuaciones, dinero, estadísticas)</option>
                  <option value="string">📝 Texto (nombres, descripciones, mensajes)</option>
                  <option value="boolean">✅ Booleano (verdadero/falso)</option>
                </Select>
                <div style={{ marginTop: '6px', fontSize: '11px', color: 'var(--color-textTertiary)', fontStyle: 'italic' }}>
                  El tipo determina qué tipo de datos puede almacenar la variable
                </div>
              </FormGroup>
            )}

            <FormGroup>
              <Label>Valor {activeTab === 'variables' ? 'Inicial' : 'del Flag'}</Label>
              {activeTab === 'flags' ? (
                <>
                <Select
                  value={formData.value ? 'true' : 'false'}
                  onChange={(e) => handleFormChange('value', e.target.value === 'true')}
                >
                    <option value="true">✅ Activado (True) - El evento ocurrió</option>
                    <option value="false">❌ Desactivado (False) - El evento no ocurrió</option>
                </Select>
                  <div style={{ marginTop: '6px', fontSize: '11px', color: 'var(--color-textTertiary)', fontStyle: 'italic' }}>
                    Estado inicial del flag cuando comienza el juego
                  </div>
                </>
              ) : (
                <>
                <Input
                  value={formData.value}
                  onChange={(e) => handleFormChange('value', e.target.value)}
                    placeholder={
                      formData.type === 'string' ? '"Alex" o "Texto descriptivo"' :
                      formData.type === 'boolean' ? 'true o false' :
                      '100 o 0'
                    }
                  />
                  <div style={{ marginTop: '6px', fontSize: '11px', color: 'var(--color-textTertiary)', fontStyle: 'italic' }}>
                    {formData.type === 'string' && "Valor inicial de texto (usa comillas para texto)"}
                    {formData.type === 'number' && "Valor numérico inicial (enteros o decimales)"}
                    {formData.type === 'boolean' && "Estado booleano: true (verdadero) o false (falso)"}
                  </div>
                </>
              )}
            </FormGroup>

            <FormGroup>
              <Label>Descripción (Opcional)</Label>
              <Input
                value={formData.description}
                onChange={(e) => handleFormChange('description', e.target.value)}
                placeholder={activeTab === 'variables' 
                  ? "Describe para qué se usa esta variable en tu juego..." 
                  : "Describe qué evento o estado representa este flag..."
                }
              />
              <div style={{ marginTop: '6px', fontSize: '11px', color: 'var(--color-textTertiary)', fontStyle: 'italic' }}>
                Una descripción clara te ayudará a recordar el propósito de esta {activeTab === 'variables' ? 'variable' : 'flag'}
              </div>
            </FormGroup>

            <ButtonGroup>
              <Button onClick={handleSaveItem}>
                {editingItem ? '💾 Actualizar' : '➕ Agregar'} {activeTab === 'variables' ? 'Variable' : 'Flag'}
              </Button>
              <Button 
                onClick={() => {
                  setShowForm(false);
                  setEditingItem(null);
                }}
                style={{ 
                  background: 'linear-gradient(135deg, var(--color-border) 0%, var(--color-borderSecondary) 100%)',
                  color: 'var(--color-textSecondary)',
                  border: '1px solid var(--color-border)'
                }}
              >
                ❌ Cancelar
              </Button>
            </ButtonGroup>
          </FormContainer>
        )}
      </Section>
      
      <ScrollIndicator visible={isScrolling}>
        🖱️ Navegando por la página...
      </ScrollIndicator>
    </PanelContainer>
  );
};

export default VariablesPanel;
