import React, { useState } from 'react';
import styled from 'styled-components';
import { useVisualNovel, isProjectEmpty } from '../context/VisualNovelContext';
import { useModalContext } from '../context/ModalContext';

const ModalOverlay = styled.div`
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: var(--color-backdrop);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1000;
  transition: background-color 0.3s ease;
`;

const ModalContent = styled.div`
  background-color: var(--color-surface);
  border: 2px solid var(--color-border);
  border-radius: 12px;
  padding: 24px;
  max-width: 600px;
  width: 90%;
  max-height: 80vh;
  overflow-y: auto;
  transition: all 0.3s ease;
  box-shadow: 0 8px 32px var(--color-shadow);

  &:hover {
    border-color: var(--color-primary);
    box-shadow: 0 12px 40px var(--color-shadowHover);
  }
`;

const ModalHeader = styled.div`
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 20px;
  border-bottom: 2px solid var(--color-border);
  padding-bottom: 16px;
  transition: border-color 0.3s ease;
`;

const ModalTitle = styled.h2`
  margin: 0;
  color: var(--color-primary);
  font-size: 24px;
  font-weight: 800;
  transition: color 0.3s ease;
  text-shadow: 0 2px 4px var(--color-shadow);
  letter-spacing: 0.3px;
`;

const CloseButton = styled.button`
  background: none;
  border: none;
  color: var(--color-textPrimary);
  font-size: 24px;
  cursor: pointer;
  padding: 8px;
  width: 40px;
  height: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all 0.3s ease;
  border-radius: 8px;
  font-weight: 700;

  &:hover {
    background-color: var(--color-error);
    color: var(--color-textInverse);
    transform: scale(1.1);
    box-shadow: 0 4px 12px var(--color-shadow);
  }
`;

const TabContainer = styled.div`
  display: flex;
  margin-bottom: 20px;
  border-bottom: 2px solid var(--color-border);
  transition: border-color 0.3s ease;
`;

const Tab = styled.button`
  padding: 14px 24px;
  background-color: ${props => props.active ? 'var(--color-primary)' : 'transparent'};
  color: ${props => props.active ? 'var(--color-textInverse)' : 'var(--color-textPrimary)'};
  border: none;
  cursor: pointer;
  font-size: 14px;
  font-weight: 700;
  transition: all 0.3s ease;
  border-radius: 8px 8px 0 0;
  position: relative;

  &:hover {
    background-color: ${props => props.active ? 'var(--color-primaryHover)' : 'var(--color-border)'};
    color: var(--color-textInverse);
    transform: translateY(-2px);
  }

  &:active {
    transform: translateY(0);
  }
`;

const TabContent = styled.div`
  display: ${props => props.active ? 'block' : 'none'};
`;

const ProjectList = styled.div`
  display: flex;
  flex-direction: column;
  gap: 12px;
  max-height: 300px;
  overflow-y: auto;
`;

const ProjectItem = styled.div`
  background-color: var(--color-surfaceSecondary);
  border: 2px solid var(--color-border);
  border-radius: 10px;
  padding: 20px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  transition: all 0.3s ease;
  box-shadow: 0 4px 12px var(--color-shadow);

  &:hover {
    border-color: var(--color-primary);
    background-color: var(--color-surfaceElevated);
    transform: translateY(-4px);
    box-shadow: 0 8px 24px var(--color-shadowHover);
  }
`;

const ProjectInfo = styled.div`
  flex: 1;
`;

const ProjectName = styled.div`
  font-weight: 800;
  color: var(--color-textPrimary);
  margin-bottom: 6px;
  font-size: 16px;
  transition: color 0.3s ease;
  text-shadow: 0 1px 2px var(--color-shadow);
`;

const ProjectMeta = styled.div`
  font-size: 14px;
  color: var(--color-textSecondary);
  font-weight: 500;
  transition: color 0.3s ease;
  text-shadow: 0 1px 2px var(--color-shadow);
`;

const ProjectActions = styled.div`
  display: flex;
  gap: 8px;
`;

const Button = styled.button`
  background-color: ${props => props.primary ? 'var(--color-primary)' : 'var(--color-border)'};
  color: var(--color-textInverse);
  border: 2px solid ${props => props.primary ? 'var(--color-primary)' : 'var(--color-border)'};
  padding: 10px 16px;
  border-radius: 8px;
  cursor: pointer;
  font-size: 14px;
  font-weight: 700;
  transition: all 0.3s ease;
  box-shadow: 0 3px 8px var(--color-shadow);

  &:hover {
    background-color: ${props => props.primary ? 'var(--color-primaryHover)' : 'var(--color-borderSecondary)'};
    border-color: ${props => props.primary ? 'var(--color-primaryHover)' : 'var(--color-borderSecondary)'};
    transform: translateY(-2px);
    box-shadow: 0 6px 16px var(--color-shadowHover);
  }

  &.danger {
    background-color: var(--color-error);
    border-color: var(--color-error);
    
    &:hover {
      background-color: var(--color-errorHover);
      border-color: var(--color-errorHover);
    }
  }
`;

const FormGroup = styled.div`
  margin-bottom: 16px;
`;

const Label = styled.label`
  display: block;
  margin-bottom: 8px;
  font-size: 16px;
  color: var(--color-textPrimary);
  font-weight: 700;
  transition: color 0.3s ease;
  text-shadow: 0 1px 2px var(--color-shadow);
`;

const Input = styled.input`
  width: 100%;
  background-color: var(--color-backgroundSecondary);
  border: 2px solid var(--color-border);
  color: var(--color-textPrimary);
  padding: 12px 16px;
  border-radius: 8px;
  font-size: 16px;
  font-weight: 500;
  transition: all 0.3s ease;
  box-shadow: 0 2px 6px var(--color-shadow);

  &:focus {
    outline: none;
    border-color: var(--color-primary);
    box-shadow: 0 4px 12px var(--color-shadowHover);
    transform: translateY(-1px);
  }

  &:hover {
    border-color: var(--color-primary);
    box-shadow: 0 3px 8px var(--color-shadowHover);
  }

  &::placeholder {
    color: var(--color-textTertiary);
    font-weight: 400;
  }
`;

const TextArea = styled.textarea`
  width: 100%;
  background-color: var(--color-backgroundSecondary);
  border: 2px solid var(--color-border);
  color: var(--color-textPrimary);
  padding: 12px 16px;
  border-radius: 8px;
  font-size: 16px;
  font-weight: 500;
  min-height: 100px;
  resize: vertical;
  transition: all 0.3s ease;
  box-shadow: 0 2px 6px var(--color-shadow);

  &:focus {
    outline: none;
    border-color: var(--color-primary);
    box-shadow: 0 4px 12px var(--color-shadowHover);
    transform: translateY(-1px);
  }

  &:hover {
    border-color: var(--color-primary);
    box-shadow: 0 3px 8px var(--color-shadowHover);
  }

  &::placeholder {
    color: var(--color-textTertiary);
    font-weight: 400;
  }
`;

const ButtonGroup = styled.div`
  display: flex;
  gap: 12px;
  margin-top: 20px;
`;

const EmptyState = styled.div`
  text-align: center;
  color: var(--color-textTertiary);
  padding: 40px 20px;
  font-size: 16px;
  font-weight: 600;
  transition: color 0.3s ease;
  text-shadow: 0 1px 2px var(--color-shadow);
`;

function ProjectManager({ isOpen, onClose }) {
  const { state, dispatch, storage } = useVisualNovel();
  const { showAlert, showConfirm } = useModalContext();
  const [activeTab, setActiveTab] = useState('load');
  const [projectName, setProjectName] = useState('');
  const [projectDescription, setProjectDescription] = useState('');
  const [savedProjects, setSavedProjects] = useState([]);

  React.useEffect(() => {
    if (isOpen) {
      setSavedProjects(storage.getProjectNames());
    }
  }, [isOpen]);

  // Sincronizar campos con el proyecto actual cuando cambie
  React.useEffect(() => {
    setProjectName(state.project.name || '');
    setProjectDescription(state.project.description || '');
  }, [state.project.name, state.project.description]);

  const handleSaveProject = async () => {
    if (!projectName.trim()) {
      await showAlert('El nombre del proyecto es obligatorio', 'Campo Obligatorio', '⚠️');
      return;
    }

    // Validar que el proyecto tenga contenido mínimo
    if (!state.project.characters || state.project.characters.length === 0) {
      const proceed = await showConfirm(
        'El proyecto no tiene personajes. ¿Desea guardarlo de todas formas?',
        'Proyecto Sin Personajes',
        '👥'
      );
      if (!proceed) return;
    }

    if (!state.project.sceneFiles || state.project.sceneFiles.length === 0) {
      const proceed = await showConfirm(
        'El proyecto no tiene archivos de script. ¿Desea guardarlo de todas formas?',
        'Proyecto Sin Script',
        '📝'
      );
      if (!proceed) return;
    }

    const projectToSave = {
      ...state.project,
      name: projectName.trim(),
      description: projectDescription.trim()
    };

    if (storage.saveProject(projectName.trim(), projectToSave)) {
      // Actualizar el estado del proyecto actual
      dispatch({
        type: 'SET_PROJECT_NAME',
        payload: projectName.trim()
      });
      dispatch({
        type: 'SET_PROJECT_DESCRIPTION',
        payload: projectDescription.trim()
      });
      
      await showAlert('Proyecto guardado exitosamente', 'Guardado Exitoso', '✅');
      setSavedProjects(storage.getProjectNames());
      setProjectName('');
      setProjectDescription('');
      setActiveTab('load');
    } else {
      await showAlert('Error al guardar el proyecto. Verifique que tenga contenido válido.', 'Error de Guardado', '❌');
    }
  };

  const handleSaveCurrentProject = async () => {
    if (!state.project.name || state.project.name.trim() === '') {
      await showAlert('El proyecto actual no tiene nombre. Por favor, asigna un nombre al proyecto antes de guardarlo.', 'Nombre Requerido', '⚠️');
      return;
    }

    const confirmMessage = `¿Estás seguro de que quieres guardar el proyecto actual "${state.project.name}"?\n\n` +
      `Esto sobreescribirá la versión guardada anteriormente.`;

    const confirmed = await showConfirm(confirmMessage, 'Confirmar Guardado', '💾');
    if (confirmed) {
      const projectToSave = {
        ...state.project,
        savedAt: new Date().toISOString()
      };

      if (storage.saveProject(state.project.name, projectToSave)) {
        await showAlert('✅ Proyecto guardado exitosamente', 'Guardado Exitoso', '✅');
        setSavedProjects(storage.getProjectNames());
        onClose();
      } else {
        await showAlert('❌ Error al guardar el proyecto', 'Error de Guardado', '❌');
      }
    }
  };

  const handleLoadProject = async (projectName) => {
    const project = storage.loadProject(projectName);
    if (project) {
      // Verificar si hay cambios sin guardar
      const hasUnsavedChanges = storage.hasUnsavedChanges(state);
      
      if (hasUnsavedChanges) {
        const proceed = await showConfirm(
          'Tienes cambios sin guardar en el proyecto actual. ¿Deseas continuar cargando el proyecto? Los cambios se perderán.',
          'Cambios Sin Guardar',
          '⚠️'
        );
        if (!proceed) return;
      }
      
      dispatch({
        type: 'LOAD_PROJECT',
        payload: project
      });
      
      // Sincronizar los campos del formulario con el proyecto cargado
      setProjectName(project.name || '');
      setProjectDescription(project.description || '');
      
      await showAlert(`Proyecto "${projectName}" cargado exitosamente`, 'Carga Exitosa', '✅');
      onClose();
    } else {
      await showAlert('Error al cargar el proyecto. El archivo puede estar corrupto o no existir.', 'Error de Carga', '❌');
    }
  };

  const handleDeleteProject = async (projectName) => {
    const confirmMessage = `¿Estás seguro de que quieres eliminar el proyecto "${projectName}"?\n\n` +
      `Esto eliminará:\n` +
      `• El proyecto y todos sus datos\n` +
      `• Todos los personajes y sus sprites\n` +
      `• Todos los archivos asociados\n` +
      `• Todo el estado actual del proyecto\n\n` +
      `Esta acción NO se puede deshacer.`;
    
    const confirmed = await showConfirm(confirmMessage, 'Confirmar Eliminación', '🗑️');
    if (confirmed) {
      try {
        const success = await storage.deleteProject(projectName);
        if (success) {
          setSavedProjects(storage.getProjectNames());
          
          // Si el proyecto eliminado es el proyecto actual, resetear el estado
          if (state.project.name === projectName) {
            console.log('🔄 Reseteando estado del contexto después de eliminar proyecto actual');
            dispatch({ type: 'RESET_TO_CLEAN_STATE' });
          }
          
          await showAlert('Proyecto y todos sus archivos eliminados exitosamente', 'Eliminación Exitosa', '✅');
        } else {
          await showAlert('Error al eliminar el proyecto', 'Error de Eliminación', '❌');
        }
      } catch (error) {
        console.error('Error eliminando proyecto:', error);
        await showAlert('Error al eliminar el proyecto: ' + error.message, 'Error de Eliminación', '❌');
      }
    }
  };

  const handleClearAllData = async () => {
    const isCurrentProjectEmpty = isProjectEmpty(state.project);
    const confirmMessage = `⚠️ ADVERTENCIA: LIMPIEZA COMPLETA ⚠️\n\n` +
      `Esto eliminará TODOS los datos:\n` +
      `• Todos los proyectos guardados\n` +
      `• Todos los personajes y sprites\n` +
      `• Todos los archivos subidos\n` +
      `• Todo el historial y configuración\n` +
      `• El nombre del proyecto actual\n\n` +
      `Esta acción es IRREVERSIBLE y eliminará TODO.\n` +
      `El editor se reiniciará completamente desde cero.\n\n` +
      `¿Estás ABSOLUTAMENTE seguro?`;
    
    const confirmed = await showConfirm(confirmMessage, '⚠️ Limpieza Completa', '🧹');
    if (confirmed) {
      const doubleConfirm = await showConfirm(
        `ÚLTIMA CONFIRMACIÓN\n\n` +
        `Vas a eliminar TODOS los datos del editor.\n` +
        `Esto incluye proyectos, personajes, archivos, todo.\n` +
        `El editor se reiniciará con un estado completamente limpio.\n\n` +
        `¿Confirmas que quieres continuar?`,
        'Confirmación Final',
        '⚠️'
      );
      
      if (doubleConfirm) {
        try {
          const success = await storage.clearAllData();
          if (success) {
            setSavedProjects([]);
            // Resetear el estado a completamente limpio
            dispatch({ type: 'RESET_TO_CLEAN_STATE' });
            await showAlert('✅ Todos los datos han sido eliminados completamente.\n\nEl editor se ha reiniciado con un estado completamente limpio.\n\nEl nombre del proyecto ahora está vacío y listo para un nuevo proyecto.', 'Limpieza Completa', '✅');
          } else {
            await showAlert('❌ Error al limpiar los datos', 'Error de Limpieza', '❌');
          }
        } catch (error) {
          console.error('Error limpiando datos:', error);
          await showAlert('❌ Error al limpiar los datos: ' + error.message, 'Error de Limpieza', '❌');
        }
      }
    }
  };

  const handleCleanupOrphanedData = async () => {
    const confirmMessage = `¿Deseas limpiar datos huérfanos?\n\n` +
      `Esto eliminará:\n` +
      `• Archivos que no pertenecen a ningún proyecto\n` +
      `• Sprites de personajes eliminados\n` +
      `• Datos corruptos o inconsistentes\n\n` +
      `Esta acción es segura y solo elimina datos no utilizados.`;
    
    const confirmed = await showConfirm(confirmMessage, 'Limpiar Datos Huérfanos', '🧹');
    if (confirmed) {
      try {
        const cleanedCount = await storage.cleanupOrphanedData();
        await showAlert(`✅ Limpieza completada.\n\nSe eliminaron ${cleanedCount} archivos huérfanos.`, 'Limpieza Exitosa', '✅');
        setSavedProjects(storage.getProjectNames());
      } catch (error) {
        console.error('Error limpiando datos huérfanos:', error);
        await showAlert('❌ Error al limpiar datos huérfanos: ' + error.message, 'Error de Limpieza', '❌');
      }
    }
  };


  if (!isOpen) return null;

  return (
    <ModalOverlay onClick={onClose}>
      <ModalContent onClick={(e) => e.stopPropagation()}>
        <ModalHeader>
          <ModalTitle>Gestión de Proyectos</ModalTitle>
          <CloseButton onClick={onClose}>×</CloseButton>
        </ModalHeader>

        <TabContainer>
          <Tab active={activeTab === 'load'} onClick={() => setActiveTab('load')}>
            Cargar Proyecto
          </Tab>
          <Tab active={activeTab === 'save'} onClick={() => setActiveTab('save')}>
            Guardar Proyecto
          </Tab>
          <Tab active={activeTab === 'cleanup'} onClick={() => setActiveTab('cleanup')}>
            Limpiar Datos
          </Tab>
        </TabContainer>

        <TabContent active={activeTab === 'load'}>
          <h3>Cargar Proyecto Guardado</h3>
          {savedProjects.length > 0 ? (
            <ProjectList>
              {savedProjects.map(project => (
                <ProjectItem key={project.name}>
                  <ProjectInfo>
                    <ProjectName>{project.name}</ProjectName>
                    <ProjectMeta>
                      Guardado: {new Date(project.savedAt).toLocaleString()}
                      <br />
                      {project.description}
                    </ProjectMeta>
                  </ProjectInfo>
                  <ProjectActions>
                    <Button primary onClick={() => handleLoadProject(project.name)}>
                      Cargar
                    </Button>
                    <Button 
                      className="danger"
                      onClick={() => handleDeleteProject(project.name)}
                    >
                      Eliminar
                    </Button>
                  </ProjectActions>
                </ProjectItem>
              ))}
            </ProjectList>
          ) : (
            <EmptyState>
              No hay proyectos guardados
            </EmptyState>
          )}
          
          <div style={{ 
            marginTop: '20px', 
            padding: '20px', 
            backgroundColor: 'var(--color-surfaceSecondary)', 
            borderRadius: '10px', 
            border: '2px solid var(--color-primary)',
            boxShadow: '0 4px 12px var(--color-shadow)',
            transition: 'all 0.3s ease'
          }}>
            <h4 style={{ 
              margin: '0 0 16px 0', 
              color: 'var(--color-primary)', 
              fontSize: '18px',
              fontWeight: '800',
              textShadow: '0 2px 4px var(--color-shadow)',
              letterSpacing: '0.3px'
            }}>
              💾 Guardar Proyecto Actual
            </h4>
            <p style={{ 
              margin: '0 0 16px 0', 
              color: 'var(--color-textPrimary)', 
              fontSize: '16px',
              fontWeight: '600',
              textShadow: '0 1px 2px var(--color-shadow)'
            }}>
              Guarda el proyecto actual con el nombre "{state.project.name}" para no perder cambios.
            </p>
            <Button 
              primary 
              onClick={() => handleSaveCurrentProject()}
              style={{ fontSize: '14px', padding: '12px 20px' }}
            >
              💾 Guardar Proyecto Actual
            </Button>
          </div>
        </TabContent>

        <TabContent active={activeTab === 'save'}>
          <h3>Guardar Proyecto Actual</h3>
          <div style={{ 
            marginBottom: '20px', 
            padding: '20px', 
            backgroundColor: 'var(--color-surfaceSecondary)', 
            borderRadius: '10px', 
            border: '2px solid var(--color-info)',
            fontSize: '16px',
            color: 'var(--color-textPrimary)',
            boxShadow: '0 4px 12px var(--color-shadow)',
            transition: 'all 0.3s ease'
          }}>
            <div style={{ 
              color: 'var(--color-info)', 
              fontWeight: '800', 
              marginBottom: '8px',
              fontSize: '18px',
              textShadow: '0 2px 4px var(--color-shadow)',
              letterSpacing: '0.3px'
            }}>
              🔄 Sincronización Automática
            </div>
            <div style={{
              fontWeight: '600',
              textShadow: '0 1px 2px var(--color-shadow)'
            }}>
              Los campos se sincronizan automáticamente con el proyecto actual: <strong>"{state.project.name}"</strong>
            </div>
          </div>
          <FormGroup>
            <Label>Nombre del proyecto</Label>
            <Input
              value={projectName}
              onChange={(e) => setProjectName(e.target.value)}
              placeholder="Mi Novela Visual"
            />
          </FormGroup>
          <FormGroup>
            <Label>Descripción (opcional)</Label>
            <TextArea
              value={projectDescription}
              onChange={(e) => setProjectDescription(e.target.value)}
              placeholder="Descripción del proyecto..."
            />
          </FormGroup>
          <ButtonGroup>
            <Button primary onClick={handleSaveProject}>
              Guardar Proyecto
            </Button>
            <Button onClick={() => {
              setProjectName('');
              setProjectDescription('');
            }}>
              Limpiar
            </Button>
          </ButtonGroup>
        </TabContent>

        <TabContent active={activeTab === 'cleanup'}>
          <h3>🧹 Limpieza de Datos</h3>
          
          <div style={{ 
            marginBottom: '20px', 
            padding: '20px', 
            backgroundColor: 'var(--color-surfaceSecondary)', 
            borderRadius: '10px', 
            border: '2px solid var(--color-warning)',
            fontSize: '16px',
            color: 'var(--color-textPrimary)',
            boxShadow: '0 4px 12px var(--color-shadow)',
            transition: 'all 0.3s ease'
          }}>
            <div style={{ 
              color: 'var(--color-warning)', 
              fontWeight: '800', 
              marginBottom: '8px',
              fontSize: '18px',
              textShadow: '0 2px 4px var(--color-shadow)',
              letterSpacing: '0.3px'
            }}>
              ⚠️ ZONA DE PELIGRO
            </div>
            <div style={{
              fontWeight: '600',
              textShadow: '0 1px 2px var(--color-shadow)'
            }}>
              Estas acciones eliminarán datos permanentemente. Úsalas solo si tienes problemas de persistencia o quieres empezar completamente desde cero.
            </div>
          </div>

          <div style={{ 
            marginBottom: '20px', 
            padding: '20px', 
            backgroundColor: 'var(--color-surfaceSecondary)', 
            borderRadius: '10px', 
            border: '2px solid var(--color-error)',
            fontSize: '16px',
            color: 'var(--color-textPrimary)',
            boxShadow: '0 4px 12px var(--color-shadow)',
            transition: 'all 0.3s ease'
          }}>
            <h4 style={{ 
              color: 'var(--color-error)', 
              fontWeight: '800', 
              marginBottom: '12px',
              fontSize: '18px',
              textShadow: '0 2px 4px var(--color-shadow)',
              letterSpacing: '0.3px'
            }}>
              🗑️ Limpieza Completa
            </h4>
            <p style={{ 
              margin: '0 0 16px 0', 
              color: 'var(--color-textPrimary)', 
              fontSize: '16px',
              fontWeight: '600',
              textShadow: '0 1px 2px var(--color-shadow)'
            }}>
              Elimina TODOS los datos del editor: proyectos, personajes, archivos, configuración, todo.
            </p>
            <Button 
              className="danger"
              onClick={handleClearAllData}
              style={{ fontSize: '14px', padding: '12px 20px' }}
            >
              🗑️ Limpiar TODOS los Datos
            </Button>
          </div>

          <div style={{ 
            marginBottom: '20px', 
            padding: '20px', 
            backgroundColor: 'var(--color-surfaceSecondary)', 
            borderRadius: '10px', 
            border: '2px solid var(--color-info)',
            fontSize: '16px',
            color: 'var(--color-textPrimary)',
            boxShadow: '0 4px 12px var(--color-shadow)',
            transition: 'all 0.3s ease'
          }}>
            <h4 style={{ 
              color: 'var(--color-info)', 
              fontWeight: '800', 
              marginBottom: '12px',
              fontSize: '18px',
              textShadow: '0 2px 4px var(--color-shadow)',
              letterSpacing: '0.3px'
            }}>
              🧹 Limpieza de Datos Huérfanos
            </h4>
            <p style={{ 
              margin: '0 0 16px 0', 
              color: 'var(--color-textPrimary)', 
              fontSize: '16px',
              fontWeight: '600',
              textShadow: '0 1px 2px var(--color-shadow)'
            }}>
              Elimina archivos que no pertenecen a ningún proyecto (sprites de personajes eliminados, etc.)
            </p>
            <Button 
              onClick={handleCleanupOrphanedData}
              style={{ fontSize: '14px', padding: '12px 20px' }}
            >
              🧹 Limpiar Datos Huérfanos
            </Button>
          </div>

          <div style={{ 
            marginBottom: '20px', 
            padding: '20px', 
            backgroundColor: 'var(--color-surfaceSecondary)', 
            borderRadius: '10px', 
            border: '2px solid var(--color-info)',
            fontSize: '16px',
            color: 'var(--color-textPrimary)',
            boxShadow: '0 4px 12px var(--color-shadow)',
            transition: 'all 0.3s ease'
          }}>
            <h4 style={{ 
              color: 'var(--color-info)', 
              fontWeight: '800', 
              marginBottom: '12px',
              fontSize: '18px',
              textShadow: '0 2px 4px var(--color-shadow)',
              letterSpacing: '0.3px'
            }}>
              💡 Cuándo usar la limpieza
            </h4>
            <ul style={{ 
              margin: '0', 
              paddingLeft: '20px',
              color: 'var(--color-textPrimary)', 
              fontSize: '14px',
              fontWeight: '500',
              textShadow: '0 1px 2px var(--color-shadow)'
            }}>
              <li>Personajes que no se eliminan al borrar proyectos</li>
              <li>Archivos corruptos o datos inconsistentes</li>
              <li>Problemas de rendimiento por datos acumulados</li>
              <li>Quieres empezar completamente desde cero</li>
            </ul>
          </div>
        </TabContent>

      </ModalContent>
    </ModalOverlay>
  );
}

export default ProjectManager;
