import React from 'react';
import styled from 'styled-components';

const AboutContent = styled.div`
  color: var(--color-textPrimary);
  line-height: 1.6;
  text-align: center;
`;

const Logo = styled.div`
  font-size: 48px;
  margin-bottom: 16px;
  background: linear-gradient(135deg, var(--color-primary) 0%, var(--color-primaryHover) 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  font-weight: 800;
`;

const Version = styled.div`
  font-size: 18px;
  color: var(--color-textSecondary);
  margin-bottom: 24px;
  font-weight: 500;
`;

const Description = styled.div`
  font-size: 16px;
  margin-bottom: 32px;
  padding: 20px;
  background: linear-gradient(135deg, var(--color-backgroundElevated) 0%, var(--color-surface) 100%);
  border-radius: 12px;
  border-left: 4px solid var(--color-primary);
  text-align: left;
`;

const FeatureGrid = styled.div`
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 16px;
  margin-bottom: 32px;
`;

const FeatureCard = styled.div`
  padding: 16px;
  background: linear-gradient(135deg, var(--color-backgroundSecondary) 0%, var(--color-surface) 100%);
  border-radius: 12px;
  border: 1px solid var(--color-border);
  text-align: left;
  transition: all 0.3s ease;

  &:hover {
    transform: translateY(-2px);
    box-shadow: 0 4px 16px rgba(0, 0, 0, 0.1);
  }
`;

const FeatureIcon = styled.div`
  font-size: 24px;
  margin-bottom: 8px;
`;

const FeatureTitle = styled.h4`
  margin: 0 0 8px 0;
  color: var(--color-primary);
  font-weight: 600;
`;

const FeatureDescription = styled.p`
  margin: 0;
  font-size: 14px;
  color: var(--color-textSecondary);
`;

const TechStack = styled.div`
  margin-bottom: 32px;
  padding: 20px;
  background: linear-gradient(135deg, var(--color-backgroundElevated) 0%, var(--color-surface) 100%);
  border-radius: 12px;
  border: 1px solid var(--color-border);
`;

const TechTitle = styled.h3`
  margin: 0 0 16px 0;
  color: var(--color-primary);
  font-weight: 600;
`;

const TechList = styled.div`
  display: flex;
  flex-wrap: wrap;
  gap: 8px;
  justify-content: center;
`;

const TechBadge = styled.span`
  background: linear-gradient(135deg, var(--color-primary) 0%, var(--color-primaryHover) 100%);
  color: var(--color-textInverse);
  padding: 4px 12px;
  border-radius: 20px;
  font-size: 12px;
  font-weight: 500;
`;

const Credits = styled.div`
  margin-top: 32px;
  padding: 20px;
  background: linear-gradient(135deg, var(--color-backgroundSecondary) 0%, var(--color-surface) 100%);
  border-radius: 12px;
  border: 1px solid var(--color-border);
`;

const CreditsTitle = styled.h3`
  margin: 0 0 16px 0;
  color: var(--color-primary);
  font-weight: 600;
`;

function AboutModalContent() {
  const features = [
    {
      icon: '🎨',
      title: 'Interfaz Intuitiva',
      description: 'Diseño moderno y fácil de usar para crear novelas visuales sin programación.'
    },
    {
      icon: '👁️',
      title: 'Vista Previa en Tiempo Real',
      description: 'Ve cómo se ve tu novela mientras la creas, con actualizaciones instantáneas.'
    },
    {
      icon: '🔄',
      title: 'Compatible con Ren\'Py',
      description: 'Exporta proyectos listos para usar en Ren\'Py, el motor más popular.'
    },
    {
      icon: '📁',
      title: 'Gestión de Recursos',
      description: 'Organiza imágenes, audio y archivos de manera eficiente y estructurada.'
    },
    {
      icon: '🎭',
      title: 'Sistema de Personajes',
      description: 'Crea y gestiona personajes con múltiples expresiones y poses.'
    },
    {
      icon: '🎯',
      title: 'Decisiones Interactivas',
      description: 'Implementa decisiones que afectan el curso de la historia fácilmente.'
    }
  ];

  const technologies = [
    'React 18', 'Styled Components', 'IndexedDB', 'HTML5', 'CSS3', 
    'JavaScript ES6+', 'Webpack', 'Node.js', 'NPM'
  ];

  return (
    <AboutContent>
      <Logo>RynEditor</Logo>
      <Version>Versión 1.0.0</Version>
      
      <Description>
        <strong>RynEditor</strong> es un editor visual moderno y completo para crear novelas visuales 
        compatibles con Ren'Py. Diseñado para escritores, desarrolladores indie y creadores de contenido 
        que quieren dar vida a sus historias de manera intuitiva y profesional.
      </Description>

      <FeatureGrid>
        {features.map((feature, index) => (
          <FeatureCard key={index}>
            <FeatureIcon>{feature.icon}</FeatureIcon>
            <FeatureTitle>{feature.title}</FeatureTitle>
            <FeatureDescription>{feature.description}</FeatureDescription>
          </FeatureCard>
        ))}
      </FeatureGrid>

      <TechStack>
        <TechTitle>🛠️ Tecnologías Utilizadas</TechTitle>
        <TechList>
          {technologies.map((tech, index) => (
            <TechBadge key={index}>{tech}</TechBadge>
          ))}
        </TechList>
      </TechStack>

      <Credits>
        <CreditsTitle>👨‍💻 Desarrollo y Créditos</CreditsTitle>
        <p>
          <strong>RynEditor</strong> es un proyecto de código abierto desarrollado con pasión 
          para la comunidad de creadores de novelas visuales.
        </p>
        
        <h4 style={{ color: 'var(--color-primary)', marginTop: '20px', marginBottom: '12px' }}>
          🌟 Características Especiales:
        </h4>
        <ul style={{ textAlign: 'left', marginBottom: '20px' }}>
          <li><strong>Autoguardado:</strong> Tu trabajo se guarda automáticamente cada 30 segundos</li>
          <li><strong>Exportación Completa:</strong> Genera proyectos Ren'Py listos para distribuir</li>
          <li><strong>Interfaz Responsiva:</strong> Se adapta a diferentes tamaños de pantalla</li>
          <li><strong>Temas Personalizables:</strong> Modo claro y oscuro disponibles</li>
          <li><strong>Gestión de Estado:</strong> Manejo inteligente de datos y preferencias</li>
        </ul>
        
        <h4 style={{ color: 'var(--color-primary)', marginTop: '20px', marginBottom: '12px' }}>
          🎯 Objetivo del Proyecto:
        </h4>
        <p style={{ textAlign: 'left' }}>
          Democratizar la creación de novelas visuales, permitiendo que cualquier persona 
          con una historia que contar pueda crear experiencias interactivas profesionales 
          sin necesidad de conocimientos técnicos avanzados.
        </p>
        
        <div style={{ marginTop: '24px', padding: '16px', backgroundColor: 'var(--color-backgroundElevated)', borderRadius: '8px' }}>
          <p style={{ margin: 0, fontSize: '14px', color: 'var(--color-textSecondary)' }}>
            💝 <strong>Agradecimientos especiales</strong> a la comunidad de Ren'Py y a todos los 
            creadores de novelas visuales que inspiran este proyecto.
          </p>
        </div>
      </Credits>
    </AboutContent>
  );
}

export default AboutModalContent;
