import React, { useState } from 'react';
import styled from 'styled-components';

const ResourcesContainer = styled.div`
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: var(--color-background);
  z-index: 3000;
  overflow-y: auto;
  transition: background-color 0.3s ease;
`;

const ResourcesHeader = styled.div`
  background-color: var(--color-surface);
  padding: 20px;
  border-bottom: 2px solid var(--color-border);
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: sticky;
  top: 0;
  z-index: 3001;
  transition: background-color 0.3s ease, border-color 0.3s ease;
`;

const ResourcesTitle = styled.h1`
  margin: 0;
  color: var(--color-success);
  font-size: 28px;
  display: flex;
  align-items: center;
  gap: 15px;
  transition: color 0.3s ease;
`;

const CloseButton = styled.button`
  background-color: var(--color-error);
  color: var(--color-textInverse);
  border: none;
  border-radius: 8px;
  padding: 12px 20px;
  cursor: pointer;
  font-size: 16px;
  font-weight: bold;
  transition: all 0.3s ease;

  &:hover {
    background-color: var(--color-errorHover);
    transform: scale(1.05);
  }
`;

const ResourcesContent = styled.div`
  padding: 30px;
  max-width: 1200px;
  margin: 0 auto;
`;

const SearchBox = styled.div`
  background-color: var(--color-surfaceSecondary);
  border: 2px solid var(--color-border);
  border-radius: 12px;
  padding: 25px;
  margin-bottom: 30px;
  transition: background-color 0.3s ease, border-color 0.3s ease;
  box-shadow: 0 4px 12px var(--color-shadow);

  &:hover {
    border-color: var(--color-primary);
    box-shadow: 0 6px 16px var(--color-shadowHover);
  }
`;

const SearchTitle = styled.h2`
  color: var(--color-success);
  margin: 0 0 15px 0;
  font-size: 20px;
  display: flex;
  align-items: center;
  gap: 10px;
  transition: color 0.3s ease;
`;

const SearchInput = styled.input`
  width: 100%;
  padding: 12px 16px;
  background-color: var(--color-backgroundSecondary);
  border: 2px solid var(--color-border);
  border-radius: 8px;
  color: var(--color-textPrimary);
  font-size: 16px;
  font-weight: 500;
  transition: all 0.3s ease;
  box-shadow: 0 2px 6px var(--color-shadow);

  &:focus {
    outline: none;
    border-color: var(--color-success);
    box-shadow: 0 4px 12px var(--color-shadowHover);
    transform: translateY(-1px);
  }

  &:hover {
    border-color: var(--color-primary);
    box-shadow: 0 3px 8px var(--color-shadowHover);
  }

  &::placeholder {
    color: var(--color-textTertiary);
    font-weight: 400;
  }
`;

const CategoryTabs = styled.div`
  display: flex;
  gap: 10px;
  margin-bottom: 30px;
  flex-wrap: wrap;
`;

const CategoryTab = styled.button`
  background-color: ${props => props.active ? 'var(--color-success)' : 'var(--color-surfaceSecondary)'};
  color: ${props => props.active ? 'var(--color-textInverse)' : 'var(--color-textPrimary)'};
  border: 2px solid ${props => props.active ? 'var(--color-success)' : 'var(--color-border)'};
  padding: 14px 22px;
  border-radius: 8px;
  cursor: pointer;
  font-size: 14px;
  font-weight: 600;
  transition: all 0.3s ease;
  box-shadow: 0 3px 8px var(--color-shadow);

  &:hover {
    background-color: ${props => props.active ? 'var(--color-successHover)' : 'var(--color-border)'};
    border-color: ${props => props.active ? 'var(--color-successHover)' : 'var(--color-primary)'};
    color: ${props => props.active ? 'var(--color-textInverse)' : 'var(--color-textInverse)'};
    transform: translateY(-3px);
    box-shadow: 0 6px 16px var(--color-shadowHover);
  }

  &:active {
    transform: translateY(-1px);
    box-shadow: 0 4px 12px var(--color-shadowHover);
  }
`;

const ResourceGrid = styled.div`
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(400px, 1fr));
  gap: 25px;
  margin-bottom: 40px;
`;

const ResourceCard = styled.div`
  background-color: var(--color-surfaceSecondary);
  border: 2px solid var(--color-border);
  border-radius: 12px;
  padding: 24px;
  transition: all 0.3s ease;
  box-shadow: 0 4px 12px var(--color-shadow);

  &:hover {
    border-color: var(--color-success);
    background-color: var(--color-surfaceElevated);
    transform: translateY(-8px);
    box-shadow: 0 12px 32px var(--color-shadowHover);
  }
`;

const ResourceHeader = styled.div`
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  margin-bottom: 15px;
`;

const ResourceTitle = styled.h3`
  margin: 0;
  color: var(--color-success);
  font-size: 18px;
  font-weight: 700;
  display: flex;
  align-items: center;
  gap: 10px;
  transition: color 0.3s ease;
  text-shadow: 0 1px 2px var(--color-shadow);
`;

const ResourceType = styled.span`
  background-color: ${props => {
    switch(props.type) {
      case 'official': return 'var(--color-info)';
      case 'community': return 'var(--color-success)';
      case 'tutorial': return 'var(--color-warning)';
      case 'tool': return 'var(--color-primary)';
      case 'asset': return 'var(--color-error)';
      case 'game': return 'var(--color-border)';
      default: return 'var(--color-borderSecondary)';
    }
  }};
  color: var(--color-textInverse);
  padding: 6px 12px;
  border-radius: 6px;
  font-size: 12px;
  font-weight: 700;
  transition: all 0.3s ease;
  box-shadow: 0 2px 6px var(--color-shadow);
  border: 1px solid rgba(255, 255, 255, 0.1);

  &:hover {
    transform: translateY(-1px);
    box-shadow: 0 4px 8px var(--color-shadowHover);
  }
`;

const ResourceDescription = styled.p`
  color: var(--color-textSecondary);
  margin: 0 0 15px 0;
  line-height: 1.6;
  font-size: 15px;
  font-weight: 500;
  transition: color 0.3s ease;
`;

const ResourceLink = styled.a`
  background-color: var(--color-success);
  color: var(--color-textInverse);
  text-decoration: none;
  padding: 12px 20px;
  border-radius: 8px;
  font-size: 14px;
  font-weight: 700;
  display: inline-block;
  transition: all 0.3s ease;
  box-shadow: 0 4px 12px var(--color-shadow);
  border: 2px solid var(--color-success);

  &:hover {
    background-color: var(--color-successHover);
    border-color: var(--color-successHover);
    transform: translateY(-2px) scale(1.02);
    box-shadow: 0 6px 20px var(--color-shadowHover);
  }

  &:active {
    transform: translateY(0) scale(0.98);
    box-shadow: 0 3px 8px var(--color-shadow);
  }
`;

const ResourceTags = styled.div`
  display: flex;
  flex-wrap: wrap;
  gap: 8px;
  margin: 15px 0;
`;

const Tag = styled.span`
  background-color: var(--color-border);
  color: var(--color-textInverse);
  padding: 6px 10px;
  border-radius: 6px;
  font-size: 12px;
  font-weight: 600;
  transition: all 0.3s ease;
  box-shadow: 0 2px 4px var(--color-shadow);
  border: 1px solid rgba(255, 255, 255, 0.1);

  &:hover {
    background-color: var(--color-primary);
    transform: translateY(-1px);
    box-shadow: 0 3px 6px var(--color-shadowHover);
  }
`;

const InfoBox = styled.div`
  background-color: var(--color-surfaceSecondary);
  border: 2px solid var(--color-success);
  border-radius: 12px;
  padding: 20px;
  margin: 20px 0;
  transition: all 0.3s ease;
  box-shadow: 0 4px 16px var(--color-shadow);

  &:hover {
    border-color: var(--color-successHover);
    box-shadow: 0 6px 20px var(--color-shadowHover);
    transform: translateY(-2px);
  }
`;

const InfoTitle = styled.div`
  color: var(--color-success);
  font-weight: 700;
  font-size: 16px;
  margin-bottom: 12px;
  display: flex;
  align-items: center;
  gap: 8px;
  transition: color 0.3s ease;
  text-shadow: 0 1px 2px var(--color-shadow);
`;

const InfoText = styled.div`
  color: var(--color-textPrimary);
  font-size: 15px;
  line-height: 1.6;
  font-weight: 500;
  transition: color 0.3s ease;
`;

const TipBox = styled.div`
  background-color: var(--color-surfaceSecondary);
  border: 2px solid var(--color-warning);
  border-radius: 12px;
  padding: 20px;
  margin: 20px 0;
  transition: all 0.3s ease;
  box-shadow: 0 4px 16px var(--color-shadow);

  &:hover {
    border-color: var(--color-warningHover);
    box-shadow: 0 6px 20px var(--color-shadowHover);
    transform: translateY(-2px);
  }
`;

const TipTitle = styled.div`
  color: var(--color-warning);
  font-weight: 700;
  font-size: 16px;
  margin-bottom: 12px;
  display: flex;
  align-items: center;
  gap: 8px;
  transition: color 0.3s ease;
  text-shadow: 0 1px 2px var(--color-shadow);
`;

const TipText = styled.div`
  color: var(--color-textPrimary);
  font-size: 15px;
  line-height: 1.6;
  font-weight: 500;
  transition: color 0.3s ease;
`;

const WarningBox = styled.div`
  background-color: var(--color-surfaceSecondary);
  border: 2px solid var(--color-error);
  border-radius: 12px;
  padding: 20px;
  margin: 20px 0;
  transition: all 0.3s ease;
  box-shadow: 0 4px 16px var(--color-shadow);

  &:hover {
    border-color: var(--color-errorHover);
    box-shadow: 0 6px 20px var(--color-shadowHover);
    transform: translateY(-2px);
  }
`;

const WarningTitle = styled.div`
  color: var(--color-error);
  font-weight: 700;
  font-size: 16px;
  margin-bottom: 12px;
  display: flex;
  align-items: center;
  gap: 8px;
  transition: color 0.3s ease;
  text-shadow: 0 1px 2px var(--color-shadow);
`;

const WarningText = styled.div`
  color: var(--color-textPrimary);
  font-size: 15px;
  line-height: 1.6;
  font-weight: 500;
  transition: color 0.3s ease;
`;

const QuickAccessGrid = styled.div`
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  gap: 15px;
  margin-bottom: 30px;
`;

const QuickAccessCard = styled.a`
  background-color: var(--color-surfaceSecondary);
  border: 2px solid var(--color-border);
  border-radius: 12px;
  padding: 24px;
  text-decoration: none;
  color: var(--color-textPrimary);
  text-align: center;
  transition: all 0.3s ease;
  box-shadow: 0 4px 12px var(--color-shadow);

  &:hover {
    border-color: var(--color-success);
    background-color: var(--color-surfaceElevated);
    transform: translateY(-5px);
    box-shadow: 0 8px 24px var(--color-shadowHover);
  }
`;

const QuickAccessIcon = styled.div`
  font-size: 32px;
  margin-bottom: 10px;
`;

const QuickAccessTitle = styled.div`
  font-weight: 700;
  font-size: 15px;
  color: var(--color-success);
  transition: color 0.3s ease;
  text-shadow: 0 1px 2px var(--color-shadow);
`;

const GameBanner = styled.div`
  width: 100%;
  height: 120px;
  background-color: var(--color-backgroundSecondary);
  border-radius: 12px;
  margin: 15px 0;
  display: flex;
  align-items: center;
  justify-content: center;
  border: 2px solid var(--color-border);
  overflow: hidden;
  position: relative;
  transition: all 0.3s ease;
  box-shadow: 0 4px 12px var(--color-shadow);

  &:hover {
    border-color: var(--color-primary);
    box-shadow: 0 6px 16px var(--color-shadowHover);
  }

  img {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }

  .placeholder {
    color: var(--color-textTertiary);
    font-size: 14px;
    text-align: center;
    transition: color 0.3s ease;
  }
`;

const GameInfo = styled.div`
  display: flex;
  flex-direction: column;
  gap: 8px;
  margin: 10px 0;
`;

const GameTitle = styled.h4`
  margin: 0;
  color: var(--color-textPrimary);
  font-size: 16px;
  font-weight: 700;
  transition: color 0.3s ease;
  text-shadow: 0 1px 2px var(--color-shadow);
`;

const GameDeveloper = styled.div`
  color: var(--color-textTertiary);
  font-size: 13px;
  font-weight: 500;
  transition: color 0.3s ease;
`;

const GamePrice = styled.div`
  color: var(--color-success);
  font-weight: 700;
  font-size: 14px;
  transition: color 0.3s ease;
  text-shadow: 0 1px 2px var(--color-shadow);
`;

const GameTags = styled.div`
  display: flex;
  flex-wrap: wrap;
  gap: 6px;
  margin: 10px 0;
`;

const GameTag = styled.span`
  background-color: var(--color-border);
  color: var(--color-textInverse);
  padding: 4px 8px;
  border-radius: 6px;
  font-size: 11px;
  font-weight: 600;
  transition: all 0.3s ease;
  box-shadow: 0 2px 4px var(--color-shadow);
  border: 1px solid rgba(255, 255, 255, 0.1);

  &:hover {
    background-color: var(--color-primary);
    transform: translateY(-1px);
    box-shadow: 0 3px 6px var(--color-shadowHover);
  }
`;

