// Sistema de sincronización en tiempo real para colaboración
import { collaborationClient } from './collaborationClient';

class SyncManager {
  constructor() {
    this.isEnabled = false;
    this.currentUserId = null;
    this.pendingOperations = new Set();
    this.operationQueue = [];
    this.isProcessingQueue = false;
    
    // Configuración de sincronización
    this.config = {
      debounceDelay: 300, // ms
      maxRetries: 3,
      retryDelay: 1000, // ms
      conflictResolution: 'last-write-wins' // o 'merge' para futuras implementaciones
    };
  }

  // Inicializar el sistema de sincronización
  initialize(userId) {
    this.currentUserId = userId;
    this.isEnabled = true;
    
    // Configurar listeners para operaciones de sincronización
    collaborationClient.on('syncOperation', this.handleIncomingSync.bind(this));
    collaborationClient.on('connected', () => {
      console.log('🔄 Sistema de sincronización activado');
    });
    
    console.log('🔄 SyncManager inicializado para usuario:', userId);
  }

  // Desactivar sincronización
  disable() {
    this.isEnabled = false;
    this.currentUserId = null;
    this.pendingOperations.clear();
    this.operationQueue = [];
    
    collaborationClient.off('syncOperation', this.handleIncomingSync.bind(this));
    console.log('🔄 Sistema de sincronización desactivado');
  }

  // Enviar operación de sincronización
  sendSyncOperation(operation, data, metadata = {}) {
    if (!this.isEnabled || !collaborationClient.isConnected) {
      console.warn('⚠️ Sincronización desactivada o sin conexión');
      return;
    }

    // Crear operación única
    const operationId = this.generateOperationId();
    const syncData = {
      type: 'sync_operation',
      operation: operation,
      data: data,
      metadata: {
        ...metadata,
        operationId,
        userId: this.currentUserId,
        timestamp: Date.now()
      }
    };

    // Evitar operaciones duplicadas
    if (this.pendingOperations.has(operationId)) {
      console.log('🔄 Operación ya en cola:', operationId);
      return;
    }

    this.pendingOperations.add(operationId);
    this.operationQueue.push(syncData);

    // Procesar cola con debounce
    this.debounceProcessQueue();

    console.log('🔄 Operación enviada:', operation, data);
  }

  // Procesar cola de operaciones con debounce
  debounceProcessQueue() {
    if (this.debounceTimeout) {
      clearTimeout(this.debounceTimeout);
    }

    this.debounceTimeout = setTimeout(() => {
      this.processQueue();
    }, this.config.debounceDelay);
  }

  // Procesar cola de operaciones
  async processQueue() {
    if (this.isProcessingQueue || this.operationQueue.length === 0) {
      return;
    }

    this.isProcessingQueue = true;

    try {
      // Procesar operaciones en lotes
      const batch = this.operationQueue.splice(0, 10); // Máximo 10 operaciones por lote
      
      for (const operation of batch) {
        await this.sendOperationToServer(operation);
        this.pendingOperations.delete(operation.metadata.operationId);
      }

      // Si hay más operaciones, procesar el siguiente lote
      if (this.operationQueue.length > 0) {
        setTimeout(() => this.processQueue(), 50);
      }

    } catch (error) {
      console.error('❌ Error procesando cola de sincronización:', error);
      // Reintentar operaciones fallidas
      this.retryFailedOperations();
    } finally {
      this.isProcessingQueue = false;
    }
  }

  // Enviar operación al servidor
  async sendOperationToServer(operation) {
    return new Promise((resolve, reject) => {
      try {
        collaborationClient.sendMessage(operation);
        resolve();
      } catch (error) {
        reject(error);
      }
    });
  }

  // Reintentar operaciones fallidas
  retryFailedOperations() {
    // Implementar lógica de reintentos si es necesario
    console.log('🔄 Reintentando operaciones fallidas...');
  }

  // Manejar operaciones entrantes de otros usuarios
  handleIncomingSync(data) {
    if (!this.isEnabled || !data.metadata || data.metadata.userId === this.currentUserId) {
      return; // Ignorar operaciones propias
    }

    console.log('🔄 Operación recibida:', data.operation, 'de usuario:', data.metadata.userId);

    // Emitir evento para que el contexto pueda manejar la sincronización
    this.emitSyncEvent(data);
  }

  // Emitir evento de sincronización
  emitSyncEvent(data) {
    const event = new CustomEvent('collaborationSync', {
      detail: {
        operation: data.operation,
        data: data.data,
        metadata: data.metadata
      }
    });
    window.dispatchEvent(event);
  }

  // Generar ID único para operaciones
  generateOperationId() {
    return `op_${Date.now()}_${Math.random().toString(36).substr(2, 9)}`;
  }

  // Métodos de conveniencia para diferentes tipos de operaciones
  syncProjectUpdate(projectData) {
    this.sendSyncOperation('project_update', projectData, {
      description: 'Actualización completa del proyecto'
    });
  }

  syncCharacterAdd(characterData) {
    this.sendSyncOperation('character_add', characterData, {
      description: `Personaje agregado: ${characterData.name}`
    });
  }

  syncCharacterEdit(characterData) {
    this.sendSyncOperation('character_edit', characterData, {
      description: `Personaje editado: ${characterData.name}`
    });
  }

  syncCharacterDelete(characterId) {
    this.sendSyncOperation('character_delete', { id: characterId }, {
      description: 'Personaje eliminado'
    });
  }

  syncScriptElementAdd(elementData) {
    this.sendSyncOperation('script_element_add', elementData, {
      description: `Elemento agregado: ${elementData.type}`
    });
  }

  syncScriptElementEdit(elementData) {
    this.sendSyncOperation('script_element_edit', elementData, {
      description: `Elemento editado: ${elementData.type}`
    });
  }

  syncScriptElementDelete(elementId) {
    this.sendSyncOperation('script_element_delete', { id: elementId }, {
      description: 'Elemento eliminado'
    });
  }

  syncScriptElementMove(elementId, newIndex) {
    this.sendSyncOperation('script_element_move', { id: elementId, newIndex }, {
      description: 'Elemento movido'
    });
  }

  syncSceneAdd(sceneData) {
    this.sendSyncOperation('scene_add', sceneData, {
      description: `Escena agregada: ${sceneData.name}`
    });
  }

  syncSceneEdit(sceneData) {
    this.sendSyncOperation('scene_edit', sceneData, {
      description: `Escena editada: ${sceneData.name}`
    });
  }

  syncSceneDelete(sceneId) {
    this.sendSyncOperation('scene_delete', { id: sceneId }, {
      description: 'Escena eliminada'
    });
  }

  syncProtagonistConfig(configData) {
    this.sendSyncOperation('protagonist_config', configData, {
      description: 'Configuración del protagonista actualizada'
    });
  }
}

// Instancia singleton
export const syncManager = new SyncManager();

export default syncManager;
