// Cliente WebSocket para colaboración en tiempo real
export class CollaborationClient {
  constructor() {
    this.ws = null;
    this.roomCode = null;
    this.isHost = false;
    this.isConnected = false;
    this.users = new Map();
    this.cursorPositions = new Map();
    this.messageHandlers = new Map();
    this.reconnectAttempts = 0;
    this.maxReconnectAttempts = 5;
    this.reconnectDelay = 1000;
  }

  // Conectar al servidor de colaboración
  connect(serverUrl = 'ws://localhost:3002/collaboration') {
    return new Promise((resolve, reject) => {
      try {
        this.ws = new WebSocket(serverUrl);
        
        this.ws.onopen = () => {
          console.log('🔗 Conectado al servidor de colaboración');
          this.isConnected = true;
          this.reconnectAttempts = 0;
          resolve();
        };
        
        this.ws.onmessage = (event) => {
          this.handleMessage(event);
        };
        
        this.ws.onclose = (event) => {
          console.log('🔌 Desconectado del servidor de colaboración');
          this.isConnected = false;
          this.handleDisconnection(event);
        };
        
        this.ws.onerror = (error) => {
          console.error('❌ Error en conexión WebSocket:', error);
          reject(error);
        };
        
      } catch (error) {
        console.error('❌ Error creando conexión WebSocket:', error);
        reject(error);
      }
    });
  }

  // Desconectar
  disconnect() {
    if (this.ws) {
      this.ws.close();
      this.ws = null;
    }
    this.isConnected = false;
    this.roomCode = null;
    this.isHost = false;
    this.users.clear();
    this.cursorPositions.clear();
  }

  // Crear sala (solo host)
  createRoom(projectData, username = 'Host') {
    if (!this.isConnected) {
      throw new Error('No conectado al servidor');
    }
    
    this.sendMessage({
      type: 'create_room',
      payload: {
        projectData,
        username
      }
    });
  }

  // Unirse a sala
  joinRoom(roomCode, username = 'Usuario') {
    if (!this.isConnected) {
      throw new Error('No conectado al servidor');
    }
    
    this.sendMessage({
      type: 'join_room',
      payload: {
        roomCode: roomCode.toUpperCase(),
        username
      }
    });
  }

  // Abandonar sala
  leaveRoom() {
    if (this.roomCode) {
      this.sendMessage({
        type: 'leave_room',
        payload: {}
      });
    }
  }

  // Enviar operación de sincronización
  sendSyncOperation(operation, data) {
    if (!this.roomCode) {
      console.warn('⚠️ No estás en una sala');
      return;
    }
    
    this.sendMessage({
      type: 'sync_operation',
      payload: {
        operation,
        data,
        timestamp: Date.now()
      }
    });
  }

  // Actualizar posición del cursor
  updateCursor(cursor) {
    if (!this.roomCode) return;
    
    this.sendMessage({
      type: 'update_cursor',
      payload: { cursor }
    });
  }

  // Enviar mensaje de chat
  sendChatMessage(message) {
    if (!this.roomCode) return;
    
    this.sendMessage({
      type: 'send_chat',
      payload: { message }
    });
  }

  // Actualizar nombre de usuario
  updateUsername(username) {
    if (!this.roomCode) return;
    
    this.sendMessage({
      type: 'update_username',
      payload: { username }
    });
  }

  // Enviar mensaje al servidor
  sendMessage(message) {
    if (this.ws && this.ws.readyState === WebSocket.OPEN) {
      try {
        this.ws.send(JSON.stringify(message));
      } catch (error) {
        console.error('❌ Error enviando mensaje:', error);
      }
    } else {
      console.warn('⚠️ WebSocket no conectado');
    }
  }

  // Manejar mensajes del servidor
  handleMessage(event) {
    try {
      const data = JSON.parse(event.data);
      
      switch (data.type) {
        case 'connected':
          this.handleConnected(data);
          break;
        case 'room_created':
          this.handleRoomCreated(data);
          break;
        case 'room_joined':
          this.handleRoomJoined(data);
          break;
        case 'user_joined':
          this.handleUserJoined(data);
          break;
        case 'user_left':
          this.handleUserLeft(data);
          break;
        case 'sync_operation':
          this.handleSyncOperation(data);
          break;
        case 'cursor_update':
          this.handleCursorUpdate(data);
          break;
        case 'chat_message':
          this.handleChatMessage(data);
          break;
        case 'username_updated':
          this.handleUsernameUpdated(data);
          break;
        case 'host_transferred':
          this.handleHostTransferred(data);
          break;
        case 'room_closed':
          this.handleRoomClosed(data);
          break;
        case 'error':
          this.handleError(data);
          break;
        default:
          console.log('⚠️ Tipo de mensaje no reconocido:', data.type);
      }
    } catch (error) {
      console.error('❌ Error procesando mensaje:', error);
    }
  }

  // Handlers de mensajes
  handleConnected(data) {
    console.log('✅ Conectado:', data.message);
    this.triggerEvent('connected', data);
  }

  handleRoomCreated(data) {
    this.roomCode = data.roomCode;
    this.isHost = true;
    console.log('🏠 Sala creada:', data.roomCode);
    this.triggerEvent('roomCreated', data);
  }

  handleRoomJoined(data) {
    this.roomCode = data.roomCode;
    this.isHost = false;
    console.log('👤 Unido a sala:', data.roomCode);
    
    // Actualizar lista de usuarios
    data.users.forEach(user => {
      this.users.set(user.id, user);
    });
    
    this.triggerEvent('roomJoined', data);
  }

  handleUserJoined(data) {
    this.users.set(data.user.id, data.user);
    console.log('👤 Usuario se unió:', data.user.username);
    this.triggerEvent('userJoined', data);
  }

  handleUserLeft(data) {
    this.users.delete(data.user.id);
    this.cursorPositions.delete(data.user.id);
    console.log('👋 Usuario se fue:', data.user.username);
    this.triggerEvent('userLeft', data);
  }

  handleSyncOperation(data) {
    console.log('🔄 Operación de sincronización:', data.operation);
    this.triggerEvent('syncOperation', data);
  }

  handleCursorUpdate(data) {
    this.cursorPositions.set(data.userId, {
      ...data.cursor,
      username: data.username
    });
    this.triggerEvent('cursorUpdate', data);
  }

  handleChatMessage(data) {
    console.log('💬 Chat:', data.username, data.message);
    this.triggerEvent('chatMessage', data);
  }

  handleUsernameUpdated(data) {
    if (this.users.has(data.userId)) {
      this.users.get(data.userId).username = data.newUsername;
    }
    console.log('📝 Usuario cambió nombre:', data.oldUsername, '→', data.newUsername);
    this.triggerEvent('usernameUpdated', data);
  }

  handleHostTransferred(data) {
    if (data.newHost.id === this.getUserId()) {
      this.isHost = true;
      console.log('👑 Eres el nuevo host');
    }
    this.triggerEvent('hostTransferred', data);
  }

  handleRoomClosed(data) {
    console.log('🗑️ Sala cerrada:', data.message);
    this.roomCode = null;
    this.isHost = false;
    this.users.clear();
    this.cursorPositions.clear();
    this.triggerEvent('roomClosed', data);
  }

  handleError(data) {
    console.error('❌ Error del servidor:', data.message);
    this.triggerEvent('error', data);
  }

  // Manejar desconexión
  handleDisconnection(event) {
    this.triggerEvent('disconnected', event);
    
    // Solo intentar reconectar si estamos en una sala activa
    if (!event.wasClean && this.roomCode && this.reconnectAttempts < this.maxReconnectAttempts) {
      this.reconnectAttempts++;
      const delay = this.reconnectDelay * Math.pow(2, this.reconnectAttempts - 1);
      
      console.log(`🔄 Intentando reconectar en ${delay}ms (intento ${this.reconnectAttempts})`);
      
      setTimeout(() => {
        this.connect().catch(error => {
          console.error('❌ Error reconectando:', error);
        });
      }, delay);
    } else if (!this.roomCode) {
      console.log('🔌 Sin sala activa, no se intentará reconectar');
    }
  }

  // Sistema de eventos
  on(event, handler) {
    if (!this.messageHandlers.has(event)) {
      this.messageHandlers.set(event, []);
    }
    this.messageHandlers.get(event).push(handler);
  }

  off(event, handler) {
    if (this.messageHandlers.has(event)) {
      const handlers = this.messageHandlers.get(event);
      const index = handlers.indexOf(handler);
      if (index > -1) {
        handlers.splice(index, 1);
      }
    }
  }

  triggerEvent(event, data) {
    if (this.messageHandlers.has(event)) {
      this.messageHandlers.get(event).forEach(handler => {
        try {
          handler(data);
        } catch (error) {
          console.error(`❌ Error en handler de ${event}:`, error);
        }
      });
    }
  }

  // Getters
  getUserId() {
    return this.ws ? this.ws.userId : null;
  }

  getRoomCode() {
    return this.roomCode;
  }

  getIsHost() {
    return this.isHost;
  }

  getIsConnected() {
    return this.isConnected;
  }

  getUsers() {
    return Array.from(this.users.values());
  }

  getCursorPositions() {
    return Array.from(this.cursorPositions.values());
  }

  // Obtener IP del servidor para compartir
  getServerIP() {
    if (this.ws && this.ws.url) {
      const url = new URL(this.ws.url);
      return `${url.hostname}:${url.port}`;
    }
    return null;
  }
}

// Instancia global del cliente de colaboración
export const collaborationClient = new CollaborationClient();
