// Utilidad para detectar la IP pública automáticamente
export class IPDetector {
  constructor() {
    this.publicIP = null;
    this.localIP = null;
    this.isDetecting = false;
    this.callbacks = [];
  }

  // Detectar IP pública usando servicios gratuitos
  async detectPublicIP() {
    if (this.isDetecting) {
      return new Promise((resolve) => {
        this.callbacks.push(resolve);
      });
    }

    this.isDetecting = true;
    this.callbacks = [];

    const services = [
      'https://api.ipify.org?format=json',
      'https://ipapi.co/json/',
      'https://api.myip.com',
      'https://ipinfo.io/json',
      'https://api.ip.sb/geoip'
    ];

    for (const service of services) {
      try {
        const response = await fetch(service, {
          method: 'GET',
          timeout: 5000
        });
        
        if (response.ok) {
          const data = await response.json();
          this.publicIP = data.ip || data.query || data.ipAddress;
          
          if (this.publicIP) {
            console.log('🌐 IP pública detectada:', this.publicIP);
            this.isDetecting = false;
            this.notifyCallbacks(this.publicIP);
            return this.publicIP;
          }
        }
      } catch (error) {
        console.warn(`⚠️ Error detectando IP con ${service}:`, error);
        continue;
      }
    }

    // Si todos los servicios fallan, intentar con WebRTC
    try {
      const webrtcIP = await this.detectIPWithWebRTC();
      if (webrtcIP) {
        this.publicIP = webrtcIP;
        console.log('🌐 IP pública detectada con WebRTC:', this.publicIP);
        this.isDetecting = false;
        this.notifyCallbacks(this.publicIP);
        return this.publicIP;
      }
    } catch (error) {
      console.warn('⚠️ Error detectando IP con WebRTC:', error);
    }

    this.isDetecting = false;
    this.notifyCallbacks(null);
    return null;
  }

  // Detectar IP usando WebRTC (más confiable para IP pública)
  async detectIPWithWebRTC() {
    return new Promise((resolve) => {
      const pc = new RTCPeerConnection({
        iceServers: [
          { urls: 'stun:stun.l.google.com:19302' },
          { urls: 'stun:stun1.l.google.com:19302' },
          { urls: 'stun:stun2.l.google.com:19302' }
        ]
      });

      pc.createDataChannel('');
      pc.createOffer().then(offer => pc.setLocalDescription(offer));

      pc.onicecandidate = (event) => {
        if (event.candidate) {
          const candidate = event.candidate.candidate;
          const ipMatch = candidate.match(/([0-9]{1,3}(\.[0-9]{1,3}){3})/);
          
          if (ipMatch) {
            const ip = ipMatch[1];
            
            // Filtrar IPs locales y privadas
            if (!this.isPrivateIP(ip) && !this.isLocalIP(ip)) {
              pc.close();
              resolve(ip);
              return;
            }
          }
        }
      };

      // Timeout después de 5 segundos
      setTimeout(() => {
        pc.close();
        resolve(null);
      }, 5000);
    });
  }

  // Detectar IP local
  detectLocalIP() {
    return new Promise((resolve) => {
      const pc = new RTCPeerConnection({
        iceServers: [{ urls: 'stun:stun.l.google.com:19302' }]
      });

      pc.createDataChannel('');
      pc.createOffer().then(offer => pc.setLocalDescription(offer));

      const localIPs = new Set();

      pc.onicecandidate = (event) => {
        if (event.candidate) {
          const candidate = event.candidate.candidate;
          const ipMatch = candidate.match(/([0-9]{1,3}(\.[0-9]{1,3}){3})/);
          
          if (ipMatch) {
            const ip = ipMatch[1];
            
            // Solo IPs locales
            if (this.isPrivateIP(ip) || this.isLocalIP(ip)) {
              localIPs.add(ip);
            }
          }
        }
      };

      setTimeout(() => {
        pc.close();
        // Preferir IPs de red local (192.168.x.x, 10.x.x.x)
        const preferredIPs = Array.from(localIPs).filter(ip => 
          ip.startsWith('192.168.') || ip.startsWith('10.')
        );
        
        this.localIP = preferredIPs[0] || Array.from(localIPs)[0];
        resolve(this.localIP);
      }, 3000);
    });
  }

  // Verificar si es IP privada
  isPrivateIP(ip) {
    const parts = ip.split('.').map(Number);
    
    // 192.168.x.x
    if (parts[0] === 192 && parts[1] === 168) return true;
    
    // 10.x.x.x
    if (parts[0] === 10) return true;
    
    // 172.16.x.x - 172.31.x.x
    if (parts[0] === 172 && parts[1] >= 16 && parts[1] <= 31) return true;
    
    return false;
  }

  // Verificar si es IP local
  isLocalIP(ip) {
    const parts = ip.split('.').map(Number);
    
    // 127.x.x.x (localhost)
    if (parts[0] === 127) return true;
    
    // 169.254.x.x (link-local)
    if (parts[0] === 169 && parts[1] === 254) return true;
    
    return false;
  }

  // Notificar callbacks
  notifyCallbacks(ip) {
    this.callbacks.forEach(callback => callback(ip));
    this.callbacks = [];
  }

  // Obtener IP pública (con cache)
  getPublicIP() {
    return this.publicIP;
  }

  // Obtener IP local (con cache)
  getLocalIP() {
    return this.localIP;
  }

  // Generar URL del servidor para compartir
  generateServerURL(port = 3002) {
    const publicIP = this.getPublicIP();
    const localIP = this.getLocalIP();
    
    if (publicIP) {
      return {
        public: `ws://${publicIP}:${port}/collaboration`,
        local: localIP ? `ws://${localIP}:${port}/collaboration` : null,
        display: `${publicIP}:${port}`
      };
    }
    
    if (localIP) {
      return {
        public: null,
        local: `ws://${localIP}:${port}/collaboration`,
        display: `${localIP}:${port}`
      };
    }
    
    return {
      public: null,
      local: null,
      display: 'localhost:3002'
    };
  }

  // Detectar automáticamente ambas IPs
  async detectAllIPs() {
    const [publicIP, localIP] = await Promise.all([
      this.detectPublicIP(),
      this.detectLocalIP()
    ]);
    
    return {
      public: publicIP,
      local: localIP
    };
  }
}

// Instancia global del detector de IP
export const ipDetector = new IPDetector();
