// === COMPONENTE PRINCIPAL ===
function FileManagerPage({ onClose }) {
  const { state } = useVisualNovel();
  const { showAlert, showConfirm } = useModalContext();
  const [selectedCategory, setSelectedCategory] = useState('characters');
  const [files, setFiles] = useState([]);
  const [filesByCharacter, setFilesByCharacter] = useState({});
  const [isLoading, setIsLoading] = useState(false);
  const [viewMode, setViewMode] = useState('grid');
  const [sortBy, setSortBy] = useState('name');
  const [searchQuery, setSearchQuery] = useState('');
  const [selectedFiles, setSelectedFiles] = useState(new Set());
  const [isUploading, setIsUploading] = useState(false);
  const [uploadProgress, setUploadProgress] = useState(0);
  const [dragOver, setDragOver] = useState(false);
  const [deletingFiles, setDeletingFiles] = useState(new Set());
  const [stats, setStats] = useState({
    totalFiles: 0,
    totalSize: 0,
    categories: {}
  });
  
  const fileInputRef = useRef(null);
  const fileManager = new FileManagerUtil();
  const assetStorage = new AssetStorage();

  // Función para obtener el nombre del personaje por ID
  const getCharacterName = (characterId) => {
    if (characterId === 'sin-personaje') {
      return 'Sin Personaje Asignado';
    }
    
    const character = state.project.characters.find(char => char.id === characterId);
    return character ? character.name : characterId;
  };

  // Función para eliminar todos los archivos de un personaje
  const handleDeleteCharacterFolder = async (characterId) => {
    const characterName = getCharacterName(characterId);
    
    const confirmed = await showConfirm(
      `¿Estás seguro de que quieres eliminar TODOS los sprites de ${characterName}? Esta acción no se puede deshacer.`,
      'Eliminar Carpeta Completa',
      '🗑️'
    );
    
    if (confirmed) {
      try {
        console.log(`Eliminando carpeta completa del personaje: ${characterName}`);
        
        const characterFiles = filesByCharacter[characterId] || [];
        
        // Eliminar cada archivo del personaje
        for (const file of characterFiles) {
          console.log(`Eliminando archivo: ${file.name}`);
          await fileManager.deleteFile(file.id);
        }
        
        // Recargar archivos y estadísticas
        await loadFiles();
        await loadStats();
        
        console.log(`Carpeta del personaje ${characterName} eliminada exitosamente`);
        await showAlert(`Todos los sprites de ${characterName} han sido eliminados`, 'Eliminación Exitosa', '✅');
      } catch (error) {
        console.error('Error al eliminar carpeta del personaje:', error);
        await showAlert('Error al eliminar los archivos del personaje: ' + error.message, 'Error de Eliminación', '❌');
      }
    }
  };

  // Función para eliminar un sprite individual de un personaje
  const handleDeleteCharacterSprite = async (fileId) => {
    const confirmed = await showConfirm(
      '¿Estás seguro de que quieres eliminar este sprite?',
      'Eliminar Sprite',
      '🗑️'
    );
    
    if (confirmed) {
      try {
        console.log(`Eliminando sprite: ${fileId}`);
        
        await fileManager.deleteFile(fileId);
        
        // Recargar archivos y estadísticas
        await loadFiles();
        await loadStats();
        
        console.log('Sprite eliminado exitosamente');
      } catch (error) {
        console.error('Error al eliminar sprite:', error);
        await showAlert('Error al eliminar el sprite: ' + error.message, 'Error de Eliminación', '❌');
      }
    }
  };

  // Categorías disponibles
  const categories = [
    { id: 'characters', name: 'Personajes', icon: '👥', description: 'Sprites de personajes' },
    { id: 'backgrounds', name: 'Fondos', icon: '🖼️', description: 'Imágenes de fondo' },
    { id: 'gui', name: 'Interfaz', icon: '🎨', description: 'Elementos de UI' },
    { id: 'effects', name: 'Efectos', icon: '✨', description: 'Efectos visuales' },
    { id: 'music', name: 'Música', icon: '🎵', description: 'Música de fondo' },
    { id: 'sounds', name: 'Sonidos', icon: '🔊', description: 'Efectos de sonido' },
    { id: 'voice', name: 'Voces', icon: '🎤', description: 'Archivos de voz' },
    { id: 'videos', name: 'Videos', icon: '🎬', description: 'Videos y cinemáticas' },
    { id: 'general', name: 'General', icon: '📁', description: 'Otros archivos' }
  ];

  // Cargar archivos cuando cambia la categoría
  useEffect(() => {
    loadFiles();
    loadStats();
  }, [selectedCategory]);

  // Cargar estadísticas globales
  const loadStats = async () => {
    try {
      console.log('=== CARGANDO ESTADÍSTICAS ===');
      
      // Obtener estadísticas de AssetStorage (IndexedDB)
      await assetStorage.init();
      const storageStats = await assetStorage.getStorageStats();
      console.log('Estadísticas de AssetStorage:', storageStats);
      
      // Obtener estadísticas de FileManager (localStorage)
      const fileManagerStats = fileManager.getAllFiles();
      console.log('Archivos en FileManager:', fileManagerStats.length);
      
      // Usar la fuente con más archivos como referencia
      const finalStats = storageStats.totalFiles >= fileManagerStats.length ? storageStats : {
        totalFiles: fileManagerStats.length,
        totalSize: fileManagerStats.reduce((sum, file) => sum + (file.size || 0), 0),
        totalSizeMB: (fileManagerStats.reduce((sum, file) => sum + (file.size || 0), 0) / (1024 * 1024)).toFixed(2),
        categories: [...new Set(fileManagerStats.map(f => f.category))]
      };
      
      console.log('Estadísticas finales:', finalStats);
      setStats(finalStats);
      console.log('=== ESTADÍSTICAS CARGADAS ===');
    } catch (error) {
      console.error('Error al cargar estadísticas:', error);
    }
  };

  // Cargar archivos de la categoría seleccionada
  const loadFiles = async () => {
    setIsLoading(true);
    try {
      console.log(`=== CARGANDO ARCHIVOS PARA CATEGORÍA: ${selectedCategory} ===`);
      
      if (selectedCategory === 'characters') {
        // Para personajes, cargar archivos organizados por personaje
        const categoryFiles = await fileManager.getFilesByCategory(selectedCategory);
        console.log(`Archivos de personajes encontrados:`, categoryFiles.length);
        
        // Organizar archivos por personaje
        const filesByCharacter = {};
        categoryFiles.forEach(file => {
          const characterId = file.characterId || 'sin-personaje';
          if (!filesByCharacter[characterId]) {
            filesByCharacter[characterId] = [];
          }
          filesByCharacter[characterId].push(file);
        });
        
        console.log('Archivos organizados por personaje:', filesByCharacter);
        setFiles(categoryFiles); // Mantener la lista original para compatibilidad
        setFilesByCharacter(filesByCharacter); // Nueva estructura organizada
      } else {
        // Para otras categorías, cargar normalmente
        const categoryFiles = await fileManager.getFilesByCategory(selectedCategory);
        console.log(`Archivos encontrados para ${selectedCategory}:`, categoryFiles.length);
        setFiles(categoryFiles);
        setFilesByCharacter({}); // Limpiar estructura de personajes
      }
    } catch (error) {
      console.error('Error al cargar archivos:', error);
      setFiles([]);
      setFilesByCharacter({});
    } finally {
      setIsLoading(false);
    }
  };

  // Filtrar archivos por búsqueda
  const filteredFiles = files.filter(file =>
    file.name.toLowerCase().includes(searchQuery.toLowerCase())
  );

  // Ordenar archivos
  const sortedFiles = [...filteredFiles].sort((a, b) => {
    switch (sortBy) {
      case 'name':
        return a.name.localeCompare(b.name);
      case 'size':
        return (b.size || 0) - (a.size || 0);
      case 'date':
        return new Date(b.uploadedAt || 0) - new Date(a.uploadedAt || 0);
      default:
        return 0;
    }
  });

  // Manejar selección de archivos
  const handleFileSelect = (fileId, isSelected) => {
    const newSelection = new Set(selectedFiles);
    if (isSelected) {
      newSelection.add(fileId);
    } else {
      newSelection.delete(fileId);
    }
    setSelectedFiles(newSelection);
  };

  // Manejar subida de archivos
  const handleFileUpload = (uploadedFiles) => {
    uploadFiles(uploadedFiles);
  };

  const uploadFiles = async (files) => {
    setIsUploading(true);
    setUploadProgress(0);

    const validFiles = files.filter(file => {
      if (!fileManager.validateFileType(file)) {
        showAlert(`El archivo ${file.name} no es un tipo válido`, 'Tipo de Archivo Inválido', '⚠️');
        return false;
      }
      if (!fileManager.validateFileSize(file)) {
        showAlert(`El archivo ${file.name} es demasiado grande (máximo 10MB)`, 'Archivo Demasiado Grande', '⚠️');
        return false;
      }
      return true;
    });

    for (let i = 0; i < validFiles.length; i++) {
      try {
        await fileManager.uploadFile(validFiles[i], selectedCategory);
        setUploadProgress(((i + 1) / validFiles.length) * 100);
      } catch (error) {
        console.error('Error al subir archivo:', error);
        await showAlert(`Error al subir ${validFiles[i].name}`, 'Error de Subida', '❌');
      }
    }

    setIsUploading(false);
    setUploadProgress(0);
    
    console.log('=== ARCHIVOS SUBIDOS, RECARGANDO ===');
    console.log('Archivos válidos subidos:', validFiles.length);
    
    // Pequeño delay para asegurar sincronización
    await new Promise(resolve => setTimeout(resolve, 100));
    
    await loadFiles();
    await loadStats();
    
    console.log('=== RECARGA COMPLETADA ===');
  };

  // Manejar drag & drop
  const handleDrop = (event) => {
    event.preventDefault();
    setDragOver(false);
    const files = Array.from(event.dataTransfer.files);
    uploadFiles(files);
  };

  const handleDragOver = (event) => {
    event.preventDefault();
    setDragOver(true);
  };

  const handleDragLeave = (event) => {
    event.preventDefault();
    setDragOver(false);
  };

  // Eliminar archivo
  const handleDeleteFile = async (fileId) => {
    const confirmed = await showConfirm(
      '¿Estás seguro de que quieres eliminar este archivo?',
      'Eliminar Archivo',
      '🗑️'
    );
    
    if (confirmed) {
      try {
        // Agregar a la lista de archivos siendo eliminados
        setDeletingFiles(prev => new Set(prev).add(fileId));
        
        console.log(`Eliminando archivo ${fileId}...`);
        const success = await fileManager.deleteFile(fileId);
        
        if (success) {
          console.log('Archivo eliminado exitosamente');
          // Recargar archivos y estadísticas
          await loadFiles();
          await loadStats();
          
          // Remover de la selección si estaba seleccionado
          const newSelection = new Set(selectedFiles);
          newSelection.delete(fileId);
          setSelectedFiles(newSelection);
        } else {
          await showAlert('Error al eliminar el archivo', 'Error de Eliminación', '❌');
        }
      } catch (error) {
        console.error('Error al eliminar archivo:', error);
        await showAlert('Error al eliminar el archivo: ' + error.message, 'Error de Eliminación', '❌');
      } finally {
        // Remover de la lista de archivos siendo eliminados
        setDeletingFiles(prev => {
          const newSet = new Set(prev);
          newSet.delete(fileId);
          return newSet;
        });
      }
    }
  };

  // Obtener icono del archivo
  const getFileIcon = (file) => {
    if (file.url && file.url.startsWith('data:image/')) {
      return null; // Mostrar imagen
    }
    
    if (file.category === 'music' || file.category === 'sounds' || file.category === 'voice') {
      return '🎵';
    }
    
    if (file.category === 'videos') {
      return '🎬';
    }
    
    return '📁';
  };

  // Formatear tamaño de archivo
  const formatFileSize = (bytes) => {
    if (!bytes) return '0 B';
    const k = 1024;
    const sizes = ['B', 'KB', 'MB', 'GB'];
    const i = Math.floor(Math.log(bytes) / Math.log(k));
    return parseFloat((bytes / Math.pow(k, i)).toFixed(1)) + ' ' + sizes[i];
  };

  // Limpiar archivos fantasma
  const handleCleanupGhostFiles = async () => {
    const confirmed = await showConfirm(
      '¿Estás seguro de que quieres limpiar archivos fantasma? Esto eliminará archivos que aparecen como eliminados pero siguen en localStorage.',
      'Limpiar Archivos Fantasma',
      '🧹'
    );
    
    if (confirmed) {
      try {
        console.log('Iniciando limpieza de archivos fantasma...');
        const cleanedCount = await fileManager.cleanupGhostFiles();
        
        if (cleanedCount > 0) {
          await showAlert(`Se eliminaron ${cleanedCount} archivos fantasma`, 'Limpieza Exitosa', '✅');
          await loadFiles();
          await loadStats();
        } else {
          await showAlert('No se encontraron archivos fantasma', 'Sin Archivos Fantasma', 'ℹ️');
        }
      } catch (error) {
        console.error('Error al limpiar archivos fantasma:', error);
        await showAlert('Error al limpiar archivos fantasma', 'Error de Limpieza', '❌');
      }
    }
  };

  // Mostrar estado del sistema
  const handleShowSystemStatus = () => {
    const status = fileManager.getSystemStatus();
    const message = `
Estado del Sistema de Archivos:
• Sincronización en progreso: ${status.syncInProgress ? 'Sí' : 'No'}
• Última sincronización: ${status.lastSyncTime || 'Nunca'}
• Archivos en memoria: ${status.totalFiles}
• Archivos marcados como eliminados: ${status.deletedFilesCount}
• IndexedDB disponible: ${status.indexedDBAvailable ? 'Sí' : 'No'}
• Quota localStorage: ${status.localStorageQuota.currentSizeMB}MB (${status.localStorageQuota.available ? 'Disponible' : 'No disponible'})
    `;
    
    console.log('Estado del sistema:', status);
    showAlert(message, 'Estado del Sistema', '📊');
  };

  // Forzar sincronización
  const handleForceSync = async () => {
    try {
      console.log('Forzando sincronización...');
      await fileManager.forceSync();
      await showAlert('Sincronización completada', 'Sincronización Exitosa', '✅');
      await loadFiles();
      await loadStats();
    } catch (error) {
      console.error('Error en sincronización forzada:', error);
      await showAlert('Error en sincronización forzada', 'Error de Sincronización', '❌');
    }
  };

  return (
    <PageContainer>
      {/* BARRA SUPERIOR */}
      <TopBar>
        <div style={{ display: 'flex', alignItems: 'center', gap: '16px' }}>
          <BackButton onClick={onClose}>
            ← Volver al Editor
          </BackButton>
          <PageTitle>📁 Gestión de Archivos</PageTitle>
        </div>
        
        <StatsBar>
          <StatItem>
            <StatValue>{stats.totalFiles}</StatValue>
            <StatLabel>Total Archivos</StatLabel>
          </StatItem>
          <StatItem>
            <StatValue>{stats.totalSizeMB}MB</StatValue>
            <StatLabel>Tamaño Total</StatLabel>
          </StatItem>
          <StatItem>
            <StatValue>{stats.categories?.length || 0}</StatValue>
            <StatLabel>Categorías</StatLabel>
          </StatItem>
          
          {/* Botones de Debug */}
          <DebugButton onClick={handleShowSystemStatus} title="Mostrar estado del sistema">
            🔧 Estado
          </DebugButton>
          <DebugButton onClick={handleForceSync} title="Forzar sincronización manual">
            🔄 Sync
          </DebugButton>
          <DebugButton className="warning" onClick={handleCleanupGhostFiles} title="Limpiar archivos fantasma">
            🧹 Limpiar
          </DebugButton>
        </StatsBar>

        <SearchBar>
          <SearchIcon>🔍</SearchIcon>
          <SearchInput
            type="text"
            placeholder="Buscar archivos..."
            value={searchQuery}
            onChange={(e) => setSearchQuery(e.target.value)}
          />
        </SearchBar>
      </TopBar>

      {/* LAYOUT PRINCIPAL */}
      <MainLayout>
        {/* SIDEBAR DE CATEGORÍAS */}
        <CategorySidebar>
          <SidebarHeader>
            <SidebarTitle>Categorías</SidebarTitle>
          </SidebarHeader>
          
          <CategoryList>
            {categories.map(category => (
              <CategoryItem
                key={category.id}
                active={selectedCategory === category.id}
                onClick={() => setSelectedCategory(category.id)}
              >
                <CategoryIcon active={selectedCategory === category.id}>
                  {category.icon}
                </CategoryIcon>
                <CategoryInfo>
                  <CategoryName>{category.name}</CategoryName>
                  <CategoryStats>
                    {stats.categories?.[category.id] || 0} archivos
                  </CategoryStats>
                </CategoryInfo>
              </CategoryItem>
            ))}
          </CategoryList>
        </CategorySidebar>

        {/* ÁREA PRINCIPAL */}
        <MainContent>
          <ContentHeader>
            <ContentTitle>
              {categories.find(c => c.id === selectedCategory)?.name} 
              ({sortedFiles.length} archivos)
            </ContentTitle>
            
            <ViewControls>
              <ViewToggle
                active={viewMode === 'grid'}
                onClick={() => setViewMode('grid')}
              >
                ⊞ Grid
              </ViewToggle>
              <ViewToggle
                active={viewMode === 'list'}
                onClick={() => setViewMode('list')}
              >
                ☰ Lista
              </ViewToggle>
              <SortSelect
                value={sortBy}
                onChange={(e) => setSortBy(e.target.value)}
              >
                <option value="name">Ordenar por nombre</option>
                <option value="size">Ordenar por tamaño</option>
                <option value="date">Ordenar por fecha</option>
              </SortSelect>
            </ViewControls>
          </ContentHeader>

          {/* GRID DE ARCHIVOS */}
          <FilesGrid viewMode={viewMode}>
            {isLoading ? (
              <EmptyState>
                <EmptyIcon>⏳</EmptyIcon>
                <EmptyText>Cargando archivos...</EmptyText>
              </EmptyState>
            ) : selectedCategory === 'characters' && Object.keys(filesByCharacter).length > 0 ? (
              // Vista especial para personajes organizados por carpeta
              Object.entries(filesByCharacter).map(([characterId, characterFiles]) => (
                <CharacterFolder key={characterId}>
                  <CharacterFolderHeader>
                    <CharacterInfo>
                      <CharacterIcon>👤</CharacterIcon>
                      <CharacterDetails>
                        <CharacterName>
                          {getCharacterName(characterId)}
                        </CharacterName>
                        <CharacterStats>
                          {characterFiles.length} sprite{characterFiles.length !== 1 ? 's' : ''}
                        </CharacterStats>
                      </CharacterDetails>
                    </CharacterInfo>
                    
                    <CharacterFolderActions>
                      <CharacterActionButton
                        danger
                        onClick={() => handleDeleteCharacterFolder(characterId)}
                      >
                        🗑️ Eliminar Todo
                      </CharacterActionButton>
                    </CharacterFolderActions>
                  </CharacterFolderHeader>
                  
                  <CharacterFilesGrid>
                    {characterFiles.map(file => (
                      <CharacterFileCard
                        key={file.id}
                        onClick={() => handleFileSelect(file.id, !selectedFiles.has(file.id))}
                      >
                        <CharacterFilePreview
                          image={file.url && file.url.startsWith('data:image/') ? file.url : null}
                        >
                          {!file.url || !file.url.startsWith('data:image/') && (
                            <CharacterFileIcon>🎭</CharacterFileIcon>
                          )}
                          
                          <CharacterFileActions>
                            <CharacterFileActionButton
                              className="danger"
                              onClick={(e) => {
                                e.stopPropagation();
                                handleDeleteCharacterSprite(file.id);
                              }}
                            >
                              🗑️
                            </CharacterFileActionButton>
                          </CharacterFileActions>
                        </CharacterFilePreview>
                        
                        <CharacterFileInfo>
                          <CharacterFileName>{file.name}</CharacterFileName>
                          <CharacterFileDetails>
                            {formatFileSize(file.size)}
                          </CharacterFileDetails>
                        </CharacterFileInfo>
                      </CharacterFileCard>
                    ))}
                  </CharacterFilesGrid>
                </CharacterFolder>
              ))
            ) : sortedFiles.length === 0 ? (
              <EmptyState>
                <EmptyIcon>
                  {categories.find(c => c.id === selectedCategory)?.icon}
                </EmptyIcon>
                <EmptyText>No hay archivos en esta categoría</EmptyText>
                <EmptySubtext>
                  {categories.find(c => c.id === selectedCategory)?.description}
                </EmptySubtext>
              </EmptyState>
            ) : (
              // Vista normal para otras categorías
              sortedFiles.map(file => (
                <FileCard
                  key={file.id}
                  selected={selectedFiles.has(file.id)}
                  onClick={() => handleFileSelect(file.id, !selectedFiles.has(file.id))}
                >
                  <FilePreview
                    viewMode={viewMode}
                    image={file.url && file.url.startsWith('data:image/') ? file.url : null}
                  >
                    {!file.url || !file.url.startsWith('data:image/') && (
                      <FileIcon viewMode={viewMode}>
                        {getFileIcon(file)}
                      </FileIcon>
                    )}
                    
                    <FileOverlay>
                      <FileActions>
                        <ActionButton primary>
                          📋 Seleccionar
                        </ActionButton>
                        <ActionButton 
                          className="danger"
                          onClick={(e) => {
                            e.stopPropagation();
                            handleDeleteFile(file.id);
                          }}
                          disabled={deletingFiles.has(file.id)}
                        >
                          {deletingFiles.has(file.id) ? '⏳ Eliminando...' : '🗑️ Eliminar'}
                        </ActionButton>
                      </FileActions>
                    </FileOverlay>
                  </FilePreview>
                  
                  <FileInfo>
                    <FileName>{file.name}</FileName>
                    <FileDetails>
                      {formatFileSize(file.size)} • {new Date(file.uploadedAt).toLocaleDateString()}
                    </FileDetails>
                    <FileTags>
                      <FileTag>{file.category}</FileTag>
                      {file.size && file.size > 1024 * 1024 && (
                        <FileTag>Grande</FileTag>
                      )}
                    </FileTags>
                  </FileInfo>
                </FileCard>
              ))
            )}
          </FilesGrid>
        </MainContent>
      </MainLayout>

      {/* PANEL INFERIOR */}
      <BottomPanel>
        <UploadSection>
          <UploadArea
            className={dragOver ? 'dragover' : ''}
            onDrop={handleDrop}
            onDragOver={handleDragOver}
            onDragLeave={handleDragLeave}
            onClick={() => fileInputRef.current?.click()}
          >
            <UploadText>
              Arrastra archivos aquí o haz clic para seleccionar
            </UploadText>
            <UploadSubtext>
              Imágenes: JPG, PNG, GIF, WebP | Audio: OGG, MP3, WAV | Videos: WebM, OGV, AVI, MP4 (máximo 10MB)
            </UploadSubtext>
            {isUploading && (
              <ProgressBar>
                <ProgressFill progress={uploadProgress} />
              </ProgressBar>
            )}
          </UploadArea>
          
          <CategorySelector
            value={selectedCategory}
            onChange={(e) => setSelectedCategory(e.target.value)}
          >
            {categories.map(category => (
              <option key={category.id} value={category.id}>
                {category.icon} {category.name}
              </option>
            ))}
          </CategorySelector>
        </UploadSection>

        <FileInput
          ref={fileInputRef}
          type="file"
          multiple
          accept="image/*,audio/*,video/*"
          onChange={(e) => uploadFiles(Array.from(e.target.files))}
        />
      </BottomPanel>
    </PageContainer>
  );
}

export default FileManagerPage;
