// Componente para seleccionar fondos
const BackgroundSelector = ({ value, onChange, placeholder }) => {
  const [showFileSelector, setShowFileSelector] = useState(false);
  const [availableBackgrounds, setAvailableBackgrounds] = useState([]);
  const [loading, setLoading] = useState(false);
  const fileManager = useMemo(() => new FileManager(), []);

  // Cargar fondos disponibles
  const loadBackgrounds = useCallback(async () => {
    setLoading(true);
    try {
      const backgrounds = await fileManager.getFilesByCategory('backgrounds');
      setAvailableBackgrounds(backgrounds);
    } catch (error) {
      console.error('Error loading backgrounds:', error);
      setAvailableBackgrounds([]);
    } finally {
      setLoading(false);
    }
  }, [fileManager]);

  // Cargar fondos cuando se abre el selector
  useEffect(() => {
    if (showFileSelector) {
      loadBackgrounds();
    }
  }, [showFileSelector, loadBackgrounds]);

  const handleFileSelect = (background) => {
    // Extraer solo el nombre del archivo sin la ruta
    const fileName = background.name.replace(/\.[^/.]+$/, ""); // Remover extensión
    onChange(fileName);
    setShowFileSelector(false);
  };

  const handleInputChange = (e) => {
    onChange(e.target.value);
  };

  return (
    <BackgroundSelectorContainer>
      <BackgroundActions>
        <BackgroundInput
          type="text"
          value={value}
          onChange={handleInputChange}
          placeholder={placeholder}
        />
        <FileSelectorButton onClick={() => setShowFileSelector(!showFileSelector)}>
          <span>📁</span>
          {showFileSelector ? 'Ocultar' : 'Seleccionar'}
        </FileSelectorButton>
      </BackgroundActions>
      
      {showFileSelector && (
        <BackgroundPreview>
          {loading ? (
            <NoBackgroundsMessage>Cargando fondos...</NoBackgroundsMessage>
          ) : availableBackgrounds.length > 0 ? (
            availableBackgrounds.map((background) => (
              <BackgroundOption
                key={background.id}
                selected={value === background.name.replace(/\.[^/.]+$/, "")}
                onClick={() => handleFileSelect(background)}
                title={background.name}
              >
                <BackgroundImage
                  src={background.url || background.data}
                  alt={background.name}
                  onError={(e) => {
                    e.target.style.display = 'none';
                  }}
                />
                <BackgroundName>{background.name}</BackgroundName>
              </BackgroundOption>
            ))
          ) : (
            <NoBackgroundsMessage>
              No hay fondos disponibles. 
              <br />
              Sube imágenes en la categoría "backgrounds" desde el gestor de archivos.
            </NoBackgroundsMessage>
          )}
        </BackgroundPreview>
      )}
    </BackgroundSelectorContainer>
  );
};

