function ScriptEditor() {
  const { state, dispatch } = useVisualNovel();
  const { showConfirm } = useModalContext();
  const [selectedElement, setSelectedElement] = useState(null);
  const [editingElement, setEditingElement] = useState(null);
  const [editFormData, setEditFormData] = useState({});
  const [showScrollHint, setShowScrollHint] = useState(false);
  const [isScrolling, setIsScrolling] = useState(false);
  const [showGuide, setShowGuide] = useState(false);
  const [contextMenuElement, setContextMenuElement] = useState(null);
  const [showZoomControl, setShowZoomControl] = useState(false);
  const [zoomLevel, setZoomLevel] = useState(100);
  const [isGridMode, setIsGridMode] = useState(false);
  const [columns, setColumns] = useState(1);
  const editorRef = useRef(null);
  const formRef = useRef(null);
  const isEditingRef = useRef(false);

  const handleElementClick = useCallback((element) => {
    // No hacer nada si el elemento está siendo editado
    if (editingElement?.id === element.id) {
      return;
    }
    setSelectedElement(element.id === selectedElement?.id ? null : element);
    
    // Mostrar menú contextual para inserción
    setContextMenuElement(element);
    
    // Navegar a este elemento en la vista previa
    handleNavigateToElement(element);
  }, [selectedElement, editingElement]);

  const handleNavigateToElement = useCallback((element) => {
    console.log('ScriptEditor: Iniciando navegación al elemento:', element);
    
    // Obtener el archivo de escena actual
    const currentSceneFile = state.project.sceneFiles.find(
      file => file.id === state.project.currentSceneFile
    );
    
    console.log('ScriptEditor: Archivo de escena actual:', currentSceneFile);
    
    if (!currentSceneFile || !currentSceneFile.elements) {
      console.warn('ScriptEditor: No se pudo navegar al elemento - archivo de escena no encontrado');
      return;
    }
    
    // Encontrar el índice del elemento en el archivo actual
    const elementIndex = currentSceneFile.elements.findIndex(el => el.id === element.id);
    
    console.log('ScriptEditor: Índice encontrado:', elementIndex, 'de', currentSceneFile.elements.length, 'elementos');
    
    if (elementIndex === -1) {
      console.warn('ScriptEditor: No se pudo navegar al elemento - elemento no encontrado en el archivo actual');
      return;
    }
    
    // Disparar evento personalizado para que PreviewPanel lo capture
    const navigateEvent = new CustomEvent('navigateToElement', {
      detail: {
        elementIndex: elementIndex,
        elementId: element.id,
        sceneFileId: currentSceneFile.id
      }
    });
    
    console.log('ScriptEditor: Disparando evento navigateToElement:', navigateEvent.detail);
    
    // Disparar el evento tanto en window como en document para mayor compatibilidad
    window.dispatchEvent(navigateEvent);
    document.dispatchEvent(navigateEvent);
    
    console.log('ScriptEditor: Evento disparado exitosamente');
    
    // También intentar activar la vista previa si no está activa
    if (!state.previewMode) {
      console.log('ScriptEditor: Activando vista previa automáticamente');
      dispatch({ type: 'TOGGLE_PREVIEW' });
    }
  }, [state.project.sceneFiles, state.project.currentSceneFile]);

  // Función para abrir el sidebar y preparar inserción después de otro elemento específico
  const handleInsertElementAfter = useCallback((elementType, afterElementId) => {
    // Verificar si la función global está disponible
    if (window.addElementToSceneWithInsertion) {
      // Encontrar el índice del elemento después del cual insertar
      const currentSceneFile = state.project.sceneFiles.find(
        file => file.id === state.project.currentSceneFile
      );
      
      if (!currentSceneFile || !currentSceneFile.elements) {
        console.warn('ScriptEditor: No se pudo insertar elemento - archivo de escena no encontrado');
        return;
      }
      
      const afterElementIndex = currentSceneFile.elements.findIndex(el => el.id === afterElementId);
      
      if (afterElementIndex === -1) {
        console.warn('ScriptEditor: No se pudo insertar elemento - elemento de referencia no encontrado');
        return;
      }
      
      // Llamar a la función global que abrirá el sidebar y preparará la inserción
      window.addElementToSceneWithInsertion(elementType, state.project.currentSceneFile, afterElementIndex + 1);
      
      // Cerrar menú contextual
      setContextMenuElement(null);
    } else {
      console.warn('ScriptEditor: La función addElementToSceneWithInsertion no está disponible');
    }
  }, [state.project.sceneFiles, state.project.currentSceneFile]);

  const handleElementDoubleClick = useCallback((element) => {
    console.log('✏️ Editando elemento:', element);
    setEditingElement(element);
    setEditFormData({ ...element });
    isEditingRef.current = true;
  }, []);

  // Enfocar automáticamente el primer campo cuando se abre el formulario
  useEffect(() => {
    if (editingElement && formRef.current) {
      const firstInput = formRef.current.querySelector('input, textarea, select');
      if (firstInput) {
        setTimeout(() => {
          firstInput.focus();
        }, 100);
      }
    }
  }, [editingElement]);

  const handleCancelEdit = useCallback(() => {
    setEditingElement(null);
    setEditFormData({});
    isEditingRef.current = false;
  }, []);

  const handleSaveEdit = useCallback(() => {
    if (!editingElement) return;

    const currentSceneFile = state.project.sceneFiles.find(
      file => file.id === state.project.currentSceneFile
    );
    
    if (currentSceneFile) {
      const updatedElements = currentSceneFile.elements.map(el => 
        el.id === editingElement.id ? { ...el, ...editFormData } : el
      );
      
      dispatch({
        type: 'UPDATE_SCENE_FILE',
        payload: {
          ...currentSceneFile,
          elements: updatedElements
        }
      });
    }
    
    setEditingElement(null);
    setEditFormData({});
    isEditingRef.current = false;
  }, [editingElement, editFormData, state.project.sceneFiles, state.project.currentSceneFile, dispatch]);

  const handleFormChange = useCallback((field, value) => {
    setEditFormData(prev => {
      const newData = {
        ...prev,
        [field]: value
      };
      return newData;
    });
  }, []);

  const handleChoiceChange = useCallback((index, value) => {
    const newChoices = [...(editFormData.choices || [])];
    newChoices[index] = value;
    handleFormChange('choices', newChoices);
  }, [editFormData.choices, handleFormChange]);

  const handleAddChoice = useCallback(() => {
    const newChoices = [...(editFormData.choices || []), ''];
    handleFormChange('choices', newChoices);
  }, [editFormData.choices, handleFormChange]);

  const handleRemoveChoice = useCallback((index) => {
    const newChoices = [...(editFormData.choices || [])];
    newChoices.splice(index, 1);
    handleFormChange('choices', newChoices);
  }, [editFormData.choices, handleFormChange]);

  // Funciones para el control de zoom
  const handleZoomChange = useCallback((newZoomLevel) => {
    setZoomLevel(newZoomLevel);
    
    // Activar grid mode solo cuando el zoom es menor a 100%
    const shouldUseGrid = newZoomLevel < 100;
    setIsGridMode(shouldUseGrid);
    
    if (shouldUseGrid) {
      // Calcular número de columnas basado en el zoom
      // A menor zoom, más columnas
      let newColumns = 1;
      if (newZoomLevel <= 25) newColumns = 6;
      else if (newZoomLevel <= 50) newColumns = 4;
      else if (newZoomLevel <= 75) newColumns = 3;
      else if (newZoomLevel <= 90) newColumns = 2;
      
      setColumns(newColumns);
    } else {
      setColumns(1);
    }
  }, []);

  const handleZoomPreset = useCallback((presetLevel) => {
    setZoomLevel(presetLevel);
    
    // Activar grid mode solo cuando el zoom es menor a 100%
    const shouldUseGrid = presetLevel < 100;
    setIsGridMode(shouldUseGrid);
    
    if (shouldUseGrid) {
      // Calcular número de columnas basado en el zoom
      let newColumns = 1;
      if (presetLevel <= 25) newColumns = 6;
      else if (presetLevel <= 50) newColumns = 4;
      else if (presetLevel <= 75) newColumns = 3;
      else if (presetLevel <= 90) newColumns = 2;
      
      setColumns(newColumns);
    } else {
      setColumns(1);
    }
  }, []);

  const toggleZoomControl = useCallback(() => {
    setShowZoomControl(prev => !prev);
  }, []);

  const resetZoom = useCallback(() => {
    setZoomLevel(100);
    setIsGridMode(false);
    setColumns(1);
  }, []);

  const handleDeleteElement = useCallback(async (elementId, e) => {
    e.stopPropagation();
    const confirmed = await showConfirm(
      '¿Estás seguro de que quieres eliminar este elemento?',
      'Confirmar Eliminación',
      '🗑️'
    );
    
    if (confirmed) {
      dispatch({
        type: 'DELETE_SCENE_FILE_ELEMENT',
        payload: {
          sceneFileId: state.project.currentSceneFile,
          elementId: elementId
        }
      });
      if (selectedElement?.id === elementId) {
        setSelectedElement(null);
      }
    }
  }, [dispatch, state.project.currentSceneFile, selectedElement]);

  const handleMoveUp = useCallback((elementId, e) => {
    e.stopPropagation();
    const currentSceneFile = state.project.sceneFiles.find(
      file => file.id === state.project.currentSceneFile
    );
    if (!currentSceneFile) return;
    
    const currentIndex = currentSceneFile.elements.findIndex(el => el.id === elementId);
    if (currentIndex > 0) {
      const newElements = [...currentSceneFile.elements];
      [newElements[currentIndex], newElements[currentIndex - 1]] = [newElements[currentIndex - 1], newElements[currentIndex]];
      
      dispatch({
        type: 'UPDATE_SCENE_FILE',
        payload: {
          ...currentSceneFile,
          elements: newElements
        }
      });
    }
  }, [state.project.sceneFiles, state.project.currentSceneFile, dispatch]);

  const handleMoveDown = useCallback((elementId, e) => {
    e.stopPropagation();
    const currentSceneFile = state.project.sceneFiles.find(
      file => file.id === state.project.currentSceneFile
    );
    if (!currentSceneFile) return;
    
    const currentIndex = currentSceneFile.elements.findIndex(el => el.id === elementId);
    if (currentIndex < currentSceneFile.elements.length - 1) {
      const newElements = [...currentSceneFile.elements];
      [newElements[currentIndex], newElements[currentIndex + 1]] = [newElements[currentIndex + 1], newElements[currentIndex]];
      
      dispatch({
        type: 'UPDATE_SCENE_FILE',
        payload: {
          ...currentSceneFile,
          elements: newElements
        }
      });
    }
  }, [state.project.sceneFiles, state.project.currentSceneFile, dispatch]);

  const renderElementContent = (element, isGridMode = false) => {
    switch (element.type) {
      case 'dialogue':
        const character = state.project.characters.find(char => char.id === element.character);
        const positionIcon = {
          'left': '⬅️',
          'center': '⬆️',
          'right': '➡️'
        };
        return (
          <>
            <ElementCharacter isGridMode={isGridMode}>
              {getElementTypeIcon(element.type)} {character?.isProtagonist ? 'Protagonista' : (character?.name || element.character)}
              {element.showSprite && (
                <span style={{ color: 'var(--color-primary)', fontSize: isGridMode ? '8px' : '12px', marginLeft: '8px' }}>
                  ({element.showSprite} {positionIcon[element.spritePosition] || '⬆️'})
                </span>
              )}
            </ElementCharacter>
            <ElementText isGridMode={isGridMode}>"{element.text.replace(/\[mc_name\]/g, 'Protagonista')}"</ElementText>
          </>
        );
      case 'narration':
        return (
          <>
            <ElementCharacter isGridMode={isGridMode}>
              {getElementTypeIcon(element.type)} Narración
              {(element.fontSize || element.textColor) && (
                <span style={{ 
                  color: 'var(--color-primary)', 
                  fontSize: isGridMode ? '9px' : '11px', 
                  marginLeft: '8px',
                  fontWeight: 'normal'
                }}>
                  ({element.fontSize || 'medium'} • {element.textColor || '#ffffff'})
                </span>
              )}
            </ElementCharacter>
            <ElementText isGridMode={isGridMode}>{element.text.replace(/\[mc_name\]/g, 'Protagonista')}</ElementText>
          </>
        );
      case 'scene':
        const backgroundTransition = element.backgroundTransition ? ` (transición: ${element.backgroundTransition})` : '';
        return (
          <>
            <ElementCharacter isGridMode={isGridMode}>
              {getElementTypeIcon(element.type)} Cambio de Escena
            </ElementCharacter>
            <ElementText isGridMode={isGridMode}>Fondo: {element.background || 'Sin fondo'}{backgroundTransition}</ElementText>
          </>
        );
      case 'transition':
        const duration = element.transitionDuration ? ` (${element.transitionDuration}s)` : '';
        const transitionConfig = getAllTransitions().find(t => t.id === element.transitionType);
        const transitionName = transitionConfig ? transitionConfig.name : element.transitionType;
        return (
          <>
            <ElementCharacter isGridMode={isGridMode}>
              {getElementTypeIcon(element.type)} Transición
            </ElementCharacter>
            <ElementText isGridMode={isGridMode}>{transitionName}{duration}</ElementText>
          </>
        );
      case 'choice':
        return (
          <>
            <ElementCharacter isGridMode={isGridMode}>
              {getElementTypeIcon(element.type)} Elección
            </ElementCharacter>
            <ElementText isGridMode={isGridMode}>{element.choices?.length || 0} opciones disponibles</ElementText>
          </>
        );
      case 'jump':
        return (
          <>
            <ElementCharacter isGridMode={isGridMode}>
              {getElementTypeIcon(element.type)} Salto
            </ElementCharacter>
            <ElementText isGridMode={isGridMode}>Destino: {element.jump}</ElementText>
          </>
        );
      case 'label':
        return (
          <>
            <ElementCharacter isGridMode={isGridMode}>
              {getElementTypeIcon(element.type)} Etiqueta
            </ElementCharacter>
            <ElementText isGridMode={isGridMode}>Punto: {element.label}</ElementText>
          </>
        );
      case 'show_sprite':
        const showCharacter = state.project.characters.find(char => char.id === element.character);
        return (
          <>
            <ElementCharacter isGridMode={isGridMode}>
              {getElementTypeIcon(element.type)} Mostrar Sprite
            </ElementCharacter>
            <ElementText isGridMode={isGridMode}>
              {showCharacter?.name || element.character} - {element.spriteName || 'Sprite por defecto'}
              <div style={{ 
                fontSize: isGridMode ? '7px' : '10px', 
                color: 'var(--color-textSecondary)', 
                marginTop: '4px',
                fontStyle: 'italic'
              }}>
                Posición: {element.spritePosition || 'center'}
              </div>
            </ElementText>
          </>
        );

      case 'hide_sprite':
        const hideCharacter = state.project.characters.find(char => char.id === element.character);
        return (
          <>
            <ElementCharacter isGridMode={isGridMode}>
              {getElementTypeIcon(element.type)} Ocultar Sprite
            </ElementCharacter>
            <ElementText isGridMode={isGridMode}>
              {hideCharacter?.name || element.character}
              <div style={{ 
                fontSize: isGridMode ? '7px' : '10px', 
                color: 'var(--color-textSecondary)', 
                marginTop: '4px',
                fontStyle: 'italic'
              }}>
                El sprite se ocultará de la escena
              </div>
            </ElementText>
          </>
        );

      case 'protagonist_setup':
        return (
          <>
            <ElementCharacter isGridMode={isGridMode}>
              {getElementTypeIcon(element.type)} Setup del Protagonista
            </ElementCharacter>
            <ElementText isGridMode={isGridMode}>
              Configuración del nombre del protagonista
              <div style={{ 
                fontSize: isGridMode ? '7px' : '10px', 
                color: 'var(--color-textSecondary)', 
                marginTop: '4px',
                fontStyle: 'italic'
              }}>
                El jugador elegirá su nombre en este punto
              </div>
            </ElementText>
          </>
        );
      default:
        return (
          <>
            <ElementCharacter isGridMode={isGridMode}>
              {getElementTypeIcon(element.type)} {getElementTypeLabel(element.type)}
            </ElementCharacter>
            <ElementText isGridMode={isGridMode}>{element.text || 'Sin contenido'}</ElementText>
          </>
        );
    }
  };

  // Función para manejar eventos del formulario
  const handleFormEvent = useCallback((e) => {
    e.stopPropagation();
  }, []);

  // Función para manejar cambios en inputs sin perder el foco
  const handleInputChange = useCallback((field, e) => {
    e.stopPropagation();
    handleFormChange(field, e.target.value);
  }, [handleFormChange]);

  // Función para renderizar el formulario de edición específico para cada tipo
  const renderEditForm = useCallback((element) => {
    switch (element.type) {
      case 'dialogue':
        return (
          <EditForm onClick={handleFormEvent} onMouseDown={handleFormEvent}>
            <FormGroup>
              <FormLabel>Personaje:</FormLabel>
              <FormSelect
                value={editFormData.character || ''}
                onChange={(e) => handleInputChange('character', e)}
              >
                <option value="">Seleccionar personaje</option>
                {state.project.characters.map(char => (
                  <option key={char.id} value={char.id}>{char.name}</option>
                ))}
              </FormSelect>
            </FormGroup>
            
            <FormGroup>
              <FormLabel>Texto del diálogo:</FormLabel>
              <FormTextarea
                value={editFormData.text || ''}
                onChange={(e) => handleInputChange('text', e)}
                placeholder="Escribe el diálogo aquí..."
              />
            </FormGroup>
            
            <FormGroup>
              <FormLabel>Mostrar sprite:</FormLabel>
              <FormInput
                type="text"
                value={editFormData.showSprite || ''}
                onChange={(e) => handleInputChange('showSprite', e)}
                placeholder="Nombre del sprite (ej: happy, sad, angry)"
              />
            </FormGroup>
            
            <FormGroup>
              <FormLabel>Posición del sprite:</FormLabel>
              <FormSelect
                value={editFormData.spritePosition || 'center'}
                onChange={(e) => handleInputChange('spritePosition', e)}
              >
                <option value="left">Izquierda</option>
                <option value="center">Centro</option>
                <option value="right">Derecha</option>
              </FormSelect>
            </FormGroup>
            
            <FormActions>
              <FormButton onClick={handleCancelEdit}>
                Cancelar
              </FormButton>
              <FormButton primary onClick={handleSaveEdit}>
                Guardar
              </FormButton>
            </FormActions>
          </EditForm>
        );

      case 'narration':
        return (
          <EditForm onClick={handleFormEvent} onMouseDown={handleFormEvent}>
            <FormGroup>
              <FormLabel>Texto de narración:</FormLabel>
              <FormTextarea
                value={editFormData.text || ''}
                onChange={(e) => handleFormChange('text', e.target.value)}
                placeholder="Escribe la narración aquí..."
              />
            </FormGroup>
            
            <FormGroup>
              <FormLabel>Tamaño del texto:</FormLabel>
              <FormSelect
                value={editFormData.fontSize || 'medium'}
                onChange={(e) => handleFormChange('fontSize', e.target.value)}
              >
                <option value="small">Pequeño (12px)</option>
                <option value="medium">Mediano (16px)</option>
                <option value="large">Grande (20px)</option>
                <option value="xlarge">Muy Grande (24px)</option>
              </FormSelect>
            </FormGroup>
            
            <FormGroup>
              <FormLabel>Color del texto:</FormLabel>
              <NarrationColorContainer>
                <NarrationColorInput
                  type="color"
                  value={editFormData.textColor || '#ffffff'}
                  onChange={(e) => handleFormChange('textColor', e.target.value)}
                />
                <NarrationColorPreview
                  style={{ backgroundColor: editFormData.textColor || '#ffffff' }}
                />
                <NarrationColorLabel>{editFormData.textColor || '#ffffff'}</NarrationColorLabel>
              </NarrationColorContainer>
            </FormGroup>
            
            <FormActions>
              <FormButton onClick={handleCancelEdit}>
                Cancelar
              </FormButton>
              <FormButton primary onClick={handleSaveEdit}>
                Guardar
              </FormButton>
            </FormActions>
          </EditForm>
        );

      case 'scene':
        return (
          <EditForm onClick={handleFormEvent} onMouseDown={handleFormEvent}>
            <FormGroup>
              <FormLabel>Fondo de escena:</FormLabel>
              <BackgroundSelector
                value={editFormData.background || ''}
                onChange={(value) => handleFormChange('background', value)}
                placeholder="Nombre del fondo (ej: bedroom, street, forest)"
              />
            </FormGroup>
            
            <FormGroup>
              <FormLabel>Transición del fondo (opcional):</FormLabel>
              <FormSelect
                value={editFormData.backgroundTransition || ''}
                onChange={(e) => handleFormChange('backgroundTransition', e.target.value)}
              >
                <option value="">Sin transición</option>
                {Object.entries(getTransitionsGroupedByElementType('backgrounds')).map(([categoryId, category]) => (
                  <optgroup key={categoryId} label={category.name}>
                    {category.transitions.map(transition => (
                      <option key={transition.id} value={transition.id}>
                        {transition.icon} {transition.name} ({transition.duration}s)
                      </option>
                    ))}
                  </optgroup>
                ))}
              </FormSelect>
              {editFormData.backgroundTransition && (
                <FormInput
                  type="number"
                  step="0.1"
                  min="0.1"
                  max="10"
                  value={editFormData.backgroundTransitionDuration || ''}
                  onChange={(e) => handleFormChange('backgroundTransitionDuration', e.target.value)}
                  placeholder="Duración personalizada (opcional)"
                  style={{ marginTop: '8px' }}
                />
              )}
            </FormGroup>
            
            <FormActions>
              <FormButton onClick={handleCancelEdit}>
                Cancelar
              </FormButton>
              <FormButton primary onClick={handleSaveEdit}>
                Guardar
              </FormButton>
            </FormActions>
          </EditForm>
        );

      case 'transition':
        return (
          <EditForm onClick={handleFormEvent} onMouseDown={handleFormEvent}>
            <FormGroup>
              <FormLabel>Tipo de transición:</FormLabel>
              <FormSelect
                value={editFormData.transitionType || 'dissolve'}
                onChange={(e) => handleFormChange('transitionType', e.target.value)}
              >
                {Object.entries(getTransitionsGroupedByCategory()).map(([categoryId, category]) => (
                  <optgroup key={categoryId} label={category.name}>
                    {category.transitions.map(transition => (
                      <option key={transition.id} value={transition.id}>
                        {transition.icon} {transition.name} ({transition.duration}s)
                      </option>
                    ))}
                  </optgroup>
                ))}
              </FormSelect>
            </FormGroup>
            
            <FormGroup>
              <FormLabel>Duración personalizada (opcional):</FormLabel>
              <FormInput
                type="number"
                step="0.1"
                min="0.1"
                max="10"
                value={editFormData.transitionDuration || ''}
                onChange={(e) => handleFormChange('transitionDuration', e.target.value)}
                placeholder="Dejar vacío para usar duración por defecto"
              />
            </FormGroup>
            
            <FormActions>
              <FormButton onClick={handleCancelEdit}>
                Cancelar
              </FormButton>
              <FormButton primary onClick={handleSaveEdit}>
                Guardar
              </FormButton>
            </FormActions>
          </EditForm>
        );

      case 'choice':
  return (
          <EditForm onClick={handleFormEvent} onMouseDown={handleFormEvent}>
            <FormGroup>
              <FormLabel>Pregunta/Elección:</FormLabel>
              <FormTextarea
                value={editFormData.question || ''}
                onChange={(e) => handleFormChange('question', e.target.value)}
                placeholder="Escribe la pregunta o situación aquí..."
              />
            </FormGroup>
            
            <FormGroup>
              <FormLabel>Opciones:</FormLabel>
              {(editFormData.choices || []).map((choice, index) => (
                <ChoiceItem key={`choice-${index}-${choice.text?.slice(0, 10) || 'empty'}`}>
                  <ChoiceInput
                    value={choice}
                    onChange={(e) => handleChoiceChange(index, e.target.value)}
                    placeholder={`Opción ${index + 1}`}
                  />
                  <ChoiceActions>
                    <ChoiceButton
                      danger
                      onClick={() => handleRemoveChoice(index)}
                      title="Eliminar opción"
                    >
                      🗑️
                    </ChoiceButton>
                  </ChoiceActions>
                </ChoiceItem>
              ))}
              <AddChoiceButton onClick={handleAddChoice}>
                + Agregar opción
              </AddChoiceButton>
            </FormGroup>
            
            <FormActions>
              <FormButton onClick={handleCancelEdit}>
                Cancelar
              </FormButton>
              <FormButton primary onClick={handleSaveEdit}>
                Guardar
              </FormButton>
            </FormActions>
          </EditForm>
        );

      case 'jump':
        return (
          <EditForm onClick={handleFormEvent} onMouseDown={handleFormEvent}>
            <FormGroup>
              <FormLabel>Etiqueta de destino:</FormLabel>
              <FormInput
                type="text"
                value={editFormData.jump || ''}
                onChange={(e) => handleFormChange('jump', e.target.value)}
                placeholder="Nombre de la etiqueta de destino"
              />
            </FormGroup>
            
            <FormActions>
              <FormButton onClick={handleCancelEdit}>
                Cancelar
              </FormButton>
              <FormButton primary onClick={handleSaveEdit}>
                Guardar
              </FormButton>
            </FormActions>
          </EditForm>
        );

      case 'label':
        return (
          <EditForm onClick={handleFormEvent} onMouseDown={handleFormEvent}>
            <FormGroup>
              <FormLabel>Nombre de la etiqueta:</FormLabel>
              <FormInput
                type="text"
                value={editFormData.label || ''}
                onChange={(e) => handleFormChange('label', e.target.value)}
                placeholder="Nombre único para esta etiqueta"
              />
            </FormGroup>
            
            <FormActions>
              <FormButton onClick={handleCancelEdit}>
                Cancelar
              </FormButton>
              <FormButton primary onClick={handleSaveEdit}>
                Guardar
              </FormButton>
            </FormActions>
          </EditForm>
        );

      default:
        return (
          <EditForm>
            <FormGroup>
              <FormLabel>Contenido:</FormLabel>
              <FormTextarea
                value={editFormData.text || ''}
                onChange={(e) => handleFormChange('text', e.target.value)}
                placeholder="Escribe el contenido aquí..."
              />
            </FormGroup>
            
            <FormActions>
              <FormButton onClick={handleCancelEdit}>
                Cancelar
              </FormButton>
              <FormButton primary onClick={handleSaveEdit}>
                Guardar
              </FormButton>
            </FormActions>
          </EditForm>
        );
    }
  }, [editFormData, handleFormChange, handleFormEvent, handleInputChange, handleCancelEdit, handleSaveEdit, state.project.characters]);

  // Efecto para mostrar hint de scroll cuando se carga el componente
  useEffect(() => {
  const currentSceneFile = state.project.sceneFiles.find(
    file => file.id === state.project.currentSceneFile
  );
  const currentElements = currentSceneFile ? currentSceneFile.elements : [];
  
    if (currentElements.length > 0) {
      const timer = setTimeout(() => {
        setShowScrollHint(true);
      }, SCRIPT_EDITOR_CONFIG.hints.showDelay);
      
      const hideTimer = setTimeout(() => {
        setShowScrollHint(false);
      }, SCRIPT_EDITOR_CONFIG.hints.hideDelay);
      
      return () => {
        clearTimeout(timer);
        clearTimeout(hideTimer);
      };
    }
  }, [state.project.currentSceneFile]); // Removido state.project.sceneFiles para evitar re-renders infinitos

  // Efecto para manejar el scroll del editor
  useEffect(() => {
    const editorElement = editorRef.current;
    if (!editorElement) return;

    let scrollTimeout; // Variable local en lugar de global

    const clearScrollTimeout = () => {
      if (scrollTimeout) {
        clearTimeout(scrollTimeout);
      }
    };

    const setScrollTimeout = () => {
      clearScrollTimeout();
      scrollTimeout = setTimeout(() => {
        setIsScrolling(false);
      }, SCRIPT_EDITOR_CONFIG.scroll.timeout);
    };

    const handleWheel = (e) => {
      e.preventDefault();
      
      const scrollAmount = e.deltaY * SCRIPT_EDITOR_CONFIG.scroll.sensitivity;
      editorElement.scrollBy({
        top: scrollAmount,
        behavior: 'smooth'
      });
      
      setIsScrolling(true);
      setScrollTimeout();
    };

    const handleScroll = (e) => {
      setIsScrolling(true);
      setScrollTimeout();
    };

    const handleKeyDown = (e) => {
      if (e.key === 'ArrowDown' || e.key === 'ArrowUp') {
        e.preventDefault();
        
        const scrollAmount = e.key === 'ArrowDown' ? SCRIPT_EDITOR_CONFIG.scroll.arrowScrollAmount : -SCRIPT_EDITOR_CONFIG.scroll.arrowScrollAmount;
        editorElement.scrollBy({
          top: scrollAmount,
          behavior: 'smooth'
        });
        
        setIsScrolling(true);
        setScrollTimeout();
      }
      
      if (e.key === 'PageDown' || e.key === 'PageUp') {
        e.preventDefault();
        
        const scrollAmount = e.key === 'PageDown' ? SCRIPT_EDITOR_CONFIG.scroll.pageScrollAmount : -SCRIPT_EDITOR_CONFIG.scroll.pageScrollAmount;
        editorElement.scrollBy({
          top: scrollAmount,
          behavior: 'smooth'
        });
        
        setIsScrolling(true);
        setScrollTimeout();
      }
      
      if (e.key === 'Home') {
        e.preventDefault();
        editorElement.scrollTo({
          top: 0,
          behavior: 'smooth'
        });
        setIsScrolling(true);
        setScrollTimeout();
      }
      
      if (e.key === 'End') {
        e.preventDefault();
        editorElement.scrollTo({
          top: editorElement.scrollHeight,
          behavior: 'smooth'
        });
        setIsScrolling(true);
        setScrollTimeout();
      }
      
      // Solo manejar Espacio si NO estamos editando un input
      if (e.key === ' ' && !e.target.matches('input, textarea, [contenteditable="true"]')) {
        e.preventDefault();
        editorElement.scrollBy({
          top: SCRIPT_EDITOR_CONFIG.scroll.spaceScrollAmount,
          behavior: 'smooth'
        });
        setIsScrolling(true);
        setScrollTimeout();
      }
      
      if (e.key === ' ' && e.shiftKey && !e.target.matches('input, textarea, [contenteditable="true"]')) {
        e.preventDefault();
        editorElement.scrollBy({
          top: -SCRIPT_EDITOR_CONFIG.scroll.spaceScrollAmount,
          behavior: 'smooth'
        });
        setIsScrolling(true);
        setScrollTimeout();
      }
    };

    editorElement.addEventListener('wheel', handleWheel, { passive: false });
    editorElement.addEventListener('scroll', handleScroll);
    editorElement.addEventListener('keydown', handleKeyDown);
    
    editorElement.setAttribute('tabindex', '0');
    
    return () => {
      editorElement.removeEventListener('wheel', handleWheel);
      editorElement.removeEventListener('scroll', handleScroll);
      editorElement.removeEventListener('keydown', handleKeyDown);
      clearScrollTimeout();
    };
  }, []);

  // Función para renderizar la guía del editor de script
  const renderScriptEditorGuide = () => (
    <GuideSection>
      <GuideTitle>
        📝 Guía del Editor de Script
      </GuideTitle>
      <GuideText>
        El Editor de Script te permite visualizar y gestionar todos los elementos de tu historia:
      </GuideText>
      <GuideList>
        <GuideListItem>
          <strong>Visualización:</strong> Ve todos los elementos en orden secuencial
        </GuideListItem>
        <GuideListItem>
          <strong>Selección:</strong> Haz clic en cualquier elemento para seleccionarlo
        </GuideListItem>
        <GuideListItem>
          <strong>Edición Inline:</strong> Haz doble clic en cualquier elemento para editarlo directamente
        </GuideListItem>
        <GuideListItem>
          <strong>Reordenamiento:</strong> Usa los botones ↑/↓ para mover elementos
        </GuideListItem>
        <GuideListItem>
          <strong>Eliminación:</strong> Usa el botón 🗑️ para eliminar elementos
        </GuideListItem>
        <GuideListItem>
          <strong>Tipos de Elementos:</strong> Cada elemento muestra su tipo con color distintivo
        </GuideListItem>
      </GuideList>
      <GuideText>
        <strong>💡 Consejos:</strong> Los elementos se sincronizan automáticamente con la Vista de Línea de Tiempo y el Mapa de Decisiones. La edición inline te permite modificar elementos sin abrir el menú lateral.
      </GuideText>
    </GuideSection>
  );

  // Usar funciones centralizadas de configuración

  // Obtener el archivo de escena actual y elementos memoizados
  const currentSceneFile = useMemo(() => 
    state.project.sceneFiles.find(file => file.id === state.project.currentSceneFile),
    [state.project.sceneFiles, state.project.currentSceneFile]
  );
  
  const currentElements = useMemo(() => 
    currentSceneFile ? currentSceneFile.elements : [],
    [currentSceneFile]
  );

  // Agrupar elementos por escenas para el grid mode
  const elementsGroupedByScenes = useMemo(() => {
    if (!isGridMode || !currentElements.length) {
      return currentElements;
    }

    const groups = [];
    let currentScene = null;
    let currentGroup = [];

    currentElements.forEach((element, index) => {
      // Si es un elemento de escena, iniciar nueva agrupación
      if (element.type === 'scene') {
        // Guardar el grupo anterior si existe
        if (currentGroup.length > 0) {
          groups.push({
            type: 'scene_group',
            scene: currentScene,
            elements: currentGroup,
            id: `scene_${currentScene || 'default'}_${groups.length}`
          });
        }
        
        // Iniciar nuevo grupo con el elemento de escena
        currentScene = element.background || `Escena ${groups.length + 1}`;
        currentGroup = [element];
      } else {
        // Agregar elemento al grupo actual
        currentGroup.push(element);
      }
    });

    // Agregar el último grupo
    if (currentGroup.length > 0) {
      groups.push({
        type: 'scene_group',
        scene: currentScene,
        elements: currentGroup,
        id: `scene_${currentScene || 'default'}_${groups.length}`
      });
    }

    return groups;
  }, [currentElements, isGridMode]);

  // Componente para renderizar grupos de escenas
  const SceneGroupComponent = useMemo(() => React.memo(({ 
    group, 
    onElementClick, 
    onElementDoubleClick, 
    onMoveUp, 
    onMoveDown, 
    onDelete,
    onRenderContent,
    onRenderEditForm,
    selectedElement,
    editingElement,
    zoomLevel
  }) => (
    <SceneGroupContainer>
      <SceneGroupHeader>
        🎭 {group.scene || 'Escena Principal'}
      </SceneGroupHeader>
      <SceneGroupElements>
        {group.elements.map((element, index) => (
           <ScriptElementItem
            key={element.id}
             element={element}
             index={index}
             isSelected={selectedElement?.id === element.id}
             isEditing={editingElement?.id === element.id}
             zoomLevel={zoomLevel}
             isGridMode={true}
             onElementClick={onElementClick}
             onElementDoubleClick={onElementDoubleClick}
             onMoveUp={onMoveUp}
             onMoveDown={onMoveDown}
             onDelete={onDelete}
             onRenderContent={onRenderContent}
             onRenderEditForm={onRenderEditForm}
           />
        ))}
      </SceneGroupElements>
    </SceneGroupContainer>
  )), []);

  // Componente memoizado para cada elemento individual
  const ScriptElementItem = useMemo(() => React.memo(({ 
    element, 
    index, 
    isSelected, 
    isEditing, 
    zoomLevel,
    isGridMode,
    onElementClick, 
    onElementDoubleClick, 
    onMoveUp, 
    onMoveDown, 
    onDelete,
    onRenderContent,
    onRenderEditForm
  }) => (
          <ScriptElement
      selected={isSelected}
      editing={isEditing}
      isGridMode={isGridMode}
      data-editing={isEditing}
      onClick={() => onElementClick(element)}
          >
            <ElementHeader>
        <ElementType color={getElementTypeColor(element.type)}>
          {getElementTypeIcon(element.type)} {getElementTypeLabel(element.type)}
        </ElementType>
              <ElementActions>
                <ActionButton 
            onClick={(e) => {
              e.stopPropagation();
              onMoveUp(element.id, e);
            }}
                  disabled={index === 0}
                  title="Mover hacia arriba"
                >
                  ↑
                </ActionButton>
                <ActionButton 
            onClick={(e) => {
              e.stopPropagation();
              onMoveDown(element.id, e);
            }}
            disabled={index === currentElements.length - 1}
                  title="Mover hacia abajo"
                >
                  ↓
                </ActionButton>
                <ActionButton 
            onClick={(e) => {
              e.stopPropagation();
              onElementDoubleClick(element);
            }}
                  title="Editar elemento"
                >
                  ✏️
                </ActionButton>
                <ActionButton 
            danger
            onClick={(e) => {
              e.stopPropagation();
              onDelete(element.id, e);
            }}
                  title="Eliminar elemento"
                >
                  🗑️
                </ActionButton>
              </ElementActions>
            </ElementHeader>
            <ElementContent>
              {onRenderContent(element, isGridMode)}
            </ElementContent>
      
      {/* Mostrar formulario de edición si este elemento está siendo editado */}
      {isEditing && (
        <div ref={formRef} onClick={(e) => e.stopPropagation()}>
          {onRenderEditForm(element)}
        </div>
      )}
          </ScriptElement>
  )), []);
  
  if (currentElements.length === 0) {
    return (
      <EditorContainer ref={editorRef}>
        <EmptyState>
          <EmptyIcon>📝</EmptyIcon>
          <EmptyText>No hay elementos en {currentSceneFile?.displayName || 'el script'}</EmptyText>
          <EmptySubtext>Usa la barra lateral para agregar personajes, escenas y elementos del script</EmptySubtext>
        </EmptyState>
        {renderScriptEditorGuide()}
      </EditorContainer>
    );
  }

  return (
    <EditorContainer ref={editorRef}>
      <ScrollHint visible={showScrollHint}>
        📜 Mouse: rueda | Teclado: ↑↓, Page Up/Down, Home/End, Espacio
      </ScrollHint>
      
      <div style={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', marginBottom: '20px' }}>
        <h3 style={{ 
          color: 'var(--color-primary)', 
          fontSize: '24px', 
          fontWeight: '700', 
          margin: '0',
          display: 'flex',
          alignItems: 'center',
          gap: '12px'
        }}>
          📝 Editor de Script
        </h3>
        <div style={{ display: 'flex', gap: '12px', alignItems: 'center' }}>
          {isGridMode && (
            <div style={{
              background: 'linear-gradient(135deg, var(--color-success) 0%, var(--color-successHover) 100%)',
              color: 'white',
              padding: '6px 12px',
              borderRadius: '16px',
              fontSize: '11px',
              fontWeight: '600',
              display: 'flex',
              alignItems: 'center',
              gap: '6px',
              boxShadow: '0 2px 8px rgba(34, 197, 94, 0.3)'
            }}>
              📊 Vista Grid Activa
            </div>
          )}
          <button
            onClick={() => setShowGuide(!showGuide)}
            style={{
              background: 'linear-gradient(135deg, var(--color-primary) 0%, var(--color-primaryHover) 100%)',
              color: 'white',
              border: '2px solid var(--color-primary)',
              padding: '8px 12px',
              borderRadius: '8px',
              cursor: 'pointer',
              fontSize: '12px',
              fontWeight: '600',
              transition: 'all 0.3s ease'
            }}
          >
            {showGuide ? '❌ Ocultar Guía' : '❓ Ver Guía'}
          </button>
        </div>
      </div>

      {showGuide && renderScriptEditorGuide()}

       <ScriptList isGridMode={isGridMode} columns={columns}>
         {isGridMode ? (
           // Modo grid: agrupar por escenas
           elementsGroupedByScenes.map((group, index) => (
             <SceneGroupComponent
               key={group.id || index}
               group={group}
               onElementClick={handleElementClick}
               onElementDoubleClick={handleElementDoubleClick}
               onMoveUp={handleMoveUp}
               onMoveDown={handleMoveDown}
               onDelete={handleDeleteElement}
               onRenderContent={renderElementContent}
               onRenderEditForm={renderEditForm}
               selectedElement={selectedElement}
               editingElement={editingElement}
               zoomLevel={zoomLevel}
             />
           ))
         ) : (
           // Modo normal: columna única
           currentElements.map((element, index) => (
             <ScriptElementItem
               key={element.id}
               element={element}
               index={index}
               isSelected={selectedElement?.id === element.id}
               isEditing={editingElement?.id === element.id}
               zoomLevel={zoomLevel}
               isGridMode={isGridMode}
               onElementClick={handleElementClick}
               onElementDoubleClick={handleElementDoubleClick}
               onMoveUp={handleMoveUp}
               onMoveDown={handleMoveDown}
               onDelete={handleDeleteElement}
               onRenderContent={renderElementContent}
               onRenderEditForm={renderEditForm}
             />
           ))
         )}
      </ScriptList>
      
      <ScrollIndicator visible={isScrolling}>
        🖱️ Navegando por el script...
      </ScrollIndicator>
      
      {/* Botón flotante de zoom */}
      <ZoomToggleButton onClick={toggleZoomControl}>
        🔍
      </ZoomToggleButton>
      
      {/* Control de zoom */}
      <ZoomControl visible={showZoomControl}>
        <ZoomHeader>
          <ZoomTitle>
            🔍 Control de Zoom
          </ZoomTitle>
          <ZoomCloseButton onClick={() => setShowZoomControl(false)}>
            ×
          </ZoomCloseButton>
        </ZoomHeader>
        
        <ZoomSliderContainer>
          <ZoomSlider
            type="range"
            min="25"
            max="200"
            value={zoomLevel}
            onChange={(e) => handleZoomChange(parseInt(e.target.value))}
          />
          <ZoomValue>
            {zoomLevel}%
          </ZoomValue>
        </ZoomSliderContainer>
        
        <ZoomPresets>
          <ZoomPresetButton
            active={zoomLevel === 25}
            onClick={() => handleZoomPreset(25)}
          >
            25%
          </ZoomPresetButton>
          <ZoomPresetButton
            active={zoomLevel === 50}
            onClick={() => handleZoomPreset(50)}
          >
            50%
          </ZoomPresetButton>
          <ZoomPresetButton
            active={zoomLevel === 75}
            onClick={() => handleZoomPreset(75)}
          >
            75%
          </ZoomPresetButton>
          <ZoomPresetButton
            active={zoomLevel === 100}
            onClick={() => handleZoomPreset(100)}
          >
            100%
          </ZoomPresetButton>
          <ZoomPresetButton
            active={zoomLevel === 125}
            onClick={() => handleZoomPreset(125)}
          >
            125%
          </ZoomPresetButton>
          <ZoomPresetButton
            active={zoomLevel === 150}
            onClick={() => handleZoomPreset(150)}
          >
            150%
          </ZoomPresetButton>
        </ZoomPresets>
      </ZoomControl>
      
      {/* Menú contextual para inserción de elementos */}
      {contextMenuElement && (
        <ContextMenuOverlay onClick={() => setContextMenuElement(null)}>
          <ContextMenuWindow onClick={(e) => e.stopPropagation()}>
            <ContextMenuHeader>
              <ContextMenuTitle>
                ➕ Insertar Elemento
              </ContextMenuTitle>
              <ContextMenuCloseButton onClick={() => setContextMenuElement(null)}>
                ×
              </ContextMenuCloseButton>
            </ContextMenuHeader>
            
            <ContextMenuText>
              ¿Qué tipo de elemento quieres insertar <strong>después</strong> de "{contextMenuElement.type}"?
            </ContextMenuText>
            
            <ContextMenuButtons>
              <ContextMenuButton
                onClick={() => handleInsertElementAfter('dialogue', contextMenuElement.id)}
                color="var(--color-success)"
                textColor="white"
                borderColor="var(--color-success)"
                hoverColor="var(--color-successHover)"
                hoverBorderColor="var(--color-successHover)"
              >
                💬 Diálogo
              </ContextMenuButton>
              
              <ContextMenuButton
                onClick={() => handleInsertElementAfter('narration', contextMenuElement.id)}
                color="var(--color-info)"
                textColor="white"
                borderColor="var(--color-info)"
                hoverColor="var(--color-infoHover)"
                hoverBorderColor="var(--color-infoHover)"
              >
                📝 Narración
              </ContextMenuButton>
              
              <ContextMenuButton
                onClick={() => handleInsertElementAfter('choice', contextMenuElement.id)}
                color="var(--color-warning)"
                textColor="white"
                borderColor="var(--color-warning)"
                hoverColor="var(--color-warningHover)"
                hoverBorderColor="var(--color-warningHover)"
              >
                ❓ Elección
              </ContextMenuButton>
              
              <ContextMenuButton
                onClick={() => handleInsertElementAfter('scene', contextMenuElement.id)}
                color="var(--color-primary)"
                textColor="white"
                borderColor="var(--color-primary)"
                hoverColor="var(--color-primaryHover)"
                hoverBorderColor="var(--color-primaryHover)"
              >
                🎭 Escena
              </ContextMenuButton>
            </ContextMenuButtons>
            
            <ContextMenuActions>
              <ContextMenuActionButton onClick={() => setContextMenuElement(null)}>
                Cancelar
              </ContextMenuActionButton>
            </ContextMenuActions>
          </ContextMenuWindow>
        </ContextMenuOverlay>
      )}
    </EditorContainer>
  );
}

export default ScriptEditor;
