import React, { useState } from 'react';
import styled from 'styled-components';

const BasicCustomizationContainer = styled.div`
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: var(--color-background);
  z-index: 3000;
  overflow-y: auto;
  transition: background-color 0.3s ease;
`;

const BasicCustomizationHeader = styled.div`
  background-color: var(--color-surface);
  padding: 20px;
  border-bottom: 2px solid var(--color-border);
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: sticky;
  top: 0;
  z-index: 3001;
  transition: background-color 0.3s ease, border-color 0.3s ease;
`;

const BasicCustomizationTitle = styled.h1`
  margin: 0;
  color: var(--color-success);
  font-size: 32px;
  font-weight: 800;
  display: flex;
  align-items: center;
  gap: 15px;
  transition: color 0.3s ease;
  text-shadow: 0 2px 4px var(--color-shadow);
  letter-spacing: 0.5px;
`;

const CloseButton = styled.button`
  background-color: var(--color-error);
  color: var(--color-textInverse);
  border: none;
  border-radius: 8px;
  padding: 12px 20px;
  cursor: pointer;
  font-size: 16px;
  font-weight: bold;
  transition: all 0.3s ease;
  box-shadow: 0 4px 12px var(--color-shadow);

  &:hover {
    background-color: var(--color-errorHover);
    transform: scale(1.05);
    box-shadow: 0 6px 16px var(--color-shadowHover);
  }
`;

const BasicCustomizationContent = styled.div`
  padding: 30px;
  max-width: 1200px;
  margin: 0 auto;
`;

const SearchBox = styled.div`
  background-color: var(--color-surfaceSecondary);
  border: 2px solid var(--color-border);
  border-radius: 12px;
  padding: 25px;
  margin-bottom: 30px;
  transition: background-color 0.3s ease, border-color 0.3s ease;
  box-shadow: 0 4px 12px var(--color-shadow);

  &:hover {
    border-color: var(--color-primary);
    box-shadow: 0 6px 16px var(--color-shadowHover);
  }
`;

const SearchTitle = styled.h2`
  color: var(--color-success);
  margin: 0 0 20px 0;
  font-size: 22px;
  font-weight: 800;
  display: flex;
  align-items: center;
  gap: 12px;
  transition: color 0.3s ease;
  text-shadow: 0 1px 3px var(--color-shadow);
  letter-spacing: 0.3px;
`;

const SearchInput = styled.input`
  width: 100%;
  padding: 12px 16px;
  background-color: var(--color-backgroundSecondary);
  border: 2px solid var(--color-border);
  border-radius: 8px;
  color: var(--color-textPrimary);
  font-size: 16px;
  font-weight: 500;
  transition: all 0.3s ease;
  box-shadow: 0 2px 6px var(--color-shadow);

  &:focus {
    outline: none;
    border-color: var(--color-success);
    box-shadow: 0 4px 12px var(--color-shadowHover);
    transform: translateY(-1px);
  }

  &:hover {
    border-color: var(--color-primary);
    box-shadow: 0 3px 8px var(--color-shadowHover);
  }

  &::placeholder {
    color: var(--color-textTertiary);
    font-weight: 400;
  }
`;

const CategoryTabs = styled.div`
  display: flex;
  gap: 10px;
  margin-bottom: 30px;
  flex-wrap: wrap;
`;

const CategoryTab = styled.button`
  background-color: ${props => props.active ? 'var(--color-success)' : 'var(--color-surfaceSecondary)'};
  color: ${props => props.active ? 'var(--color-textInverse)' : 'var(--color-textPrimary)'};
  border: 2px solid ${props => props.active ? 'var(--color-success)' : 'var(--color-border)'};
  padding: 14px 22px;
  border-radius: 8px;
  cursor: pointer;
  font-size: 14px;
  font-weight: 700;
  transition: all 0.3s ease;
  box-shadow: 0 3px 8px var(--color-shadow);

  &:hover {
    background-color: ${props => props.active ? 'var(--color-successHover)' : 'var(--color-border)'};
    border-color: ${props => props.active ? 'var(--color-successHover)' : 'var(--color-primary)'};
    color: var(--color-textInverse);
    transform: translateY(-3px);
    box-shadow: 0 6px 16px var(--color-shadowHover);
  }

  &:active {
    transform: translateY(-1px);
    box-shadow: 0 4px 12px var(--color-shadowHover);
  }
`;

const CustomizationGrid = styled.div`
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(400px, 1fr));
  gap: 25px;
  margin-bottom: 40px;
`;

const CustomizationCard = styled.div`
  background-color: var(--color-surfaceSecondary);
  border: 2px solid var(--color-border);
  border-radius: 12px;
  padding: 24px;
  transition: all 0.3s ease;
  box-shadow: 0 4px 12px var(--color-shadow);

  &:hover {
    border-color: var(--color-success);
    background-color: var(--color-surfaceElevated);
    transform: translateY(-8px);
    box-shadow: 0 12px 32px var(--color-shadowHover);
  }
`;

const CustomizationHeader = styled.div`
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  margin-bottom: 15px;
`;

const CustomizationTitle = styled.h3`
  margin: 0;
  color: var(--color-success);
  font-size: 20px;
  font-weight: 800;
  display: flex;
  align-items: center;
  gap: 12px;
  transition: color 0.3s ease;
  text-shadow: 0 2px 4px var(--color-shadow);
  letter-spacing: 0.3px;
`;

const CustomizationType = styled.span`
  background-color: ${props => {
    switch(props.type) {
      case 'gui': return 'var(--color-info)';
      case 'config': return 'var(--color-success)';
      case 'style': return 'var(--color-warning)';
      case 'screen': return 'var(--color-primary)';
      case 'image': return 'var(--color-border)';
      default: return 'var(--color-borderSecondary)';
    }
  }};
  color: var(--color-textInverse);
  padding: 6px 12px;
  border-radius: 6px;
  font-size: 12px;
  font-weight: 700;
  transition: all 0.3s ease;
  box-shadow: 0 2px 6px var(--color-shadow);
  border: 1px solid rgba(255, 255, 255, 0.1);

  &:hover {
    transform: translateY(-1px);
    box-shadow: 0 4px 8px var(--color-shadowHover);
  }
`;

const CustomizationDescription = styled.p`
  color: var(--color-textPrimary);
  margin: 0 0 18px 0;
  line-height: 1.7;
  font-size: 16px;
  font-weight: 600;
  transition: color 0.3s ease;
  text-shadow: 0 1px 2px var(--color-shadow);
`;

const CustomizationTags = styled.div`
  display: flex;
  flex-wrap: wrap;
  gap: 8px;
  margin: 15px 0;
`;

const Tag = styled.span`
  background-color: var(--color-border);
  color: var(--color-textInverse);
  padding: 6px 10px;
  border-radius: 6px;
  font-size: 12px;
  font-weight: 600;
  transition: all 0.3s ease;
  box-shadow: 0 2px 4px var(--color-shadow);
  border: 1px solid rgba(255, 255, 255, 0.1);

  &:hover {
    background-color: var(--color-primary);
    transform: translateY(-1px);
    box-shadow: 0 3px 6px var(--color-shadowHover);
  }
`;

const CodeExample = styled.div`
  background-color: var(--color-backgroundSecondary);
  border: 3px solid var(--color-primary);
  border-radius: 10px;
  padding: 20px;
  margin: 18px 0;
  font-family: 'Courier New', monospace;
  font-size: 14px;
  font-weight: 600;
  color: var(--color-primary);
  overflow-x: auto;
  transition: all 0.3s ease;
  box-shadow: 0 4px 12px var(--color-shadow);
  position: relative;

  &:hover {
    border-color: var(--color-primaryHover);
    box-shadow: 0 6px 16px var(--color-shadowHover);
    transform: translateY(-2px);
  }

  &::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    height: 3px;
    background: linear-gradient(90deg, var(--color-primary), var(--color-primaryHover));
    border-radius: 10px 10px 0 0;
  }
`;

const InfoBox = styled.div`
  background-color: var(--color-surfaceSecondary);
  border: 3px solid var(--color-success);
  border-radius: 14px;
  padding: 24px;
  margin: 24px 0;
  transition: all 0.3s ease;
  box-shadow: 0 6px 20px var(--color-shadow);
  position: relative;

  &:hover {
    border-color: var(--color-successHover);
    box-shadow: 0 8px 24px var(--color-shadowHover);
    transform: translateY(-3px);
  }

  &::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    height: 4px;
    background: linear-gradient(90deg, var(--color-success), var(--color-successHover));
    border-radius: 14px 14px 0 0;
  }
`;

const InfoTitle = styled.div`
  color: var(--color-success);
  font-weight: 800;
  font-size: 18px;
  margin-bottom: 16px;
  display: flex;
  align-items: center;
  gap: 10px;
  transition: color 0.3s ease;
  text-shadow: 0 2px 4px var(--color-shadow);
  letter-spacing: 0.3px;
`;

const InfoText = styled.div`
  color: var(--color-textPrimary);
  font-size: 16px;
  line-height: 1.7;
  font-weight: 600;
  transition: color 0.3s ease;
  text-shadow: 0 1px 2px var(--color-shadow);
`;

const TipBox = styled.div`
  background-color: var(--color-surfaceSecondary);
  border: 3px solid var(--color-warning);
  border-radius: 14px;
  padding: 24px;
  margin: 24px 0;
  transition: all 0.3s ease;
  box-shadow: 0 6px 20px var(--color-shadow);
  position: relative;

  &:hover {
    border-color: var(--color-warningHover);
    box-shadow: 0 8px 24px var(--color-shadowHover);
    transform: translateY(-3px);
  }

  &::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    height: 4px;
    background: linear-gradient(90deg, var(--color-warning), var(--color-warningHover));
    border-radius: 14px 14px 0 0;
  }
`;

const TipTitle = styled.div`
  color: var(--color-warning);
  font-weight: 800;
  font-size: 18px;
  margin-bottom: 16px;
  display: flex;
  align-items: center;
  gap: 10px;
  transition: color 0.3s ease;
  text-shadow: 0 2px 4px var(--color-shadow);
  letter-spacing: 0.3px;
`;

const TipText = styled.div`
  color: var(--color-textPrimary);
  font-size: 16px;
  line-height: 1.7;
  font-weight: 600;
  transition: color 0.3s ease;
  text-shadow: 0 1px 2px var(--color-shadow);
`;

const WarningBox = styled.div`
  background-color: var(--color-surfaceSecondary);
  border: 3px solid var(--color-error);
  border-radius: 14px;
  padding: 24px;
  margin: 24px 0;
  transition: all 0.3s ease;
  box-shadow: 0 6px 20px var(--color-shadow);
  position: relative;

  &:hover {
    border-color: var(--color-errorHover);
    box-shadow: 0 8px 24px var(--color-shadowHover);
    transform: translateY(-3px);
  }

  &::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    height: 4px;
    background: linear-gradient(90deg, var(--color-error), var(--color-errorHover));
    border-radius: 14px 14px 0 0;
  }
`;

const WarningTitle = styled.div`
  color: var(--color-error);
  font-weight: 800;
  font-size: 18px;
  margin-bottom: 16px;
  display: flex;
  align-items: center;
  gap: 10px;
  transition: color 0.3s ease;
  text-shadow: 0 2px 4px var(--color-shadow);
  letter-spacing: 0.3px;
`;

const WarningText = styled.div`
  color: var(--color-textPrimary);
  font-size: 16px;
  line-height: 1.7;
  font-weight: 600;
  transition: color 0.3s ease;
  text-shadow: 0 1px 2px var(--color-shadow);
`;

function BasicCustomizationPage({ onClose }) {
  const [activeCategory, setActiveCategory] = useState('gui');
  const [searchTerm, setSearchTerm] = useState('');

  const customizationData = {
    gui: [
      {
        id: 'gui-colors',
        title: 'Colores y Tamaños',
        icon: '🎨',
        type: 'gui',
        description: 'Personalización básica de colores, tamaños y apariencia general de la interfaz.',
        tags: ['colores', 'tamaño', 'interfaz', 'básico'],
        codeExample: `# En options.rpy
define config.screen_width = 1920
define config.screen_height = 1080
define gui.accent_color = '#ff6b6b'
define gui.idle_color = '#888888'`
      },
      {
        id: 'gui-fonts',
        title: 'Fuentes y Texto',
        icon: '📝',
        type: 'gui',
        description: 'Configuración de fuentes, tamaños de texto y estilos tipográficos.',
        tags: ['fuentes', 'texto', 'tipografía', 'estilos'],
        codeExample: `# En gui.rpy
define gui.text_font = "DejaVuSans.ttf"
define gui.name_text_size = 45
define gui.text_size = 33
define gui.history_text_size = 30`
      },
      {
        id: 'gui-buttons',
        title: 'Botones y Controles',
        icon: '🔘',
        type: 'gui',
        description: 'Personalización de botones, menús y elementos de control.',
        tags: ['botones', 'menús', 'controles', 'interfaz'],
        codeExample: `# En gui.rpy
define gui.button_text_size = 33
define gui.button_width = 420
define gui.button_height = None
define gui.button_borders = Borders(6, 6, 6, 6)`
      }
    ],
    config: [
      {
        id: 'config-basic',
        title: 'Configuración Básica',
        icon: '⚙️',
        type: 'config',
        description: 'Configuración fundamental del juego como título, resolución y opciones básicas.',
        tags: ['configuración', 'título', 'resolución', 'básico'],
        codeExample: `# En options.rpy
define config.name = _("Mi Juego")
define config.version = "1.0"
define config.screen_width = 1920
define config.screen_height = 1080
define config.window = "auto"`
      },
      {
        id: 'config-audio',
        title: 'Configuración de Audio',
        icon: '🔊',
        type: 'config',
        description: 'Configuración de canales de audio, volúmenes y efectos de sonido.',
        tags: ['audio', 'volumen', 'canales', 'sonido'],
        codeExample: `# En options.rpy
define config.has_sound = True
define config.has_music = True
define config.has_voice = True
define config.voice_tag = "voice"`
      },
      {
        id: 'config-save',
        title: 'Sistema de Guardado',
        icon: '💾',
        type: 'config',
        description: 'Configuración del sistema de guardado y carga de partidas.',
        tags: ['guardado', 'carga', 'partidas', 'sistema'],
        codeExample: `# En options.rpy
define config.auto_voice = "voice"
define config.has_quicksave = True
define config.has_autosave = True
define config.autosave_frequency = 250`
      }
    ],
    styles: [
      {
        id: 'style-basic',
        title: 'Estilos Básicos',
        icon: '🎭',
        type: 'style',
        description: 'Definición de estilos básicos para texto, botones y elementos de interfaz.',
        tags: ['estilos', 'básicos', 'interfaz', 'diseño'],
        codeExample: `# En screens.rpy
style default:
    font "DejaVuSans.ttf"
    size 33
    color "#fff"
    outlines [(2, "#000", 0, 0)]`
      },
      {
        id: 'style-buttons',
        title: 'Estilos de Botones',
        icon: '🔲',
        type: 'style',
        description: 'Personalización de estilos para diferentes tipos de botones.',
        tags: ['botones', 'estilos', 'interfaz', 'personalización'],
        codeExample: `# En screens.rpy
style button:
    background Frame("gui/button/idle.png", Borders(6, 6, 6, 6))
    hover_background Frame("gui/button/hover.png", Borders(6, 6, 6, 6))
    padding (25, 25, 25, 25)`
      },
      {
        id: 'style-text',
        title: 'Estilos de Texto',
        icon: '📄',
        type: 'style',
        description: 'Configuración de estilos para diferentes tipos de texto en el juego.',
        tags: ['texto', 'estilos', 'tipografía', 'formato'],
        codeExample: `# En screens.rpy
style say_label:
    font "DejaVuSans-Bold.ttf"
    size 45
    color "#fff"
    outlines [(2, "#000", 0, 0)]`
      }
    ],
    images: [
      {
        id: 'image-backgrounds',
        title: 'Fondos y Escenarios',
        icon: '🖼️',
        type: 'image',
        description: 'Configuración y uso de imágenes de fondo y escenarios.',
        tags: ['fondos', 'escenarios', 'imágenes', 'visual'],
        codeExample: `# En script.rpy
scene bg room
show bg room at center
with fade`
      },
      {
        id: 'image-characters',
        title: 'Sprites de Personajes',
        icon: '👤',
        type: 'image',
        description: 'Configuración y uso de sprites de personajes.',
        tags: ['personajes', 'sprites', 'imágenes', 'visual'],
        codeExample: `# En script.rpy
show eileen happy at center
show eileen concerned at right
hide eileen`
      },
      {
        id: 'image-gui',
        title: 'Elementos de GUI',
        icon: '🖥️',
        type: 'image',
        description: 'Imágenes para elementos de interfaz como botones y marcos.',
        tags: ['GUI', 'interfaz', 'botones', 'elementos'],
        codeExample: `# En gui.rpy
define gui.main_menu_background = "gui/main_menu.png"
define gui.game_menu_background = "gui/game_menu.png"`
      }
    ]
  };

  const getCurrentCustomization = () => {
    let currentCustomization = customizationData[activeCategory] || [];
    
    if (searchTerm) {
      currentCustomization = currentCustomization.filter(item =>
        item.title.toLowerCase().includes(searchTerm.toLowerCase()) ||
        item.description.toLowerCase().includes(searchTerm.toLowerCase()) ||
        item.tags.some(tag => tag.toLowerCase().includes(searchTerm.toLowerCase()))
      );
    }
    
    return currentCustomization;
  };

  const getTypeText = (type) => {
    switch(type) {
      case 'gui': return 'GUI';
      case 'config': return 'Config';
      case 'style': return 'Estilo';
      case 'image': return 'Imagen';
      default: return 'Otro';
    }
  };

  return (
    <BasicCustomizationContainer>
      <BasicCustomizationHeader>
        <BasicCustomizationTitle>
          🎨 Personalización Básica
        </BasicCustomizationTitle>
        <CloseButton onClick={onClose}>
          ✕ Cerrar
        </CloseButton>
      </BasicCustomizationHeader>

      <BasicCustomizationContent>
        <SearchBox>
          <SearchTitle>
            🔍 Buscar en Personalización
          </SearchTitle>
          <SearchInput
            type="text"
            placeholder="Busca configuraciones, estilos o elementos específicos..."
            value={searchTerm}
            onChange={(e) => setSearchTerm(e.target.value)}
          />
        </SearchBox>

        <CategoryTabs>
          <CategoryTab 
            active={activeCategory === 'gui'} 
            onClick={() => setActiveCategory('gui')}
          >
            🎨 GUI
          </CategoryTab>
          <CategoryTab 
            active={activeCategory === 'config'} 
            onClick={() => setActiveCategory('config')}
          >
            ⚙️ Configuración
          </CategoryTab>
          <CategoryTab 
            active={activeCategory === 'styles'} 
            onClick={() => setActiveCategory('styles')}
          >
            🎭 Estilos
          </CategoryTab>
          <CategoryTab 
            active={activeCategory === 'images'} 
            onClick={() => setActiveCategory('images')}
          >
            🖼️ Imágenes
          </CategoryTab>
        </CategoryTabs>

        <CustomizationGrid>
          {getCurrentCustomization().map((item) => (
            <CustomizationCard key={item.id}>
              <CustomizationHeader>
                <CustomizationTitle>
                  {item.icon} {item.title}
                </CustomizationTitle>
                <CustomizationType type={item.type}>
                  {getTypeText(item.type)}
                </CustomizationType>
              </CustomizationHeader>
              
              <CustomizationDescription>
                {item.description}
              </CustomizationDescription>

              {item.codeExample && (
                <CodeExample>
                  {item.codeExample}
                </CodeExample>
              )}

              <CustomizationTags>
                {item.tags.map((tag, index) => (
                  <Tag key={index}>{tag}</Tag>
                ))}
              </CustomizationTags>
            </CustomizationCard>
          ))}
        </CustomizationGrid>

        {getCurrentCustomization().length === 0 && searchTerm && (
          <div style={{ 
            textAlign: 'center', 
            color: 'var(--color-textTertiary)', 
            fontSize: '18px',
            fontWeight: '600',
            marginTop: '50px',
            textShadow: '0 1px 2px var(--color-shadow)'
          }}>
            No se encontraron elementos que coincidan con "{searchTerm}"
          </div>
        )}

        <InfoBox>
          <InfoTitle>
            📚 Información Importante
          </InfoTitle>
          <InfoText>
            La personalización básica de Ren'Py se realiza principalmente en los archivos options.rpy, gui.rpy y screens.rpy. 
            Estos archivos controlan la apariencia y comportamiento fundamental del juego según la documentación oficial.
          </InfoText>
        </InfoBox>

        <TipBox>
          <TipTitle>
            💡 Consejo de Personalización
          </TipTitle>
          <TipText>
            Comienza con la personalización básica antes de avanzar a modificaciones más complejas. 
            Los archivos gui.rpy y options.rpy son ideales para empezar a personalizar tu juego.
          </TipText>
        </TipBox>

        <WarningBox>
          <WarningTitle>
            ⚠️ Importante
          </WarningTitle>
          <WarningText>
            Siempre haz una copia de seguridad de tus archivos antes de modificarlos. 
            Algunos cambios pueden afectar la funcionalidad del juego si no se implementan correctamente.
          </WarningText>
        </WarningBox>

        <InfoBox>
          <InfoTitle>
            🔗 Recursos Adicionales
          </InfoTitle>
          <InfoText>
            Para más información detallada sobre personalización, consulta la documentación oficial de Ren'Py:
            <br />• <a href="https://www.renpy.org/doc/html/gui.html" target="_blank" rel="noopener noreferrer" style={{color: 'var(--color-primary)', fontWeight: '600', textShadow: '0 1px 2px var(--color-shadow)'}}>GUI Customization Guide</a>
            <br />• <a href="https://www.renpy.org/doc/html/quickstart.html" target="_blank" rel="noopener noreferrer" style={{color: 'var(--color-primary)', fontWeight: '600', textShadow: '0 1px 2px var(--color-shadow)'}}>Quickstart Guide</a>
            <br />• <a href="https://lemmasoft.renai.us/forums/viewforum.php?f=51" target="_blank" rel="noopener noreferrer" style={{color: 'var(--color-primary)', fontWeight: '600', textShadow: '0 1px 2px var(--color-shadow)'}}>Ren'Py Cookbook</a>
          </InfoText>
        </InfoBox>
      </BasicCustomizationContent>
    </BasicCustomizationContainer>
  );
}

export default BasicCustomizationPage;

