import React, { useState, useEffect } from 'react';
import styled from 'styled-components';
import { DndProvider } from 'react-dnd';
import { HTML5Backend } from 'react-dnd-html5-backend';
import { VisualNovelProvider } from './context/VisualNovelContext';
import { ModalProvider } from './context/ModalContext';
import { NotificationProvider } from './context/NotificationContext';
import { initializeTheme } from './utils/themeManager';
import Sidebar from './components/Sidebar';
import MainEditor from './components/MainEditor';
import PreviewPanel from './components/PreviewPanel';
import TopBar from './components/TopBar';
import FloatingButtonsManager from './components/FloatingButtonsManager';
import InfoModalContent from './components/modals/InfoModalContent';
import OptionsModalContent from './components/modals/OptionsModalContent';
import HelpModalContent from './components/modals/HelpModalContent';
import AboutModalContent from './components/modals/AboutModalContent';
import TemplatesPage from './components/TemplatesPage';
import TroubleshootingPage from './components/TroubleshootingPage';
import BeginnersGuidePage from './components/BeginnersGuidePage';
import ResourcesPage from './components/ResourcesPage';
import ProjectStructurePage from './components/ProjectStructurePage';
import BasicCustomizationPage from './components/BasicCustomizationPage';
import TroubleshootingPage2 from './components/TroubleshootingPage';
import GlossaryPage from './components/GlossaryPage';
import GuidePage from './components/GuidePage';
import { FileManagerPage } from './components/pages';
import AutoSaveIndicator from './components/AutoSaveIndicator';
import LoadingScreen from './components/LoadingScreen';
import IntroScreen from './components/IntroScreen';

const AppContainer = styled.div`
  display: flex;
  flex-direction: column;
  height: 100vh;
  background-color: var(--color-background);
  color: var(--color-textPrimary);
  transition: background-color 0.3s ease, color 0.3s ease;
`;

const MainContent = styled.div`
  display: flex;
  flex: 1;
  overflow: hidden;
  margin-left: 60px; /* Espacio base para el sidebar */
  transition: margin-left 0.4s cubic-bezier(0.4, 0, 0.2, 1);
  
  /* Ajustar margen según el estado del sidebar */
  @media (min-width: 768px) {
    margin-left: 60px;
  }
`;

const EditorContainer = styled.div`
  display: flex;
  flex: 1;
  background-color: var(--color-backgroundSecondary);
  transition: background-color 0.3s ease;
`;

function App() {
  const [showIntro, setShowIntro] = useState(true);
  
  // Debug: Log del estado
  console.log('App render - showIntro:', showIntro);
  const [showTemplatesPage, setShowTemplatesPage] = useState(false);
  const [showTroubleshootingPage, setShowTroubleshootingPage] = useState(false);
  const [showBeginnersGuidePage, setShowBeginnersGuidePage] = useState(false);
  const [showResourcesPage, setShowResourcesPage] = useState(false);
  const [showProjectStructurePage, setShowProjectStructurePage] = useState(false);

  // Inicializar tema al cargar la aplicación
  useEffect(() => {
    initializeTheme();
  }, []);
  const [showBasicCustomizationPage, setShowBasicCustomizationPage] = useState(false);
  const [showTroubleshootingPage2, setShowTroubleshootingPage2] = useState(false);
  const [showGlossaryPage, setShowGlossaryPage] = useState(false);
  const [showGuidePage, setShowGuidePage] = useState(false);
  const [showFileManagerPage, setShowFileManagerPage] = useState(false);

  const handleNavigateToTemplates = () => {
    setShowTemplatesPage(true);
  };

  const handleCloseTemplates = () => {
    setShowTemplatesPage(false);
  };

  const handleNavigateToTroubleshooting = () => {
    setShowTroubleshootingPage(true);
  };

  const handleCloseTroubleshooting = () => {
    setShowTroubleshootingPage(false);
  };

  const handleNavigateToBeginnersGuide = () => {
    setShowBeginnersGuidePage(true);
  };

  const handleCloseBeginnersGuide = () => {
    setShowBeginnersGuidePage(false);
  };

  const handleNavigateToResources = () => {
    setShowResourcesPage(true);
  };

  const handleCloseResources = () => {
    setShowResourcesPage(false);
  };

  const handleNavigateToProjectStructure = () => {
    setShowProjectStructurePage(true);
  };

  const handleCloseProjectStructure = () => {
    setShowProjectStructurePage(false);
  };

  const handleNavigateToBasicCustomization = () => {
    setShowBasicCustomizationPage(true);
  };

  const handleCloseBasicCustomization = () => {
    setShowBasicCustomizationPage(false);
  };

  const handleNavigateToTroubleshooting2 = () => {
    setShowTroubleshootingPage2(true);
  };

  const handleCloseTroubleshooting2 = () => {
    setShowTroubleshootingPage2(false);
  };

  const handleNavigateToGlossary = () => {
    setShowGlossaryPage(true);
  };

  const handleCloseGlossary = () => {
    setShowGlossaryPage(false);
  };

  const handleNavigateToGuide = () => {
    setShowGuidePage(true);
  };

  const handleCloseGuide = () => {
    setShowGuidePage(false);
  };

  const handleNavigateToFileManager = () => {
    setShowFileManagerPage(true);
  };

  const handleCloseFileManager = () => {
    setShowFileManagerPage(false);
  };

  const handleIntroComplete = () => {
    console.log('handleIntroComplete called - setting showIntro to false');
    setShowIntro(false);
  };

  return (
    <VisualNovelProvider>
      <ModalProvider>
        <NotificationProvider>
          <DndProvider backend={HTML5Backend}>
          <AppContainer>
          {/* Pantalla de introducción */}
          {showIntro && <IntroScreen onComplete={handleIntroComplete} />}
          
          {!showIntro && showTemplatesPage ? (
            <TemplatesPage onClose={handleCloseTemplates} />
          ) : showTroubleshootingPage ? (
            <TroubleshootingPage onClose={handleCloseTroubleshooting} />
          ) : showBeginnersGuidePage ? (
            <BeginnersGuidePage onClose={handleCloseBeginnersGuide} />
          ) : showResourcesPage ? (
            <ResourcesPage onClose={handleCloseResources} />
          ) : showProjectStructurePage ? (
            <ProjectStructurePage onClose={handleCloseProjectStructure} />
          ) : showBasicCustomizationPage ? (
            <BasicCustomizationPage onClose={handleCloseBasicCustomization} />
          ) : showTroubleshootingPage2 ? (
            <TroubleshootingPage2 onClose={handleCloseTroubleshooting2} />
          ) : showGlossaryPage ? (
            <GlossaryPage onClose={handleCloseGlossary} />
          ) : showGuidePage ? (
            <GuidePage onClose={handleCloseGuide} />
          ) : !showIntro && showFileManagerPage ? (
            <FileManagerPage onClose={handleCloseFileManager} />
          ) : !showIntro ? (
            <>
              <TopBar onNavigateToFileManager={handleNavigateToFileManager} />
              <Sidebar />
              <MainContent>
                <EditorContainer>
                  <MainEditor />
                  <PreviewPanel />
                </EditorContainer>
              </MainContent>
              <FloatingButtonsManager
                onMenuClick={() => console.log('Menu clicked')}
                onInfoClick={() => console.log('Info modal opened')}
                onOptionsClick={() => console.log('Options modal opened')}
                onHelpClick={() => console.log('Help modal opened')}
                onAboutClick={() => console.log('About modal opened')}
                // Handlers del submenú
                onNavigateToTemplates={handleNavigateToTemplates}
                onNavigateToTroubleshooting={handleNavigateToTroubleshooting}
                onNavigateToBeginnersGuide={handleNavigateToBeginnersGuide}
                onNavigateToResources={handleNavigateToResources}
                onNavigateToProjectStructure={handleNavigateToProjectStructure}
                onNavigateToBasicCustomization={handleNavigateToBasicCustomization}
                onNavigateToTroubleshooting2={handleNavigateToTroubleshooting2}
                onNavigateToGlossary={handleNavigateToGlossary}
                onNavigateToGuide={handleNavigateToGuide}
              >
                {{
                  info: <InfoModalContent />,
                  options: <OptionsModalContent />,
                  help: <HelpModalContent />,
                  about: <AboutModalContent />
                }}
              </FloatingButtonsManager>
              
              {/* Indicador de guardado automático */}
              <AutoSaveIndicator />
            </>
          ) : null}
        </AppContainer>
        </DndProvider>
        </NotificationProvider>
      </ModalProvider>
    </VisualNovelProvider>
  );
}

export default App;
