import React, { createContext, useContext, useState, useCallback } from 'react';
import styled, { keyframes, css } from 'styled-components';

// Animaciones
const slideInRight = keyframes`
  from {
    opacity: 0;
    transform: translateX(100%);
  }
  to {
    opacity: 1;
    transform: translateX(0);
  }
`;

const slideOutRight = keyframes`
  from {
    opacity: 1;
    transform: translateX(0);
  }
  to {
    opacity: 0;
    transform: translateX(100%);
  }
`;

const fadeIn = keyframes`
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
`;

const progressBar = keyframes`
  from {
    width: 100%;
  }
  to {
    width: 0%;
  }
`;

// Styled Components
const NotificationContainer = styled.div`
  position: fixed;
  top: 20px;
  right: 20px;
  z-index: 10001;
  display: flex;
  flex-direction: column;
  gap: 12px;
  max-width: 400px;
  pointer-events: none;
  
  @media (max-width: 768px) {
    top: 10px;
    right: 10px;
    left: 10px;
    max-width: none;
  }
`;

const Notification = styled.div`
  background: var(--color-surface);
  border: 2px solid ${props => {
    switch (props.type) {
      case 'success': return 'var(--color-success)';
      case 'error': return 'var(--color-error)';
      case 'warning': return 'var(--color-warning)';
      case 'info': return 'var(--color-primary)';
      default: return 'var(--color-border)';
    }
  }};
  border-radius: 12px;
  padding: 16px 20px;
  box-shadow: 0 8px 24px var(--color-shadowElevated);
  backdrop-filter: blur(8px);
  animation: ${props => props.isClosing ? css`${slideOutRight} 0.4s ease-out` : css`${slideInRight} 0.4s ease-out`};
  pointer-events: auto;
  position: relative;
  overflow: hidden;
  
  &::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    height: 4px;
    background: ${props => {
      switch (props.type) {
        case 'success': return 'var(--color-success)';
        case 'error': return 'var(--color-error)';
        case 'warning': return 'var(--color-warning)';
        case 'info': return 'var(--color-primary)';
        default: return 'var(--color-border)';
      }
    }};
  }
`;

const NotificationHeader = styled.div`
  display: flex;
  align-items: center;
  gap: 12px;
  margin-bottom: 8px;
`;

const NotificationIcon = styled.span`
  font-size: 20px;
  line-height: 1;
`;

const NotificationTitle = styled.h4`
  margin: 0;
  color: var(--color-textPrimary);
  font-size: 14px;
  font-weight: 600;
  flex: 1;
`;

const NotificationClose = styled.button`
  background: none;
  border: none;
  color: var(--color-textSecondary);
  cursor: pointer;
  font-size: 18px;
  line-height: 1;
  padding: 4px;
  border-radius: 4px;
  transition: all 0.2s ease;
  
  &:hover {
    color: var(--color-textPrimary);
    background: var(--color-surfaceSecondary);
  }
`;

const NotificationMessage = styled.div`
  color: var(--color-textSecondary);
  font-size: 13px;
  line-height: 1.5;
  white-space: pre-line;
`;

const ProgressBar = styled.div`
  position: absolute;
  bottom: 0;
  left: 0;
  height: 3px;
  background: ${props => {
    switch (props.type) {
      case 'success': return 'var(--color-success)';
      case 'error': return 'var(--color-error)';
      case 'warning': return 'var(--color-warning)';
      case 'info': return 'var(--color-primary)';
      default: return 'var(--color-border)';
    }
  }};
  border-radius: 0 0 12px 12px;
  animation: ${props => props.duration ? css`${progressBar} ${props.duration}ms linear` : 'none'};
`;

// Context
const NotificationContext = createContext();

export const useNotification = () => {
  const context = useContext(NotificationContext);
  if (!context) {
    throw new Error('useNotification debe usarse dentro de NotificationProvider');
  }
  return context;
};

export const NotificationProvider = ({ children }) => {
  const [notifications, setNotifications] = useState([]);

  const addNotification = useCallback((notification) => {
    const id = Date.now() + Math.random();
    const duration = notification.duration || 5000;
    
    const newNotification = {
      id,
      type: notification.type || 'info',
      title: notification.title || 'Notificación',
      message: notification.message || '',
      icon: notification.icon || '📢',
      duration,
      timestamp: Date.now()
    };

    setNotifications(prev => [...prev, newNotification]);

    // Auto-remove after duration
    if (duration > 0) {
      setTimeout(() => {
        removeNotification(id);
      }, duration);
    }

    return id;
  }, []);

  const removeNotification = useCallback((id) => {
    setNotifications(prev => {
      const notification = prev.find(n => n.id === id);
      if (notification) {
        // Trigger closing animation
        notification.isClosing = true;
        setTimeout(() => {
          setNotifications(current => current.filter(n => n.id !== id));
        }, 400);
        return [...prev];
      }
      return prev.filter(n => n.id !== id);
    });
  }, []);

  const clearAllNotifications = useCallback(() => {
    setNotifications([]);
  }, []);

  // Convenience methods
  const showSuccess = useCallback((message, title = 'Éxito', icon = '✅') => {
    return addNotification({ type: 'success', title, message, icon });
  }, [addNotification]);

  const showError = useCallback((message, title = 'Error', icon = '❌') => {
    return addNotification({ type: 'error', title, message, icon, duration: 7000 });
  }, [addNotification]);

  const showWarning = useCallback((message, title = 'Advertencia', icon = '⚠️') => {
    return addNotification({ type: 'warning', title, message, icon, duration: 6000 });
  }, [addNotification]);

  const showInfo = useCallback((message, title = 'Información', icon = '📢') => {
    return addNotification({ type: 'info', title, message, icon });
  }, [addNotification]);

  const showServerStatus = useCallback((message, type = 'info', icon = '🚀') => {
    const titles = {
      success: 'Servidor Iniciado',
      error: 'Error del Servidor',
      warning: 'Advertencia del Servidor',
      info: 'Estado del Servidor'
    };
    return addNotification({ 
      type, 
      title: titles[type] || 'Servidor', 
      message, 
      icon,
      duration: type === 'error' ? 7000 : 5000
    });
  }, [addNotification]);

  const value = {
    notifications,
    addNotification,
    removeNotification,
    clearAllNotifications,
    showSuccess,
    showError,
    showWarning,
    showInfo,
    showServerStatus
  };

  return (
    <NotificationContext.Provider value={value}>
      {children}
      <NotificationContainer>
        {notifications.map(notification => (
          <Notification
            key={notification.id}
            type={notification.type}
            isClosing={notification.isClosing}
          >
            <NotificationHeader>
              <NotificationIcon>{notification.icon}</NotificationIcon>
              <NotificationTitle>{notification.title}</NotificationTitle>
              <NotificationClose onClick={() => removeNotification(notification.id)}>
                ×
              </NotificationClose>
            </NotificationHeader>
            <NotificationMessage>{notification.message}</NotificationMessage>
            {notification.duration > 0 && (
              <ProgressBar 
                type={notification.type} 
                duration={notification.duration}
              />
            )}
          </Notification>
        ))}
      </NotificationContainer>
    </NotificationContext.Provider>
  );
};

export default NotificationContext;
