import { useEffect, useRef } from 'react';

// Hook personalizado para detectar cambios en el estado y activar guardado automático
export function useAutoSave(state, storage, isLoading, setSaveStatus) {
  const prevStateRef = useRef(null);
  const saveTimeoutRef = useRef(null);

  useEffect(() => {
    // Solo ejecutar si no está cargando y hay un proyecto
    if (isLoading || !state.project.name) {
      console.log('⏸️ AutoSave: No ejecutando - condiciones no cumplidas', {
        isLoading,
        hasProject: !!state.project.name
      });
      return;
    }

    // Si es la primera vez, solo guardar la referencia
    if (!prevStateRef.current) {
      prevStateRef.current = state;
      console.log('🔄 AutoSave: Primera ejecución, guardando referencia');
      return;
    }

    // Comparar estado anterior con actual
    const prevState = prevStateRef.current;
    const hasChanges = JSON.stringify(prevState) !== JSON.stringify(state);

    if (hasChanges) {
      console.log('🔄 AutoSave: Cambios detectados', {
        prevCharacters: prevState.project.characters.length,
        newCharacters: state.project.characters.length,
        prevElements: prevState.project.sceneFiles[0]?.elements.length || 0,
        newElements: state.project.sceneFiles[0]?.elements.length || 0,
        prevScene: prevState.currentScene,
        newScene: state.currentScene,
        prevSelected: prevState.selectedElement?.type,
        newSelected: state.selectedElement?.type
      });

      // Limpiar timeout anterior si existe
      if (saveTimeoutRef.current) {
        clearTimeout(saveTimeoutRef.current);
      }

      // Programar guardado con debounce
      saveTimeoutRef.current = setTimeout(() => {
        console.log('💾 AutoSave: Ejecutando guardado automático...');
        
        try {
          setSaveStatus('saving');
          const success = storage.saveFullState(state);
          
          if (success) {
            setSaveStatus('saved');
            console.log('✅ AutoSave: Estado guardado exitosamente');
          } else {
            setSaveStatus('error');
            console.error('❌ AutoSave: Error al guardar estado');
          }
        } catch (error) {
          setSaveStatus('error');
          console.error('❌ AutoSave: Error al guardar estado:', error);
        }
      }, 1000); // 1 segundo de debounce
    } else {
      console.log('⏸️ AutoSave: No hay cambios detectados');
    }

    // Actualizar referencia del estado anterior
    prevStateRef.current = state;
  }, [state, storage, isLoading, setSaveStatus]);

  // Limpiar timeout al desmontar
  useEffect(() => {
    return () => {
      if (saveTimeoutRef.current) {
        clearTimeout(saveTimeoutRef.current);
      }
    };
  }, []);
}
